import { StyleSheet } from 'react-native';
import { FileThumbnailSize } from '../../types';

export const getThumbnailSize = (size: FileThumbnailSize) => {
  switch (size) {
    case 'small':
      return 32;
    case 'medium':
      return 48;
    case 'large':
      return 80;
    default:
      return 48;
  }
};

export const styles = StyleSheet.create({
  container: {
    width: '100%',
    height: '100%',
    borderRadius: 8,
    overflow: 'hidden',
  },
}); 