import { useCallback, useEffect } from 'react';
import { logger } from '../utils';
import { useDispatch } from 'react-redux';

interface UseTheTestProps {
  trainingId: string;
}

export const useTheTest = (props: UseTheTestProps) => {
  const { trainingId } = props;
  const dispatch = useDispatch();

  const fetchTrainingQuestions = useCallback(async () => {
    try {
      const redux = await import('auth/redux');
      dispatch(redux.actionContainer.getAllTrainingTestAsync(trainingId));
    } catch (error) {
      logger('error fetchTrainingQuestions', error);
    }
  }, [dispatch, trainingId]);

  useEffect(() => {
    fetchTrainingQuestions();
  }, [fetchTrainingQuestions]);
};
