import * as Yup from 'yup';

export const getBankInfoSchema = (t: any) =>
  Yup.object().shape({
    BANK_NAME: Yup.string()
      .required(t('BANK_NAME_CANNOT_BE_EMPTY'))
      .matches(
        /^[A-Za-z\s]+$/,
        t('FIELD_INVALID_FORMAT', {
          field: t('BANK_NAME'),
          format: t('REGEX_ONLY_ALLOW_LETTERS').toLowerCase(),
        }),
      )
      .min(
        3,
        t('FIELD_MIN_LENGTH', {
          field: t('BANK_NAME'),
          number: 3,
        }),
      )
      .max(
        150,
        t('FIELD_MAX_LENGTH', {
          field: t('BANK_NAME'),
          number: 150,
        }),
      ),
    ACCOUNT_NUMBER: Yup.string()
      .required(t('ACCOUNT_NUMBER_CANNOT_BE_EMPTY'))
      .matches(
        /^[0-9]+$/,
        t('FIELD_INVALID_FORMAT', {
          field: t('ACCOUNT_NUMBER'),
          format: t('REGEX_ONLY_ALLOW_NUMBERS').toLowerCase(),
        }),
      )
      .min(
        5,
        t('FIELD_MIN_LENGTH', {
          field: t('ACCOUNT_NUMBER'),
          number: 5,
        }),
      ),
    ROUTING_NUMBER: Yup.string()
      .required(t('ROUTING_NUMBER_CANNOT_BE_EMPTY'))
      .matches(
        /^[0-9]+$/,
        t('FIELD_INVALID_FORMAT', {
          field: t('ROUTING_NUMBER'),
          format: t('REGEX_ONLY_ALLOW_NUMBERS').toLowerCase(),
        }),
      )
      .min(
        9,
        t('FIELD_MIN_LENGTH', {
          field: t('ROUTING_NUMBER'),
          number: 9,
        }),
      ),
    // .max(
    //   9,
    //   t('FIELD_MAX_LENGTH', {
    //     field: t('ROUTING_NUMBER'),
    //     number: 9,
    //   }),
    // ),
  });
