import { Image, StyleSheet, Switch, View } from 'react-native';
import { SestionCardProps } from '../type';
import React from 'react';
import { Button, SCREEN_WIDTH, Text } from 'tradewize-component';
import { SvgXml } from 'react-native-svg';
import { IconArrowRight } from '../../../assets/svg';
import { colors } from '../../../theme';
import { useAppTranslation } from '../../../hooks/useAppTranslation';
import { useDispatch, useSelector } from 'react-redux';
import i18n from '../../../i18n';
import languages from '@cospired/i18n-iso-languages';
import { playInstructionalVideos } from '../../../assets/images';
import { logger } from '../../../utils';

const SectionCard = (props: SestionCardProps) => {
  const { t } = useAppTranslation();
  const { section, sectionIndex } = props;
  const dispatch = useDispatch();

  const biometricAuthentication = useSelector(
    (state: any) => state.auth.biometricAuthentication,
  );
  const language = useSelector((state: any) => state.app.language);
  const dataMedia = useSelector((state: any) => state.app.dataMedia);

  const getLanguage = () => {
    const supportedCodes = Object.keys(i18n?.options.resources || {});

    supportedCodes.forEach(code => {
      try {
        const langData = require(`@cospired/i18n-iso-languages/langs/${code}.json`);
        languages.registerLocale(langData);
      } catch {
        // fallback nu locale khng tn ti trong th vin
        console.warn(`No locale found for: ${code}`);
      }
    });
    const arrLanguages = supportedCodes
      .map(code => ({
        key: code,
        value:
          languages.getName(code, language) ||
          languages.getName(code, 'en') ||
          code.toUpperCase(),
      }))
      .find(item => item.key === language);

    return arrLanguages?.value;
  };

  return (
    <View
      style={[
        styles.sectionWrapper,
        sectionIndex === 0 && styles.sectionWrapperFirst,
      ]}
    >
      <View style={styles.sectionHeader}>
        <Text style={styles.sectionHeaderText}>{t(section.title)}</Text>
        {sectionIndex === 0 && <Button
          variant="ghost"
          size="small"
          style={styles.sectionHeaderButton}
          onPress={async () => {
            try {
              const redux = await import('auth/redux');
              dispatch(redux.actionContainer.showVideoModal({
                initialSubtitle: language || 'en',
                source: dataMedia?.['ACCOUNT_MAIN_SCREEN']?.url || {},
                subtitle: dataMedia?.['ACCOUNT_MAIN_SCREEN']?.subtitle || {},
                onClose: () => {
                  dispatch(redux.actionContainer.hideVideoModal());
                },
              }));
            } catch (error) {
              logger('error onPressPlayInstructionalVideos', error);
            }
          }}
        >
          <Image source={playInstructionalVideos} style={styles.playInstructionalVideos} />
        </Button>}
      </View>
      <View style={styles.sectionContent}>
        {section.data.map(item => (
          <View key={item.id} style={styles.menuItemContainer}>
            <View style={styles.menuItem}>
              <Button
                disabled={item.disabled}
                variant="ghost"
                style={[
                  styles.menuItemLeftButton,
                  item.hasArrow && styles.menuItemLeftButtonWithArrow,
                ]}
                onPress={item?.onPress}
                activeOpacity={0.7}
              >
                <View style={styles.menuItemLeft}>
                  <SvgXml xml={item.icon} width={24} height={24} />
                  <Text style={styles.menuItemText}>{t(item.title)}</Text>
                </View>
              </Button>
              <View style={styles.menuItemRight}>
                {item.hasValue && (
                  <Text style={styles.menuItemValue}>
                    {item?.key === 'language' ? getLanguage() : item.value}
                  </Text>
                )}
                {item.hasSwitch && (
                  <Switch
                    value={biometricAuthentication?.data}
                    onValueChange={item?.onPress}
                    trackColor={{
                      false: colors.grayScale40,
                      true: colors.mainColorPrimary,
                    }}
                    thumbColor={colors.white}
                  />
                )}
                {item.hasArrow && (
                  <Button
                    variant="ghost"
                    disabled={item.disabled}
                    onPress={item?.onArrowPress || item?.onPress}
                    activeOpacity={0.7}
                    style={styles.arrowButton}
                  >
                    <SvgXml
                      xml={IconArrowRight(colors.black)}
                      width={20}
                      height={20}
                    />
                  </Button>
                )}
              </View>
            </View>
          </View>
        ))}
      </View>
    </View>
  );
};

const styles = StyleSheet.create({
  sectionWrapper: {
    marginHorizontal: 16,
    marginTop: 24,
    borderRadius: 12,
    borderWidth: 1,
    borderColor: colors.grayScale30,
    backgroundColor: colors.white,
    overflow: 'hidden',
  },
  sectionWrapperFirst: {
    marginTop: -30,
    borderTopLeftRadius: 12,
    borderTopRightRadius: 12,
  },
  sectionHeader: {
    flexDirection: 'row',
    justifyContent: 'space-between',
    alignItems: 'center',
    paddingHorizontal: 16,
    paddingTop: 16,
    paddingBottom: 12,
    backgroundColor: colors.white,
  },
  sectionContent: {
    paddingHorizontal: 16,
    paddingBottom: 16,
  },
  sectionHeaderText: {
    fontSize: 18,
    fontWeight: '600',
    color: colors.black,
  },
  playButton: {
    width: 24,
    height: 24,
    borderRadius: 4,
    backgroundColor: colors.mainColorPrimary,
    alignItems: 'center',
    justifyContent: 'center',
  },
  menuItemContainer: {
    marginBottom: 12,
    borderRadius: 12,
    borderWidth: 1,
    borderColor: colors.grayScale30,
    backgroundColor: colors.white,
    overflow: 'hidden',
  },
  menuItem: {
    flexDirection: 'row',
    justifyContent: 'space-between',
    alignItems: 'center',
    paddingHorizontal: 16,
    paddingVertical: 10,
    backgroundColor: colors.white,
    minHeight: 44,
  },
  menuItemLeftButton: {
    flex: 1,
    justifyContent: 'center',
    alignItems: 'flex-start',
    alignSelf: 'stretch',
    minHeight: 0,
    paddingVertical: 0,
  },
  menuItemLeftButtonWithArrow: {
    paddingRight: 0,
  },
  menuItemLeft: {
    flexDirection: 'row',
    alignItems: 'center',
    flex: 1,
  },
  arrowButton: {
    paddingVertical: 0,
    paddingHorizontal: 0,
    minHeight: 0,
  },
  menuItemText: {
    fontSize: 16,
    color: colors.black,
    marginLeft: 12,
    flex: 1,
    lineHeight: 24,
  },
  menuItemRight: {
    flexDirection: 'row',
    alignItems: 'center',
    marginLeft: 12,
    alignSelf: 'center',
  },
  menuItemValue: {
    fontSize: 16,
    color: colors.grayScale60,
    marginRight: 12,
    lineHeight: 24,
  },
  playInstructionalVideos: {
    width: SCREEN_WIDTH * 0.08,
    height: SCREEN_WIDTH * 0.08,
    borderRadius: 8,
  },
  sectionHeaderButton: {
    paddingVertical: 0,
    paddingHorizontal: 0,
    minHeight: 0,
  },
});

export default SectionCard;
