import { View } from 'react-native';
import { Button, SCREEN_HEIGHT, Text } from 'tradewize-component';
import BottomSheet, {
  BottomSheetFlashList,
  BottomSheetView,
  useBottomSheetScrollableCreator,
  useBottomSheetSpringConfigs,
} from '@gorhom/bottom-sheet';
import {
  forwardRef,
  useCallback,
  useEffect,
  useImperativeHandle,
  useMemo,
  useRef,
} from 'react';
import { styles } from './styles';
import { useAppTranslation } from '../../../../hooks/useAppTranslation';
import { useSelector } from 'react-redux';
import { JobItem } from './types';
import JobItemCard from '../JobItemCard';
import { useSafeAreaInsets } from 'react-native-safe-area-context';

interface ListJobsProps {
  isFocusedSearch: boolean;
}

export interface ListJobsRef {
  snapToIndex: (index: number) => void;
  close: () => void;
}

const ListJobs = forwardRef<ListJobsRef, ListJobsProps>((props, ref) => {
  const { t } = useAppTranslation();
  const bottomSheetRef = useRef<BottomSheet>(null);
  const insets = useSafeAreaInsets();
  const BottomSheetListScrollable = useBottomSheetScrollableCreator();

  const animationConfigs = useBottomSheetSpringConfigs({
    damping: 100,
    overshootClamping: true,
    stiffness: 400,
  });

  const listJobs: JobItem[] = useSelector(
    (state: any) => state.jobBoard.houseHasJobWorker.data,
  );
  const isLoading = useSelector((state: any) => state.app.loadingOverlay);

  const snapPoints = useMemo(() => ['35%', '75%'], []);

  const keyExtractor = useCallback(
    (item: any) => item?.houseId + item?.jobId,
    [],
  );

  useImperativeHandle(ref, () => ({
    snapToIndex: (index: number) => {
      bottomSheetRef.current?.snapToIndex(index);
    },
    close: () => {
      bottomSheetRef.current?.close();
    },
  }));

  useEffect(() => {
    if (props?.isFocusedSearch) {
      bottomSheetRef.current?.snapToIndex(0);
    }
  }, [props.isFocusedSearch]);

  return (
    <BottomSheet
      index={1}
      snapPoints={snapPoints}
      ref={bottomSheetRef}
      animationConfigs={animationConfigs}
      containerStyle={styles.containerPosition}
      animateOnMount={true}
      enableContentPanningGesture={true}
      enableHandlePanningGesture={true}
      enableDynamicSizing={false}
      enablePanDownToClose={false}
      enableOverDrag={false}
    >
      <BottomSheetView style={styles.contentContainer}>
        <View style={styles.headerContainer}>
          <Text style={styles.label}>{t('JOBS_NEAR_YOUR_LOCATION')}</Text>
          <Button
            onPress={() => {
              bottomSheetRef.current?.snapToIndex(1);
            }}
            variant="ghost"
            textStyle={styles.buttonText}
            style={styles.button}
            title={t('SEE_ALL')}
          />
        </View>

        <BottomSheetFlashList
          style={[
            {
              height: SCREEN_HEIGHT * 0.75 - insets.bottom - 56,
            },
            styles.flashListContainer,
          ]}
          contentContainerStyle={styles.listJobsContainer}
          data={listJobs || []}
          keyExtractor={keyExtractor}
          renderItem={({ item, index }: { item: JobItem; index: number }) => (
            <JobItemCard item={item} isFirst={index === 0} />
          )}
          initialNumToRender={30}
          maxToRenderPerBatch={25}
          windowSize={25}
          renderScrollComponent={BottomSheetListScrollable}
          estimatedItemSize={88}
          removeClippedSubviews
          ListEmptyComponent={
            <View style={styles.emptyContainer}>
              {!isLoading?.visible && (
                <Text style={styles.emptyText}>{t('NO_JOBS_FOUND')}</Text>
              )}
            </View>
          }
        />
      </BottomSheetView>
    </BottomSheet>
  );
});

ListJobs.displayName = 'ListJobs';

export default ListJobs;
