import React, { useMemo } from 'react';
import { View, Image } from 'react-native';
import { CheckboxButton, Button, Text } from 'tradewize-component';
import { SvgXml } from 'react-native-svg';
import { IconClose } from '../../../assets/svg';
import colors from '../../../theme/colors';
import { styles } from '../styles';
import {
  CompanyAnswer,
  handleDeleteImage,
  handleUploadMedia,
} from '../../../helper/registerTest';

interface NecessaryToolsProps {
  selectedCompanies: Array<{ companyId: string; companyName: string }>;
  companyAnswers: CompanyAnswer[];
  images: any[];
  setImages: React.Dispatch<React.SetStateAction<any[]>>;
  handleAnswerChange: (companyKey: string, answer: 'yes' | 'no') => void;
  errorQuestion: string[];
  itemRefs: React.MutableRefObject<{ [key: string]: View | null }>;
  t: (key: string, opts?: any) => string;
  dispatch: any;
}

const NecessaryTools: React.FC<NecessaryToolsProps> = ({
  selectedCompanies,
  companyAnswers,
  images,
  setImages,
  handleAnswerChange,
  errorQuestion,
  itemRefs,
  t,
  dispatch,
}) => {
  const renderContent = useMemo(() => {
    if (!selectedCompanies || !Array.isArray(selectedCompanies)) {
      return null;
    }

    const getCompanyAnswer = (companyKey: string): 'yes' | 'no' | null => {
      const answer = companyAnswers.find(
        item => item.companyKey === companyKey,
      );
      return answer?.answer ?? 'no';
    };

    return (
      <View>
        {selectedCompanies.length > 0 &&
          selectedCompanies.map(company => {
            const currentAnswer = getCompanyAnswer(company.companyId);
            const isYes = currentAnswer === 'yes';
            const isNo = currentAnswer === 'no';

            return (
              <View
                ref={ref => {
                  itemRefs.current[company.companyId] = ref;
                }}
                style={[
                  styles.boxNecessaryTools,
                  errorQuestion?.includes(company.companyId) &&
                    styles.boxNecessaryToolsError,
                ]}
                key={company.companyId}
              >
                <Text style={styles.titleNecessaryTools}>
                  {t('DO_YOU_NEED_TOOLS_FOR_YOUR_JOB', {
                    companyName: company.companyName,
                  })}
                </Text>
                <View style={styles.groundCheckboxButton}>
                  <CheckboxButton
                    label={t('YES')}
                    textStyle={styles.checkboxButtonText}
                    onChange={() =>
                      handleAnswerChange(company.companyId, 'yes')
                    }
                    isChecked={isYes}
                    checkboxStyle={[
                      styles.checkboxButtonStyle,
                      isYes && {
                        backgroundColor: colors.white,
                        borderColor: colors.mainColorPrimary,
                      },
                    ]}
                    customCheckmark={
                      isYes ? <View style={styles.checkmarkBox} /> : null
                    }
                  />
                  <CheckboxButton
                    label={t('NO')}
                    textStyle={styles.checkboxButtonText}
                    onChange={() => handleAnswerChange(company.companyId, 'no')}
                    isChecked={isNo}
                    checkboxStyle={[
                      styles.checkboxButtonStyle,
                      isNo && {
                        backgroundColor: colors.white,
                        borderColor: colors.mainColorPrimary,
                      },
                    ]}
                    customCheckmark={
                      isNo ? <View style={styles.checkmarkBox} /> : null
                    }
                  />
                </View>

                {isYes && (
                  <View style={styles.groundButtonMedia}>
                    <Button
                      style={styles.buttonMedia}
                      title={t('UPLOAD_IMAGE')}
                      onPress={async () => {
                        // Clear error for this company when user uploads image
                        const redux = await import('auth/redux');
                        const { setErrorQuestion } = redux.actionContainer;
                        const currentErrors = errorQuestion.filter(
                          (id: any) => id !== company.companyId,
                        );
                        dispatch(setErrorQuestion(currentErrors));

                        handleUploadMedia({
                          companyKey: company.companyId,
                          type: 'picker',
                          images,
                          setImages,
                        });
                      }}
                    />
                    <Button
                      style={styles.buttonMedia}
                      title={t('TAKE_PHOTO')}
                      onPress={async () => {
                        // Clear error for this company when user takes photo
                        const redux = await import('auth/redux');
                        const { setErrorQuestion } = redux.actionContainer;
                        const currentErrors = errorQuestion.filter(
                          (id: any) => id !== company.companyId,
                        );
                        dispatch(setErrorQuestion(currentErrors));

                        handleUploadMedia({
                          companyKey: company.companyId,
                          type: 'takePhoto',
                          images,
                          setImages,
                        });
                      }}
                    />
                  </View>
                )}
                <View style={styles.groundImages}>
                  {images?.length > 0 &&
                    images
                      ?.find(item => item.companyKey === company.companyId)
                      ?.images?.map((item: any) => {
                        return (
                          <View key={item.id}>
                            <Image
                              source={{ uri: item.uri }}
                              style={styles.image}
                            />

                            <Button
                              activeOpacity={1}
                              size="small"
                              variant="ghost"
                              style={styles.imageDeleteButton}
                              onPress={async () => {
                                // Clear error for this company when user deletes image
                                const redux = await import('auth/redux');
                                const { setErrorQuestion } =
                                  redux.actionContainer;
                                const currentErrors = errorQuestion.filter(
                                  (id: any) => id !== company.companyId,
                                );
                                dispatch(setErrorQuestion(currentErrors));

                                handleDeleteImage({
                                  companyKey: company.companyId,
                                  imageId: item.id,
                                  setImages,
                                });
                              }}
                            >
                              <SvgXml
                                xml={IconClose(colors.white)}
                                width={18}
                                height={18}
                              />
                            </Button>
                          </View>
                        );
                      })}
                </View>
              </View>
            );
          })}
      </View>
    );
  }, [
    selectedCompanies,
    companyAnswers,
    images,
    errorQuestion,
    handleAnswerChange,
    setImages,
    itemRefs,
    t,
    dispatch,
  ]);

  return <>{renderContent}</>;
};

export default NecessaryTools;
