import { useDispatch, useSelector } from 'react-redux';
import { useCallback, useEffect } from 'react';
import { logger } from '../utils';

export const useRoleType = () => {
  const dispatch = useDispatch();
  // const { t } = useAppTranslation();
  const { data: roleTypeData, loading } = useSelector(
    (state: any) => state.roleType,
  );

  const fetchGetRoleType = useCallback(async () => {
    try {
      const redux = await import('auth/redux');
      dispatch(redux.actionContainer.getRoleTypeAsync());
    } catch (error) {
      logger('error fetchGetRoleType', error);
    }
  }, [dispatch]);

  useEffect(() => {
    fetchGetRoleType();
  }, [fetchGetRoleType]);

  return {
    roleTypeData,
    loading,
  };
};
