import { LayoutChangeEvent, View } from 'react-native';
import { Button, Text } from 'tradewize-component';
import { styles } from './styles';
import { useSafeAreaInsets } from 'react-native-safe-area-context';
import {
  IconArrowBack,
  IconFilterJobBoard,
  IconSearch,
} from '../../../../assets/svg';
import { SvgXml } from 'react-native-svg';
import { NavigationService } from '../../../../services/NavigationService';
import React, { useCallback, useRef } from 'react';
import { logger } from '../../../../utils';
import { useDispatch } from 'react-redux';
import {
  GooglePlaceData,
  GooglePlaceDetail,
  GooglePlacesAutocomplete,
  GooglePlacesAutocompleteRef,
} from 'react-native-google-places-autocomplete';
import { useAppTranslation } from '../../../../hooks/useAppTranslation';
import { colors } from '../../../../theme';

interface HeaderProps {
  onLayout?: (event: LayoutChangeEvent) => void;
  setIsFocused?: (isFocused: boolean) => void;
  onSelectLocationSearch?: (
    data: GooglePlaceData,
    details: GooglePlaceDetail | null,
  ) => void;
  isFocused?: boolean;
  handlePressFilter?: () => void;
}

const Header = ({
  onLayout,
  setIsFocused,
  isFocused,
  handlePressFilter,
  onSelectLocationSearch,
}: HeaderProps) => {
  const insets = useSafeAreaInsets();
  const dispatch = useDispatch();
  const { t } = useAppTranslation();
  const ref = useRef<GooglePlacesAutocompleteRef>(null);

  const handlePressBack = useCallback(async () => {
    try {
      const redux = await import('auth/redux');
      NavigationService.goBack();
      dispatch(redux.actionContainer.clearJobBoardState());
    } catch (error) {
      logger('error handlePressBack', error);
    }
  }, [dispatch]);

  const handleFocus = () => {
    setIsFocused?.(true);
  };

  const handleBlur = () => {
    setIsFocused?.(false);
  };

  return (
    <View
      onLayout={onLayout}
      style={[styles.container, { marginTop: insets.top }]}
    >
      <Button variant="ghost" onPress={handlePressBack} style={styles.btnBack}>
        <SvgXml xml={IconArrowBack()} width={24} height={24} />
      </Button>

      <GooglePlacesAutocomplete
        ref={ref}
        enablePoweredByContainer={false}
        debounce={200}
        listViewDisplayed="auto"
        placeholder={t('ENTER_LOCATION')}
        onPress={(data, details = null) => {
          onSelectLocationSearch?.(data, details);
        }}
        query={{
          key: 'AIzaSyDLncyTjn_rTzFhsxdYdlI0KibohWzR4pY',
          language: 'en',
          types: 'geocode',
        }}
        nearbyPlacesAPI="GooglePlacesSearch"
        minLength={4}
        textInputProps={{
          onFocus: () => {
            handleFocus();
          },
          onBlur: () => {
            handleBlur();
          },
        }}
        renderRow={rowData => {
          const title =
            rowData?.structured_formatting?.main_text ||
            rowData?.description ||
            'Unknown';
          const address = rowData?.structured_formatting?.secondary_text || '';

          return (
            <View pointerEvents="none" style={styles.searchResultItem}>
              <View style={styles.searchResultContent}>
                <Text style={styles.searchResultTitle}>{title}</Text>
                {!!address && (
                  <Text style={styles.searchResultAddress}>{address}</Text>
                )}
              </View>
            </View>
          );
        }}
        renderLeftButton={() => {
          return (
            <View style={styles.viewIconSearch}>
              <SvgXml
                xml={IconSearch(colors.grayScale70)}
                width={24}
                height={24}
              />
            </View>
          );
        }}
        renderRightButton={() => {
          return (
            <Button
              onPress={() => {
                ref?.current?.blur();
                setTimeout(() => {
                  handlePressFilter?.();
                }, 100);
              }}
              variant="ghost"
              style={styles.viewIconFilter}
            >
              <View style={styles.lineSeparator} />
              <SvgXml
                xml={IconFilterJobBoard(colors.grayScale70)}
                width={24}
                height={24}
              />
            </Button>
          );
        }}
        styles={{
          container: [
            styles.ggAutoCompleteContainer,
            isFocused && styles.ggFocusedTextInputContainer,
          ],
          textInput: styles.ggAutoCompleteTextInput,
          row: styles.ggAutoCompleteRow,
        }}
        onFail={error => {
          logger(' GooglePlacesAutocomplete onFail:', error);
        }}
        onNotFound={() => {
          logger(' GooglePlacesAutocomplete onNotFound');
        }}
        onTimeout={() => {
          logger(' GooglePlacesAutocomplete onTimeout');
        }}
        fetchDetails={true}
        timeout={20000}
        keepResultsAfterBlur={false}
        predefinedPlaces={[]}
        currentLocation={true}
        currentLocationLabel="Current location"
      />
    </View>
  );
};

export default Header;
