import React from 'react';
import { View, Text, TouchableOpacity } from 'react-native';
import { styles } from '../styles';
import { useAppTranslation } from '../../../hooks/useAppTranslation';

interface UnreadCount {
  category: number;
  unreadCount: number;
}

interface TabbarProps {
  activeTab: number;
  onTabPress: (category: number) => void;
  unreadCounts: UnreadCount[];
  onMarkAllRead: () => void;
}

const Tabbar: React.FC<TabbarProps> = ({
  activeTab,
  onTabPress,
  unreadCounts,
  onMarkAllRead,
}) => {
  const { t } = useAppTranslation();
  const getUnreadCount = (category: number): number => {
    const unread = unreadCounts.find(item => item.category === category);
    return unread?.unreadCount || 0;
  };

  const tabs = [
    { key: 1, label: t('JOBS') },
    { key: 2, label: t('PAYMENTS') },
    { key: 0, label: t('OTHERS') },
  ];

  return (
    <View style={styles.tabContainer}>
      <View style={styles.tabs}>
        {tabs.map(tab => {
          const isActive = activeTab === tab.key;
          const count = getUnreadCount(tab.key);
          const showBadge = count > 0;

          return (
            <TouchableOpacity
              key={tab.key}
              style={[styles.tab, isActive && styles.tabActive]}
              onPress={() => onTabPress(tab.key)}
            >
              <View style={styles.tabTextContainer}>
                <Text
                  style={[styles.tabText, isActive && styles.tabTextActive]}
                >
                  {tab.label}
                </Text>
                {showBadge && (
                  <View
                    style={[
                      styles.tabBadge,
                      isActive && styles.tabBadgeActive,
                    ]}
                  >
                    <Text
                      style={[
                        styles.tabBadgeText,
                        isActive && styles.tabBadgeTextActive,
                      ]}
                    >
                      {count}
                    </Text>
                  </View>
                )}
              </View>
            </TouchableOpacity>
          );
        })}
      </View>
    </View>
  );
};

export default Tabbar;
