import React, { useCallback, useEffect, useState } from 'react';
import { ActivityIndicator, View } from 'react-native';
import { SvgXml } from 'react-native-svg';
import { Text, Button } from 'tradewize-component';
import { IconDownloadFile, IconPen, IconUpload } from '../../../../assets/svg';
import colors from '../../../../theme/colors';
import { useAppTranslation } from '../../../../hooks/useAppTranslation';
import {
  downloadPdfBlobUtil,
  getFileLabel,
} from '../../../../helper/PaymentInfoW9.helper';
import { BankInfo, FileW9 } from '../../../../types/PaymentInfoW9';
import { styles } from './styles';

export function W9Section({
  bankInfoSelected,
}: {
  bankInfoSelected: BankInfo;
}) {
  const { t } = useAppTranslation();

  const [pdf, setPdf] = useState<FileW9 | null>(null);
  const [loading, _setLoading] = useState(false);
  const [uploading, setUploading] = useState(false);

  useEffect(() => {
    if (bankInfoSelected?.fileW9) {
      setPdf(bankInfoSelected?.fileW9);
    }
  }, [bankInfoSelected]);

  const handlePickPdf = useCallback(async () => {
    try {
      setUploading(true);
      const picked: any = {
        uri: 'file:///mock/W9_12-09-2025.pdf',
        name: 'W9_12-09-2025.pdf',
        type: 'application/pdf',
      };
      setPdf(picked);
    } catch (e) {
      // handle error nu cn
    } finally {
      setUploading(false);
    }
  }, []);

  return (
    <View style={styles.w9Wrapper}>
      <Text style={styles.sectionTitle}>{t('W9')}</Text>

      <View style={styles.supportDownloadPdfContainer}>
        <View style={styles.supportDownloadPdfRow}>
          <Text style={styles.supportDownloadPdf}>
            {t('SUPPORT_DOWNLOAD_PDF')}
          </Text>
          <Button
            activeOpacity={0.7}
            onPress={() => {
              downloadPdfBlobUtil(
                'https://s3.us-east-1.amazonaws.com/public.tradewize.biz/fw9.pdf',
              );
            }}
            style={styles.clickHereButton}
          >
            <Text style={styles.txtClickHere}>{t('HERE').toLowerCase()}</Text>
            <SvgXml
              xml={IconDownloadFile(colors.black)}
              width={16}
              height={16}
              style={styles.downloadIcon}
            />
          </Button>
        </View>
      </View>

      <View
        style={[
          styles.layoutPdf,
          loading ? styles.justifyCenter : styles.justifyStart,
        ]}
      >
        {loading ? (
          <View style={styles.loadingContainer}>
            <ActivityIndicator
              style={styles.loadingIndicatorSsn}
              size={24}
              color={colors.mainColorPrimary}
            />
          </View>
        ) : (
          <>
            {!pdf?.fileName ? (
              <Button
                onPress={handlePickPdf}
                style={styles.uploadButton}
                activeOpacity={0.7}
              >
                {uploading ? (
                  <ActivityIndicator
                    size={16}
                    color={colors.mainColorPrimary}
                  />
                ) : (
                  <View style={styles.uploadButtonContent}>
                    <SvgXml
                      xml={IconUpload(colors.white)}
                      width={20}
                      height={20}
                      style={styles.uploadIcon}
                    />
                    <Text style={styles.btnUploadPdf}>{t('UPLOAD_W9')}</Text>
                  </View>
                )}
              </Button>
            ) : (
              <View style={styles.pdfContainer}>
                <Text style={styles.pdfLabel}>
                  {t('YOUR_W9_FILE')}: {getFileLabel(pdf || {})}
                </Text>
                <Button
                  onPress={handlePickPdf}
                  style={styles.pdfNameContainer}
                  activeOpacity={0.7}
                >
                  <SvgXml xml={IconPen(colors.black)} width={18} height={18} />
                </Button>
              </View>
            )}
          </>
        )}
      </View>
    </View>
  );
}

export default W9Section;
