import { Dispatch, SetStateAction } from 'react';
import { StyleProp, StyleSheet, View, ViewStyle } from 'react-native';
import { Button, Text } from 'tradewize-component';
import { useAppTranslation } from '../../../hooks/useAppTranslation';
import { colors } from '../../../theme';

interface HeaderCardProps {
  isShowViewAllButton?: boolean;
  setIsShowViewAllButton?: Dispatch<SetStateAction<boolean>>;
  onShowViewAllButton?: () => void;
  watchedCount?: number;
  totalTutorials?: number;
  variant?: 'system' | 'quality';
  titleQuality?: string;
  titleSystem?: string;
  testCount?: number;
  totalTestCount?: number;
  styleContainer?: StyleProp<ViewStyle>;
}

const HeaderCard = ({
  isShowViewAllButton,
  setIsShowViewAllButton,
  onShowViewAllButton,
  watchedCount = 0,
  totalTutorials = 0,
  variant = 'system',
  titleQuality = '',
  titleSystem,
  testCount = 0,
  totalTestCount = 0,
  styleContainer,
}: HeaderCardProps) => {
  const { t } = useAppTranslation();

  return (
    <Button
      onPress={() => {
        setIsShowViewAllButton?.(prev => !prev);
        onShowViewAllButton?.();
      }}
      variant="ghost"
      style={[
        styles.headerContainer,
        styleContainer,
        isShowViewAllButton && styles.headerContainerShowAllButton,
        variant === 'system' && styles.alignItemsCenter,
        (variant === 'quality' || titleSystem) && styles.itemStart,
      ]}
    >
      {variant === 'system' && (
        <View>
          {titleSystem && <Text style={styles.title}>{titleSystem}</Text>}
          <View style={styles.contentContainerSystem}>
            <Text style={[styles.headerText, { color: colors.greenBlue }]}>
              {t('COMPLETED')}:{' '}
              <Text style={styles.contentTextHeader}>
                {watchedCount} {t('VIDEOS')},
              </Text>
            </Text>
            <Text
              style={[styles.headerText, { color: colors.mainColorPrimary }]}
            >
              {' '}
              {t('REMAINING')}:{' '}
              <Text style={styles.contentTextHeader}>
                {totalTutorials - watchedCount}
              </Text>
            </Text>
          </View>
        </View>
      )}

      {variant === 'quality' && (
        <View>
          <Text style={styles.title}>{titleQuality}</Text>
          <Text style={styles.headerText}>
            {t('VIDEOS')}:{' '}
            <Text style={[styles.headerText, { color: colors.greenBlue }]}>
              {t('COMPLETED')}:{' '}
              <Text style={styles.contentTextHeader}>
                {watchedCount} {t('VIDEOS')},
              </Text>
            </Text>
            <Text
              style={[styles.headerText, { color: colors.mainColorPrimary }]}
            >
              {' '}
              {t('REMAINING')}:{' '}
              <Text style={styles.contentTextHeader}>
                {totalTutorials - watchedCount}
              </Text>
            </Text>
          </Text>

          <Text style={styles.headerText}>
            {t('TESTS')}:{' '}
            <Text style={[styles.headerText, { color: colors.greenBlue }]}>
              {t('COMPLETED')}:{' '}
              <Text style={styles.contentTextHeader}>
                {testCount} {t('TESTS')},
              </Text>
            </Text>
            <Text
              style={[styles.headerText, { color: colors.mainColorPrimary }]}
            >
              {' '}
              {t('REMAINING')}:{' '}
              <Text style={styles.contentTextHeader}>
                {totalTestCount - testCount}
              </Text>
            </Text>
          </Text>
        </View>
      )}

      <Button
        disabled={true}
        title={
          variant === 'system'
            ? `${watchedCount}/${totalTutorials} ${
                isShowViewAllButton ? '' : ''
              }`
            : `${isShowViewAllButton ? '' : ''}`
        }
        style={[
          styles.viewAllButton,
          (variant === 'quality' || titleSystem) && styles.itemStart,
        ]}
        variant="ghost"
        size="medium"
        textStyle={styles.viewAllButtonText}
      />
    </Button>
  );
};

const styles = StyleSheet.create({
  headerContainer: {
    backgroundColor: colors.grayScale20,
    paddingVertical: 8,
    borderTopLeftRadius: 8,
    borderTopRightRadius: 8,
    overflow: 'hidden',
    borderColor: colors.grayScale50,
    borderBottomWidth: 0,
    paddingHorizontal: 16,
    flexDirection: 'row',
    minHeight: 0,
  },
  alignItemsCenter: {
    alignItems: 'center',
  },
  headerText: {
    fontSize: 12,
    fontWeight: '500',
    color: colors.black,
    lineHeight: 16,
  },
  contentTextHeader: {
    fontSize: 12,
    fontWeight: '400',
    color: colors.grayScale70,
  },
  viewAllButton: {
    marginLeft: 'auto',
    paddingHorizontal: 0,
    paddingVertical: 0,
    height: 'auto',
    minHeight: 0,
  },
  itemStart: {
    alignItems: 'flex-start',
  },
  viewAllButtonText: {
    fontSize: 12,
    fontWeight: '600',
    color: colors.mainColorPrimary,
  },
  headerContainerShowAllButton: {
    borderRadius: 8,
  },
  title: {
    fontSize: 16,
    fontWeight: 'bold',
    color: colors.mainColorPrimary,
  },
  contentContainerSystem: {
    flexDirection: 'row',
  },
});

export default HeaderCard;
