import React, { useCallback } from 'react';
import { Text, View } from 'react-native';
import { useAppTranslation } from '../../../../hooks/useAppTranslation';
import { navigationRef } from '../../../../services/NavigationService';
import { SCREEN_ENUM } from '../../../../types/global';
import { styles } from './styles';
import { Button } from 'tradewize-component';
import { SvgXml } from 'react-native-svg';
import {
  IconEye,
  IconEyeSlash,
  IconPen,
  IconPlus,
} from '../../../../assets/svg';
import { OtpVerifyModal } from '../../../../components/OtpVerifyModal';
import { useBankInfo } from '../../../../hooks/useBankInfo';
import { OtpInputRef } from 'react-native-otp-entry';
import { maskString } from '../../../../helper/PaymentInfoW9.helper';

export function BankInfo() {
  const { t } = useAppTranslation();
  const {
    isOTPShowAccountNumber,
    otp,
    setOtp,
    otpInputRef,
    handleCloseOtp,
    bankInfoSelected: bankInfo,
    toggleAccountVisibility,
    verifyTokenVerifyOtp,
    isShowAccountNumber,
    userProfile,
    reSendShowAccountNumber,
  } = useBankInfo();

  const handleAddBank = useCallback(() => {
    navigationRef.navigate(SCREEN_ENUM.BankInfoDataScreen);
  }, []);

  return (
    <View style={styles.container}>
      <Text style={styles.label}>{t('BANK_INFORMATION')}</Text>
      {!bankInfo?.requestId && (
        <Button
          style={styles.addButton}
          onPress={handleAddBank}
          activeOpacity={0.7}
        >
          <Text style={styles.addButtonText}>{t('ADD_NEW_BANK_ACCOUNT')}</Text>
          <SvgXml xml={IconPlus()} width={20} height={20} />
        </Button>
      )}
      {bankInfo ? (
        <View style={styles.bankInfoContainer}>
          <View style={styles.bankInfoHeader}>
            <Text style={styles.bankInfoHeaderName}>
              {isShowAccountNumber
                ? bankInfo?.bankAccountName || ''
                : maskString(bankInfo?.bankAccountName || '', 'name')}
            </Text>
            <Text style={styles.bankInfoHeaderStatus}>
              {!bankInfo?.isVeried ? t('PENDING_UPDATE') : t('ACTIVE')}
            </Text>
          </View>
          <View style={styles.bankInfoContent}>
            <Text style={styles.accountNumber}>
              {isShowAccountNumber
                ? bankInfo?.bankAccountNumber || ''
                : maskString(bankInfo?.bankAccountNumber || '', 'number')}
            </Text>
            <View style={styles.actions}>
              <Button
                style={styles.iconButton}
                onPress={toggleAccountVisibility}
              >
                <SvgXml
                  xml={isShowAccountNumber ? IconEye() : IconEyeSlash()}
                  width={18}
                  height={18}
                />
              </Button>
              <Button style={styles.iconButton} onPress={handleAddBank}>
                <SvgXml xml={IconPen()} width={18} height={18} />
              </Button>
            </View>
          </View>
        </View>
      ) : (
        <View style={styles.emptyState}>
          <Text style={styles.emptyStateText}>
            {t('NO_BANK_ACCOUNT_INFORMATION_AVAILABLE')}
          </Text>
        </View>
      )}

      <OtpVerifyModal
        visible={isOTPShowAccountNumber}
        onClose={handleCloseOtp}
        onConfirm={verifyTokenVerifyOtp}
        otp={otp}
        otpInputRef={otpInputRef as React.RefObject<OtpInputRef>}
        title={t('VERIFY_OTP')}
        description={t('ENTER_THE_OTP_SENT_TO_PHONE_NUMBER', {
          phoneNumber: userProfile?.phone || '',
        })}
        confirmText={t('VERIFY')}
        onChangeOtp={setOtp}
        resendText={t('RESEND_CODE')}
        initialCountdown={60}
        formatCountdownText={(remaining: number) => {
          return `${remaining}s`;
        }}
        onResend={reSendShowAccountNumber}
        isConfirmLoading={false}
      />
    </View>
  );
}

export default BankInfo;
