import { FlatList, View } from 'react-native';
import { styles } from './styles';
import Loading from '../loading';
import { useDispatch, useSelector } from 'react-redux';
import CardItemVideo, { CardItemVideoData } from '../CardItemVideo';
import { useState } from 'react';
import HeaderCard from '../HeaderCard';
import { logger } from '../../../../utils';

const SystemGuide = ({ isLoading }: { isLoading: boolean }) => {
  const dispatch = useDispatch();
  const tutorials = useSelector((state: any) => state.training.tutorials);

  const [isShowViewAllButton, setIsShowViewAllButton] = useState(false);

  const handlePressItem = async (data: CardItemVideoData) => {
    try {
      const redux = await import('auth/redux');
      if (!data?.isWatched) {
        dispatch(
          redux.actionContainer.markAsWatchedAsync({
            id: data?.id,
            typeQualityGuide: false,
          }),
        );
      }
    } catch (error) {
      logger('error handlePressItem', error);
    }
  };

  return (
    <View style={styles.container}>
      <FlatList
        style={styles.list}
        contentContainerStyle={[
          styles.listContent,
          !isShowViewAllButton && styles.listContentShowAllButton,
        ]}
        data={!isShowViewAllButton ? tutorials?.data?.tutorials : []}
        renderItem={({ item }) => (
          <CardItemVideo onPress={handlePressItem} data={item} />
        )}
        keyExtractor={(item, index) => item?.id + index}
        ListHeaderComponent={() =>
          HeaderCard({
            isShowViewAllButton,
            setIsShowViewAllButton,
            watchedCount: tutorials?.data?.totalWatched,
            totalTutorials: tutorials?.data?.totalTutorials,
            styleContainer: !isShowViewAllButton && styles.headerContainer,
          })
        }
      />
      {isLoading && <Loading />}
    </View>
  );
};

export default SystemGuide;
