import { AppState, AppStateStatus } from 'react-native';

class AppBackgroundService {
  private appState: AppStateStatus = AppState.currentState;
  private listeners = new Set<(isBg: boolean) => void>();

  constructor() {
    AppState.addEventListener('change', this.handleChange);
  }

  private handleChange = (nextState: AppStateStatus) => {
    const wasBg = this.isBackground(this.appState);
    const nowBg = this.isBackground(nextState);

    if (wasBg !== nowBg) {
      this.listeners.forEach(cb => cb(nowBg));
    }

    this.appState = nextState;
  };

  private isBackground(state: AppStateStatus) {
    return state === 'background' || state === 'inactive';
  }

  subscribe(cb: (isBg: boolean) => void) {
    this.listeners.add(cb);

    return () => {
      this.listeners.delete(cb);
    };
  }
}

export const appBackgroundService = new AppBackgroundService();
