import { useCallback, useEffect, useMemo, useRef, useState } from 'react';
import { useDispatch, useSelector } from 'react-redux';
import { ScrollView, View } from 'react-native';
import { CompanyAnswer } from '../helper/registerTest';
import { handleSubmitTest, validateTestData } from '../helper/registerTest';

interface RouteParams {
  selectedCompanies?: Array<{ companyId: string; companyName: string }>;
  cleanData?: () => void;
}

export const useRegisterCompanyTestScreen = (routeParams: RouteParams) => {
  const dispatch = useDispatch();

  const scrollViewRef = useRef<ScrollView>(null);
  const itemRefs = useRef<{ [key: string]: View | null }>({});

  const { selectedCompanies: routeCompanies, cleanData } = routeParams || {};

  const companyAndRoleData = useSelector(
    (state: any) => state?.registerCompany?.data || [],
  );
  const errorQuestion = useSelector(
    (state: any) => state?.register?.errorQuestion || [],
  );

  const selectedCompanies = useMemo(
    () => routeCompanies || [],
    [routeCompanies],
  );

  const selectedCompanyIds = useMemo(
    () => selectedCompanies.map(c => c.companyId),
    [selectedCompanies],
  );

  const [companyAnswers, setCompanyAnswers] = useState<CompanyAnswer[]>([]);
  const [images, setImages] = useState<any[]>([]);

  // Fetch questions for selected companies (avoid duplicate fetches)
  useEffect(() => {
    const fetchQuestions = async () => {
      if (selectedCompanyIds?.length > 0) {
        const redux = await import('auth/redux');
        const currentState = redux.store.getState();
        const existingQuestionData =
          currentState?.register?.getAllOnboardingQuestion?.data || [];

        selectedCompanyIds.forEach((companyId: string) => {
          const hasDataForCompany =
            Array.isArray(existingQuestionData) &&
            existingQuestionData.some(
              (question: any) => question.id === companyId,
            );

          if (!hasDataForCompany) {
            dispatch(
              redux.actionContainer.getOnboardingQuestionAsync(companyId),
            );
          }
        });
      }
    };
    fetchQuestions();
  }, [selectedCompanyIds, dispatch]);

  // Cleanup when leave screen
  const cleanupOnUnmount = useCallback(() => {
    const cleanup = async () => {
      const redux = await import('auth/redux');
      dispatch(redux.actionContainer.clearErrorQuestion());
      dispatch(redux.actionContainer.clearQuestionAnswers());
    };
    cleanup();
  }, [dispatch]);

  const scrollToItem = useCallback((itemKey: string) => {
    const itemRef = itemRefs.current[itemKey];
    if (itemRef && scrollViewRef.current) {
      itemRef.measureLayout(
        scrollViewRef.current as any,
        (_x, y) => {
          scrollViewRef.current?.scrollTo({
            y: y - 50,
            animated: true,
          });
        },
        () => {},
      );
    }
  }, []);

  useEffect(() => {
    if (errorQuestion?.length > 0) {
      const timeoutId = setTimeout(() => {
        scrollToItem(errorQuestion[0]);
      }, 100);
      return () => clearTimeout(timeoutId);
    }
  }, [errorQuestion, scrollToItem]);

  const handleAnswerChange = useCallback(
    (companyKey: string, answer: 'yes' | 'no') => {
      const clearErrorForCompany = async () => {
        const redux = await import('auth/redux');
        const { setErrorQuestion } = redux.actionContainer;
        const currentErrors = errorQuestion.filter(
          (id: any) => id !== companyKey,
        );
        dispatch(setErrorQuestion(currentErrors));
      };
      clearErrorForCompany();

      setCompanyAnswers(prevAnswers => {
        const existingAnswer = prevAnswers.find(
          item => item.companyKey === companyKey,
        );

        if (existingAnswer) {
          if (existingAnswer.answer === answer) {
            return prevAnswers.filter(item => item.companyKey !== companyKey);
          }
          return prevAnswers.map(item =>
            item.companyKey === companyKey ? { ...item, answer } : item,
          );
        }

        return [...prevAnswers, { companyKey, answer }];
      });
    },
    [errorQuestion, dispatch],
  );

  const handleSubmit = useCallback(async () => {
    const isValid = await validateTestData(
      selectedCompanyIds,
      companyAnswers,
      images,
    );

    if (isValid) {
      handleSubmitTest(selectedCompanyIds, companyAnswers, images, cleanData);
    }
  }, [selectedCompanyIds, companyAnswers, images, cleanData]);

  return {
    scrollViewRef,
    itemRefs,
    companyAndRoleData,
    errorQuestion,
    selectedCompanies,
    selectedCompanyIds,
    companyAnswers,
    images,
    setImages,
    handleAnswerChange,
    handleSubmit,
    cleanupOnUnmount,
    dispatch,
  };
};

export default useRegisterCompanyTestScreen;
