import React from 'react';
import { View } from 'react-native';
import { Text } from 'tradewize-component';
import { styles } from '../styles';
import { AddressData } from '../../../../../types/myJobs.types';
import { SvgXml } from 'react-native-svg';
import { colors } from '../../../../../theme';
import { IconLocation } from '../../../../../assets/svg';

interface AddressProps {
  addressData: AddressData;
}

export default function Address({ addressData }: AddressProps) {
  // Build address string
  const addressParts = [
    addressData.houseNumber,
    addressData.street,
    addressData.city,
    addressData.state,
    addressData.zipCode,
  ].filter(Boolean);

  const fullAddress = addressParts.join(', ');

  return (
    <View style={styles.addressRow}>
      <View style={styles.addressContent}>
        <SvgXml
          xml={IconLocation(colors.mainColorPrimary)}
          width={20}
          height={20}
        />
        <Text style={styles.txtAddress} numberOfLines={2}>
          {fullAddress}
        </Text>
      </View>
    </View>
  );
}
