import React, { useMemo } from 'react';
import { View, SectionList, StyleSheet } from 'react-native';
import { Text, Button } from 'tradewize-component';
import { useSelector } from 'react-redux';
import { RootState } from '../../../../../../Auth/src/redux/store/types';
import { styles } from '../../styles';
import CardItem from '../CardItem';
import DateHeader from '../DateHeader';
import { JobItem, JobStartTabStatus } from '../../../../types/myJobs.types';
import { mapToJobItems, STATUS_MAP } from '../../../../helper/myJob.helpter';
import { colors } from '../../../../theme';
import { useTranslation } from 'react-i18next';

interface ScreenTabProps {
  tabType: JobStartTabStatus;
}

interface SectionData {
  title: string;
  date: Date;
  data: JobItem[];
}

export default function ScreenTab({ tabType }: ScreenTabProps) {
  const rawData = useSelector((state: RootState) => state.myjob.myJobs.data);

  const isLoading = useSelector(
    (state: RootState) => state.myjob.myJobs.isLoading,
  );
  const { t } = useTranslation();

  // Filter and group data by month/year with mapping
  const sections = useMemo(() => {
    // Map raw data to JobItem format
    const data = mapToJobItems(Array.isArray(rawData) ? rawData : []);

    // Group by month/year based on activityGroup.estStartDate or estEndDate
    const grouped = data.reduce((acc, item) => {
      const dateString =
        item?.activityGroup?.estStartDate ||
        item?.activityGroup?.estEndDate ||
        '';

      // Parse date once, use fallback for invalid dates
      const date = dateString ? new Date(dateString) : null;
      const isValidDate = date && !isNaN(date.getTime());

      const key = isValidDate
        ? `${date!.getFullYear()}-${date!.getMonth()}`
        : 'Unknown';
      const sectionDate = isValidDate ? date! : new Date();

      if (!acc[key]) {
        acc[key] = { title: key, date: sectionDate, data: [] };
      }
      acc[key].data.push(item);

      return acc;
    }, {} as Record<string, SectionData>);

    // Convert to array and sort by date (newest first)
    const sectionsArray = Object.values(grouped).sort(
      (a: SectionData, b: SectionData) => {
        if (a.title === 'Unknown') return 1;
        if (b.title === 'Unknown') return -1;
        return b.date.getTime() - a.date.getTime();
      },
    );

    return sectionsArray;
  }, [rawData]);

  const renderItem = ({ item }: { item: JobItem }) => {
    const changeStatusMyJob = STATUS_MAP[tabType as keyof typeof STATUS_MAP];
    return <CardItem item={item} jumpTo={changeStatusMyJob} />;
  };

  const renderSectionHeader = ({ section }: { section: SectionData }) => {
    if (section.title === 'Unknown') {
      return null;
    }
    return <DateHeader date={section.date} />;
  };

  return (
    <View style={localStyles.container}>
      {isLoading ? (
        <></>
      ) : (
        <SectionList
          sections={sections}
          renderItem={renderItem}
          renderSectionHeader={renderSectionHeader}
          keyExtractor={item => item.id}
          contentContainerStyle={localStyles.sectionListContent}
          ListEmptyComponent={
            <View style={localStyles.emptyContainer}>
              <Text style={localStyles.emptyText}>{t('NO_JOBS_FOUND')}</Text>
            </View>
          }
        />
      )}
    </View>
  );
}

export const renderTabBar = (props: any) => {
  const { navigationState, jumpTo } = props;

  return (
    <View style={styles.tabBarContainer}>
      {navigationState.routes.map((route: any, index: number) => {
        const isActive = index === navigationState.index;
        return (
          <Button
            key={route.key}
            variant="ghost"
            onPress={() => jumpTo(route.key)}
            style={[styles.tabButton, isActive && styles.tabButtonActive]}
          >
            <Text style={[styles.tabText, isActive && styles.tabTextActive]}>
              {route.title}
            </Text>
          </Button>
        );
      })}
    </View>
  );
};

const localStyles = StyleSheet.create({
  container: {
    flex: 1,
  },
  sectionListContent: {
    paddingHorizontal: 12,
    paddingBottom: 12,
    gap: 12,
  },
  emptyContainer: {
    padding: 20,
    alignItems: 'center',
  },
  emptyText: {
    fontSize: 14,
    color: colors.grayScale70,
  },
});
