import React, { useEffect, useState, useCallback } from 'react';
import { View, ScrollView } from 'react-native';
import { BaseContainer, HeaderBase, Button, Text } from 'tradewize-component';
import { SvgXml } from 'react-native-svg';
import { useSelector } from 'react-redux';
import { navigationRef } from '../../services/NavigationService';
import { colors } from '../../theme';
import { styles } from './styles';
import MyScore from './Components/MyScore';
import ListItem from './Components/ListItem';
import Top3Ranking from './Components/Top3Ranking';
import FilterBottomSheet from './Components/FilterBottomSheet';
import { useAppTranslation } from '../../hooks/useAppTranslation';
import { getLeaderBoard, getCompany } from '../../api/endpoint';
import { IconFilter } from '../../assets/svg';

interface CompanyApiResponse {
  id: string;
  name: string;
  code: string;
  businessName: string;
  businessAddress: string;
  [key: string]: any;
}

interface CompanyOption {
  companyId: string;
  companyName: string;
}


const LeaderBoard = () => {
  const { t } = useAppTranslation();
  const userProfile = useSelector((state: any) => state.auth.userProfile);
  const [mockTop3Users, setMockTop3Users] = useState<any[]>([]);
  const [mockListUsers, setMockListUsers] = useState<any[]>([]);
  const [currentRank, setCurrentRank] = useState(9);
  const [isFilterVisible, setIsFilterVisible] = useState(false);
  const [selectedMonth, setSelectedMonth] = useState<Date>(new Date());
  const [selectedCompanyId, setSelectedCompanyId] = useState<string | null>(null);
  const [companies, setCompanies] = useState<CompanyOption[]>([]);
  const [isLoadingCompanies, setIsLoadingCompanies] = useState(false);

  const fetchLeaderBoard = useCallback(async () => {
    try {
      const monthToUse = selectedMonth || new Date();
      const year = monthToUse.getFullYear();
      const monthNumber = monthToUse.getMonth() + 1;
      const companyId = selectedCompanyId || userProfile?.companyRoles?.[0]?.companyId;
      const currentAccountId = userProfile?.accountId;

      if (!companyId) return;

      const client = await import('auth/api');
      const response = await client.getRequest(
        `${getLeaderBoard}?Year=${year}&Month=${monthNumber}&CompanyId=${companyId}`,
      );

      let dataArray: any[] = [];
      if (Array.isArray(response?.data)) {
        dataArray = response.data;
      } else if (response?.data?.data && Array.isArray(response.data.data)) {
        dataArray = response.data.data;
      }

      const transformedUsers = dataArray.map((item: any) => {
        const name = item.fullName || item.name || `${item.firstName || ''} ${item.lastName || ''}`.trim() || 'Unknown';
        const role = item.roleName || item.role || 'N/A';
        const points = item.totalScore || item.score || item.points || 0;
        const avatar = item.avatar || item.photoUrl || item.bucketUrl;
        const workerId = item.workerId;
        
        let isCurrentUser = false;
        if (currentAccountId && workerId) {
          isCurrentUser = String(currentAccountId) === String(workerId);
        }

        return {
          rank: item.rank || 0,
          name,
          role,
          points,
          avatar,
          isCurrentUser,
          userId: item.workerId,
        };
      });

      const top3 = transformedUsers.filter((u) => u.rank >= 1 && u.rank <= 3).sort((a, b) => a.rank - b.rank);
      const rest = transformedUsers.filter((u) => u.rank > 3).sort((a, b) => a.rank - b.rank);

      setMockTop3Users(top3);
      setMockListUsers(rest);

      const currentUser = transformedUsers.find((u) => u.isCurrentUser);
      if (currentUser) {
        setCurrentRank(currentUser.rank);
      }
    } catch (error) {
      console.error('Error fetching leaderboard:', error);
    }
  }, [userProfile, selectedMonth, selectedCompanyId]);

  useEffect(() => {
    fetchLeaderBoard();
  }, [fetchLeaderBoard]);

  // Fetch companies
  const fetchCompanies = useCallback(async () => {
    setIsLoadingCompanies(true);
    try {
      const client = await import('auth/api');
      const response = await client.getRequest(getCompany);

      let companiesData: CompanyApiResponse[] = [];
      if (Array.isArray(response?.data)) {
        companiesData = response.data;
      } else if (response?.data?.data && Array.isArray(response.data.data)) {
        companiesData = response.data.data;
      }

      // Map API response to CompanyOption format
      const mappedCompanies: CompanyOption[] = companiesData.map((company) => ({
        companyId: company.id,
        companyName: company.name,
      }));

      setCompanies(mappedCompanies);
    } catch (error) {
      console.error('Error fetching companies:', error);
      setCompanies([]);
    } finally {
      setIsLoadingCompanies(false);
    }
  }, []);

  useEffect(() => {
    if (isFilterVisible) {
      fetchCompanies();
    }
  }, [isFilterVisible, fetchCompanies]);

  const handleFilterPress = () => {
    setIsFilterVisible(true);
  };

  const handleFilterApply = (filters: { month: Date | null; companyId: string | null }) => {
    if (filters.month) {
      setSelectedMonth(filters.month);
    }
    setSelectedCompanyId(filters.companyId);
  };

  return (
    <BaseContainer style={styles.container}>
      <HeaderBase
        title="Leaderboard"
        onPressBack={() => {
          navigationRef.goBack();
        }}
        rightActions={[
          {
            icon: (
              <Button variant="ghost" onPress={handleFilterPress}>
                <SvgXml
                  xml={IconFilter(colors.black)}
                  width={24}
                  height={24}
                />
              </Button>
            ),
          },
        ]}
      />

      <ScrollView
        style={styles.scrollView}
        contentContainerStyle={styles.scrollContent}
        showsVerticalScrollIndicator={false}
      >
        <MyScore 
          selectedMonth={selectedMonth}
          selectedCompanyId={selectedCompanyId}
        />

        <View style={styles.rankingSection}>
          <View style={styles.rankingHeader}>
            <Text style={styles.rankingTitle}>{t('Ranking')}</Text>
            <View style={styles.currentRankBadge}>
              <Text style={styles.currentRankText}>#{currentRank}</Text>
            </View>
          </View>

          <Top3Ranking users={mockTop3Users} />

          <View style={styles.listContainer}>
            {mockListUsers.map((user) => (
              <ListItem key={user.rank} user={user} />
            ))}
          </View>
        </View>
      </ScrollView>

      <FilterBottomSheet
        isVisible={isFilterVisible}
        onClose={() => setIsFilterVisible(false)}
        onApply={handleFilterApply}
        companies={companies}
        isLoadingCompanies={isLoadingCompanies}
        initialMonth={selectedMonth}
        initialCompanyId={selectedCompanyId}
      />
    </BaseContainer>
  );
};

export default LeaderBoard;
