import { useMemo } from 'react';
import { useSelector } from 'react-redux';

const EXCLUDE_GROUPS = [
  'verifyMaterialLeft',
  'verifyMaterialVPO',
  'warrantyJobs',
];

export const useImportantThing = () => {
  const importantJobs = useSelector(
    (state: any) => state.home.importantJobs.data,
  );

  function flattenJobs(
    data: any,
    options: { excludeGroups?: string[]; excludeFields?: string[] } = {
      excludeGroups: [],
      excludeFields: [],
    },
  ): any[] {
    const {
      excludeGroups = [], // loi theo tn nhm
      excludeFields = [], // loi theo tn field ca item
    } = options;

    const flat: any[] = [];

    Object.keys(data).forEach(groupKey => {
      // B qua nhm khng cn
      if (excludeGroups.includes(groupKey)) return;

      const arr = data[groupKey];

      if (Array.isArray(arr)) {
        arr.forEach(item => {
          // Clone  trnh mutate d liu gc
          const newItem = { type: groupKey, ...item };

          // Loi b field khng mong mun
          excludeFields.forEach(field => {
            delete newItem[field];
          });

          flat.push(newItem);
        });
      }
    });

    return flat;
  }

  const listJobs = useMemo(() => {
    return flattenJobs(importantJobs?.arrayJobs, {
      excludeGroups: EXCLUDE_GROUPS,
    });
  }, [importantJobs]);

  return { listJobs };
};
