// cho 544-444-4444 hoc 555-55-5555
export const formatInput = (input: string, type: 'phone' | 'ssn') => {
  try {
    const len = type === 'phone' ? [3, 6] : [3, 5];
    const length = type === 'phone' ? 10 : 9;
    if (input?.length > length) {
      return input;
    }
    const digits = input?.replace(/\D/g, '')?.slice(0, len[1] + 4);
    return digits?.replace(
      new RegExp(`(\\d{${len[0]}})(\\d{0,${len[1] - len[0]}})?(\\d{0,4})?`),
      (_, p1, p2, p3) => [p1, p2, p3]?.filter(Boolean)?.join('-'),
    );
  } catch (error) {
    console.error('formatInput err', error);
  }
};
