import React from 'react';
import { View } from 'react-native';
import { Text } from 'tradewize-component';
import {
  JobItem,
  JobStartTabStatus,
  JobTypeEnum,
  WarrantyData,
  JobStatusData,
  JobInfoData,
  HeaderData,
  AddressData,
  ActionData,
  WeatherData,
  HighlightStatus,
  ActivityGroup,
} from '../../../../types/myJobs.types';
import { styles } from './styles';
import DateCard from './components/DateCard';
import Header from './components/Header';
import Address from './components/Address';
import JobStatus from './components/JobStatus';
import JobInfo from './components/JobInfo';
import Weather from './components/Weather';
import ActionButtons from './components/ActionButtons';
import Warranty from './components/Warranty';
import { colors } from '../../../../theme';

interface CardItemProps {
  item: JobItem;
  jumpTo?: JobStartTabStatus;
}

export default function CardItem({ item, jumpTo }: CardItemProps) {
  const warrantyData: WarrantyData = item.warrantyData || {};
  const jobStatusData: JobStatusData = item.jobStatusData || {};
  const activityGroup: ActivityGroup = item.activityGroup || {};
  const jobInfoData: JobInfoData = item.jobInfoData || {};
  const headerData: HeaderData = item.headerData || {};
  const addressData: AddressData = item.addressData || {};
  const actionData: ActionData = item.actionData || {};
  const weatherData: WeatherData = item.weatherInfo || {};

  // Determine display date from dateCardData
  const displayDate = activityGroup?.estStartDate || '';

  // Check if it's a warranty job
  const jobTypeValue =
    item.jobType !== undefined && item.jobType !== null
      ? Number(item.jobType)
      : JobTypeEnum.OTHER;
  const isWarrantyJob = jobTypeValue === JobTypeEnum.WARRANTY;

  // Highlight data
  const highlightStatusName = item.highlightGroup?.highlightStatusName;
  const highlightStatusEnum = item.highlightGroup?.highlightStatusEnum;
  const statusHighlight = item.highlightGroup?.statusHighlight;

  return (
    <View style={styles.container}>
      <>
        {/* Date Card */}
        <DateCard date={displayDate} />

        {/* Main Card */}
        <View style={styles.card}>
          {/* Status Highlight Badge */}
          {highlightStatusName && (
            <View
              style={[
                highlightStatusEnum === HighlightStatus.ATTENTION_REQUIRED
                  ? styles.qiReviewBadge
                  : styles.projectReadyInprogress,
                styles.statusBadgeAbsolute,
              ]}
            >
              <Text
                style={{
                  color:
                    highlightStatusEnum === HighlightStatus.ATTENTION_REQUIRED
                      ? colors.red
                      : colors.green,
                }}
              >
                {statusHighlight?.name}
              </Text>
            </View>
          )}

          {/* Header - needs: headerData */}
          <Header headerData={headerData} isWarrantyJob={isWarrantyJob} />

          {/* Divider Line */}
          <View style={styles.divider} />

          {/* Address - needs: addressData */}
          <Address addressData={addressData} />

          {!isWarrantyJob ? (
            <View style={styles.mainContent}>
              {/* JobStatus - needs: jobStatusData */}
              <JobStatus jobStatusData={jobStatusData} jumpTo={jumpTo} />

              {/* JobInfo - needs: jobInfoData */}
              <JobInfo jobInfoData={jobInfoData} />

              {/* Weather - needs: weatherData */}
              {jumpTo === JobStartTabStatus.IN_PROGRESS && (
                <Weather weatherData={weatherData} />
              )}
            </View>
          ) : (
            /* Warranty - needs: warrantyData */
            <Warranty warrantyData={warrantyData} />
          )}

          {/* Divider Line */}
          <View style={styles.divider} />

          {/* ActionButtons - needs: actionData */}
          <ActionButtons
            jumpTo={jumpTo}
            actionData={actionData}
            isWarrantyJob={isWarrantyJob}
          />
        </View>
      </>
    </View>
  );
}
