import React, {
  forwardRef,
  useEffect,
  useImperativeHandle,
  useMemo,
  useRef,
  useState,
} from 'react';
import {
  Animated,
  ScrollView,
  View,
  NativeScrollEvent,
  NativeSyntheticEvent,
} from 'react-native';
import { Button, SCREEN_WIDTH, Text } from 'tradewize-component';
import { styles } from './styles';
import { ScrollTabViewProps, ScrollTabViewTabProps } from './types';

const Tab: React.FC<ScrollTabViewTabProps> = ({ children }) => <>{children}</>;

/* ================= REF ================= */
export type ScrollTabViewRef = {
  setIndex: (index: number) => void;
  jumpTo: (keyName: string) => void;
  getCurrentIndex: () => number;
};

/* ================= ROOT ================= */
const ScrollTabViewRoot = forwardRef<ScrollTabViewRef, ScrollTabViewProps>(
  (
    {
      children,
      initialIndex = 0,
      onIndexChange,
      swipeEnabled = true,
      lazy = false,
      lazyPreloadDistance = 0,
      keepAlive = true,
      style,
      tabBarStyle,
      indicatorStyle,
      activeIndicatorStyle,
      renderTabBar,
      scrollViewProps,
      activeTabStyle,
      activeTabTextStyle,
      activeTabIconStyle,
      textStyleInactive,
      styleSubtitle,
    },
    ref,
  ) => {
    const scrollRef = useRef<ScrollView>(null);
    const scrollX = useRef(
      new Animated.Value(initialIndex * SCREEN_WIDTH),
    ).current;

    const [activeIndex, setActiveIndex] = useState(initialIndex);
    const [mountedTabs, setMountedTabs] = useState<Set<number>>(
      new Set([initialIndex]),
    );

    const tabs = useMemo(
      () =>
        React.Children.toArray(children).filter(
          c => React.isValidElement(c) && c.type === Tab,
        ) as React.ReactElement<ScrollTabViewTabProps>[],
      [children],
    );

    /* ========== helpers ========== */
    const activateTab = (index: number) => {
      setActiveIndex(prev => {
        if (prev !== index) {
          onIndexChange?.(index);
        }
        return index;
      });

      setMountedTabs(prev => {
        if (!keepAlive) return new Set([index]);
        if (prev.has(index)) return prev;
        return new Set(prev).add(index);
      });
    };

    const setIndex = (index: number) => {
      if (index < 0 || index >= tabs.length) return;
      if (tabs[index].props.disabled) return;

      scrollRef.current?.scrollTo({
        x: index * SCREEN_WIDTH,
        animated: true,
      });
      activateTab(index);
    };

    const jumpTo = (keyName: string) => {
      const index = tabs.findIndex(t => t.props.keyName === keyName);
      if (index !== -1) setIndex(index);
    };

    const getCurrentIndex = () => activeIndex;

    useImperativeHandle(ref, () => ({
      setIndex,
      jumpTo,
      getCurrentIndex,
    }));

    const onMomentumScrollEnd = (
      e: NativeSyntheticEvent<NativeScrollEvent>,
    ) => {
      const index = Math.round(e.nativeEvent.contentOffset.x / SCREEN_WIDTH);
      activateTab(index);
    };

    /* ========== indicator ========== */
    const indicatorWidth = SCREEN_WIDTH / tabs.length;
    const translateX = scrollX.interpolate({
      inputRange: [0, SCREEN_WIDTH * (tabs.length - 1)],
      outputRange: [0, indicatorWidth * (tabs.length - 1)],
      extrapolate: 'clamp',
    });

    /* ========== did init ========== */
    const didInit = useRef(false);

    useEffect(() => {
      if (!didInit.current) {
        didInit.current = true;
        onIndexChange?.(initialIndex);
      }
    }, [initialIndex, onIndexChange]);

    /* ========== default tab bar ========== */
    const renderDefaultTabBar = () => (
      <View style={[styles.tabBar, tabBarStyle]}>
        {tabs.map((tab, index) => {
          const isActive = index === activeIndex;
          return (
            <Button
              key={tab.props.keyName}
              variant="ghost"
              style={[styles.tabItem, isActive && activeTabStyle]}
              disabled={tab.props.disabled}
              onPress={() => {
                if (tab.props.onPressTab) {
                  tab.props.onPressTab?.(index, setIndex);
                } else {
                  setIndex(index);
                }
              }}
            >
              {tab.props.icon && (
                <View style={[isActive && activeTabIconStyle]}>
                  {tab.props.icon}
                </View>
              )}

              <Text
                style={[
                  styles.tabText,
                  textStyleInactive,
                  isActive && styles.tabTextActive,
                  isActive && activeTabTextStyle,
                  tab.props.disabled && styles.tabTextDisabled,
                ]}
              >
                {tab.props.title}
                {tab.props.subtitle && '\n'}
                {tab.props.subtitle && (
                  <Text style={[styles.tabTextSubtitle, styleSubtitle]}>
                    {tab.props.subtitle}
                  </Text>
                )}
              </Text>

              {typeof tab.props.badge === 'number' && (
                <View style={styles.badge}>
                  <Text style={styles.badgeText}>{tab.props.badge}</Text>
                </View>
              )}
            </Button>
          );
        })}

        <Animated.View
          style={[
            styles.indicator,
            indicatorStyle,
            activeIndicatorStyle,
            {
              width: indicatorWidth,
              transform: [{ translateX }],
            },
          ]}
        />
      </View>
    );

    return (
      <View style={[styles.container, style]}>
        {renderTabBar
          ? renderTabBar({
              tabs: tabs.map((t: any) => ({
                keyName: t.props.keyName,
                title: t.props.title,
                lazy: t.props.lazy ?? lazy,
              })),
              activeIndex,
              jumpTo: setIndex,
              scrollX,
            })
          : renderDefaultTabBar()}

        <Animated.ScrollView
          ref={scrollRef}
          bounces={false}
          horizontal
          pagingEnabled
          scrollEnabled={swipeEnabled}
          showsHorizontalScrollIndicator={false}
          scrollEventThrottle={16}
          onMomentumScrollEnd={onMomentumScrollEnd}
          onScroll={Animated.event(
            [{ nativeEvent: { contentOffset: { x: scrollX } } }],
            { useNativeDriver: false },
          )}
          {...scrollViewProps}
        >
          {tabs.map((tab, index) => {
            const tabLazy = tab.props.lazy ?? lazy;

            const shouldRender =
              !tabLazy ||
              mountedTabs.has(index) ||
              Math.abs(index - activeIndex) <= lazyPreloadDistance;

            return (
              <View
                key={tab.props.keyName}
                style={[styles.page, { width: SCREEN_WIDTH }]}
              >
                {shouldRender ? tab.props.children : null}
              </View>
            );
          })}
        </Animated.ScrollView>
      </View>
    );
  },
);

export const ScrollTabView = Object.assign(ScrollTabViewRoot, { Tab });
