import React, { useEffect, useState } from 'react';
import { View, StyleSheet } from 'react-native';
import { colors } from '../../../../theme';
import { TouchableOpacity } from 'react-native';
import { SCREEN_WIDTH, Text } from 'tradewize-component';
import {
  STORAGE_KEY,
  StorageService,
} from '../../../../services/StorageService';

export const generateColor = (str: string): string => {
  let hash = 0;
  for (let i = 0; i < str.length; i++) {
    hash = str.charCodeAt(i) + ((hash << 5) - hash);
  }

  const hue = Math.abs(hash) % 360;
  return `hsl(${hue}, 65%, 65%)`;
};

interface GroupAvatarProps {
  membersInfo: {
    id: string;
    fullName?: string;
  }[];
  size?: number;
  onPress?: () => void;
  disableAvatar?: boolean;
}

const getInitials = (fullName?: string): string => {
  if (!fullName) {
    return '?';
  }
  return fullName.trim().charAt(0).toUpperCase();
};

export const GroupAvatar: React.FC<GroupAvatarProps> = ({
  membersInfo,
  size = SCREEN_WIDTH * 0.12,
  onPress,
  disableAvatar = false,
}) => {
  const [colorMap, setColorMap] = useState<Record<string, string>>({});

  useEffect(() => {
    const loadColors = async () => {
      try {
        const storedColors = await StorageService.get<Record<string, string>>(
          STORAGE_KEY.USER_AVATAR_COLORS,
        );

        const newColorMap = { ...storedColors };
        let hasNewColors = false;

        membersInfo.forEach(member => {
          if (!newColorMap[member.id]) {
            newColorMap[member.id] = generateColor(member.id);
            hasNewColors = true;
          }
        });

        if (hasNewColors) {
          await StorageService.set(STORAGE_KEY.USER_AVATAR_COLORS, newColorMap);
        }

        setColorMap(newColorMap);
      } catch (error) {
        console.error('Li khi ti mu avatar:', error);
        const newColorMap: Record<string, string> = {};
        membersInfo.forEach(member => {
          newColorMap[member.id] = generateColor(member.id);
        });
        setColorMap(newColorMap);
      }
    };

    loadColors();
  }, [membersInfo]);

  if (membersInfo.length === 1) {
    const member = membersInfo[0];
    const initial = getInitials(member.fullName);
    const backgroundColor = colorMap[member.id] || generateColor(member.id);

    return (
      <View
        style={[
          styles.singleAvatar,
          {
            width: size,
            height: size,
            borderRadius: size / 2,
            backgroundColor,
          },
        ]}
      >
        <Text style={[styles.initialText, { fontSize: size * 0.4 }]}>
          {initial}
        </Text>
      </View>
    );
  }

  const getLayout = () => {
    const avatarSize = size * 0.7; // 70% of container size

    switch (membersInfo.length) {
      case 2:
        return {
          positions: [
            { left: 0, top: 0, width: avatarSize, height: avatarSize },
            { right: 0, bottom: 0, width: avatarSize, height: avatarSize },
          ],
        };
      case 3:
        return {
          positions: [
            { left: 0, top: 0, width: avatarSize, height: avatarSize },
            { right: 0, top: 0, width: avatarSize, height: avatarSize },
            {
              left: size * 0.15,
              bottom: 0,
              width: avatarSize,
              height: avatarSize,
            },
          ],
        };
      case 4:
        return {
          positions: [
            { left: 0, top: 0, width: avatarSize, height: avatarSize },
            { right: 0, top: 0, width: avatarSize, height: avatarSize },
            { left: 0, bottom: 0, width: avatarSize, height: avatarSize },
            { right: 0, bottom: 0, width: avatarSize, height: avatarSize },
          ],
        };
      default:
        return {
          positions: [
            { left: 0, top: 0, width: avatarSize, height: avatarSize },
            { right: 0, top: 0, width: avatarSize, height: avatarSize },
            { left: 0, bottom: 0, width: avatarSize, height: avatarSize },
            { right: 0, bottom: 0, width: avatarSize, height: avatarSize },
          ],
        };
    }
  };

  const { positions } = getLayout();
  const visibleMembers = membersInfo.slice(0, 4);
  const remainingCount = membersInfo.length - 4;

  return (
    <TouchableOpacity
      disabled={disableAvatar}
      onPress={onPress}
      style={[styles.container, { width: size, height: size }]}
    >
      {visibleMembers.map((member, index) => {
        const initial = getInitials(member.fullName);
        const backgroundColor = colorMap[member.id] || generateColor(member.id);

        return (
          <View
            key={member.id || index}
            style={[
              styles.avatarContainer,
              positions[index],
              { borderRadius: positions[index].width / 2, backgroundColor },
            ]}
          >
            <Text
              style={[
                styles.initialText,
                { fontSize: positions[index].width * 0.5 },
              ]}
            >
              {initial}
            </Text>
          </View>
        );
      })}
      {remainingCount > 0 && (
        <View style={[styles.remainingContainer, { borderRadius: size / 4 }]}>
          <Text style={styles.remainingText}>+{remainingCount}</Text>
        </View>
      )}
    </TouchableOpacity>
  );
};

const styles = StyleSheet.create({
  container: {
    position: 'relative',
    overflow: 'hidden',
    backgroundColor: colors.twColors.background.secondary,
    borderRadius: 20,
  },
  singleAvatar: {
    overflow: 'hidden',
    justifyContent: 'center',
    alignItems: 'center',
  },
  avatarContainer: {
    position: 'absolute',
    overflow: 'hidden',
    borderWidth: 2,
    borderColor: colors.twColors.background.primary,
    justifyContent: 'center',
    alignItems: 'center',
  },
  initialText: {
    color: 'white',
    fontWeight: 'bold',
    textAlign: 'center',
    textShadowColor: 'rgba(0, 0, 0, 0.2)',
    textShadowOffset: { width: 0, height: 1 },
    textShadowRadius: 1,
  },
  remainingContainer: {
    position: 'absolute',
    bottom: 0,
    right: 0,
    width: '50%',
    height: '50%',
    backgroundColor: colors.twColors.background.secondary,
    justifyContent: 'center',
    alignItems: 'center',
  },
  remainingText: {
    color: colors.twColors.text.darkGray,
    fontSize: 12,
    fontWeight: 'bold',
  },
});
