import { useEffect, useMemo, useRef, useState } from 'react';
import {
  AppState,
  AppStateStatus,
  Keyboard,
  Platform,
  TouchableWithoutFeedback,
  View,
} from 'react-native';
import { BaseContainer, HeaderBase, Text } from 'tradewize-component';
import { NavigationService } from '../../services/NavigationService';
import { useAppTranslation } from '../../hooks/useAppTranslation';
import { styles } from './styles';
import { useRoute } from '@react-navigation/native';
import { GestureHandlerRootView } from 'react-native-gesture-handler';
import { ChatHeader } from './components/Header';
import { SocketStatus } from '../../services/ChatSocketService';
import { KeyboardAvoidingView } from 'react-native-keyboard-controller';
import ChatContainer from '../../components/ChatContainer';
import { keyboardOffset } from '../../global/styles';

interface RouteParams {
  chatId: string;
  chatName: string;
  memberInfo: any[];
  isGroup: boolean;
  workOrderId?: string;
  purchaseOrderId?: string;
  scheduleActivityId?: string;
  isUnread?: boolean;
  jumpTo?: string;
}

const MessageDetailScreen = () => {
  const route = useRoute();
  const params = route?.params as RouteParams;
  const appState = useRef<AppStateStatus>(AppState.currentState);

  const [statusSocket, setStatusSocket] = useState(SocketStatus.CONNECTING);

  const { t } = useAppTranslation();

  useEffect(() => {
    const subscription = AppState.addEventListener('change', nextAppState => {
      const prevState = appState.current;

      // Foreground -> Background
      if (prevState === 'active' && nextAppState.match(/inactive|background/)) {
        Keyboard.dismiss();
      }

      appState.current = nextAppState;
    });

    return () => subscription.remove();
  }, []);

  const getTextStatus = useMemo(() => {
    switch (statusSocket) {
      case SocketStatus.CONNECTING:
        return t('__MESSAGE_SCREEN.CONNECTION_STATUS.CONNECTING');
      case SocketStatus.DISCONNECTED:
        return t('__MESSAGE_SCREEN.CONNECTION_STATUS.DISCONNECTED');
      case SocketStatus.RECONNECTING:
        return t('__MESSAGE_SCREEN.CONNECTION_STATUS.RECONNECTING');
      case SocketStatus.ERROR:
        return t('__MESSAGE_SCREEN.CONNECTION_STATUS.ERROR');
      default:
        break;
    }
  }, [statusSocket, t]);

  return (
    <GestureHandlerRootView style={styles.container}>
      <BaseContainer style={styles.container}>
        <HeaderBase
          title={t('MESSAGES')}
          onPressBack={() => NavigationService.goBack()}
          titleAlign="left"
          styleButtonLeft={styles.buttonHeaderLeft}
        />

        <ChatHeader
          chatName={params?.chatName || ''}
          memberInfo={params?.memberInfo}
          isGroup={params?.isGroup}
        />

        {statusSocket !== SocketStatus.CONNECTED && (
          <View style={styles.statusSocket}>
            <View style={styles.statusSocketDot} />
            <Text style={styles.statusSocketText}>{getTextStatus}</Text>
          </View>
        )}

        <KeyboardAvoidingView
          keyboardVerticalOffset={keyboardOffset}
          behavior={Platform.OS === 'ios' ? 'height' : 'padding'}
          style={styles.chatContainer}
        >
          <TouchableWithoutFeedback onPress={() => Keyboard.dismiss()}>
            <View style={styles.chatContainer}>
              <ChatContainer
                isKeyboardInternallyHandled={false}
                chatId={params?.chatId}
                // isUnread={isUnread}
                onCheckStatusSocket={status => {
                  setStatusSocket(status);
                }}
              />
            </View>
          </TouchableWithoutFeedback>
        </KeyboardAvoidingView>
      </BaseContainer>
    </GestureHandlerRootView>
  );
};

export default MessageDetailScreen;
