import React, { useCallback, useEffect, useRef, useState } from 'react';
import { useSelector } from 'react-redux';
import {
  ChatSocketService,
  SocketStatus,
} from '../../services/ChatSocketService';
import {
  AttachmentProps,
  ChatContainerProps,
  SocketChatMessage,
} from './types';
import { AppState, StyleSheet, View } from 'react-native';
import {
  handleSendMessage,
  normalizeId,
  onMessage,
  preprocessMessages,
  setTypeFile,
} from '../../helper/Message.helper';
import { GiftedChat, IMessage } from 'tradewize-component';
// import { getNotiMessagesAsync } from 'src/reduxs/slices/app-slice';
import { colors } from '../../theme';
import { getMessagesByRoomId } from '../../api/endpoint';
import { logger } from '../../utils';

const ChatContainer = ({
  chatId,
  isKeyboardInternallyHandled = true,
  onFocusInput,
  // isUnread,
  onCheckStatusSocket,
  onBlurInput,
  useScrollView = false,
  giftedChatProps,
  isLoading,
}: ChatContainerProps) => {
  // const  dispatch = useDispatch();
  const socketService = ChatSocketService.getInstance(chatId);
  const prevTypingRef = useRef(false);
  const hasFetchedRef = useRef(false);

  const { userProfile } = useSelector((state: any) => state?.auth);
  const { token } = useSelector((state: any) => state?.auth);

  const [messages, setMessages] = useState<IMessage[]>([]);
  const messagesRef = useRef<IMessage[]>([]);
  const [isTyping, setIsTyping] = useState(false);
  const [isFetching, setIsFetching] = useState(false);
  const [inputText, setInputText] = useState('');
  const [statusSocket, setStatusSocket] = useState(SocketStatus.CONNECTING);

  socketService.subscribeStatus((status: SocketStatus) => {
    onCheckStatusSocket?.(status);
    setStatusSocket(status);
  });

  const handleSendTyping = useCallback(
    (typing: boolean) => {
      try {
        socketService.send({
          action: SocketChatMessage.USER_TYPING,
          isTyping: typing,
          userId: userProfile?.accountId,
          token,
        });
      } catch (error) {
        console.log('handleSendTyping error', error);
      }
    },
    [socketService, token, userProfile?.accountId],
  );

  const handleFocusInput = useCallback(() => {
    try {
      setIsFetching(true);
      onFocusInput?.();
    } catch (error) {
      console.log('handleFocusInput error', error);
    }
  }, [onFocusInput]);

  const handleBlurInput = useCallback(() => {
    try {
      setIsFetching(false);
      onBlurInput?.();
    } catch (error) {
      console.log('handleBlurInput error', error);
    }
  }, [onBlurInput]);

  // Memoize onMessage callback  trnh re-render khng cn thit
  const handleOnMessage = useCallback(
    async (data: any) => {
      await onMessage(data, setMessages, messagesRef, setIsTyping);
    },
    [setMessages, messagesRef, setIsTyping],
  );

  const fetchData = useCallback(async () => {
    try {
      const client = await import('auth/api');
      const rawMessages = await client.getRequest(
        `${getMessagesByRoomId}/${chatId}`,
      );
      const formattedMessages: IMessage[] = rawMessages?.data
        ?.sort(
          (a: any, b: any) =>
            new Date(b?.sentAt).getTime() - new Date(a?.sentAt).getTime(),
        )
        ?.map((item: any, index: number) => {
          let baseMessage: IMessage = {
            _id: item?.id + '-' + (Math.random() + 1).toString(),
            isLast: index === 0,
            text: item?.content,
            createdAt: item?.sentAt,
            user: {
              _id: item?.senderId,
              name: `${item?.firstName} ${item?.lastName}`,
            },
          };

          if (item?.quotedMessage) {
            let file: any[] = [];
            if (item.quotedMessage?.attachment?.length > 0) {
              file = item.quotedMessage.attachment.map(
                (attachment: AttachmentProps) => ({
                  ...attachment,
                  uri: attachment?.url,
                  typeFile: setTypeFile(attachment?.fileExtension),
                  progress: 100,
                }),
              );
            }

            const messageReply: IMessage = {
              _id: item.quotedMessage.id,
              text: item.quotedMessage.content,
              createdAt: new Date(item.quotedMessage.sentAt),
              user: {
                _id: item.quotedMessage.senderId,
                name: `${item.quotedMessage.firstName} ${item.quotedMessage.lastName}`,
              },
              ...(file?.length > 0 && { file }),
            };

            baseMessage.messageReply = messageReply;
          }

          if (item?.attachment?.length > 0) {
            const file = item.attachment.map((attachment: AttachmentProps) => ({
              ...attachment,
              uri: attachment?.url,
              typeFile: setTypeFile(attachment?.fileExtension),
              progress: 100,
            }));
            baseMessage.file = file;
          }

          if (item?.messageReactions?.length > 0) {
            baseMessage.reactionEmoji = item?.messageReactions;
          }

          return baseMessage;
        });

      const processedMessages = preprocessMessages(
        formattedMessages,
        userProfile?.accountId || '',
      );

      messagesRef.current = processedMessages;
      setMessages(processedMessages);
    } catch (error) {
      console.error('Failed to fetch messages:', error);
    } finally {
      // isUnread && dispatch(getNotiMessagesAsync() as any);
    }
    // eslint-disable-next-line react-hooks/exhaustive-deps
  }, [chatId]);

  const pushSocketEvent = useCallback((payload: any) => {
    try {
      setInputText('');

      if (!payload) {
        throw new Error('Payload is required');
      }

      delete payload.file;

      socketService.send({
        action: SocketChatMessage.SEND_MESSAGE,
        token,
        ...payload,
      });
    } catch (error) {
      logger('[pushSocketEvent]  Failed to push socket event:', error);
    }
    // eslint-disable-next-line react-hooks/exhaustive-deps
  }, []);

  useEffect(() => {
    const _isTyping = inputText?.length > 0 && isFetching;

    // ch call khi chuyn t false  true
    if (_isTyping && !prevTypingRef.current) {
      handleSendTyping(true);
    }

    // ch call khi chuyn t true  false
    if (!_isTyping && prevTypingRef.current) {
      handleSendTyping(false);
    }

    prevTypingRef.current = _isTyping;
  }, [inputText, isFetching, handleSendTyping]);

  // Reset state khi chatId thay i  trnh cache messages
  useEffect(() => {
    hasFetchedRef.current = false;
    setMessages([]);
    messagesRef.current = [];
    setInputText('');
    setIsTyping(false);
  }, [chatId]);

  useEffect(() => {
    if (chatId && !hasFetchedRef.current && !isLoading) {
      fetchData();
      hasFetchedRef.current = true;
    }
  }, [chatId, fetchData, isLoading]);

  useEffect(() => {
    const sub = AppState.addEventListener('change', state => {
      if (state === 'active' && chatId) {
        fetchData();
      }
    });
    return () => sub.remove();
  }, [chatId, fetchData]);

  // Sync messagesRef vi messages state
  useEffect(() => {
    if (messages) {
      messagesRef.current = messages;
    }
  }, [messages]);

  useEffect(() => {
    socketService.connect(handleOnMessage);
    return () => {
      socketService.close();
    };
  }, [handleOnMessage, socketService]);

  return (
    <View style={styles.container}>
      <GiftedChat
        useScrollView={useScrollView}
        shouldUpdateMessage={(prev: any, next: any) => prev._id !== next._id}
        disableComposer={statusSocket !== SocketStatus.CONNECTED}
        isTyping={isTyping}
        isKeyboardInternallyHandled={isKeyboardInternallyHandled}
        messages={messages}
        onSend={(messages: IMessage[]) => {
          handleSendMessage(
            messages,
            chatId,
            setMessages,
            messagesRef,
            pushSocketEvent,
          );
        }}
        onInputTextChanged={(text: string) => setInputText(text)}
        onFocusInput={handleFocusInput}
        onBlurInput={handleBlurInput}
        user={{
          _id: userProfile?.accountId || '',
          name: userProfile?.fullName || '',
        }}
        alwaysShowSend
        listViewProps={{
          initialNumToRender: 10,
          maxToRenderPerBatch: 10,
          windowSize: 5,
          removeClippedSubviews: true,
          nestedScrollEnabled: true,
        }}
        onReactionEmoji={(emoji: string, messageId: string) => {
          setMessages(prevMessages =>
            prevMessages.map(msg => {
              if (msg._id === messageId) {
                const currentReactions = msg.reactionEmoji || [];
                const existingReactionIndex = currentReactions.findIndex(
                  (reaction: any) => reaction.userId === userProfile?.accountId,
                );

                let updatedReactions;
                if (existingReactionIndex >= 0) {
                  // Cp nht phn ng hin c
                  updatedReactions = currentReactions.map(
                    (reaction: any, index: number) =>
                      index === existingReactionIndex
                        ? { ...reaction, reaction: emoji }
                        : reaction,
                  );
                } else {
                  // Thm phn ng mi
                  updatedReactions = [
                    ...currentReactions,
                    {
                      id: `temp-reaction-${Date.now()}`,
                      reaction: emoji,
                      userId: userProfile?.accountId || '',
                      accountId: userProfile?.accountId || '',
                      createdAt: new Date().toISOString(),
                    },
                  ];
                }

                return {
                  ...msg,
                  reactionEmoji: updatedReactions,
                };
              }
              return msg;
            }),
          );

          socketService.send({
            action: SocketChatMessage.ADD_REACTION,
            token,
            messageId: normalizeId(messageId),
            reaction: emoji,
            userId: userProfile?.accountId || '',
            chatId,
          });
        }}
        {...giftedChatProps}
      />
    </View>
  );
};

const styles = StyleSheet.create({
  container: {
    flex: 1,
  },
  textDisconnect: {
    color: colors.black,
    fontSize: 14,
    textAlign: 'center',
  },
});

export default ChatContainer;
