import React from 'react';
import { ActivityIndicator, FlatList, View } from 'react-native';
import { Button, Text } from 'tradewize-component';
import { useAppTranslation } from '../../../../hooks/useAppTranslation';
import { useDispatch, useSelector } from 'react-redux';
import { styles } from './styles';
import { IconClose } from '../../../../assets/svg';
import { colors } from '../../../../theme';
import { SvgXml } from 'react-native-svg';
import { logger } from '../../../../utils';
import { useImportantThing } from '../../../../hooks/useImportantThing';

enum TypeJobs {
  reportJobsProgress = 'reportJobsProgress',
  jobReadyInspection = 'jobReadyInspection',
  jobStart = 'jobStart',
  verifyMaterialPO = 'verifyMaterialPO',
  verifyMaterialVPOJob = 'verifyMaterialVPOJob',
}

const JobItem = ({ item }: { item: any }) => {
  const { t } = useAppTranslation();

  const renderTextJobs = (
    typeJobs: TypeJobs,
  ): { title: string; buttonText: string } => {
    try {
      const { street, city, state } = item.addressData || {};
      const parts = [street, city, state].filter(Boolean);
      const addressInfo = parts.length > 0 ? ` - ${parts.join(', ')}` : '';

      switch (typeJobs) {
        case 'reportJobsProgress':
          return {
            title: `${t('REPORT_JOB_PROGRESS_FOR')}: ${item.communityCode}/${
              item.lotNumber
            }${addressInfo}`,
            buttonText: t('CLICK_TO_ENTER_PROGRESS'),
          };
        case 'jobReadyInspection':
          return {
            title: `${t('JOB_READY_INSPECTION_FOR')}: ${item.communityCode}/${
              item.lotNumber
            }-${item.houseNumber}${addressInfo}`,
            buttonText: t('OPEN_JOB_READY_CHECKLIST'),
          };
        case 'jobStart':
          return {
            title: `${t('JOB_START_CHECK_LIST_FOR')}: ${item.communityCode}/${
              item.lotNumber
            }${addressInfo}`,
            buttonText: t('OPEN_JOB_START_CHECKLIST'),
          };
        case 'verifyMaterialPO':
          return {
            title: `${t('VERIFY_MATERIAL_FOR')}: ${item.communityCode}/${
              item.lotNumber
            }${addressInfo}`,
            buttonText: t('OPEN_VERIFY_MATERIAL'),
          };
        case 'verifyMaterialVPOJob':
          return {
            title: `${t('VERIFY_MATERIAL_VPO_FOR')}: ${item.communityCode}/${
              item.lotNumber
            }${addressInfo}`,
            buttonText: t('OPEN_VERIFY_MATERIAL_VPO'),
          };
        default:
          return { title: '', buttonText: '' };
      }
    } catch (error) {
      logger('error renderTitleJobs', error);
      return { title: '', buttonText: '' };
    }
  };

  return (
    <View style={styles.jobItemContainer}>
      <Text style={styles.jobItemTitle}>
        {renderTextJobs(item.type)?.title}
      </Text>
      <Button
        style={styles.jobItemButton}
        title={renderTextJobs(item.type)?.buttonText}
        textStyle={styles.jobItemButtonText}
      />
    </View>
  );
};

const ImportantThings = () => {
  const { t } = useAppTranslation();
  const dispatch = useDispatch();

  const { listJobs } = useImportantThing();

  const isLoading = useSelector(
    (state: any) => state.home.importantJobs.isLoading,
  );

  const handleClose = async () => {
    try {
      const redux = await import('auth/redux');
      dispatch(redux.actionContainer?.hideCustomModal());
    } catch (error) {
      logger('error handleClose', error);
    }
  };

  const renderEmptyComponent = () => {
    if (isLoading) {
      return (
        <View style={styles.jobsListEmptyContainer}>
          <ActivityIndicator size="large" color={colors.mainColorPrimary} />
        </View>
      );
    }
    return (
      <View style={styles.jobsListEmptyContainer}>
        <Text style={styles.jobsListEmptyText}>{t('NO_JOBS_FOUND')}</Text>
      </View>
    );
  };

  return (
    <View style={styles.container}>
      <View style={styles.contentContainer}>
        <View style={styles.headerContainer}>
          <Text style={styles.headerTitle}>{t('IMPORTANT_THINGS_TO_DO')}</Text>
          <Button
            onPress={handleClose}
            variant="ghost"
            style={styles.closeButton}
          >
            <SvgXml xml={IconClose(colors.black)} width={24} height={24} />
          </Button>
        </View>

        <View style={styles.jobsContainer}>
          <Text style={styles.jobsTitle}>
            {t('YOU_HAVE_NUMBER_PENDING_ACTIONS', { number: listJobs.length })}
          </Text>

          <FlatList
            data={listJobs}
            renderItem={({ item }) => <JobItem item={item} />}
            keyExtractor={(_item, index) => index.toString()}
            ListEmptyComponent={renderEmptyComponent}
            showsVerticalScrollIndicator={false}
            style={styles.jobsListContainer}
          />
        </View>
      </View>
    </View>
  );
};

export default ImportantThings;
