import React, { useCallback, useState } from 'react';
import {
  View,
  FlatList,
  TouchableNativeFeedback,
  Keyboard,
} from 'react-native';
import {
  BaseContainer,
  HeaderBase,
  Input,
  SCREEN_WIDTH,
} from 'tradewize-component';
import { NavigationService } from '../../services/NavigationService';
import { styles } from './styles';
import { SvgXml } from 'react-native-svg';
import { IconSearch } from '../../assets/svg';
import { colors } from '../../theme';
import { useMessage } from '../../hooks/useMessage';
import { useSelector } from 'react-redux';
import { MyRooms } from './types';
import { ChatListItem } from './components/CardItem';
import { useSafeAreaInsets } from 'react-native-safe-area-context';
import { useAppTranslation } from '../../hooks/useAppTranslation';
import { SCREEN_ENUM } from '../../types/global';
import BadgeService from '../../services/BadgeService';

const MessageScreen = () => {
  const { t } = useAppTranslation();
  const [isFocused, setIsFocused] = useState(false);
  const insets = useSafeAreaInsets();
  const myRooms: MyRooms[] = useSelector(
    (state: any) => state.message.myRooms.data,
  );
  useMessage();

  const handleChatPress = useCallback(async (chat: MyRooms) => {
    await BadgeService.decrease(chat?.unreadMessages || 0);
    NavigationService.navigate(SCREEN_ENUM.MessageDetailScreen, {
      chatId: chat.id,
      chatName: chat.chatName,
      memberInfo: chat.memberInfo,
      isGroup: chat.isGroup,
      useChatV2: true,
      purchaseOrderId: chat?.purchaseOrderId,
      scheduleActivityId: chat?.scheduleActivityId,
    });
  }, []);

  return (
    <BaseContainer
      isPaddingToSafeArea={false}
      style={[styles.safeArea, { paddingTop: insets.top }]}
    >
      <TouchableNativeFeedback onPress={() => Keyboard.dismiss()}>
        <View style={styles.contentContainer}>
          <HeaderBase
            title={t('MESSAGES')}
            titleAlign="left"
            onPressBack={() => NavigationService.goBack()}
            styleButtonLeft={styles.buttonHeaderLeft}
          />
          <View style={styles.container}>
            <Input
              containerStyle={styles.searchInputContainer}
              inputContainerStyle={[
                styles.inputContainerStyle,
                isFocused && styles.inputContainerStyleFocused,
              ]}
              style={styles.searchInput}
              onFocus={() => setIsFocused(true)}
              onBlur={() => setIsFocused(false)}
              placeholder={t('SEARCH')}
              placeholderTextColor={colors.grayScale60}
              leftIcon={
                <SvgXml
                  xml={IconSearch(colors.grayScale70)}
                  width={SCREEN_WIDTH * 0.05}
                  height={SCREEN_WIDTH * 0.05}
                />
              }
            />

            <FlatList
              data={myRooms || []}
              renderItem={({ item }) => (
                <ChatListItem
                  chat={item}
                  onPress={() => handleChatPress(item)}
                />
              )}
              keyExtractor={(item: MyRooms) => item?.id}
              contentContainerStyle={styles.listContent}
              showsVerticalScrollIndicator={false}
            />
          </View>
        </View>
      </TouchableNativeFeedback>
    </BaseContainer>
  );
};

export default MessageScreen;
