import { useCallback, useEffect } from 'react';
import { logger } from '../utils';
import { useDispatch } from 'react-redux';

export const useMessage = () => {
  const dispatch = useDispatch();
  const fetchGetMyRooms = useCallback(async () => {
    try {
      const redux = await import('auth/redux');
      dispatch(redux.actionContainer.getMyRoomsAsync());
    } catch (error) {
      logger('error fetchGetMyRooms', error);
    }
  }, [dispatch]);

  useEffect(() => {
    fetchGetMyRooms();
  }, [fetchGetMyRooms]);
};
