import { useCallback, useEffect, useState } from 'react';
import { useDispatch, useSelector } from 'react-redux';
import { logger } from '../utils';
import { getCurrentLocation } from '../helper';
import { JobItem } from '../screens/MyJobScreen/types';
import RNMapView from 'react-native-maps';
import { FilterOption } from '../screens/JobBoard/components/Filter/types';
import { initialFilterOptions } from '../screens/JobBoard/components/Filter/model';

export interface FetchHouseHasJobWorkerParams {
  FullName?: string;
  LotNumber?: number;
  Latitude?: number;
  Longitude?: number;
  CommunityId?: string;
  Radius?: number;
  CompanyId?: string;
  WorkCategoryId?: string;
  State?: string;
  City?: string;
  Country?: string;
  StartDate?: Date;
  EndDate?: Date;
  LocationName?: string;
}

export interface Location {
  latitude: number;
  longitude: number;
  latitudeDelta: number;
  longitudeDelta: number;
}

interface UseJobBoardProps {
  mapRef: React.RefObject<RNMapView>;
}

export const useJobBoard = ({ mapRef }: UseJobBoardProps) => {
  const dispatch = useDispatch();
  const allJobs = useSelector(
    (state: any) => state.jobBoard.houseHasJobWorker.data || [],
  );
  const [currentLocation, setCurrentLocation] = useState<Location>({
    latitude: 0,
    longitude: 0,
    latitudeDelta: 0.1,
    longitudeDelta: 0.1,
  });
  const [filter, setFilter] = useState<FilterOption[]>(initialFilterOptions);

  const fetchHouseHasJobWorker = useCallback(async () => {
    try {
      const redux = await import('auth/redux');
      dispatch(redux.actionContainer.showLoadingOverlay());
      const location = await getCurrentLocation();
      setCurrentLocation({
        latitude: location?.latitude,
        longitude: location?.longitude,
        latitudeDelta: 0.1,
        longitudeDelta: 0.1,
      });
      mapRef.current?.animateToRegion({
        latitude: location?.latitude || 0,
        longitude: location?.longitude || 0,
        latitudeDelta: 0.1,
        longitudeDelta: 0.1,
      });
      let payload: FetchHouseHasJobWorkerParams = {};
      if (location) {
        payload = {
          ...payload,
          Latitude: location.latitude,
          Longitude: location.longitude,
        };
      }

      dispatch(redux.actionContainer.fetchHouseHasJobWorkerAsync(payload));
    } catch (error) {
      logger('error fetchHouseHasJobWorker', error);
    }
  }, [dispatch, mapRef]);

  const fetchCommunityUserHasJob = useCallback(async () => {
    try {
      const redux = await import('auth/redux');
      dispatch(redux.actionContainer.getCommunityUserHasJobAsync());
    } catch (error) {
      logger('error fetchCommunityUserHasJob', error);
    }
  }, [dispatch]);

  const moveToUserLocation = useCallback(() => {
    try {
      const arrJobsSelected = allJobs?.filter((job: JobItem) => job?.isChecked);
      if (arrJobsSelected.length > 0) {
        const latSelected = arrJobsSelected[0].location.latitude;
        const lngSelected = arrJobsSelected[0].location.longitude;
        mapRef.current?.animateToRegion(
          {
            latitude: latSelected,
            longitude: lngSelected,
            latitudeDelta: 0.01,
            longitudeDelta: 0.01,
          },
          1500,
        );
      }
    } catch (error) {
      logger('error moveToUserLocation', error);
    }
  }, [allJobs, mapRef]);

  useEffect(() => {
    moveToUserLocation();
  }, [moveToUserLocation]);

  useEffect(() => {
    fetchHouseHasJobWorker();
  }, [fetchHouseHasJobWorker]);

  useEffect(() => {
    fetchCommunityUserHasJob();
  }, [fetchCommunityUserHasJob]);

  return {
    currentLocation,
    filter,
    setFilter,
  };
};
