import { Linking, Platform } from 'react-native';
import RNFS from 'react-native-fs';
import Share from 'react-native-share';
import { FileW9 } from '../types/PaymentInfoW9';

export const validateSsnValue = (value: string) => {
  if (!value?.trim()) {
    return 'SSN_OR_ITIN_IS_REQUIRED';
  }
  const digits = value.replace(/\D/g, '');
  if (digits.length !== 9) {
    return 'MUST_BE_IN_FORMAT_XXX_XX_XXXX_E_G_231_45_4047';
  }
  return null;
};

export interface BankInfoFormValues {
  BANK_NAME: string;
  ACCOUNT_NUMBER: string;
  ROUTING_NUMBER: string;
}

export const initialValues: BankInfoFormValues = {
  BANK_NAME: '',
  ACCOUNT_NUMBER: '',
  ROUTING_NUMBER: '',
};

// Move fields outside component to ensure it's always the same reference
export const FIELDS = [
  'BANK_NAME',
  'ACCOUNT_NUMBER',
  'ROUTING_NUMBER',
] as (keyof BankInfoFormValues)[];

export const getFieldLabel = (field: keyof BankInfoFormValues, t: any) => {
  switch (field) {
    case 'BANK_NAME':
      return t('BANK_NAME');
    case 'ACCOUNT_NUMBER':
      return t('ACCOUNT_NUMBER');
    case 'ROUTING_NUMBER':
      return t('ROUTING_NUMBER');
    default:
      return '';
  }
};

export const getFieldPlaceholder = (
  field: keyof BankInfoFormValues,
  t: any,
) => {
  switch (field) {
    case 'BANK_NAME':
      return t('ENTER_BANK_NAME') || t('BANK_NAME');
    case 'ACCOUNT_NUMBER':
      return t('ENTER_ACCOUNT_NUMBER') || t('ACCOUNT_NUMBER');
    case 'ROUTING_NUMBER':
      return t('ENTER_ROUTING_NUMBER') || t('ROUTING_NUMBER');
    default:
      return '';
  }
};

export const downloadPdfBlobUtil = async (
  pdfUrl: string,
  fileName = 'document.pdf',
) => {
  try {
    if (Platform.OS === 'android') {
      Linking.openURL(pdfUrl);
      return;
    } else {
      const localPath = `${RNFS.DocumentDirectoryPath}/${fileName}`;

      await RNFS.downloadFile({
        fromUrl: pdfUrl,
        toFile: localPath,
      }).promise;

      await Share.open({
        url: `file://${localPath}`,
        type: 'application/pdf',
        saveToFiles: true,
        filename: fileName,
      });
    }
  } catch (error: any) {
    console.error('Download error:', error);
  }
};

export const getFileLabel = (pdf: FileW9) => {
  if (!pdf?.fileName) return '';
  return pdf?.fileName?.length > 25
    ? `${pdf?.fileName?.slice(0, 22)}...`
    : pdf?.fileName;
};

export const maskString = (input: string, type: 'name' | 'number'): string => {
  if (!input || input.length === 0) return '';

  if (type === 'name') {
    return '*' + input.slice(1);
  }

  if (type === 'number') {
    if (input.length <= 7) {
      return '*'.repeat(input.length);
    }
    return '*'.repeat(7) + input.slice(7);
  }

  return input;
};
