import React, { useState } from 'react';
import { StyleSheet, View } from 'react-native';
import { Text, Button, SCREEN_WIDTH } from 'tradewize-component';
import { colors } from '../../../../../theme';
import { SvgXml } from 'react-native-svg';
import { IconCheck, IconComment } from '../../../../../assets/svg';
import {
  ChecklistItem,
  FileSetting,
} from '../../../../../types/jobDetail.type';
import { useAppTranslation } from '../../../../../hooks/useAppTranslation';
import {
  FileThumbnail,
  FileInfo,
  FileViewer,
} from '../../../../../components/FileViewer';
import { dispatch } from '../../../../../helper';
import { actionContainer } from '../../../../../../../Auth/src/redux';
import { isPassValidateFilesByFileSetting } from '../../../../../helper/checkList.helpter';
import {
  hideAlertAsync,
  showAlertAsync,
} from '../../../../../../../Auth/src/redux/features/app/actions';

interface ChecklistItemContentProps {
  item: ChecklistItem;
}

const ChecklistItemContent: React.FC<ChecklistItemContentProps> = ({
  item,
}) => {
  const { t } = useAppTranslation();
  const [selectedFile, setSelectedFile] = useState<FileInfo | null>(null);

  // Get all media from systemFiles and systemVideos (instruction files)
  const systemFiles = item.systemFiles || [];
  const systemVideos = item.systemVideos || [];

  const allSystemMedia: FileInfo[] = [];

  console.log('item', item);

  systemFiles.forEach((file: any) => {
    if (file?.url) {
      allSystemMedia.push({
        ...file,
        uri: file.url,
        type: 'file',
      });
    }
  });

  systemVideos.forEach((video: any) => {
    if (video?.url) {
      allSystemMedia.push({
        ...video,
        uri: video.url,
        type: 'video',
      });
    }
  });

  const hasSystemMedia = allSystemMedia.length > 0;
  const hasResponses = item.response && item.response.length > 0;

  const handleMediaPress = (file: FileInfo) => {
    setSelectedFile(file);
  };

  const handleCloseViewer = () => {
    setSelectedFile(null);
  };

  const handleCheckboxPress = () => {
    if (!isPassValidateFilesByFileSetting(item)) {
      dispatch(
        showAlertAsync({
          visible: true,
          title: t('WARNING'),
          message: t('CHECK_REQUIRED_FILES'),
          type: 'warning',
          buttonText: t('CLOSE'),
          onClose: () => {
            dispatch(hideAlertAsync());
          },
        }),
      );
      return;
    }

    dispatch(
      actionContainer.getCommentChecklistAsync({
        id: item.id,
        isChecked: !item.isChecked,
      }),
    );
  };

  const renderFileRequirements = () => {
    const fileSettings = item.fileSettings;
    if (!fileSettings || fileSettings.length === 0) return null;

    return fileSettings.map((setting: FileSetting, idx: number) => (
      <View key={idx} style={styles.requirementContainer}>
        <Text style={styles.requirementText}>
          {t('REQUIRED')}: {setting.minFiles}~{setting.maxFiles} {t('PHOTOS')}
        </Text>
      </View>
    ));
  };

  const renderSystemMedia = () => {
    if (!hasSystemMedia) return null;

    return (
      <View style={styles.systemMediaContainer}>
        <Text style={styles.mediaLabel}>{t('CHECKLIST_FILES_2')}:</Text>
        <View style={styles.mediaContainer}>
          {allSystemMedia.map((file, index) => (
            <FileThumbnail
              key={`${file.id || index}`}
              file={file}
              size="medium"
              onPress={() => handleMediaPress(file)}
              isInstruction
            />
          ))}
        </View>
      </View>
    );
  };

  const renderResponses = () => {
    if (!hasResponses) return null;

    return (
      <View style={styles.responsesContainer}>
        {item.response?.map((response: any, responseIndex: number) => {
          const responseFiles = response.fileEntities || [];

          return (
            <View key={responseIndex} style={styles.responseItem}>
              {/* User name */}
              <Text style={styles.responseUserName}>
                {/* {response.createdName || 'Unknown'} */}
                hehe
              </Text>

              {/* Comment text */}
              {response.comment && (
                <Text style={styles.responseComment}>{response.comment}</Text>
              )}

              {/* User's media */}
              {responseFiles.length > 0 && (
                <View style={styles.responseMediaContainer}>
                  {responseFiles.map((file: any, idx: number) => {
                    const fileInfo: FileInfo = {
                      ...file,
                      uri: file.url || file.fileUrl,
                      type: file.fileExtension === '.mp4' ? 'video' : 'file',
                      name: file.fileName || file.name,
                    };

                    return (
                      <FileThumbnail
                        key={`${file.id || idx}`}
                        file={fileInfo}
                        size="medium"
                        onPress={() => handleMediaPress(fileInfo)}
                        readOnly
                      />
                    );
                  })}
                </View>
              )}
            </View>
          );
        })}
      </View>
    );
  };

  return (
    <View style={styles.container}>
      <View style={styles.rowTop}>
        <Button
          style={styles.checkbox}
          onPress={handleCheckboxPress}
          activeOpacity={0.7}
        >
          <View
            style={[
              styles.checkboxBox,
              item.isChecked === true && styles.checkboxBoxChecked,
            ]}
          >
            {item.isChecked === true && (
              <SvgXml xml={IconCheck(colors.white)} width={14} height={14} />
            )}
          </View>
        </Button>
        <Text style={styles.rowNumber}>{item.orderNumber}.</Text>
        <Text style={styles.rowName} numberOfLines={5}>
          {item.name}
        </Text>
        <Button
          style={styles.checkbox}
          onPress={handleCheckboxPress}
          activeOpacity={0.7}
        >
          <SvgXml xml={IconComment(colors.darkBlue)} width={24} height={24} />
        </Button>
      </View>

      {/* File Requirements */}
      {renderFileRequirements()}

      {/* System Media (Checklist Files) */}
      {renderSystemMedia()}

      {/* User Responses */}
      {renderResponses()}

      {/* File Viewer */}
      {selectedFile && (
        <FileViewer
          visible={!!selectedFile}
          onClose={handleCloseViewer}
          file={selectedFile}
        />
      )}
    </View>
  );
};

const styles = StyleSheet.create({
  container: {
    backgroundColor: colors.white,
    borderRadius: 8,
    marginBottom: 8,
    borderWidth: 1,
    borderColor: colors.grayScale30,
    overflow: 'hidden',
    padding: 12,
  },
  rowTop: {
    flexDirection: 'row',
    alignItems: 'flex-start',
    marginBottom: 8,
  },
  checkbox: {
    marginRight: 8,
    paddingHorizontal: 0,
    paddingVertical: 0,
    backgroundColor: colors.white,
    minHeight: 0,
  },
  checkboxBox: {
    width: 24,
    height: 24,
    borderRadius: 4,
    borderWidth: 2,
    borderColor: colors.grayScale40,
    backgroundColor: colors.white,
    justifyContent: 'center',
    alignItems: 'center',
  },
  checkboxBoxChecked: {
    backgroundColor: colors.mainColorPrimary,
    borderColor: colors.mainColorPrimary,
  },
  rowNumber: {
    fontSize: 14,
    fontWeight: '600',
    color: colors.black,
    marginRight: 8,
  },
  rowName: {
    fontSize: 14,
    color: colors.black,
    flex: 1,
    lineHeight: 20,
  },
  requirementContainer: {
    marginBottom: 8,
  },
  requirementText: {
    fontSize: 12,
    color: colors.grayScale70,
    fontWeight: '500',
  },
  systemMediaContainer: {
    marginBottom: 12,
    flexDirection: 'row',
    alignItems: 'flex-start',
    gap: 8,
  },
  mediaLabel: {
    fontSize: 12,
    color: colors.grayScale70,
    marginBottom: 6,
    lineHeight: 16,
  },
  mediaContainer: {
    flexDirection: 'row',
    flexWrap: 'wrap',
    gap: 6,
  },
  responsesContainer: {
    gap: 12,
  },
  responseItem: {
    borderTopWidth: 1,
    borderTopColor: colors.grayScale30,
  },
  responseUserName: {
    fontSize: 14,
    fontWeight: '700',
    color: colors.black,
  },
  responseComment: {
    fontSize: 13,
    color: colors.black,
    lineHeight: 18,
  },
  responseMediaContainer: {
    flexDirection: 'row',
    flexWrap: 'wrap',
    gap: 6,
  },
});

export default ChecklistItemContent;
