import { ActivityIndicator, FlatList, View } from 'react-native';
import { Button, Text } from 'tradewize-component';
import { styles } from './styles';
import { colors } from '../../../../theme';
import { useAppTranslation } from '../../../../hooks/useAppTranslation';
import { useSelector } from 'react-redux';
import dayjs from 'dayjs';
import EmptyComponent from '../EmptyComponent';
import { SignedContract } from '../../../../types/contract';

const Content = ({
  setSelectedContract,
  setIsShowPdf,
}: {
  setSelectedContract: (contract: SignedContract) => void;
  setIsShowPdf: (isShowPdf: boolean) => void;
}) => {
  const { t } = useAppTranslation();
  const signedContract = useSelector(
    (state: any) => state?.contract?.signedContract,
  );

  if (!signedContract?.isLoading && !signedContract?.data?.length) {
    return null;
  }

  return (
    <FlatList
      style={styles.flatList}
      contentContainerStyle={styles.contentContainer}
      ListEmptyComponent={
        signedContract?.isLoading ? null : (
          <EmptyComponent
            style={styles.emptyComponent}
            text={t('NO_SIGNED_CONTRACTS_YET')}
          />
        )
      }
      data={signedContract?.data}
      keyExtractor={item => item?.id?.toString()}
      renderItem={({ item }) => (
        <Button
          variant="ghost"
          style={styles.cardContract}
          onPress={() => {
            setSelectedContract(item);
            setIsShowPdf(true);
          }}
        >
          <Text style={styles.nameContract} numberOfLines={1}>
            {item?.companyName} {t('CONTRACT')}
          </Text>
          <Text style={styles.dateContract} numberOfLines={1}>
            {t('SIGNED_ON')}: {dayjs(item?.createdAt).format('MM/DD/YYYY')}
          </Text>
        </Button>
      )}
    />
  );
};

const SignedContracts = ({
  setSelectedContract,
  setIsShowPdf,
}: {
  setSelectedContract: (contract: SignedContract) => void;
  setIsShowPdf: (isShowPdf: boolean) => void;
}) => {
  const signedContract = useSelector(
    (state: any) => state?.contract?.signedContract,
  );
  return (
    <View style={styles.contractsToSignContainer}>
      <Content
        setSelectedContract={setSelectedContract}
        setIsShowPdf={setIsShowPdf}
      />
      {signedContract?.isLoading && (
        <View style={styles.cardContractSkeleton}>
          <ActivityIndicator size={24} color={colors.mainColorPrimary} />
        </View>
      )}
    </View>
  );
};

export default SignedContracts;
