import React from 'react';
import { StyleSheet, View } from 'react-native';
import { Button, SCREEN_WIDTH, Text } from 'tradewize-component';
import { JobToday } from '../../../../types/jobToday.ts';
import FastImage from 'react-native-fast-image';
import { click } from '../../../../assets/gift/index.tsx';
import { colors } from '../../../../theme/index.tsx';
import { checkInPass } from '../../../../assets/images/index.ts';
import { getTimeString } from '../../../../helper/HomeScreen.helper.ts';
import { useAppTranslation } from '../../../../hooks/useAppTranslation.ts';

type BoxCheckInProps = {
  job: JobToday;
};

const BoxCheckIn = (props: BoxCheckInProps) => {
  const { t } = useAppTranslation();
  const { job } = props;

  // 1. Nu khng c job hoc khng cn check-in
  if (!job || !job?.needCheckin) {
    return null;
  }

  // 2. Nu  check-in
  if (job?.checkInOn) {
    return (
      <View style={styles.containerCheckInPass}>
        <FastImage source={checkInPass} style={styles.imgCheckInPass} />
        <Text style={styles.checkInText}>{t('CHECKED_IN_AT')}</Text>
        <Text style={styles.checkInText}>{getTimeString(job?.checkInOn)}</Text>
      </View>
    );
  }

  // 3. Nu ang check-in (loading)
  //   if (isCheckingIn) {
  //     return <ActivityIndicator size="small" color={colors.mainColorPrimary} />;
  //   }

  //4. Hin th nt check-in
  return (
    <Button variant="ghost" style={styles.giftImageContainer}>
      <Text style={styles.checkInText}>{t('CHECK_IN')}</Text>
      <FastImage source={click} style={styles.giftImage} />
    </Button>
  );
};

const styles = StyleSheet.create({
  giftImageContainer: {
    alignItems: 'center',
    justifyContent: 'center',
    flexDirection: 'column',
    minHeight: 0,
    paddingHorizontal: 0,
    paddingVertical: 0,
  },
  giftImage: {
    width: SCREEN_WIDTH * 0.1,
    height: SCREEN_WIDTH * 0.1,
  },
  checkInText: {
    fontSize: 14,
    fontWeight: '400',
    color: colors.black,
    lineHeight: 20,
    letterSpacing: -0.1,
  },
  imgCheckInPass: {
    width: SCREEN_WIDTH * 0.12,
    height: SCREEN_WIDTH * 0.12,
  },
  containerCheckInPass: {
    alignItems: 'center',
    justifyContent: 'center',
    flexDirection: 'column',
  },
});

export default BoxCheckIn;
