import React from 'react';
import { View } from 'react-native';
import { Text } from 'tradewize-component';
import FastImage from 'react-native-fast-image';
import { styles } from '../styles';
import { useAppTranslation } from '../../../../../hooks/useAppTranslation';

interface WeatherData {
  location?: {
    city?: string;
    country?: string;
  };
  current?: {
    condition?: {
      text?: string;
      icon?: string;
    };
    tempF?: number;
    humidity?: number;
    windKph?: number;
    visKm?: number;
  };
}
interface WeatherProps {
  weatherData: {
    weatherData?: WeatherData;
  };
}

export default function Weather({ weatherData }: WeatherProps) {
  const { t } = useAppTranslation();
  const weather = weatherData?.weatherData;

  if (!weather) return null;

  return (
    <View style={styles.weatherCard}>
      <View style={styles.weatherHeader}>
        <Text style={styles.weatherLocation}>
          {weather.location?.city || ''}, {weather.location?.country || ''} -{' '}
          {weather.current?.condition?.text || ''}
        </Text>
      </View>
      <View style={styles.weatherContent}>
        {weather.current?.condition?.icon && (
          <FastImage
            source={{
              uri: `https:${weather.current.condition.icon}`,
            }}
            style={styles.weatherIcon}
            resizeMode={FastImage.resizeMode.contain}
          />
        )}
        <View style={styles.weatherDetails}>
          <Text style={styles.weatherTemp}>
            {weather.current?.tempF?.toFixed(1) || '0'} F
          </Text>
          <View style={styles.weatherRow}>
            <Text style={styles.weatherDetail}>
              {t('HUMIDITY')}: {weather.current?.humidity || 0}%
            </Text>
            <Text style={styles.weatherDetail}>
              {t('WIND')}:{' '}
              {((weather.current?.windKph || 0) * 0.621371).toFixed(2)} mph
            </Text>
            <Text style={styles.weatherDetail}>
              {t('VISIBILITY')}:{' '}
              {((weather.current?.visKm || 0) * 0.621371).toFixed(2)} mi
            </Text>
          </View>
        </View>
      </View>
    </View>
  );
}
