import React, { useState, useEffect, useCallback } from 'react';
import { TabView, SceneMap } from 'react-native-tab-view';
import { Dimensions } from 'react-native';
import { BaseContainer, HeaderBase } from 'tradewize-component';
import ScreenTab, { renderTabBar } from './components/ScreenTab';
import FilterModal from './components/FilterModal';
import { navigationRef } from '../../services/NavigationService';
import { useDispatch, useSelector } from 'react-redux';
import { RootState } from '../../../../Auth/src/redux/store/types';
import { SvgXml } from 'react-native-svg';
import { IconFilter } from '../../assets/svg';
import { colors } from '../../theme';
import { STATUS_MAP, STATUS_SORT_BY } from '../../helper/myJob.helpter';
import { JobStartTabStatus } from '../../types/myJobs.types';
import { useTranslation } from 'react-i18next';
const { width } = Dimensions.get('window');

// Define scene components outside the render function
const InProgressScene = () => (
  <ScreenTab tabType={JobStartTabStatus.IN_PROGRESS} />
);
const NotStartedScene = () => (
  <ScreenTab tabType={JobStartTabStatus.NOT_STARTED} />
);
const CompletedScene = () => (
  <ScreenTab tabType={JobStartTabStatus.COMPLETED} />
);

export default function MyJobsScreen() {
  const dispatch = useDispatch();
  const { t } = useTranslation();

  const [index, setIndex] = useState(0);
  const [filterParams, setFilterParams] = useState<{
    CommunityId?: string;
    LotNumber?: string;
    StartDate?: string;
  }>({});
  const [showFilterModal, setShowFilterModal] = useState(false);

  const [routes] = useState([
    { key: JobStartTabStatus.IN_PROGRESS, title: t('IN_PROGRESS') },
    { key: JobStartTabStatus.NOT_STARTED, title: t('NOT_STARTED') },
    { key: JobStartTabStatus.COMPLETED, title: t('COMPLETED') },
  ]);

  const communitiesData = useSelector(
    (state: RootState) => state.myjob.communities.data,
  );

  const fetchMyJobs = useCallback(() => {
    const status = STATUS_MAP[routes[index].key];
    const params = {
      Status: status,
      SortBy: STATUS_SORT_BY[routes[index].key],
      PageIndex: 1,
      ...(filterParams.CommunityId && {
        CommunityId: filterParams.CommunityId,
      }),
      ...(filterParams.LotNumber && { LotNumber: filterParams.LotNumber }),
      ...(filterParams.StartDate && { StartDate: filterParams.StartDate }),
    };

    const redux = require('auth/redux');
    dispatch(redux.actionContainer.getMyJobsAsync(params));
  }, [index, filterParams, routes, dispatch]);

  const fetchCommunities = useCallback(() => {
    const redux = require('auth/redux');
    dispatch(redux.actionContainer.getCommunitiesAsync());
  }, [dispatch]);

  useEffect(() => {
    fetchCommunities();
  }, [fetchCommunities]);

  useEffect(() => {
    fetchMyJobs();
  }, [fetchMyJobs]);

  const handleFilterPress = useCallback(() => {
    setShowFilterModal(true);
  }, []);

  const handleApplyFilter = useCallback(
    (newFilters: {
      CommunityId?: string;
      LotNumber?: string;
      StartDate?: string;
    }) => {
      setFilterParams(newFilters);
      setShowFilterModal(false);
    },
    [],
  );

  const handleResetFilter = useCallback(() => {
    setFilterParams({});
    setShowFilterModal(false);
  }, []);

  const handleCloseFilter = useCallback(() => {
    setShowFilterModal(false);
  }, []);

  const renderScene = SceneMap({
    [JobStartTabStatus.IN_PROGRESS]: InProgressScene,
    [JobStartTabStatus.NOT_STARTED]: NotStartedScene,
    [JobStartTabStatus.COMPLETED]: CompletedScene,
  });

  const activeFilterCount = Object.keys(filterParams).filter(
    key =>
      filterParams[key as keyof typeof filterParams] !== undefined &&
      filterParams[key as keyof typeof filterParams] !== '',
  ).length;

  return (
    <BaseContainer>
      <HeaderBase
        title="My Jobs"
        onPressBack={() => {
          navigationRef.goBack();
        }}
        rightActions={[
          {
            icon: (
              <SvgXml
                xml={IconFilter(
                  activeFilterCount > 0
                    ? colors.mainColorPrimary
                    : colors.grayScale70,
                )}
                width={20}
                height={20}
              />
            ),
            onPress: handleFilterPress,
          },
        ]}
        backgroundColor={colors.white}
      />
      <TabView
        lazy
        navigationState={{ index, routes }}
        renderScene={renderScene}
        onIndexChange={setIndex}
        renderTabBar={renderTabBar}
        initialLayout={{ width }}
      />

      <FilterModal
        isVisible={showFilterModal}
        communities={(communitiesData || []).map((item: any) => ({
          id: item.id,
          name: item.name,
        }))}
        initialFilter={filterParams}
        onApply={handleApplyFilter}
        onReset={handleResetFilter}
        onClose={handleCloseFilter}
      />
    </BaseContainer>
  );
}
