
import { ChecklistItem, FileSetting, FileType, qIStatus, userRole, VideoType } from '../types/jobDetail.type';


// utils

export const isPassValidateFilesByFileSetting = (
  checklistItem: ChecklistItem,
  isUpload: boolean = true,
) => {
  // if (checklistItem.response && checklistItem.response?.length > 0) {
  //   return true;
  // }

  let files = [];
  let videos = [];
  const isNeedComment =
    checklistItem.response?.length === 0 ||
    (checklistItem.response &&
      checklistItem.response[checklistItem.response.length - 1]
        ?.isExMyResponse === false);

  // const commentText = checklistItem.comment;

  const [photo, video] = parserFilesSetting(checklistItem.fileSettings);

  if (isUpload) {
    files =
      checklistItem.fileEntities?.filter((item: any) =>
        item?.type?.includes('image'),
      ) ?? [];
    videos =
      checklistItem.fileEntities?.filter((item: any) =>
        item?.type?.includes('video'),
      ) ?? [];
    const isCommentBefore =
      (checklistItem?.response?.length &&
        checklistItem?.response?.length > 0) ||
      (checklistItem?.responses?.length &&
        checklistItem?.responses?.length > 0);

    if (isNeedComment && !isCommentBefore) {
      if (photo) {
        if (photo.minFiles > photo.maxFiles) {
          return false;
        }
        if (files && files.length < photo.minFiles) {
          return false;
        }
      }

      if (video) {
        if (video.minFiles > video.maxFiles) {
          return false;
        }
        if (videos && videos.length < video.minFiles) {
          return false;
        }
      }
    }
  } else {
    const allFileEntities = checklistItem.response?.flatMap(
      (item: any) => item.fileEntities,
    );

    files =
      allFileEntities?.filter(
        (item: any) => !VideoType?.includes(item?.fileExtension),
      ) ?? [];
    videos =
      allFileEntities?.filter((item: any) =>
        VideoType?.includes(item?.fileExtension),
      ) ?? [];

    if (
      checklistItem.role === userRole.INSPECTOR &&
      checklistItem.qiStatus !== qIStatus.Pending
    ) {
      return false;
    }

    if (isNeedComment) {
      if (
        checklistItem.leaderCheckIncomplete &&
        checklistItem.isChecked === true
      ) {
        return false;
      }
      if (checklistItem.role === userRole.INSPECTOR) {
        if (checklistItem.qiStatus === qIStatus.Pending) {
          if (
            (photo && photo?.minFiles > 0) ||
            (video && video?.minFiles > 0)
          ) {
            if (!checklistItem.response?.length) {
              return false;
            } else {
              // Validate photo requirements
              if (photo && photo.minFiles > 0) {
                if (photo.minFiles > photo.maxFiles) {
                  return false;
                }
                if (
                  files.length < photo.minFiles ||
                  files.length > photo.maxFiles
                ) {
                  return false;
                }
              }

              // Validate video requirements
              if (video && video.minFiles > 0) {
                if (video.minFiles > video.maxFiles) {
                  return false;
                }
                if (
                  videos.length < video.minFiles ||
                  videos.length > video.maxFiles
                ) {
                  return false;
                }
              }
            }
          } else {
            if (
              !checklistItem.response?.length &&
              checklistItem.isChecked === false
            ) {
              return false;
            }
          }
        }
      } else {
        if ((photo && photo?.minFiles > 0) || (video && video?.minFiles > 0)) {
          if (!checklistItem.response?.length) {
            return false;
          } else {
            // Validate photo requirements
            if (photo && photo.minFiles > 0) {
              if (photo.minFiles > photo.maxFiles) {
                return false;
              }
              if (
                files.length < photo.minFiles ||
                files.length > photo.maxFiles
              ) {
                return false;
              }
            }

            // Validate video requirements
            if (video && video.minFiles > 0) {
              if (video.minFiles > video.maxFiles) {
                return false;
              }
              if (
                videos.length < video.minFiles ||
                videos.length > video.maxFiles
              ) {
                return false;
              }
            }
          }
        }
      }
    }
  }
  return true;
};

export const parserFilesSetting = (fileSettings: FileSetting[]) => {
  const photoSetting = fileSettings.find(
    item => item.fileType === FileType.PHOTO,
  );
  const videoSetting = fileSettings.find(
    item => item.fileType === FileType.VIDEO,
  );
  return [photoSetting, videoSetting];
};
