import React, { useEffect, useMemo, useState } from 'react';
import { Platform, StyleSheet, View } from 'react-native';
import ReactNativeModal from 'react-native-modal';
import { Button, SCREEN_HEIGHT, SCREEN_WIDTH, Text } from 'tradewize-component';
import { OtpInput, OtpInputRef } from 'react-native-otp-entry';
import colors from '../theme/colors';
import { useAppTranslation } from '../hooks/useAppTranslation';
import { SvgXml } from 'react-native-svg';
import { IconClose } from '../assets/svg';

interface OtpVerifyModalProps {
  visible: boolean;
  otp: string;
  otpInputRef: React.RefObject<OtpInputRef>;
  title: string;
  description?: string;
  confirmText: string;
  onClose: () => void;
  onConfirm: (code: string) => void;
  onChangeOtp: (code: string) => void;
  isConfirmLoading?: boolean;
  onResend?: () => void;
  resendText: string;
  initialCountdown?: number; // seconds
  formatCountdownText?: (remaining: number) => string;
}

export function OtpVerifyModal({
  visible,
  otp,
  otpInputRef,
  title,
  description,
  confirmText,
  onClose,
  onConfirm,
  onChangeOtp,
  isConfirmLoading = false,
  onResend,
  resendText,
  initialCountdown = 60,
  formatCountdownText,
}: OtpVerifyModalProps) {
  const [remaining, setRemaining] = useState(initialCountdown);
  const [showIncompleteError, setShowIncompleteError] = useState(false);
  const { t } = useAppTranslation();

  useEffect(() => {
    if (!visible) {
      setRemaining(initialCountdown);
      setShowIncompleteError(false);
      return;
    }
    setRemaining(initialCountdown);
    const timer = setInterval(() => {
      setRemaining(prev => (prev > 0 ? prev - 1 : 0));
    }, 1000);
    return () => clearInterval(timer);
  }, [visible, initialCountdown]);

  const canResend = useMemo(
    () => remaining === 0 && !isConfirmLoading,
    [remaining, isConfirmLoading],
  );

  const handleResend = () => {
    if (!canResend) return;
    onResend?.();
    setRemaining(initialCountdown);
  };

  const countdownLabel = useMemo(() => {
    if (canResend) return '';
    const formatFn =
      formatCountdownText || ((value: number) => `${resendText} (${value}s)`);
    return formatFn(remaining);
  }, [canResend, formatCountdownText, remaining, resendText]);

  const descriptionText = useMemo(() => {
    if (description) return description;
    return '';
  }, [description]);

  const handleTextChange = (code: string) => {
    onChangeOtp(code);
    setShowIncompleteError(false);
    if (code?.length === 6) {
      otpInputRef?.current?.blur();
      setTimeout(() => {
        onConfirm?.(code);
      }, 150);
    }
  };

  const handleConfirm = () => {
    if (otp.length < 6) {
      setShowIncompleteError(true);
      return;
    }
    onConfirm(otp);
  };

  const borderColor = useMemo(() => {
    if (showIncompleteError) {
      return colors.alertColorError;
    }
    return colors.grayScale40;
  }, [showIncompleteError]);

  return (
    <ReactNativeModal
      isVisible={visible}
      // onBackdropPress={onClose}
      onBackButtonPress={onClose}
      useNativeDriver
      hideModalContentWhileAnimating
    >
      <View style={styles.container}>
        <Button variant="ghost" style={styles.buttonGhost} onPress={onClose}>
          <SvgXml width={24} height={24} xml={IconClose()} />
        </Button>
        <Text style={styles.title}>{title}</Text>
        {!!descriptionText && (
          <Text style={styles.description}>{descriptionText}</Text>
        )}
        <View style={styles.otpContainer}>
          <OtpInput
            ref={otpInputRef}
            numberOfDigits={6}
            onTextChange={handleTextChange}
            onFilled={code => onChangeOtp(code ?? '')}
            // onFocus={() => {
            //   setFocusOtp(true);
            // }}
            // onBlur={() => {
            //   setFocusOtp(false);
            // }}
            theme={{
              pinCodeTextStyle: {
                fontSize: 16,
                fontWeight: '400',
                color: colors.black,
              },
              pinCodeContainerStyle: {
                ...styles.otpItem,
                borderColor: borderColor,
              },
            }}
            focusStickBlinkingDuration={500}
            type="numeric"
            textProps={{
              accessibilityRole: 'text',
              accessibilityLabel: 'OTP digit',
              allowFontScaling: false,
            }}
            textInputProps={{
              keyboardType: 'numeric',
              autoComplete:
                Platform.OS === 'android' ? 'sms-otp' : 'one-time-code',
              textContentType: 'oneTimeCode',
            }}
            focusColor={colors.mainColorPrimary}
            autoFocus={false}
          />
        </View>
        <View style={styles.actions}>
          <Button
            variant="primary"
            style={styles.buttonPrimary}
            onPress={() => handleConfirm()}
            disabled={isConfirmLoading}
            isLoading={isConfirmLoading}
          >
            <Text style={styles.primaryText}>{confirmText}</Text>
          </Button>
        </View>
        <View style={styles.resendRow}>
          <Text style={styles.resendTextDescription}>
            {t('RESEND_CODE_DESCRIPTION')}
          </Text>
          <Button
            variant="ghost"
            style={styles.resendButton}
            onPress={handleResend}
            disabled={!canResend}
          >
            <Text
              style={[
                styles.resendText,
                !canResend && styles.resendTextDisabled,
              ]}
            >
              {t('RESEND_CODE')}
            </Text>
          </Button>
          {!canResend && !!countdownLabel && (
            <Text style={styles.countdownText}>{countdownLabel}</Text>
          )}
        </View>
      </View>
    </ReactNativeModal>
  );
}

const styles = StyleSheet.create({
  container: {
    borderRadius: 12,
    backgroundColor: colors.white,
    padding: 16,
    gap: 4,
    position: 'relative',
  },
  title: {
    fontSize: 18,
    fontWeight: '700',
    color: colors.black,
    textAlign: 'center',
  },
  description: {
    fontSize: 14,
    color: colors.grayScale80,
  },
  otpContainer: {
    gap: 8,
  },
  actions: {
    flexDirection: 'row',
    justifyContent: 'flex-end',
    gap: 12,
    marginTop: 8,
  },
  buttonGhost: {
    backgroundColor: colors.white,
    paddingHorizontal: 16,
    paddingVertical: 12,
    position: 'absolute',
    top: 0,
    right: 0,
    zIndex: 1,
  },
  buttonPrimary: {
    backgroundColor: colors.mainColorPrimary,
    paddingHorizontal: 16,
    paddingVertical: 12,
    flex: 1,
  },
  ghostText: {
    color: colors.grayScale80,
    fontWeight: '600',
  },
  primaryText: {
    color: colors.white,
    fontWeight: '700',
  },
  otpItem: {
    borderRadius: 8,
    borderWidth: 1,
    height: SCREEN_HEIGHT * 0.05,
    width: SCREEN_WIDTH / 6 - 20,
  },
  otpText: {
    fontSize: 14,
    fontWeight: '700',
    color: colors.black,
  },
  errorContainer: {
    height: 20,
  },
  errorText: {
    fontSize: 12,
    color: colors.alertColorError,
  },
  resendRow: {
    flexDirection: 'row',
    alignItems: 'center',
    justifyContent: 'space-between',
  },
  resendButton: {
    paddingHorizontal: 0,
    paddingVertical: 0,
  },
  resendText: {
    color: colors.mainColorPrimary,
    fontWeight: '700',
  },
  resendTextDisabled: {
    color: colors.grayScale70,
  },
  resendTextDescription: {
    color: colors.grayScale80,
    fontSize: 14,
    fontWeight: '500',
  },
  countdownText: {
    color: colors.grayScale80,
    fontSize: 14,
    fontWeight: '500',
  },
});

export default OtpVerifyModal;
