import { colors } from '../theme';
import { NavigationService } from '../services/NavigationService';
import { FORMAT_TIME_CLIENT_JOB_START, SCREEN_ENUM } from '../types/global';
import { logger } from '../utils';
import { JobItem } from '../screens/JobBoard/components/ListJobs/types';
import i18n from '../i18n';
import { Dispatch, SetStateAction } from 'react';
import { FilterOption } from '../screens/JobBoard/components/Filter/types';
import dayjs from 'dayjs';
import { ViewStyle } from 'react-native';

const baseContainerStyle: ViewStyle = {
  marginTop: 4,
};

interface HandleRegisterProps {
  isDisabledButtonRegister: boolean;
  allJobs: JobItem[];
}

interface AllJobsProps {
  allJobs: JobItem[];
}

const handleRegisterConfirm = async ({ allJobs }: AllJobsProps) => {
  try {
    const redux = await import('auth/redux');
    redux.store.dispatch(redux.actionContainer.hideConfirmModal());
    setTimeout(() => {
      const jobIds = allJobs
        .filter((job: JobItem) => job?.isChecked)
        .map((job: JobItem) => job.jobId);
      redux.store.dispatch(
        redux.actionContainer.registerJobsAsync({
          jobIds,
        }),
      );
    }, 350);
  } catch (error) {
    logger('error handleRegisterConfirm', error);
  }
};

export const handleRegister = async ({
  isDisabledButtonRegister,
  allJobs,
}: HandleRegisterProps) => {
  try {
    const redux = await import('auth/redux');
    const profileData = redux?.store?.getState()?.auth?.userProfile;
    if (isDisabledButtonRegister) {
      return;
    }
    if (profileData?.isContract && !profileData?.isFinishedTraining) {
      return redux.store.dispatch(
        redux.actionContainer.showAlertAsync({
          title: i18n?.t('TRAINING_TEST'),
          message: i18n?.t('PLEASE_PASS_TRAINING_TO_START_WORKING'),
          titleStyle: {
            color: colors.black,
          },
          buttonText: i18n?.t('GO_TO_TRAINING'),
          isBackdropPress: false,
          buttonStyle: {
            backgroundColor: colors.mainColorPrimary,
          },
          onConfirm: () => {
            setTimeout(() => {
              NavigationService.navigate(SCREEN_ENUM.SystemQualityGuideScreen);
            }, 350);
          },
        }),
      );
    }

    if (profileData?.needEnterBankInfo || profileData?.needUploadFileW9) {
      return redux.store.dispatch(
        redux.actionContainer.showAlertAsync({
          title: i18n?.t('PAYMENT_INFO_AND_W9'),
          message: i18n?.t(
            'PLEASE_PROVIDE_YOUR_BANK_INFORMATION_BEFORE_TAKE_JOB',
          ),
          titleStyle: {
            color: colors.black,
          },
          buttonText: i18n?.t('GO_TO_PAYMENT'),
          isBackdropPress: false,
          buttonStyle: {
            backgroundColor: colors.mainColorPrimary,
          },
          type: 'warning',
          onClose: () => {
            setTimeout(() => {
              NavigationService.navigate(SCREEN_ENUM.PaymentAccountScreen);
            }, 350);
          },
        }),
      );
    }

    redux.store.dispatch(
      redux.actionContainer.showConfirmModal({
        description: i18n?.t('ARE_YOU_SURE_YOU_WANT_TO_REGISTER_FOR_THIS'),
        confirmText: i18n?.t('CONFIRM'),
        cancelText: i18n?.t('CANCEL'),
        isBackdropPress: false,
        onConfirm: () => handleRegisterConfirm({ allJobs }),
        titleStyle: {
          color: colors.black,
        },
      }),
    );
  } catch (error) {
    logger('error handleRegister', error);
  }
};

export const handleChangeDate = async ({
  rangeDate,
  setFilter,
}: {
  rangeDate: { startDate: string; endDate: string };
  setFilter: Dispatch<SetStateAction<FilterOption[]>>;
}) => {
  try {
    setFilter((prev: any) => {
      return prev.map((item: FilterOption) => {
        if (item.key === 'SELECT_DATE_RANGE') {
          return {
            ...item,
            startDate: rangeDate.startDate,
            endDate: rangeDate.endDate,
            value: `${dayjs(rangeDate.startDate).format(
              FORMAT_TIME_CLIENT_JOB_START,
            )} - ${dayjs(rangeDate.endDate).format(
              FORMAT_TIME_CLIENT_JOB_START,
            )}`,
          };
        }
        return { ...item, startDate: '', endDate: '' };
      });
    });
  } catch (error) {
    logger('error handleChangeDate', error);
  }
};

export const customDatesStylesFn = (
  date: Date,
  rangeDate: { startDate: string; endDate: string },
) => {
  const isToday = dayjs(date).isSame(dayjs(), 'day');
  const selectedStartDate = rangeDate.startDate;
  const selectedEndDate = rangeDate.endDate;

  const isStartDay = selectedStartDate
    ? dayjs(date).isSame(dayjs(selectedStartDate), 'day')
    : false;
  const isEndDay = selectedEndDate
    ? dayjs(date).isSame(dayjs(selectedEndDate), 'day')
    : false;

  const isSelected =
    selectedStartDate &&
    selectedEndDate &&
    dayjs(date).isBetween(
      dayjs(selectedStartDate),
      dayjs(selectedEndDate),
      'day',
      '[]',
    );

  // RANGE
  if (isSelected) {
    const containerStyle: ViewStyle = {
      ...baseContainerStyle,
      backgroundColor: colors.grayScale30,
      borderRadius: 0,
    };

    if (isStartDay) {
      containerStyle.borderTopLeftRadius = 8;
      containerStyle.borderBottomLeftRadius = 8;
      containerStyle.backgroundColor = colors.mainColorPrimary;
    }

    if (isEndDay) {
      containerStyle.borderTopRightRadius = 8;
      containerStyle.borderBottomRightRadius = 8;
      containerStyle.backgroundColor = colors.mainColorPrimary;
    }

    return {
      containerStyle: containerStyle as any,
      textStyle: {
        fontSize: 16,
        color: colors.black,
        fontWeight: 'bold',
      } as any,
    } as any;
  }

  if (isStartDay || isEndDay) {
    return {
      containerStyle: {
        ...baseContainerStyle,
        backgroundColor: colors.mainColorPrimary,
        borderRadius: 8,
      } as any,
      textStyle: {
        fontSize: 16,
        color: colors.white,
        fontWeight: 'bold',
      } as any,
    } as any;
  }

  if (isToday) {
    return {
      containerStyle: {
        ...baseContainerStyle,
        borderWidth: 1,
        borderColor: colors.mainColorPrimary,
        borderRadius: 8,
        backgroundColor: colors.white,
      } as any,
    } as any;
  }

  return {
    containerStyle: { ...baseContainerStyle } as any,
    textStyle: {
      color: colors.black,
      fontSize: 16,
      fontWeight: 'bold',
    } as any,
  } as any;
};
