import React, { useCallback, useState } from 'react';
import { BaseContainer, HeaderBase } from 'tradewize-component';
import { styles } from './styles';
import { colors } from '../../theme';
import { useAppTranslation } from '../../hooks/useAppTranslation';
import { NavigationService } from '../../services/NavigationService';
import { useContract } from '../../hooks/useContract';
import { useDispatch } from 'react-redux';

import { SignedContract } from '../../types/contract';
import PdfView from './components/Pdf';
import { logger } from '../../utils';
import CanvasView from './components/Canvas';
import { ScrollTabView } from '../../components/ScrollTabView';
import ContractsToSign from './components/ContractsToSign';
import SignedContracts from './components/SignedContracts';

const ContractScreen = () => {
  const { t } = useAppTranslation();
  const dispatch = useDispatch();
  const [isContractAccepted, setIsContractAccepted] = useState<boolean>(false);

  const [selectedContract, setSelectedContract] =
    useState<SignedContract | null>(null);
  const [isShowPdf, setIsShowPdf] = useState<boolean>(false);

  useContract();

  const handleBack = async () => {
    try {
      const redux = await import('auth/redux');
      dispatch(redux.actionContainer.clearTemplateContractDetail());
      dispatch(redux.actionContainer.clearSignedContractDetail());
      NavigationService.goBack();
    } catch (error) {
      logger('error handleBack', error);
    }
  };

  const fetchGetTemplateContractDetail = useCallback(async () => {
    try {
      const redux = await import('auth/redux');
      dispatch(redux.actionContainer.getPendingContractAsync());
    } catch (error) {
      logger('error fetchGetTemplateContractDetail', error);
    }
  }, [dispatch]);

  const fetchGetSignedContract = useCallback(async () => {
    try {
      const redux = await import('auth/redux');
      dispatch(redux.actionContainer.getSignedContractAsync());
    } catch (error) {
      logger('error fetchGetSignedContract', error);
    }
  }, [dispatch]);

  const handleIndexChange = (index: number) => {
    try {
      if (index === 0) {
        fetchGetTemplateContractDetail();
      } else {
        fetchGetSignedContract();
      }
    } catch (error) {
      logger('error handleTabSelect', error);
    }
  };

  return (
    <BaseContainer
      backgroundColor={colors.mainColorPrimary}
      style={styles.container}
      backgroundStatusBarColor={colors.mainColorPrimary}
      statusBarStyle="light-content"
      backgroundBottomSafeAreaColor={colors.white}
    >
      <HeaderBase
        title={t('CONTRACTS')}
        titleStyle={{ color: colors.white }}
        backgroundColor={colors.mainColorPrimary}
        colorIconLeft={colors.white}
        onPressBack={handleBack}
      />

      <ScrollTabView
        lazy
        onIndexChange={handleIndexChange}
        activeTabTextStyle={styles.activeTabText}
        activeIndicatorStyle={styles.activeIndicator}
        textStyleInactive={styles.textStyleInactive}
        tabBarStyle={styles.tabBar}
      >
        <ScrollTabView.Tab
          keyName="contracts-to-sign"
          title={t('CONTRACTS_TO_SIGN')}
        >
          <ContractsToSign setIsContractAccepted={setIsContractAccepted} />
        </ScrollTabView.Tab>

        <ScrollTabView.Tab
          keyName="signed-contracts"
          title={t('SIGNED_CONTRACTS')}
        >
          <SignedContracts
            setSelectedContract={setSelectedContract}
            setIsShowPdf={setIsShowPdf}
          />
        </ScrollTabView.Tab>
      </ScrollTabView>

      <CanvasView
        onClose={() => setIsContractAccepted(false)}
        visible={isContractAccepted}
      />

      <PdfView
        onClose={() => {
          setIsShowPdf(false);
          setSelectedContract(null);
        }}
        visible={isShowPdf}
        contract={selectedContract}
      />
    </BaseContainer>
  );
};

export default ContractScreen;
