import React, { useState } from 'react';
import { View, StyleSheet } from 'react-native';
import { Button, Text } from 'tradewize-component';
import { colors } from '../../../../theme';
import { useAppTranslation } from '../../../../hooks/useAppTranslation';
import {
  checklistCategory,
  ChecklistItem,
} from '../../../../types/jobDetail.type';
import { useSelector } from 'react-redux';
import { RootState } from '../../../../../../Auth/src/redux/store/types';
import CategorySection from './components/CategorySection';

// Category configuration matching the backend enum
const CategoryConfig = {
  [checklistCategory.JobStart]: {
    color: colors.alertColorSuccess,
    icon: 'play-circle',
    label: 'JOB_START',
  },
  [checklistCategory.General]: {
    color: colors.mainColorPrimary,
    icon: 'list-check',
    label: 'GENERAL',
  },
  [checklistCategory.EndOfDay]: {
    color: colors.alertColorWarning,
    icon: 'flag-checkered',
    label: 'END_OF_DAY',
  },
  [checklistCategory.Material]: {
    color: colors.cyanDark,
    icon: 'list-check',
    label: 'MATERIAL',
  },
  [checklistCategory.MaterialDelivery]: {
    color: colors.lightBlue,
    icon: 'list-check',
    label: 'MATERIAL_DELIVERY',
  },
};

interface ChecklistProps {
  checklistsGroup?: {
    reasonType?: string;
    canUpdateChecklist?: boolean;
    [key: string]: any;
  };
}

const Checklist: React.FC<ChecklistProps> = ({ checklistsGroup }) => {
  const { t } = useAppTranslation();
  const checklists = useSelector(
    (state: RootState) => state.jobDetail.checklists,
  ) as ChecklistItem[];

  // Fake data for status if not available
  const reasonType = checklistsGroup?.reasonType;
  const canUpdateChecklist = checklistsGroup?.canUpdateChecklist ?? true;

  // TODO: Get actual role from user state
  const isLeader = true; // Fake for now - should come from user role

  const [expandedCategories, setExpandedCategories] = useState<{
    [key: string]: boolean;
  }>({
    [checklistCategory.JobStart]: true,
    [checklistCategory.General]: true,
    [checklistCategory.EndOfDay]: true,
    [checklistCategory.Material]: true,
    [checklistCategory.MaterialDelivery]: true,
  });

  const toggleCategory = (categoryKey: string) => {
    setExpandedCategories(prev => ({
      ...prev,
      [categoryKey]: !prev[categoryKey],
    }));
  };

  const getCheckedCount = (items: ChecklistItem[]) =>
    items?.filter(item => item.isChecked).length || 0;

  const totalChecked = getCheckedCount(checklists);
  const totalCount = checklists?.length || 0;

  // Group checklists by category
  const groupedChecklists = (checklists || []).reduce(
    (acc: { [key: string]: ChecklistItem[] }, item) => {
      const categoryKey = String(item.category);
      if (!acc[categoryKey]) {
        acc[categoryKey] = [];
      }
      acc[categoryKey].push(item);
      return acc;
    },
    {} as { [key: string]: ChecklistItem[] },
  );

  const categoryKeys = Object.keys(groupedChecklists).sort(
    (a, b) => Number(a) - Number(b),
  );

  // Show overlay if checklist cannot be updated
  const showOverlay = !canUpdateChecklist;

  return (
    <View style={[styles.container, showOverlay && styles.disabledContainer]}>
      {showOverlay && (
        <Button
          activeOpacity={1}
          style={styles.overlayButton}
          onPress={() => {
            // Show error message based on reasonType
            if (reasonType === 'needFinishJobStart' && isLeader) {
              console.log('Open Job Start first');
            } else {
              console.log('Checklist cannot be updated:', reasonType);
            }
          }}
        />
      )}

      {/* Main Header */}
      <View style={styles.header}>
        <Text style={styles.headerText}>{t('ROLE_CHECKLIST')}</Text>
        <View style={styles.headerBadge}>
          <Text style={styles.headerBadgeText}>
            {totalChecked}/{totalCount}
          </Text>
        </View>
      </View>

      {/* Category Groups */}
      <View style={styles.content}>
        {categoryKeys.map(categoryKey => {
          const categoryEnum = Number(categoryKey) as checklistCategory;
          const config = CategoryConfig[categoryEnum];

          // Skip if category not in config
          if (!config) return null;

          const items = groupedChecklists[categoryKey];

          return (
            <CategorySection
              key={categoryKey}
              categoryKey={categoryKey}
              categoryEnum={categoryEnum}
              items={items}
              expandedCategories={expandedCategories}
              toggleCategory={toggleCategory}
            />
          );
        })}

        {categoryKeys.length === 0 && (
          <Text style={styles.emptyText}>{t('NO_DATA')}</Text>
        )}
      </View>
    </View>
  );
};

const styles = StyleSheet.create({
  container: {
    backgroundColor: colors.white,
    borderRadius: 8,
    borderWidth: 1,
    borderColor: colors.grayScale30,
    marginBottom: 16,
    overflow: 'hidden',
  },
  disabledContainer: {
    opacity: 0.5,
  },
  overlayButton: {
    ...StyleSheet.absoluteFillObject,
    zIndex: 10,
  },
  header: {
    flexDirection: 'row',
    alignItems: 'center',
    justifyContent: 'space-between',
    padding: 16,
    borderBottomWidth: 1,
    borderBottomColor: colors.grayScale30,
  },
  headerText: {
    fontSize: 18,
    fontWeight: '700',
    color: colors.black,
  },
  headerBadge: {
    backgroundColor: colors.mainColorPrimary,
    paddingHorizontal: 12,
    paddingVertical: 4,
    borderRadius: 12,
  },
  headerBadgeText: {
    fontSize: 12,
    fontWeight: '600',
    color: colors.white,
  },
  content: {
    padding: 8,
  },
  emptyText: {
    fontSize: 14,
    color: colors.grayScale70,
    textAlign: 'center',
    paddingVertical: 20,
  },
});

export default Checklist;
