import { View } from 'react-native';
import { Button, Input, SCREEN_HEIGHT, Text } from 'tradewize-component';
import { styles } from './styles';
import BottomSheet from '@gorhom/bottom-sheet';
import React, {
  forwardRef,
  useCallback,
  useImperativeHandle,
  useMemo,
  useRef,
} from 'react';
import { useAppTranslation } from '../../../../hooks/useAppTranslation';
import BottomSheetKeyboardAwareScrollView from '../../../../components/BottomSheetKeyboardAwareScrollView';
import { colors } from '../../../../theme';
import { keyboardExtraSpace, keyboardOffset } from '../../../../global/styles';
import { SvgXml } from 'react-native-svg';
import { IconCalendar } from '../../../../assets/svg';
import { useDispatch, useSelector } from 'react-redux';
import { Dropdown } from 'react-native-element-dropdown';
import { FilterOption, FilterProps, FilterRef } from './types';
import { useSafeAreaInsets } from 'react-native-safe-area-context';
import { logger } from '../../../../utils';
import { FetchHouseHasJobWorkerParams } from '../../../../hooks/useJobBoard';
import { GooglePlacesAutocomplete, GooglePlacesAutocompleteRef } from 'react-native-google-places-autocomplete';
import dayjs from 'dayjs';
import { initialFilterOptions } from './model';
import { CompanyRole } from '../../../../types/profile';

const Filter = forwardRef<FilterRef, FilterProps>((props, ref) => {
  const { t } = useAppTranslation();
  const insets = useSafeAreaInsets();
  const dispatch = useDispatch();
  const snapPoints = useMemo(() => ['80%'], []);
  const bottomSheetRef = useRef<BottomSheet>(null);
  const ggPlacesAutocompleteRef = useRef<GooglePlacesAutocompleteRef | null>(null);
  const userProfile = useSelector((state: any) => state.auth.userProfile);
  const communityUserHasJob = useSelector(
    (state: any) => state.jobBoard.communityUserHasJob.data,
  );
  const arrCompany = useMemo(() => {
    return userProfile?.companyRoles?.map((item: CompanyRole) => ({
      label: item?.companyName,
      value: item?.companyId,
    })) || [];
  }, [userProfile]);

  useImperativeHandle(ref, () => ({
    open: () => {
      bottomSheetRef?.current?.snapToIndex(0);
    },
    close: () => {
      bottomSheetRef?.current?.close();
      props?.onClose?.();
    },
  }));

  const handleConfirm = useCallback(async () => {
    try {
      const redux = await import('auth/redux');
      bottomSheetRef?.current?.close();
      const payload: FetchHouseHasJobWorkerParams = {
        Latitude:
          props?.filter?.find(el => el.key === 'LOCATION')?.latitude || 0,
        Longitude:
          props?.filter?.find(el => el.key === 'LOCATION')?.longitude || 0,
        Radius: Number(props?.filter?.find(el => el.key === 'RADIUS')?.value),
        CommunityId: props?.filter?.find(el => el.key === 'COMMUNITY')?.value,
        CompanyId: props?.filter?.find(el => el.key === 'SELECT_COMPANY')?.value,
        StartDate: props?.filter?.find(el => el.key === 'SELECT_DATE_RANGE')
          ?.startDate
          ? dayjs(
            props?.filter?.find(el => el.key === 'SELECT_DATE_RANGE')
              ?.startDate,
          )
            .startOf('day')
            .toDate()
          : undefined,
        EndDate: props?.filter?.find(el => el.key === 'SELECT_DATE_RANGE')
          ?.endDate
          ? dayjs(
            props?.filter?.find(el => el.key === 'SELECT_DATE_RANGE')
              ?.endDate,
          )
            .endOf('day')
            .toDate()
          : undefined,
        WorkCategoryId: props?.filter?.find(el => el.key === 'WORKER')?.value,
        LocationName: props?.filter?.find(el => el.key === 'LOCATION')?.value,
      };

      const isEmptyPayload = Object.values(payload).every(value => !value);
      if (isEmptyPayload) {
        return;
      }

      const payloadFinal = Object.fromEntries(
        Object.entries(payload).filter(([_, value]) => !!value),
      );

      if (
        props?.filter?.find(el => el.key === 'LOCATION')?.latitude &&
        props?.filter?.find(el => el.key === 'LOCATION')?.longitude
      ) {
        props?.moveToLocation?.({
          latitude:
            props?.filter?.find(el => el.key === 'LOCATION')?.latitude || 0,
          longitude:
            props?.filter?.find(el => el.key === 'LOCATION')?.longitude || 0,
          latitudeDelta: 0.01,
          longitudeDelta: 0.01,
        });
      }
      dispatch(redux.actionContainer.fetchHouseHasJobWorkerAsync(payloadFinal));
    } catch (error) {
      logger('error handleConfirm', error);
    }
  }, [props, dispatch]);

  const renderRightIcon = useCallback(
    (key: string) => {
      if (key === 'RADIUS') {
        return <Text style={styles.filterOptionRightIcon}>{t('MILES')}</Text>;
      }
      if (key === 'SELECT_DATE_RANGE') {
        return (
          <Button
            onPress={() => {
              props?.selectDateRange?.();
            }}
            variant="ghost"
            style={styles.filterOptionRightIconButton}
          >
            <SvgXml
              xml={IconCalendar(colors.grayScale70)}
              width={24}
              height={24}
            />
          </Button>
        );
      }
      return undefined;
    },
    [t, props],
  );

  const disabledInput = useCallback((key: string) => {
    if (key === 'COMMUNITY' || key === 'SELECT_DATE_RANGE') {
      return true;
    }
    return false;
  }, []);

  const renderContent = useCallback(
    (option: FilterOption) => {
      if (option?.isSelected) {
        const optionData = option?.key === 'COMMUNITY' ? communityUserHasJob : arrCompany;
        return (
          <>
            <Text style={styles.filterOptionLabel}>{t(option?.label)}</Text>
            <Dropdown
              style={[
                styles.filterOptionInputContainer,
                styles.filterOptionSelect,
                option?.isFocused && styles.ggFocusedTextInputContainer,
              ]}
              onFocus={() => {
                props?.setFilter?.(prev => {
                  const update = prev?.map(el =>
                    el.key === option.key ? { ...el, isFocused: true } : el,
                  );
                  return update;
                });
              }}
              onBlur={() => {
                props?.setFilter?.(prev => {
                  const update = prev?.map(el =>
                    el.key === option.key ? { ...el, isFocused: false } : el,
                  );
                  return update;
                });
              }}
              placeholderStyle={styles.placeholderSelectStyle}
              selectedTextStyle={styles.selectedTextStyle}
              inputSearchStyle={styles.inputSearchSelectStyle}
              containerStyle={[
                styles.selectDropdownContainer,
                option?.isFocused && styles.ggFocusedTextInputContainer,
              ]}
              itemContainerStyle={styles.itemContainerSelect}
              data={optionData || []}
              search
              maxHeight={SCREEN_HEIGHT * 0.3}
              labelField="label"
              valueField="value"
              placeholder={t(option?.placeholder)}
              searchPlaceholder={`${t('SEARCH')}...`}
              value={option?.value}
              onChange={item => {
                props?.setFilter?.(prev => {
                  const update = prev?.map(el =>
                    el.key === option.key ? { ...el, value: item.value } : el,
                  );
                  return update;
                });
              }}
            />
          </>
        );
      } else if (option?.key === 'LOCATION') {
        return (
          <>
            <Text style={styles.filterOptionLabel}>{t(option?.label)}</Text>
            <GooglePlacesAutocomplete
              ref={ggPlacesAutocompleteRef}
              placeholder={t(option?.placeholder)}
              onPress={(_data, details = null) => {
                props?.setFilter?.(prev => {
                  const update = prev?.map((el: any) =>
                    el.key === option.key
                      ? {
                        ...el,
                        value: details?.formatted_address,
                        latitude: details?.geometry?.location?.lat,
                        longitude: details?.geometry?.location?.lng,
                      }
                      : el,
                  );
                  return update;
                });
              }}
              query={{
                key: 'AIzaSyDLncyTjn_rTzFhsxdYdlI0KibohWzR4pY',
                language: 'en',
                types: 'geocode',
              }}
              nearbyPlacesAPI="GooglePlacesSearch"
              minLength={4}
              renderRow={rowData => {
                const title =
                  rowData?.structured_formatting?.main_text ||
                  rowData?.description ||
                  'Unknown';
                const address =
                  rowData?.structured_formatting?.secondary_text || '';

                return (
                  <View pointerEvents="none" style={styles.searchResultItem}>
                    <View style={styles.searchResultContent}>
                      <Text style={styles.searchResultTitle}>{title}</Text>
                      {!!address && (
                        <Text style={styles.searchResultAddress}>
                          {address}
                        </Text>
                      )}
                    </View>
                  </View>
                );
              }}
              textInputProps={{
                onFocus: () => {
                  props?.setFilter?.(prev => {
                    const update = prev?.map(el =>
                      el.key === option.key ? { ...el, isFocused: true } : el,
                    );
                    return update;
                  });
                },
                onBlur: () => {
                  props?.setFilter?.(prev => {
                    const update = prev?.map(el =>
                      el.key === option.key ? { ...el, isFocused: false } : el,
                    );
                    return update;
                  });
                },
              }}
              enablePoweredByContainer={false}
              disableScroll={true}
              listViewDisplayed="auto"
              debounce={200}
              styles={{
                container: [
                  styles.ggAutoCompleteContainer,
                  option?.isFocused && styles.ggFocusedTextInputContainer,
                ],
                textInput: styles.ggAutoCompleteTextInput,
                row: styles.ggAutoCompleteRow,
              }}
              fetchDetails={true}
              timeout={20000}
              keepResultsAfterBlur={false}
              predefinedPlaces={[]}
              currentLocation={true}
              currentLocationLabel="Current location"
            />
          </>
        );
      } else {
        return (
          <Input
            onFocus={() => {
              props?.setFilter?.(prev => {
                const update = prev?.map(el =>
                  el.key === option.key ? { ...el, isFocused: true } : el,
                );
                return update;
              });
            }}
            onBlur={() => {
              props?.setFilter?.(prev => {
                const update = prev?.map(el =>
                  el.key === option.key ? { ...el, isFocused: false } : el,
                );
                return update;
              });
            }}
            editable={!disabledInput(option?.key)}
            keyboardType={option?.key === 'RADIUS' ? 'numeric' : 'default'}
            label={t(option?.label)}
            value={option?.value}
            labelStyle={styles.filterOptionLabel}
            inputStyle={styles.filterOptionInput}
            inputContainerStyle={[
              styles.filterOptionInputContainer,
              option?.isFocused && styles.ggFocusedTextInputContainer,
            ]}
            placeholder={t(option?.placeholder)}
            placeholderTextColor={colors.grayScale70}
            rightIcon={renderRightIcon(option?.key)}
            onChangeText={text => {
              props?.setFilter?.(prev => {
                const update = prev?.map(el =>
                  el.key === option.key ? { ...el, value: text } : el,
                );
                return update;
              });
            }}
          />
        );
      }
    },
    [communityUserHasJob, disabledInput, props, renderRightIcon, t, arrCompany],
  );
  const checkDisabledButtonApply = useCallback(() => {
    const payload: FetchHouseHasJobWorkerParams = {
      Latitude: props?.filter?.find(el => el.key === 'LOCATION')?.latitude || 0,
      Longitude:
        props?.filter?.find(el => el.key === 'LOCATION')?.longitude || 0,
      Radius: Number(props?.filter?.find(el => el.key === 'RADIUS')?.value),
      CommunityId: props?.filter?.find(el => el.key === 'COMMUNITY')?.value,
      CompanyId: props?.filter?.find(el => el.key === 'SELECT_COMPANY')?.value,
      StartDate: props?.filter?.find(el => el.key === 'SELECT_DATE_RANGE')
        ?.startDate
        ? dayjs(
          props?.filter?.find(el => el.key === 'SELECT_DATE_RANGE')
            ?.startDate,
        )
          .startOf('day')
          .toDate()
        : undefined,
      EndDate: props?.filter?.find(el => el.key === 'SELECT_DATE_RANGE')
        ?.endDate
        ? dayjs(
          props?.filter?.find(el => el.key === 'SELECT_DATE_RANGE')?.endDate,
        )
          .endOf('day')
          .toDate()
        : undefined,
      WorkCategoryId: props?.filter?.find(el => el.key === 'WORKER')?.value,
      LocationName: props?.filter?.find(el => el.key === 'LOCATION')?.value,
    };

    const isEmptyPayload = Object.values(payload).every(value => !value);
    return isEmptyPayload;
  }, [props?.filter]);

  return (
    <BottomSheet
      ref={bottomSheetRef}
      index={-1}
      animateOnMount={true}
      enableContentPanningGesture={true}
      enableHandlePanningGesture={true}
      enablePanDownToClose={true}
      enableDynamicSizing={false}
      containerStyle={styles.containerPosition}
      keyboardBehavior="interactive"
      keyboardBlurBehavior="restore"
      snapPoints={snapPoints}
      style={styles.container}
      onClose={() => {
        props?.onClose?.();
      }}
    >
      <BottomSheetKeyboardAwareScrollView
        bottomOffset={keyboardOffset}
        extraKeyboardSpace={keyboardExtraSpace}
      >
        <Text style={styles.titleFilter}>{t('FILTER')}</Text>

        <View style={styles.filterOptionsContainer}>
          {props?.filter?.map(option => (
            <View style={styles.filterOption} key={option?.key}>
              {renderContent(option)}
            </View>
          ))}
        </View>
      </BottomSheetKeyboardAwareScrollView>
      <View style={[styles.footerContainer, { paddingBottom: insets.bottom }]}>
        <Button
          onPress={() => {
            props?.setFilter?.(initialFilterOptions);
            ggPlacesAutocompleteRef?.current?.blur();
            ggPlacesAutocompleteRef?.current?.clear();
          }}
          variant="secondary"
          style={[styles.footerButton, styles.footerButtonCancel]}
          title={t('CLEAR_ALL')}
        />
        <Button
          isDisabled={checkDisabledButtonApply()}
          onPress={handleConfirm}
          style={styles.footerButton}
          title={t('APPLY')}
        />
      </View>
    </BottomSheet>
  );
});

Filter.displayName = 'Filter';

export default Filter;
