import { useCallback, useEffect, useRef, useState } from 'react';
import { OtpInputRef } from 'react-native-otp-entry';
import { useSelector } from 'react-redux';
import { BankInfoFormValues } from '../helper/PaymentInfoW9.helper';
import { BankInfo } from '../types/paymentInfoW9';
import { dispatch } from '../helper';
import { navigationRef } from '../services/NavigationService';
import { logger } from '../utils';

export const useBankInfo = () => {
  // const dispatch = useDispatch();
  // const { t } = useAppTranslation();
  const userProfile = useSelector((state: any) => state.auth.userProfile);
  const bankInfo = useSelector((state: any) => state.paymentInfoW9.data);
  const isShowAccountNumber = useSelector(
    (state: any) => state.paymentInfoW9.isShowAccountNumber,
  );
  const { isShow, isAddBankAccount, type, requestId } = useSelector(
    (state: any) => state.paymentInfoW9.isShowVerifyOtpModal,
  );

  const [bankInfoSelected, setBankInfoSelected] = useState<BankInfo>({});
  const [isOTPShowAccountNumber, setIsOTPShowAccountNumber] = useState(false);
  const [isOTPVerifyBankAccount, setIsOTPVerifyBankAccount] = useState(false);
  const [otp, setOtp] = useState('');
  const otpInputRef = useRef<OtpInputRef>(null) as React.RefObject<OtpInputRef>;

  const handleCloseOtp = async () => {
    const redux = await import('auth/redux');
    await dispatch(redux.actionContainer.showVerifyOtpReset());
    setIsOTPShowAccountNumber(false);
    setIsOTPVerifyBankAccount(false);
    setOtp('');
    otpInputRef.current?.clear();
  };

  const saveBankInfo = async (values: BankInfoFormValues) => {
    try {
      setIsOTPShowAccountNumber(false);
      setIsOTPVerifyBankAccount(true);
      const redux = await import('auth/redux');
      await dispatch(
        redux.actionContainer.addBankAccountAsync({
          accountId: userProfile?.accountId,
          bankAccountName: values.BANK_NAME,
          bankAccountNumber: values.ACCOUNT_NUMBER,
          routingNumber: values.ROUTING_NUMBER,
        }),
      );
    } catch (error) {
      logger('error saveBankInfo', error);
    }
  };

  const handleConfirmOtp = async (otpCode: string) => {
    if (type === 'phone') {
      const redux = await import('auth/redux');
      await dispatch(
        redux.actionContainer.verifyOtpBankAccountAsync({
          requestId: requestId,
          otp: otpCode,
          type: 'phone',
        }),
      );
    } else {
      const redux = await import('auth/redux');
      await dispatch(
        redux.actionContainer.verifyOtpBankAccountAsync({
          requestId: requestId,
          otp: otpCode,
          type: 'email',
          navigationRef: navigationRef,
        }),
      );
    }
    handleCloseOtp();
  };

  ///
  const toggleAccountVisibility = useCallback(async () => {
    try {
      if (isShowAccountNumber) {
        const redux = await import('auth/redux');
        // dispatch(redux.actionContainer.verifyTokenVerifyOtpReset());
        dispatch(redux.actionContainer.showAccountNumberAsync(false));
        return;
      } else {
        const redux = await import('auth/redux');
        dispatch(redux.actionContainer.sendTokenVerifyOtpAsync());
        setIsOTPShowAccountNumber(true);
        setIsOTPVerifyBankAccount(false);
      }
    } catch (error) {
      logger('error toggleAccountVisibility', error);
    }
  }, [isShowAccountNumber]);

  const verifyTokenVerifyOtp = async (code?: string) => {
    try {
      const redux = await import('auth/redux');
      await dispatch(redux.actionContainer.verifyTokenVerifyOtpAsync(code));
      setOtp('');
      otpInputRef.current?.clear();
    } catch (error) {
      logger('error verifyTokenVerifyOtp', error);
    }
  };

  const reSendShowAccountNumber = async () => {
    try {
      const redux = await import('auth/redux');
      dispatch(redux.actionContainer.sendTokenVerifyOtpAsync());
      setOtp('');
    } catch (error) {
      logger('error reSendShowAccountNumber', error);
    }
  };

  const reSendVerifyBankAccount = async () => {
    try {
      const redux = await import('auth/redux');
      dispatch(
        redux.actionContainer.sendOtpBankAccountAsync({
          requestId: requestId,
          type: type,
        }),
      );
      setOtp('');
    } catch (error) {
      logger('error reSendVerifyBankAccount', error);
    }
  };

  const goback = async () => {
    navigationRef.goBack();
    const redux = await import('auth/redux');
    await dispatch(redux.actionContainer.bankInfoReset());
  };

  useEffect(() => {
    if (bankInfo?.approvedBank.requestId) {
      setBankInfoSelected({ ...bankInfo?.approvedBank, isVeried: true });
    } else {
      setBankInfoSelected({ ...bankInfo?.verifiedBank, isVeried: false });
    }
  }, [bankInfo]);

  useEffect(() => {
    if (!isShow) {
      setIsOTPShowAccountNumber(false);
      setIsOTPVerifyBankAccount(false);
    } else if (isShow && isAddBankAccount) {
      setIsOTPVerifyBankAccount(true);
      setIsOTPShowAccountNumber(false);
    } else if (isShow && !isAddBankAccount) {
      setIsOTPShowAccountNumber(true);
      setIsOTPVerifyBankAccount(false);
    }
  }, [isShow, isAddBankAccount]);

  return {
    isOTPShowAccountNumber,
    isOTPVerifyBankAccount,
    setIsOTPShowAccountNumber,
    setIsOTPVerifyBankAccount,
    otp,
    setOtp,
    otpInputRef,
    handleCloseOtp,
    handleConfirmOtp,
    saveBankInfo,
    bankInfoSelected,
    // hiden account
    toggleAccountVisibility,
    userProfile,
    verifyTokenVerifyOtp,
    isShowAccountNumber,
    reSendShowAccountNumber,
    goback,
    type,
    reSendVerifyBankAccount,
  };
};
