import React, { memo, useState, useCallback } from 'react';
import { StyleSheet, View } from 'react-native';
import FastImage from 'react-native-fast-image';
import { FileInfo, FileThumbnailSize } from '../../types';
import VideoThumbnail from '../VideoThumbnail';
import DocumentThumbnail from '../DocumentThumbnail';
import { getFileType } from '../../utils/fileUtils';
import { SvgXml } from 'react-native-svg';
import Animated, { Layout, FadeIn, FadeOut } from 'react-native-reanimated';
import { getThumbnailSize } from './styles';
import { IconClose } from '../../../../assets/svg';
import { Button, SCREEN_WIDTH } from 'tradewize-component';
import { colors } from '../../../../theme';

interface FileThumbnailProps {
  file: FileInfo;
  size?: FileThumbnailSize;
  onPress?: () => void;
  readOnly?: boolean;
  isInstruction?: boolean;
  onRemove?: () => void;
  isAnimated?: boolean;
  priority?: 'low' | 'normal' | 'high';
}

const OptimizedImageThumbnail = ({ file, size, priority = 'normal' }: {
  file: FileInfo;
  size: FileThumbnailSize;
  priority?: 'low' | 'normal' | 'high';
}) => {
  const [imageError, setImageError] = useState(false);
  const thumbnailSize = getThumbnailSize(size);

  const handleError = useCallback(() => {
    setImageError(true);
  }, []);

  const getFastImagePriority = () => {
    switch (priority) {
      case 'high': return FastImage.priority.high;
      case 'low': return FastImage.priority.low;
      default: return FastImage.priority.normal;
    }
  };

  return (
    <FastImage
      source={{ 
        uri: file.url || file.uri,
        priority: getFastImagePriority(),
      }}
      style={StyleSheet.absoluteFill}
      resizeMode={FastImage.resizeMode.cover}
      onError={handleError}
    />
  );
};

const FileThumbnail: React.FC<FileThumbnailProps> = ({
  file,
  size = 'medium',
  onPress,
  readOnly = false,
  isInstruction = false,
  onRemove,
  isAnimated = true,
  priority = 'normal',
}) => {
  const fileUri = file.url || file.uri;
  const fileType = getFileType(fileUri, file.fileExtension);
  const thumbnailSize = getThumbnailSize(size);
  const styles = getStyles(thumbnailSize);

  // Memoize render function
  const renderThumbnail = useCallback(() => {
    switch (fileType) {
      case 'video':
        return <VideoThumbnail file={file} size={size} />;
      // case 'youtube':
      //   return <YouTubeThumbnail file={file} size={size} />;
      case 'document':
        return <DocumentThumbnail file={file} size={size} />;
      default:
        return (
          <OptimizedImageThumbnail 
            file={file} 
            size={size} 
            priority={priority}
          />
        );
    }
  }, [file, fileType, size, priority]);

  // Memoize onPress handler
  const handlePress = useCallback(() => {
    onPress?.();
  }, [onPress]);

  const handleRemove = useCallback(() => {
    onRemove?.();
  }, [onRemove]);

  if (readOnly || isInstruction) {
    return (
      <Button
        style={styles.container}
        onPress={handlePress}
        activeOpacity={0.8}
      >
        {renderThumbnail()}
      </Button>
    );
  }

  const AnimatedContainer = isAnimated ? Animated.View : View;
  const animationProps = isAnimated ? {
    entering: FadeIn.duration(200),
    exiting: FadeOut.duration(150),
    layout: Layout.springify(),
  } : {};

  return (
    <AnimatedContainer {...animationProps} style={styles.container}>
      <Button
        style={styles.mediaWrapper}
        onPress={handlePress}
        activeOpacity={0.8}
      >
        {renderThumbnail()}
      </Button>
      {!file?.isOld && (
        <Button
          onPress={handleRemove}
          style={styles.removeButtonCompact}
          hitSlop={{ top: 8, bottom: 8, left: 8, right: 8 }}
        >
          <SvgXml xml={IconClose(colors.white)} width={14} height={14} />
        </Button>
      )}
    </AnimatedContainer>
  );
};

const getStyles = (thumbnailSize: any) =>
  StyleSheet.create({
    container: {
      width: SCREEN_WIDTH * 0.1,
      height: SCREEN_WIDTH * 0.1,
      paddingHorizontal: 0,
      paddingVertical: 0,
      minHeight: 0,
      borderRadius: 4,
      position: 'relative',
      overflow: 'hidden',
      borderWidth: 1,
      borderColor: colors.line,
      backgroundColor: '#f8f8f8',
    },
    mediaWrapper: {
      flex: 1,
      paddingHorizontal: 0,
      paddingVertical: 0,
      minHeight: 0,
    },
    removeButtonCompact: {
      position: 'absolute',
      top: 4,
      right: 4,
      backgroundColor: 'rgba(0,0,0,0.7)',
      borderRadius: 12,
      width: 24,
      height: 24,
      alignItems: 'center',
      justifyContent: 'center',
      paddingHorizontal: 0,
      paddingVertical: 0,
      minHeight: 0,
    },
    errorContainer: {
      justifyContent: 'center',
      alignItems: 'center',
      backgroundColor: '#f5f5f5',
    },
    errorText: {
      fontSize: 16,
    },
  });

export default FileThumbnail;
