import React from 'react';
import { BaseContainer, HeaderBase, Button, Text } from 'tradewize-component';
import { useAppTranslation } from '../../hooks/useAppTranslation';
import { navigationRef } from '../../services/NavigationService';
import { styles } from './styles.ts';
import { Formik } from 'formik';
import { ScrollView, View, TextInput } from 'react-native';
import { getBankInfoSchema } from '../../validators/bankInfo.ts';
import {
  BankInfoFormValues,
  FIELDS,
  getFieldLabel,
  getFieldPlaceholder,
  initialValues,
} from '../../helper/PaymentInfoW9.helper';
import OtpVerifyModal from '../../components/OtpVerifyModal';
import { useBankInfo } from '../../hooks/useBankInfo';
import { OtpInputRef } from 'react-native-otp-entry';
import { colors } from '../../theme/index.tsx';

export default function BankInfoDataScreen() {
  const { t } = useAppTranslation();

  const validationSchema = getBankInfoSchema(t);

  const {
    otp,
    setOtp,
    otpInputRef,
    handleCloseOtp,
    handleConfirmOtp,
    saveBankInfo,
    type,
    userProfile,
    reSendVerifyBankAccount,
    isOTPVerifyBankAccount,
  } = useBankInfo();

  return (
    <BaseContainer style={styles.container}>
      <HeaderBase
        title={t('BANK_INFORMATION')}
        onPressBack={() => {
          navigationRef.goBack();
        }}
      />
      <Formik<BankInfoFormValues>
        initialValues={initialValues}
        validationSchema={validationSchema}
        onSubmit={values => {
          setTimeout(() => {
            saveBankInfo(values);
          }, 100);
        }}
        validateOnBlur={true}
        validateOnChange={false}
      >
        {({
          handleChange,
          handleSubmit,
          handleBlur,
          values,
          errors,
          touched,
        }) => (
          <View style={styles.safeArea}>
            <Text style={styles.titleBankInfo}>
              {t('ADD_YOUR_BANK_INFORMATION')}
            </Text>
            <ScrollView
              style={styles.scrollView}
              contentContainerStyle={styles.scrollViewContent}
              showsVerticalScrollIndicator={false}
            >
              {FIELDS.map(field => {
                return (
                  <View key={field}>
                    <Text style={styles.labelInput}>
                      {getFieldLabel(field, t)}
                    </Text>
                    <TextInput
                      placeholder={getFieldPlaceholder(field, t)}
                      value={values[field]}
                      onChangeText={text => {
                        handleChange(field)(text.trim());
                      }}
                      onBlur={handleBlur(field)}
                      keyboardType={
                        field === 'ACCOUNT_NUMBER' || field === 'ROUTING_NUMBER'
                          ? 'numeric'
                          : 'default'
                      }
                      maxLength={
                        field === 'BANK_NAME'
                          ? 150
                          : field === 'ACCOUNT_NUMBER'
                          ? 17
                          : 9
                      }
                      style={styles.inputContainer}
                      placeholderTextColor={colors.grayScale60}
                    />
                    <View style={styles.errorContainer}>
                      {errors[field] && touched[field] && (
                        <Text style={styles.errorText}>{errors[field]}</Text>
                      )}
                    </View>
                  </View>
                );
              })}
            </ScrollView>
            <View style={styles.buttonContainer}>
              <Button
                title={t('SAVE')}
                onPress={() => handleSubmit()}
                style={styles.saveButton}
              />
            </View>

            <OtpVerifyModal
              visible={isOTPVerifyBankAccount}
              onClose={handleCloseOtp}
              onConfirm={handleConfirmOtp}
              otp={otp}
              otpInputRef={otpInputRef as React.RefObject<OtpInputRef>}
              title={t('VERIFY_OTP')}
              description={t('ENTER_THE_OTP_SENT_TO', {
                val: type === 'email' ? userProfile?.email : userProfile?.phone,
              })}
              confirmText={t('VERIFY')}
              onChangeOtp={setOtp}
              resendText={t('RESEND_CODE')}
              initialCountdown={60}
              formatCountdownText={(remaining: number) => {
                return `${remaining}s`;
              }}
              onResend={reSendVerifyBankAccount}
              isConfirmLoading={false}
            />
          </View>
        )}
      </Formik>
    </BaseContainer>
  );
}
