import {
  CompanyRegistrationStatusEnum,
  CompanyRegistrationStatus,
} from '../types/register-company';
import colors from '../theme/colors';

export function getCompanyRegistrationStatus(
  status: CompanyRegistrationStatusEnum | undefined,
): CompanyRegistrationStatus {
  switch (status) {
    case CompanyRegistrationStatusEnum.NotRegistered: // cha ng k
      return {
        label: 'REGISTER',
        color: colors.borderGray,
        statusName: '',
        message: undefined,
      };
    case CompanyRegistrationStatusEnum.UnDoneOnboardingTest: // cha lm onboarding test
      return {
        label: 'FAILED_ONBOARDING_TEST',
        color: colors.alertColorError,
        statusName: 'FAILED',
        message: undefined,
      };
    case CompanyRegistrationStatusEnum.DoneOnboardingTest: //  lm onboarding test
      return {
        label: 'DONE_ONBOARDING_TEST',
        color: colors.alertColorSuccess,
        statusName: 'ACTIVE',
        message: 'DONE_ONBOARDING_TEST',
      };
    case CompanyRegistrationStatusEnum.PendingApproval: // ch admin approval
      return {
        label: 'PENDING_APPROVAL',
        color: colors.alertColorWarning,
        statusName: 'PENDING',
        message: 'WAIT_FOR_REVIEW',
      };
    case CompanyRegistrationStatusEnum.PendingInterview: // ch phng vn sau khi c approval
      return {
        label: 'PENDING_INTERVIEW',
        color: colors.alertColorWarning,
        statusName: 'PENDING',
        message: 'WAIT_FOR_REVIEW',
      };
    case CompanyRegistrationStatusEnum.InterviewFailed: // false phng vn
      return {
        label: 'INTERVIEW_FAILED',
        color: colors.alertColorError,
        statusName: 'FAILED',
        message: undefined,
      };
    case CompanyRegistrationStatusEnum.InterviewPassed: // pass phng vn
      return {
        label: 'INTERVIEW_PASSED',
        color: colors.alertColorSuccess,
        statusName: 'ACTIVE',
        message: undefined,
      };
    default:
      return {
        label: '',
        color: '#9E9E9E',
        statusName: '',
      };
  }
}
