import React from 'react';
import { View } from 'react-native';
import { Text, Button } from 'tradewize-component';
import { SvgXml } from 'react-native-svg';
import { styles } from '../styles';
import { IconPdfRed } from '../../../../../assets/svg/index';
import {
  JobStartTabStatus,
  ActionData,
} from '../../../../../types/myJobs.types';
import { useAppTranslation } from '../../../../../hooks/useAppTranslation';
import { dispatch } from '../../../../../helper';
import i18n from '../../../../../../../Auth/src/i18n';
import { NavigationService } from '../../../../../services/NavigationService';
import { SCREEN_ENUM } from '../../../../../types/global';

interface ActionButtonsProps {
  jumpTo?: JobStartTabStatus;
  actionData?: ActionData;
  isWarrantyJob?: boolean;
}

export default function ActionButtons({
  jumpTo,
  actionData,
  isWarrantyJob,
}: ActionButtonsProps) {
  const { t } = useAppTranslation();

  const cancelAction = async () => {
    const redux = await import('auth/redux');
    dispatch(
      redux.actionContainer.showConfirmModal({
        isShowCloseButton: false,
        isBackdropPress: false,
        isShowIcon: true,
        title: i18n?.t('CANCEL_JOB'),
        description: i18n?.t('ARE_YOU_SURE_YOU_WANT_TO_CANCEL_THIS_JOB?'),
        confirmText: i18n?.t('CONFIRM'),
        cancelText: i18n?.t('CANCEL'),
        onConfirm: () => {
          dispatch(redux.actionContainer.hideConfirmModal());
        },
        onCancel: () => {
          dispatch(redux.actionContainer.hideConfirmModal());
        },
      }),
    );
    return;
  };

  const goToJobDetail = () => {
    NavigationService.navigate(SCREEN_ENUM.JobDetailScreen, {
      id: actionData?.id,
      scheduleActivityId: actionData?.scheduleActivitytId,
    });
  };

  return (
    <View style={styles.groupBtn}>
      <View style={styles.flexRow}>
        {jumpTo !== JobStartTabStatus.COMPLETED &&
          jumpTo !== JobStartTabStatus.IN_PROGRESS && (
            <Button
              variant="outline"
              style={[styles.btnDetails, styles.btnCancel, styles.equalButton]}
              onPress={cancelAction}
            >
              <Text style={styles.btnCancelText}>{t('CANCEL')}</Text>
            </Button>
          )}

        {!isWarrantyJob && (
          <Button
            variant="outline"
            style={[styles.btnDetails, styles.btnPdf, styles.equalButton]}
            onPress={() => {}}
          >
            <Text style={styles.btnPdfText}>{t('WO')}</Text>
            <SvgXml xml={IconPdfRed()} width={18} height={18} />
          </Button>
        )}
        <Button
          variant="primary"
          style={[styles.btnDetails, styles.equalButton, styles.btnNoMargin]}
          onPress={goToJobDetail}
        >
          <Text style={styles.btnDetailsText}>{t('DETAILS')}</Text>
        </Button>
      </View>

      {/* <Modal
        isVisible={false}
        onBackdropPress={() => {}}
        backdropOpacity={0.4}
        useNativeDriver
        style={{ justifyContent: 'center', alignItems: 'center', margin: 0 }}
      >
        <View style={styles.modalContainer}>
          <Text style={styles.closeIconWrap} onPress={() => {}}>
            <SvgXml xml={IconClose()} width={18} height={18} />
          </Text>
          <Text style={styles.confirmTitle}>{t('CONFIRM')}</Text>
          <Text style={styles.confirmMsg}>
            {t('ARE_YOU_SURE_YOU_WANT_TO_CANCEL_THIS_JOB?')}
          </Text>
          <TextInput
            value={''}
            onChangeText={() => {}}
            placeholder={t('ENTER_CANCEL_REASON')}
            style={styles.cancelInput}
            placeholderTextColor={colors.grayScale50}
          />
          <View style={styles.actionsRow}>
            <Button style={styles.btnNo} onPress={() => {}}>
              <Text style={styles.txtNo}>{t('NO')}</Text>
            </Button>
            <Button
              variant="primary"
              style={[
                styles.btnYes,
                { backgroundColor: colors.mainColorPrimary },
              ]}
              onPress={() => {}}
              disabled={false}
            >
              <Text style={styles.txtYes}>{t('YES')}</Text>
            </Button>
          </View>
        </View>
      </Modal> */}
    </View>
  );
}
