import { QuestionItemProps } from '../screens/TakeTheTest/components/Item';
import { logger } from '../utils';

export const validateQuizItem = (data: QuestionItemProps[]) => {
  try {
    const dataUnSelected = data.filter(
      item => !item.options.some(option => option.isChecked),
    );
    if (dataUnSelected.length > 0) {
      return dataUnSelected;
    }
    const dataMissingRequiredImage = data.filter(
      item => item.requiredImage && !item.questionAnswerImages?.length,
    );

    if (dataMissingRequiredImage.length > 0) {
      return dataMissingRequiredImage;
    }
    return [];
  } catch (error) {
    logger('error validateQuizItem', error);
    return data;
  }
};
