import React from 'react';
import { View } from 'react-native';
import { Text } from 'tradewize-component';
import { formatCurrency } from '../../../../../utils/curencyUtil';
import { styles } from '../styles';
import {
  formatAmount,
  formatChecklist,
} from '../../../../../helper/myJob.helpter';
import { useAppTranslation } from '../../../../../hooks/useAppTranslation';
import { JobInfoData } from '../../../../../types/myJobs.types';

interface JobInfoProps {
  jobInfoData: JobInfoData;
}

export default function JobInfo({ jobInfoData }: JobInfoProps) {
  const { t } = useAppTranslation();

  return (
    <View style={styles.jobStatusBox}>
      <View style={styles.jobStatusRow}>
        <Text style={styles.jobStatusText}>{t('AMOUNT')}</Text>
        <Text style={styles.myJobInfo}>
          {formatAmount(jobInfoData.totalAmount || 0)}
          {jobInfoData.bonusAmount ? (
            <Text>
              {' '}
              ({t('INCLUDE_BONUS')} {formatCurrency(jobInfoData.bonusAmount)})
            </Text>
          ) : null}
        </Text>
      </View>
      <View style={styles.jobStatusRow}>
        <Text style={styles.jobStatusText}>{t('CHECKLIST')}</Text>
        <Text style={styles.myJobInfo}>
          {formatChecklist(
            jobInfoData.checklistChecked,
            jobInfoData.checklistTotal,
          )}
        </Text>
      </View>
      <View style={styles.jobStatusRow}>
        <Text style={styles.jobStatusText}>{t('LOT_NUMBER')}</Text>
        <Text style={styles.myJobInfo}>{jobInfoData.lotNumber}</Text>
      </View>
      <View style={styles.jobStatusLabel}>
        <Text style={styles.headerLabel}>{t('MY_JOB_INFO')}</Text>
      </View>
    </View>
  );
}
