import { Image } from 'react-native';
import { BaseContainer, HeaderBase } from 'tradewize-component';
import { useAppTranslation } from '../../hooks/useAppTranslation';
import { styles } from './styles';
import { colors } from '../../theme';
import { NavigationService } from '../../services/NavigationService';
import { playInstructionalVideos } from '../../assets/images';
import { useDispatch, useSelector } from 'react-redux';
import { logger } from '../../utils';
import { ScrollTabView } from '../../components/ScrollTabView';
import SystemGuide from './components/System';
import QualityGuide from './components/Quanlity';
import { useTraining } from '../../hooks/useTraining';

function getTotalTrainings(data: any, arrayName: string, keyName: string) {
  if (!data || !data[arrayName] || !Array.isArray(data[arrayName])) {
    return {
      total: 0,
      watched: 0,
      due: 0,
    };
  }

  let total = 0;
  let watched = 0;
  let due = 0;

  data[arrayName].forEach((company: any) => {
    if (company[keyName] && Array.isArray(company[keyName])) {
      company[keyName].forEach((training: any) => {
        total++;

        if (training.isWatched === true) {
          watched++;
        }

        if (training.isDue === true) {
          due++;
        }
      });
    }
  });

  return {
    total,
    watched,
    due,
  };
}

const SystemQualityGuideScreen = () => {
  const dispatch = useDispatch();
  const { t, i18n } = useAppTranslation();

  const dataMedia = useSelector((state: any) => state.app.dataMedia);
  const tutorials = useSelector((state: any) => state.training.tutorials);
  const myTrainings = useSelector((state: any) => state.training.myTrainings);

  const {
    total: totalTrainingVideo,
    watched: watchedTrainingVideo,
    due: dueTrainingVideo,
  } = getTotalTrainings(myTrainings?.data, 'trainings', 'trainings');
  const {
    total: totalTutorialVideo,
    watched: watchedTutorialVideo,
    due: dueTutorialVideo,
  } = getTotalTrainings(myTrainings?.data, 'tutorialsByCompany', 'tutorials');
  const totalVideoTraining = totalTrainingVideo + totalTutorialVideo;
  const totalWatchedVideoTraining = watchedTrainingVideo + watchedTutorialVideo;
  const totalDueVideoTraining = dueTrainingVideo + dueTutorialVideo;
  const totalDueVideoTutorials = tutorials?.data?.tutorials?.filter(
    (tutorial: any) => tutorial.isDue === true,
  ).length;

  const handlePlayInstructionalVideos = async () => {
    try {
      const redux = await import('auth/redux');
      dispatch(
        redux.actionContainer.showVideoModal({
          initialSubtitle: i18n?.language || 'en',
          source:
            dataMedia?.['TRAINING_SESSION_SCREEN' as keyof typeof dataMedia]
              ?.url || '',
          subtitle:
            dataMedia?.['TRAINING_SESSION_SCREEN' as keyof typeof dataMedia]
              ?.subtitle || {},
          onClose: () => {
            dispatch(redux.actionContainer.hideVideoModal());
          },
        }),
      );
    } catch (error) {
      logger('error handlePlayInstructionalVideos', error);
    }
  };

  const { loading } = useTraining();

  return (
    <BaseContainer
      backgroundColor={colors.mainColorPrimary}
      statusBarStyle="light-content"
      backgroundBottomSafeAreaColor={colors.white}
    >
      <HeaderBase
        containerStyle={styles.headerContainer}
        title={t('TRAINING')}
        titleStyle={styles.headerTitle}
        colorIconLeft={colors.white}
        onPressBack={() => NavigationService.goBack()}
        rightActions={[
          {
            icon: (
              <Image
                source={playInstructionalVideos}
                style={styles.playInstructionalVideos}
              />
            ),
            onPress: handlePlayInstructionalVideos,
          },
        ]}
      />

      <ScrollTabView
        lazy
        activeTabTextStyle={styles.activeTabText}
        activeIndicatorStyle={styles.activeIndicator}
        textStyleInactive={styles.textStyleInactive}
        tabBarStyle={[
          styles.tabBar,
          (!totalDueVideoTutorials || !totalDueVideoTraining) &&
            styles.tabBarNoDueVideo,
        ]}
        styleSubtitle={styles.styleSubtitle}
      >
        <ScrollTabView.Tab
          keyName="SYSTEM_GUIDE"
          title={
            tutorials?.data?.totalTutorials > 0
              ? `${t('SYSTEM_GUIDE')} (${tutorials?.data?.totalWatched}/${
                  tutorials?.data?.totalTutorials
                })`
              : t('SYSTEM_GUIDE')
          }
          subtitle={
            totalDueVideoTutorials > 0
              ? `${totalDueVideoTutorials} ${t('VIDEO_DUE')}`
              : ''
          }
        >
          <SystemGuide isLoading={loading} />
        </ScrollTabView.Tab>
        <ScrollTabView.Tab
          onPressTab={async (index, setIndex) => {
            if (totalDueVideoTutorials > 0) {
              const redux = await import('auth/redux');
              dispatch(
                redux.actionContainer.showAlert({
                  title: t('WARNING'),
                  message: t('PLEASE_WATCH_ALL_THE_SYSTEM_GUIDE_VIDEOS_FIRST'),
                  type: 'warning',
                  isShowCloseButton: false,
                  isBackdropPress: false,
                  isShowIcon: true,
                  onClose: () => {
                    dispatch(redux.actionContainer.hideAlert());
                  },
                }),
              );
            } else {
              setIndex(index);
            }
          }}
          keyName="QUALITY_GUIDE"
          subtitle={
            totalDueVideoTraining > 0
              ? `${totalDueVideoTraining} ${t('VIDEO_DUE')}`
              : ''
          }
          title={
            totalVideoTraining > 0
              ? `${t(
                  'QUALITY_GUIDE',
                )} (${totalWatchedVideoTraining}/${totalVideoTraining})`
              : t('QUALITY_GUIDE')
          }
        >
          <QualityGuide isLoading={loading} />
        </ScrollTabView.Tab>
      </ScrollTabView>
    </BaseContainer>
  );
};

export default SystemQualityGuideScreen;
