import React from 'react';
import { ActivityIndicator, View } from 'react-native';
import {
  Text,
  BaseContainer,
  HeaderBase,
  Input,
  Button,
} from 'tradewize-component';
import colors from '../../theme/colors.ts';
import { styles } from './styles.ts';
import { NavigationService } from '../../services/NavigationService.ts';
import { useAppTranslation } from '../../hooks/useAppTranslation.ts';
import { useState } from 'react';
import { EyeIconOff, EyeIcon, IconCheck } from '../../assets/svg/index.ts';
import { SvgXml } from 'react-native-svg';
import PasswordStrengthBar from './PassStrengthBar/index.tsx';
import { getPasswordStrength } from '../../utils/index.tsx';
import { changePassword } from '../../api/endpoint.ts';
import { logger } from '../../utils/index.tsx';
import { dispatch, updateProfile } from '../../helper/index.tsx';
import Toast from 'react-native-toast-message';

const ARR_INPUT = ['OLD_PASSWORD', 'NEW_PASSWORD', 'CONFIRM_PASSWORD'];

const ChangePasswordMainScreen = () => {
  const { t } = useAppTranslation();
  const [values, setValues] = useState({
    old_password: '',
    new_password: '',
    confirm_password: '',
  });
  const [isSecureText, setIsSecureText] = useState<string[]>([]);
  const [oldPasswordError, setOldPasswordError] = useState<string | null>(null);
  const [isLoading, setIsLoading] = useState(false);

  // Check if passwords match
  const passwordsDoNotMatch =
    values.confirm_password.length > 0 &&
    values.new_password.length > 0 &&
    values.new_password !== values.confirm_password;

  // Validation for new password
  const newPasswordErrors: string[] = [];
  if (values.new_password.length > 0) {
    if (values.new_password.length < 8) {
      newPasswordErrors.push(
        t('PASSWORD_MUST_BE_AT_LEAST_NUMBER_CHARACTERS_LONG', { number: 8 }),
      );
    }
    const hasUppercase = /[A-Z]/.test(values.new_password);
    const hasSpecialChar = /[@/#]/.test(values.new_password);
    if (!hasUppercase || !hasSpecialChar) {
      newPasswordErrors.push(t('PASSWORD_MUST_CONTAIN_AT_LEAST'));
    }
  }
  const hasNewPasswordError = newPasswordErrors.length > 0;

  const handleChangePassword = async () => {
    const client = await import('auth/api');
    const redux = await import('auth/redux');
    try {
      setOldPasswordError(null);
      setIsLoading(true);
      if (hasValidationErrors) {
        dispatch(
          redux?.actionContainer.showAlertAsync({
            title: t('WARNING'),
            message: t('PLEASE_FILL_IN_ALL_FIELDS'),
            type: 'warning',
            buttonText: t('CONFIRM'),
            titleStyle: {
              color: colors.black,
            },
            buttonStyle: {
              backgroundColor: colors.mainColorPrimary,
            },
          }),
        );
        return;
      }
      const response = await client.putRequest(changePassword, {
        password: values.old_password,
        newPassword: values.new_password,
      });
      if (response?.status === 200) {
        Toast.show({
          text1: t('CHANGE_PASSWORD_SUCCESS'),
          type: 'success',
        });
        await updateProfile({ isFirstResetPassword: true });
        setTimeout(() => {
          NavigationService.goBack();
        }, 350);
      } else {
        const errorMessage =
          response?.message || t('AN_ERROR_OCCURRED_PLEASE_TRY_AGAIN');
        if (errorMessage.toLowerCase().includes('password is incorrect')) {
          setOldPasswordError(t('CURRENT_PASSWORD_IS_INCORRECT'));
        } else {
          dispatch(
            redux?.actionContainer.showAlertAsync({
              title: t('CHANGE_PASSWORD_FAILED'),
              message: errorMessage,
              type: 'error',
              buttonText: t('OK'),
              titleStyle: {
                color: colors.black,
              },
              buttonStyle: {
                backgroundColor: colors.mainColorPrimary,
              },
            }),
          );
        }
      }
    } catch (error: any) {
      logger('error handleChangePassword', error);
      const errorMessage =
        error?.response?.data?.message ||
        error?.message ||
        t('AN_ERROR_OCCURRED_PLEASE_TRY_AGAIN');

      if (errorMessage.toLowerCase().includes('password is incorrect')) {
        setOldPasswordError(t('CURRENT_PASSWORD_IS_INCORRECT'));
      } else {
        dispatch(
          redux?.actionContainer.showAlertAsync({
            title: t('CHANGE_PASSWORD_FAILED'),
            message: errorMessage,
            type: 'error',
            buttonText: t('OK'),
          }),
        );
      }
    } finally {
      setIsLoading(false);
    }
  };

  const hasEmptyFields =
    !values.old_password || !values.new_password || !values.confirm_password;
  const hasValidationErrors =
    hasNewPasswordError || passwordsDoNotMatch || hasEmptyFields;

  return (
    <BaseContainer style={styles.container}>
      <HeaderBase
        title="Change Password"
        onPressBack={() => {
          NavigationService.goBack();
        }}
      />
      <Text style={styles.title}>{t('ENTER_YOUR_NEW_PASSWORD')}</Text>
      <View style={styles.formInputContainer}>
        {ARR_INPUT.map(item => (
          <Input
            key={item}
            inputContainerStyle={styles.inputContainer}
            inputStyle={styles.input}
            label={t(item)}
            labelStyle={styles.labelInput}
            placeholder={t(`ENTER_YOUR_${item}`)}
            placeholderTextColor={colors.darkGray}
            value={values[item.toLowerCase() as keyof typeof values]}
            onChangeText={text => {
              setValues({
                ...values,
                [item.toLowerCase() as keyof typeof values]: text,
              });
              if (item === 'OLD_PASSWORD') {
                setOldPasswordError(null);
              }
            }}
            secureTextEntry={!isSecureText?.includes(item)}
            rightIcon={
              <Button
                variant="ghost"
                onPress={() =>
                  setIsSecureText(prev =>
                    prev.includes(item)
                      ? prev.filter(i => i !== item)
                      : [...prev, item],
                  )
                }
              >
                <SvgXml
                  xml={isSecureText?.includes(item) ? EyeIcon() : EyeIconOff()}
                  width={24}
                  height={24}
                />
              </Button>
            }
            isError={
              (item === 'OLD_PASSWORD' && !!oldPasswordError) ||
              (item === 'CONFIRM_PASSWORD' && passwordsDoNotMatch) ||
              (item === 'NEW_PASSWORD' && hasNewPasswordError)
            }
            errorText={
              item === 'OLD_PASSWORD' && oldPasswordError
                ? oldPasswordError
                : item === 'CONFIRM_PASSWORD' && passwordsDoNotMatch
                ? t('PASSWORDS_DO_NOT_MATCH')
                : undefined
            }
          />
        ))}
        {hasNewPasswordError && (
          <View style={styles.passwordRequirements}>
            {newPasswordErrors.map((error, index) => (
              <View key={index} style={styles.requirementItem}>
                <SvgXml xml={IconCheck(colors.red)} width={14} height={14} />
                <Text style={styles.requirementText}>{error}</Text>
              </View>
            ))}
          </View>
        )}
        <PasswordStrengthBar
          score={getPasswordStrength(values.new_password)}
          style={styles.passwordStrengthBar}
        />
      </View>
      <Button
        title={t('CHANGE_PASSWORD')}
        onPress={handleChangePassword}
        style={styles.button}
        disabled={isLoading}
      />
      {isLoading && (
        <View style={styles.loadingContainer}>
          <ActivityIndicator size="large" color={colors.mainColorPrimary} />
        </View>
      )}
    </BaseContainer>
  );
};

export default ChangePasswordMainScreen;
