import React from 'react';
import { StyleSheet } from 'react-native';
import { Button, Text } from 'tradewize-component';
import { SvgXml } from 'react-native-svg';
import { colors } from '../../../../theme';
import { IconArrowDown2 } from '../../../../assets/svg';
import { useAppTranslation } from '../../../../hooks/useAppTranslation';
import { SCREEN_ENUM } from '../../../../types/global';
import { NavigationService } from '../../../../services/NavigationService';
import { useDispatch } from 'react-redux';

interface MaterialsProps {
  scheduleActivityId: string;
}

const Materials = ({ scheduleActivityId }: MaterialsProps) => {
  const { t } = useAppTranslation();
  const dispatch = useDispatch();

  const handlePress = async () => {
    try {
      const redux = await import('auth/redux');
      await dispatch(
        redux.actionContainer.getMaterialListAsync({
          scheduleActivityId,
        }),
      );
      NavigationService.navigate(SCREEN_ENUM.MaterialListScreen);
    } catch (error) {
      console.log('Error fetching material list:', error);
    }
  };

  return (
    <Button style={styles.container} onPress={handlePress} activeOpacity={0.7}>
      <Text style={styles.label}>{t('MATERIALS')}</Text>
      <SvgXml
        style={styles.icon}
        xml={IconArrowDown2(colors.black)}
        width={16}
        height={16}
      />
    </Button>
  );
};

const styles = StyleSheet.create({
  container: {
    flexDirection: 'row',
    alignItems: 'center',
    justifyContent: 'space-between',
    paddingVertical: 8,
    paddingHorizontal: 12,
    backgroundColor: colors.hummingBird,

    borderRadius: 8,
    borderWidth: 1,
    borderColor: colors.grayScale30,
  },
  label: {
    fontSize: 14,
    color: colors.black,
    fontWeight: '500',
  },
  icon: {
    transform: [{ rotate: '-90deg' }],
  },
});

export default Materials;
