import { StyleProp, StyleSheet, View, ViewStyle } from 'react-native';
import { Button, SCREEN_WIDTH, Text } from 'tradewize-component';
import { colors } from '../../../theme';
import FastImage from 'react-native-fast-image';
import { useAppTranslation } from '../../../hooks/useAppTranslation';
import { playIcon } from '../../../assets/images';
import { logger } from '../../../utils';
import { useDispatch } from 'react-redux';
import { useMemo } from 'react';

export interface CardItemVideoData {
  id: string;
  title: string;
  thumbnailUrl: string;
  isWatched: boolean;
  isDue: boolean;
  remainingDays: number;
  dueInDays: number;
  effectiveStartDate: string;
  expNeedDone: string;
  description: string;
  videoUrl: {
    en: string;
    es?: string;
  };
  subTitle: {
    en: string;
    es?: string;
  };
  trainingRoles: {
    id: string;
    name: string;
    code: string;
  }[];
  isPassed: boolean;
  required: boolean;
  isHasQuestion: boolean;
}

type CardItemVideoProps = {
  data: CardItemVideoData;
  styleContainer?: StyleProp<ViewStyle>;
  variant?: 'system' | 'quality';
  onPress?: (data: CardItemVideoData) => void;
  onTakeTest?: (data: CardItemVideoData) => void;
};

const CardItemVideo = (props: CardItemVideoProps) => {
  const { data, styleContainer, variant = 'system', onTakeTest } = props;
  const { t, i18n } = useAppTranslation();
  const dispatch = useDispatch();

  const isRequireWatch = useMemo(() => {
    if (variant === 'quality' && data?.isHasQuestion && !data?.isWatched) {
      return true;
    }
    return data?.required && !data?.isWatched;
  }, [variant, data?.isHasQuestion, data?.isWatched, data?.required]);

  const handlePress = async () => {
    try {
      const redux = await import('auth/redux');
      dispatch(
        redux.actionContainer.showVideoModal({
          initialSubtitle: i18n?.language || 'en',
          source: data?.videoUrl,
          subtitle: data?.subTitle,
          onClose: () => {
            dispatch(redux.actionContainer.hideVideoModal());
          },
        }),
      );
      if (props.onPress) {
        props.onPress(data);
      }
    } catch (error) {
      logger('error handlePress', error);
    }
  };

  const handleTakeTest = async () => {
    try {
      const redux = await import('auth/redux');
      if (isRequireWatch) {
        dispatch(
          redux.actionContainer.showAlertAsync({
            title: t('WARNING'),
            message: t('PLEASE_WATCH_THE_VIDEO_FIRST'),
            type: 'warning',
            buttonText: t('OK'),
          }),
        );
      } else {
        onTakeTest?.(data);
      }
    } catch (error) {
      logger('error handleTakeTest', error);
    }
  };

  return (
    <View style={[styles.container, styleContainer]}>
      <Button
        onPress={handlePress}
        variant="ghost"
        style={[
          styles.contentContainer,
          isRequireWatch &&
            variant === 'system' &&
            styles.requireWatchContainer,
          data?.isHasQuestion && styles.isHasQuestionContainer,
        ]}
      >
        {data?.thumbnailUrl ? (
          <FastImage
            source={{ uri: data?.thumbnailUrl }}
            style={styles.thumbnail}
            resizeMode={
              variant === 'quality'
                ? FastImage.resizeMode.stretch
                : FastImage.resizeMode.contain
            }
          />
        ) : (
          <View style={[styles.thumbnail, styles.thumbnailPlaceholder]}>
            <Text style={styles.thumbnailPlaceholderText}>
              {t('TRADEWIZE_TRAINING')}
            </Text>
          </View>
        )}
        <View style={styles.content}>
          <Text numberOfLines={2} style={styles.title}>
            {data?.title}
          </Text>
          <Text style={styles.description}>{data?.description}</Text>
        </View>
        {variant === 'quality' && (
          <View style={styles.roleContainer}>
            {data?.trainingRoles?.map((role: any, index: number) => (
              <Text key={role?.id} style={styles.roleText}>
                {index === 0 && t('TRAINING_FOR')} {role?.name}
                {index !== data?.trainingRoles?.length - 1 && ','}
              </Text>
            ))}
          </View>
        )}

        {data?.isHasQuestion && variant === 'quality' && !data?.isPassed && (
          <Button
            onPress={handleTakeTest}
            title={t('TAKE_THE_TEST')}
            variant="primary"
            textStyle={styles.takeTestButtonText}
            fullWidth
            style={[
              styles.takeTestButton,
              isRequireWatch && styles.takeTestButtonRequireWatch,
            ]}
          />
        )}

        <FastImage source={playIcon} style={styles.playIcon} />

        {/* Status */}
        {data?.isPassed && (
          <View style={styles.statusPassedContainer}>
            <Text style={styles.statusText}>{t('PASSED')}</Text>
          </View>
        )}
        {(isRequireWatch || data?.isWatched) && (
          <View
            style={[
              styles.statusWatchedContainer,
              isRequireWatch && styles.statusRequiredContainer,
            ]}
          >
            <Text style={styles.statusText}>
              {isRequireWatch ? t('REQUIRED') : t('WATCHED')}
            </Text>
          </View>
        )}
        {data?.required && !data?.isWatched && variant === 'system' && (
          <View style={styles.statusRequiredWatchContainer}>
            <Text style={styles.statusText}>{t('YOU_NEED_TO_WATCH_NOW')}</Text>
          </View>
        )}
      </Button>
    </View>
  );
};

const styles = StyleSheet.create({
  container: {
    backgroundColor: colors.white,
    paddingHorizontal: 16,
  },
  contentContainer: {
    borderWidth: 1,
    borderColor: colors.grayScale50,
    borderRadius: 8,
    flexDirection: 'column',
    paddingVertical: 0,
    paddingHorizontal: 0,
    alignItems: 'flex-start',
    paddingBottom: 8,
  },
  requireWatchContainer: {
    borderColor: colors.red,
  },
  thumbnail: {
    width: '100%',
    height: SCREEN_WIDTH * 0.4,
    borderTopLeftRadius: 8,
    borderTopRightRadius: 8,
  },
  title: {
    fontSize: 14,
    fontWeight: 'bold',
    color: colors.mainColorPrimary,
    lineHeight: 20,
    marginTop: 12,
  },
  description: {
    fontSize: 14,
    fontWeight: '400',
    color: colors.grayScale70,
    lineHeight: 16,
    marginTop: 4,
  },
  thumbnailPlaceholder: {
    backgroundColor: colors.colorF0F0F0,
    justifyContent: 'center',
    alignItems: 'center',
    borderBottomEndRadius: 0,
    borderBottomStartRadius: 0,
  },
  content: {
    paddingHorizontal: 12,
  },
  thumbnailPlaceholderText: {
    fontSize: 14,
    fontWeight: '600',
    color: colors.grayScale70,
    lineHeight: 16,
  },
  playIcon: {
    width: SCREEN_WIDTH * 0.12,
    height: SCREEN_WIDTH * 0.12,
    position: 'absolute',
    alignSelf: 'center',
    top: SCREEN_WIDTH * 0.13,
  },
  roleContainer: {
    flexDirection: 'row',
    flexWrap: 'wrap',
    paddingHorizontal: 12,
  },
  roleText: {
    fontSize: 12,
    fontWeight: 'bold',
    color: colors.black,
    letterSpacing: -0.2,
    flexShrink: 1,
  },
  statusPassedContainer: {
    flexDirection: 'row',
    alignItems: 'center',
    justifyContent: 'center',
    paddingHorizontal: 12,
    backgroundColor: colors.mainColorPrimary,
    borderRadius: 8,
    position: 'absolute',
    top: 12,
    right: 12,
    zIndex: 1,
  },
  statusWatchedContainer: {
    flexDirection: 'row',
    alignItems: 'center',
    justifyContent: 'center',
    paddingHorizontal: 12,
    backgroundColor: colors.alertColorSuccess,
    borderRadius: 8,
    position: 'absolute',
    top: 12,
    left: 12,
    zIndex: 1,
  },
  statusText: {
    fontSize: 12,
    fontWeight: 'bold',
    color: colors.white,
    letterSpacing: -0.2,
  },
  statusRequiredContainer: {
    backgroundColor: colors.alertColorWarning,
  },
  statusRequiredWatchContainer: {
    backgroundColor: colors.red,
    paddingHorizontal: 4,
    borderRadius: 4,
    position: 'absolute',
    right: 12,
    top: SCREEN_WIDTH * 0.4 - 36,
    zIndex: 1,
  },
  isHasQuestionContainer: {
    paddingBottom: 0,
  },
  takeTestButton: {
    backgroundColor: colors.mainColorPrimary,
    marginTop: 8,
    borderRadius: 8,
    borderTopLeftRadius: 0,
    borderTopRightRadius: 0,
  },
  takeTestButtonRequireWatch: {
    backgroundColor: colors.grayScale80,
  },
  takeTestButtonText: {
    fontSize: 14,
    fontWeight: '500',
    color: colors.white,
  },
});

export default CardItemVideo;
