import { View } from 'react-native';
import { BaseContainer, HeaderBase } from 'tradewize-component';
import { styles } from './styles';
import React, { useEffect } from 'react';
import { useAppTranslation } from '../../hooks/useAppTranslation';
import { SsnField } from './components/ssn';
import { W9Section } from './components/w9';
import { BankInfo } from './components/bankInfo';
import { dispatch } from '../../helper';
import { useBankInfo } from '../../hooks/useBankInfo';

export default function UserProfileScreen() {
  const { t } = useAppTranslation();
  const { bankInfoSelected, goback } = useBankInfo();

  useEffect(() => {
    (async () => {
      const redux = await import('auth/redux');
      await dispatch(redux.actionContainer.bankInfoAsync());
    })();
  }, []);

  return (
    <BaseContainer style={styles.container}>
      <HeaderBase title={t('PAYMENT_INFO_AND_W_9')} onPressBack={goback} />
      <View style={styles.content}>
        <SsnField />
        <W9Section bankInfoSelected={bankInfoSelected} />
        <BankInfo />
      </View>
    </BaseContainer>
  );
}
