export interface Role {
  id: string;
  code: string;
  scope: string | null;
  level: number | null;
  name: string;
  roleType: number;
  isMyRole: boolean;
}

export enum CompanyRegistrationStatusEnum {
  NotRegistered = 0, // cha ng k
  UnDoneOnboardingTest = 1, // cha lm onboarding test
  DoneOnboardingTest = 2, //  lm onboarding test
  PendingApproval = 3, // ch admin approval
  PendingInterview = 4, // ch phng vn sau khi c approval
  InterviewFailed = 5, // false phng vn
  InterviewPassed = 6, // pass phng vn
}

export interface Company {
  companyId: string;
  companyName: string;
  companyRegistrationStatus: CompanyRegistrationStatusEnum;
  canRegister?: boolean;
  roles: Role[];
}

export interface CompanyAndRoleData {
  data: Company[];
  loading?: boolean;
}

export interface CompanyRegistrationStatus {
  label?: string;
  statusName?: string;
  message?: string;
  color: string;
}
