import React, { useState, useMemo, useCallback } from 'react';
import { View, Text, FlatList} from 'react-native';
import { BaseContainer, HeaderBase } from 'tradewize-component';
import { navigationRef } from '../../services/NavigationService';
import Tabbar from './Components/Tabbar';
import ListItem from './Components/ListItem';
import { styles } from './styles';
import testData from './DataTest.json';
import { useAppTranslation } from '../../hooks/useAppTranslation';

interface NotificationItem {
  id: string;
  type: string;
  photoUrl: string | null;
  title: string;
  body: string;
  sendTime: string;
  isRead: boolean;
  category: number;
  categoryName: string;
}

interface UnreadCount {
  category: number;
  unreadCount: number;
}

const NotificationScreen: React.FC = () => {
  const [activeTab, setActiveTab] = useState<number>(1);
  const { t } = useAppTranslation();
  const notifications = useMemo(() => {
    return (testData?.data?.notifications?.items || []) as NotificationItem[];
  }, []);

  const unreadCounts = useMemo(() => {
    return (testData?.data?.unRead || []) as UnreadCount[];
  }, []);

  const filteredNotifications = useMemo(() => {
    return notifications.filter(item => item.category === activeTab);
  }, [notifications, activeTab]);

  const handleTabPress = useCallback((category: number) => {
    setActiveTab(category);
  }, []);

  const handleMarkAllRead = useCallback(() => {
    console.log('Mark all as read');
  }, []);

  const keyExtractor = useCallback(
    (item: NotificationItem) => item.id,
    [],
  );

  const renderItem = useCallback(
    ({ item }: { item: NotificationItem }) => <ListItem item={item} />,
    [],
  );

  return (
    <BaseContainer style={styles.container}>
      <HeaderBase
        title="Notifications"
        onPressBack={() => {
          navigationRef.goBack();
        }}
      />
      <Tabbar
        activeTab={activeTab}
        onTabPress={handleTabPress}
        unreadCounts={unreadCounts}
        onMarkAllRead={handleMarkAllRead}
      />
      <View style={styles.markAllReadButton}>
        <Text style={styles.markAllReadText} onPress={handleMarkAllRead}>{t('MARK_ALL_AS_READ')}</Text>
      </View>
      <View style={styles.listContainer}>
        <FlatList
          data={filteredNotifications}
          renderItem={renderItem}
          keyExtractor={keyExtractor}
          contentContainerStyle={styles.listContent}
          showsVerticalScrollIndicator={false}
        />
      </View>
    </BaseContainer>
  );
};

export default NotificationScreen;
