import React from 'react';
import { StyleSheet, View } from 'react-native';
import { Button, Text } from 'tradewize-component';
import { SvgXml } from 'react-native-svg';
import { colors } from '../../../../../theme';
import {
  IconArrowRight,
  IconPlay,
  IconCheck,
  IconClock,
  IconCalendar,
  IconCompany,
} from '../../../../../assets/svg';
import { useAppTranslation } from '../../../../../hooks/useAppTranslation';
import {
  checklistCategory,
  ChecklistItem,
} from '../../../../../types/jobDetail.type';
import ChecklistItemContent from './ChecklistItemContent';

interface CategorySectionProps {
  categoryKey: string;
  categoryEnum: checklistCategory;
  items: ChecklistItem[];
  expandedCategories: { [key: string]: boolean };
  toggleCategory: (key: string) => void;
}

const CategoryConfig = {
  [checklistCategory.JobStart]: {
    color: colors.mainColorPrimary,
    icon: IconPlay,
    label: 'JOB_START',
  },
  [checklistCategory.General]: {
    color: colors.mainColorPrimary,
    icon: IconCheck,
    label: 'GENERAL',
  },
  [checklistCategory.EndOfDay]: {
    color: colors.mainColorPrimary,
    icon: IconClock,
    label: 'END_OF_DAY',
  },
  [checklistCategory.Material]: {
    color: colors.mainColorPrimary,
    icon: IconCalendar,
    label: 'MATERIAL',
  },
  [checklistCategory.MaterialDelivery]: {
    color: colors.mainColorPrimary,
    icon: IconCompany,
    label: 'MATERIAL_DELIVERY',
  },
};

const CategorySection: React.FC<CategorySectionProps> = ({
  categoryKey,
  categoryEnum,
  items,
  expandedCategories,
  toggleCategory,
}) => {
  const { t } = useAppTranslation();
  const config = CategoryConfig[categoryEnum];

  if (!config) return null;

  // const checkedCount = items.filter(item => item.isChecked).length;
  const isExpanded = expandedCategories[categoryKey] || false;

  return (
    <View style={styles.categoryWrapper}>
      <Button
        variant="ghost"
        style={styles.categoryHeader}
        onPress={() => toggleCategory(categoryKey)}
        activeOpacity={0.7}
      >
        <View style={styles.categoryTitleContainer}>
          {/* <SvgXml xml={config.icon(config.color)} width={20} height={20} /> */}
          <Text style={[styles.categoryLabel, { color: config.color }]}>
            {t(config.label)}
          </Text>
          <View style={[styles.countBadge]}>
            <Text style={[styles.countText, { color: config.color }]}>
              ({items.length})
            </Text>
          </View>
        </View>
        <SvgXml
          xml={IconArrowRight(colors.black)}
          width={16}
          height={16}
          style={isExpanded ? styles.iconRotated : {}}
        />
      </Button>

      {isExpanded && (
        <View style={styles.categoryContent}>
          {items.map(item => (
            <ChecklistItemContent key={item.id} item={item} />
          ))}
        </View>
      )}
    </View>
  );
};

const styles = StyleSheet.create({
  categoryWrapper: {
    // marginBottom: 8,
  },
  categoryHeader: {
    flexDirection: 'row',
    alignItems: 'center',
    justifyContent: 'space-between',
    // padding: 12,
    paddingVertical: 0,
    paddingHorizontal: 8,
    borderRadius: 8,
  },
  categoryTitleContainer: {
    flexDirection: 'row',
    alignItems: 'center',
    gap: 4,
  },
  categoryLabel: {
    fontSize: 14,
    fontWeight: '700',
  },
  countBadge: {
    // paddingHorizontal: 8,
    paddingVertical: 2,
    borderRadius: 10,
  },
  countText: {
    fontSize: 14,
    fontWeight: '700',
  },
  iconRotated: {
    transform: [{ rotate: '90deg' }],
  },
  categoryContent: {
    paddingHorizontal: 8,
    paddingBottom: 8,
  },
});

export default CategorySection;
