import React from 'react';
import { View } from 'react-native';
import { Text, Button } from 'tradewize-component';
import { styles } from '../styles';
import { useAppTranslation } from '../../../../../hooks/useAppTranslation';
import {
  HeaderData,
  JobStartTabStatus,
} from '../../../../../types/myJobs.types';

interface HeaderProps {
  headerData: HeaderData;
  isWarrantyJob: boolean;
  jumpTo?: JobStartTabStatus;
}

export default function Header({
  headerData,
  isWarrantyJob,
  jumpTo,
}: HeaderProps) {
  const { t } = useAppTranslation();

  const lotText = headerData.communityCode
    ? `${headerData.communityCode}/${headerData.lotNumber || ''}`
    : headerData.lotNumber || '';

  const companyText = headerData.companyName
    ? `${headerData.companyName}${
        headerData.companyRoleName ? ` (${headerData.companyRoleName})` : ''
      }`
    : headerData.companyRoleName || '';

  return (
    <View style={styles.headerContainer}>
      <Text style={styles.headerLeft} numberOfLines={1}>
        {isWarrantyJob && (
          <Text style={styles.warrantyText}>{t('WARRANTY')} - </Text>
        )}
        {lotText}
        {companyText && !isWarrantyJob ? ` - ${companyText}` : ''}
      </Text>
      {jumpTo === JobStartTabStatus.COMPLETED && (
        <Button variant="ghost" style={styles.headerRight} onPress={() => {}}>
          <Text style={styles.underLine}>{t('RATING')}</Text>
        </Button>
      )}
    </View>
  );
}
