import React from 'react';
import { View, StyleSheet } from 'react-native';
import { SvgXml } from 'react-native-svg'; 
import { FileInfo, FileThumbnailSize } from '../../types';
import { getIconSize } from '../../styles';
import { colors } from '../../../../theme';
import { IconCsv, IconExcel, IconPdf, IconWord } from '../../../../assets/svg';

interface DocumentThumbnailProps {
  file: FileInfo;
  size: FileThumbnailSize;
}

const DocumentThumbnail: React.FC<DocumentThumbnailProps> = ({ file, size }) => {
  const iconSize = getIconSize(size);
  const icon = getDocumentIcon(file.uri);

  return (
    <View style={styles.pdfContainer}>
      <SvgXml 
        xml={icon()} 
        width={iconSize * 2} 
        height={iconSize * 2} 
      />
    </View>
  );
};

const getDocumentIcon = (uri?: string) => {
  if (!uri) return IconPdf;
  
  const lowerCaseUri = uri.toLowerCase();
  if (lowerCaseUri.endsWith('.pdf')) return IconPdf;
  if (lowerCaseUri.endsWith('.xlsx') || lowerCaseUri.endsWith('.xls')) return IconExcel;
  if (lowerCaseUri.endsWith('.docx') || lowerCaseUri.endsWith('.doc')) return IconWord;
  if (lowerCaseUri.endsWith('.csv')) return IconCsv;
  return IconPdf;
};

const styles = StyleSheet.create({
  pdfContainer: {
    flex: 1,
    justifyContent: 'center',
    alignItems: 'center',
    backgroundColor: colors.lightGray,
  },
});

export default DocumentThumbnail; 