import { navigationRef } from '../services/NavigationService';
import { logger } from '../utils';
import ImagePicker from 'react-native-image-crop-picker';

export type CompanyAnswer = {
  companyKey: string;
  answer: 'yes' | 'no' | null;
};

const getStore = async () => {
  const redux = await import('auth/redux');
  return redux.store;
};

const getI18n = async () => {
  const i18n = await import('../i18n');
  return i18n.default;
};

export const handleUploadMedia = async ({
  images = [],
  setImages,
  companyKey,
  type = 'picker',
}: {
  images: any[];
  setImages: (images: any) => void;
  companyKey: string;
  type: 'picker' | 'takePhoto';
}) => {
  try {
    const maxFiles =
      images?.find(item => item.companyKey === companyKey)?.images?.length || 0;

    let dataSave: any = null;

    if (type === 'picker') {
      const image = await ImagePicker.openPicker({
        multiple: true,
        maxFiles: 5 - maxFiles,
        mediaType: 'photo',
        forceJpg: true,
        includeBase64: false,
        includeExif: true,
      });
      dataSave = {
        companyKey,
        images: image?.map((item: any) => ({
          id: `${companyKey}_${Date.now()}_${Math.random()
            .toString(36)
            .substr(2, 9)}`,
          uri: item?.path,
          type: item?.mime,
          fileName: item?.filename,
        })),
      };
    } else {
      const image = await ImagePicker.openCamera({
        cropping: false,
        includeExif: true,
        mediaType: 'photo',
        forceJpg: true,
        includeBase64: false,
      });

      dataSave = {
        companyKey,
        images: [
          {
            id: `${companyKey}_${Date.now()}_${Math.random()
              .toString(36)
              .substr(2, 9)}`,
            uri: image?.path,
            type: image?.mime,
            fileName: image?.filename,
          },
        ],
      };
    }
    if (dataSave) {
      setImages((prevImages: any) => {
        const existingData = prevImages.find(
          (item: any) => item.companyKey === companyKey,
        );
        if (existingData) {
          return prevImages.map((item: any) =>
            item.companyKey === companyKey
              ? { ...item, images: [...item.images, ...dataSave.images] }
              : item,
          );
        }
        return [...prevImages, dataSave];
      });
    }
  } catch (error) {
    logger('error handleUploadMedia', error);
  }
};

export const handleDeleteImage = ({
  companyKey,
  imageId,
  setImages,
}: {
  companyKey: string;
  imageId: string;
  setImages: (images: any) => void;
}) => {
  setImages((prevImages: any) => {
    return prevImages
      .map((item: any) => {
        if (item.companyKey === companyKey) {
          const updatedImages = item.images.filter(
            (img: any) => img.id !== imageId,
          );

          // If no images left, remove the entire company entry
          if (updatedImages.length === 0) {
            return null;
          }

          return {
            ...item,
            images: updatedImages,
          };
        }
        return item;
      })
      .filter(Boolean); // Remove null entries
  });
};

export const validateTestData = async (
  selectedCompanyIds: string[],
  companyAnswers: CompanyAnswer[],
  imagesCompanyAnswers: any[],
): Promise<boolean> => {
  try {
    const store = await getStore();
    const redux = await import('auth/redux');
    const { clearErrorQuestion, setErrorQuestion } = redux.actionContainer;
    const { showAlertAsync, hideAlertAsync } = redux.actionContainer;

    const onboardingQuestionData =
      store.getState().register.getAllOnboardingQuestion?.data;
    const questionAnswers = store.getState().register.questionAnswers;

    // Validate questions
    const mergedArrayQuestion = onboardingQuestionData
      ?.filter((group: any) => selectedCompanyIds.includes(group.id))
      ?.map((group: any) => ({
        ...group,
        data: group?.data?.map((q: any) => {
          const foundQuestion = questionAnswers.find(
            (ans: any) => ans.questionId === q.id,
          );
          return {
            ...q,
            options: q.options.map((opt: any) => {
              const found = questionAnswers.find(
                (ans: any) =>
                  ans.questionId === q.id && ans.optionId === opt.optionId,
              );
              return {
                ...opt,
                isChecked: found ? found.isChecked : false,
              };
            }),
            images: foundQuestion?.images || q.images || [],
          };
        }),
      }));

    const uncheckedQuestionIds = mergedArrayQuestion?.flatMap((item: any) =>
      item?.data
        ?.filter((q: any) => q.options.every((opt: any) => !opt.isChecked))
        ?.map((q: any) => q.id),
    );

    const invalidImageItems = mergedArrayQuestion?.flatMap((group: any) =>
      group.data
        ?.filter((q: any) => {
          const selectedOpt = q.options.find((opt: any) => opt.isChecked);
          return selectedOpt?.requiredImage && q.images?.length === 0;
        })
        ?.map((q: any) => q.id),
    );

    // Combine all question errors
    const allQuestionErrors = [
      ...(uncheckedQuestionIds || []),
      ...(invalidImageItems || []),
    ];

    // Validate necessary tools
    const companyAndRoleData =
      store.getState().registerCompany?.data ||
      store.getState().auth?.registerCompany?.data ||
      [];

    const mergedArray = selectedCompanyIds.map((companyId: string) => {
      const company = companyAndRoleData.find(
        (item: any) => item?.companyId === companyId,
      );
      const companyAnswer = companyAnswers.find(
        (item: any) => item.companyKey === companyId,
      );
      const companyNecessaryToolImages = imagesCompanyAnswers.find(
        (item: any) => item.companyKey === companyId,
      );

      return {
        companyKey: companyId,
        answer: companyAnswer?.answer || 'no',
        images: companyNecessaryToolImages?.images || [],
        companyName: company?.companyName || '',
      };
    });

    const checkRequiredImages = mergedArray
      .filter((item: any) => item.answer === 'yes' && item.images.length === 0)
      ?.map((item: any) => item.companyName);

    const checkRequiredImagesIds = mergedArray
      .filter((item: any) => item.answer === 'yes' && item.images.length === 0)
      ?.map((item: any) => item.companyKey);

    // Combine all errors - prioritize necessary tool errors first, then question errors
    // This ensures scroll will go to necessary tool errors first if both exist
    const allErrors = [...(checkRequiredImagesIds || []), ...allQuestionErrors];

    if (allErrors.length > 0) {
      store.dispatch(setErrorQuestion(allErrors));

      const i18n = await getI18n();
      const errorMessages: string[] = [];

      // Collect all error messages
      if (checkRequiredImages?.length > 0) {
        const message =
          i18n?.t('YOU_MUST_UPLOAD_IMAGE_NECESSARY_TOOL_FOR_COMPANY', {
            companyName: checkRequiredImages.join(', '),
          }) ||
          `You must upload necessary tool image for\n'${checkRequiredImages.join(
            ', ',
          )}'`;
        errorMessages.push(message);
      }

      if (uncheckedQuestionIds?.length > 0) {
        const message =
          i18n?.t('YOU_MUST_ANSWER_ALL_THE_QUESTIONS') ||
          'You must answer all the questions';
        errorMessages.push(message);
      }

      if (invalidImageItems?.length > 0) {
        const message =
          i18n?.t('HAVE_QUESTION_TO_ADD_IMAGE') ||
          'Have question to add image.';
        errorMessages.push(message);
      }

      // Combine all error messages with newline separator
      const errorMessage = errorMessages.join('\n');

      store.dispatch(
        showAlertAsync({
          visible: true,
          title: i18n?.t('WARNING') || 'Warning',
          message: errorMessage,
          type: 'warning',
          buttonText: i18n?.t('CLOSE') || 'Close',
          onClose: () => {
            store.dispatch(hideAlertAsync());
          },
        }),
      );
      return false;
    }

    store.dispatch(clearErrorQuestion());
    return true;
  } catch (error) {
    logger('error validate test data', error);
    return false;
  }
};

export const handleSubmitTest = async (
  selectedCompanyIds: string[],
  companyAnswers: CompanyAnswer[],
  imagesCompanyAnswers: any[],
  cleanData?: () => void,
) => {
  try {
    const store = await getStore();
    const redux = await import('auth/redux');
    const { saveYourTestAsync } = redux.actionContainer;

    const formData = new FormData();
    const accountId = store.getState().auth.userProfile?.accountId;
    formData.append('AccountId', accountId);
    formData.append('IsCrewLead', false);

    const onboardingQuestionData =
      store.getState().register.getAllOnboardingQuestion?.data;
    const questionAnswers = store.getState().register.questionAnswers;

    // Get necessary tool data
    const companyAndRoleDataForNecessary =
      store.getState().registerCompany?.data ||
      store.getState().auth?.registerCompany?.data ||
      [];

    const necessaryToolData = selectedCompanyIds
      .map((companyId: string) => {
        const company = companyAndRoleDataForNecessary.find(
          (item: any) => item?.companyId === companyId,
        );
        const companyAnswer = companyAnswers.find(
          (item: any) => item.companyKey === companyId,
        );
        const companyNecessaryToolImages = imagesCompanyAnswers.find(
          (item: any) => item.companyKey === companyId,
        );

        return {
          companyKey: companyId,
          answer: companyAnswer?.answer || 'no',
          images: companyNecessaryToolImages?.images || [],
          companyName: company?.companyName || '',
        };
      })
      .filter((item: any) => item.answer !== null);

    // Append NecessaryTool data to FormData
    necessaryToolData.forEach((element: any, index: number) => {
      formData.append(`NecessaryTool[${index}].companyId`, element.companyKey);
      formData.append(`NecessaryTool[${index}].question`, element.companyName);
      formData.append(
        `NecessaryTool[${index}].necessary`,
        element.answer === 'yes' ? 'true' : 'false',
      );

      element.images.forEach((img: any, imgIndex: number) => {
        formData.append(`NecessaryTool[${index}].images[${imgIndex}]`, {
          uri: img.uri,
          type: img.type,
          name: img.fileName,
        });
      });
    });

    const mergedArrayQuestion = onboardingQuestionData
      ?.filter((group: any) => selectedCompanyIds.includes(group.id))
      ?.map((group: any) => ({
        ...group,
        data: group?.data?.map((q: any) => {
          const foundQuestion = questionAnswers.find(
            (ans: any) => ans.questionId === q.id,
          );
          return {
            ...q,
            options: q.options.map((opt: any) => {
              const found = questionAnswers.find(
                (ans: any) =>
                  ans.questionId === q.id && ans.optionId === opt.optionId,
              );
              return {
                ...opt,
                isChecked: found ? found.isChecked : false,
              };
            }),
            images: foundQuestion?.images || q.images || [],
          };
        }),
      }));

    const companyAndRoleData =
      store.getState().registerCompany?.data ||
      store.getState().auth?.registerCompany?.data ||
      [];

    mergedArrayQuestion?.forEach((element: any, index: number) => {
      const companyName =
        companyAndRoleData?.find((item: any) => item?.companyId === element.id)
          ?.companyName || '';

      formData.append(`Tests[${index}].CompanyId`, element.id);
      formData.append(`Tests[${index}].CompanyName`, companyName);
      element.data.forEach((q: any, qIndex: number) => {
        formData.append(
          `Tests[${index}].Questions[${qIndex}].QuestionId`,
          q.id,
        );
        formData.append(
          `Tests[${index}].Questions[${qIndex}].QuestionName`,
          q.question,
        );
        if (q.isRequired) {
          formData.append(
            `Tests[${index}].Questions[${qIndex}].IsRequired`,
            q.isRequired.toString(),
          );
        }
        q.options.forEach((opt: any, optIndex: number) => {
          formData.append(
            `Tests[${index}].Questions[${qIndex}].ListAnswers[${optIndex}].key`,
            opt.optionId,
          );
          formData.append(
            `Tests[${index}].Questions[${qIndex}].ListAnswers[${optIndex}].Value`,
            opt.option,
          );
        });
        q.options.forEach((opt: any, optIndex: number) => {
          if (opt.isChecked) {
            formData.append(
              `Tests[${index}].Questions[${qIndex}].Answers[${optIndex}].key`,
              opt.optionId,
            );
            formData.append(
              `Tests[${index}].Questions[${qIndex}].Answers[${optIndex}].Value`,
              opt.option,
            );
          }
        });

        if (q.images?.length > 0) {
          q.images.forEach((img: any, imgIndex: number) => {
            formData.append(
              `Tests[${index}].Questions[${qIndex}].Files[${imgIndex}]`,
              {
                uri: img.path,
                type: img.typeFile,
                name: img.fileName,
              },
            );
          });
        }
      });

      store.dispatch(
        saveYourTestAsync({
          formData,
          isRegisterCompany: true,
          navigationRef,
        }),
      );
      cleanData?.();
    });
  } catch (error) {
    logger('error submit test', error);
  }
};
