import { colors } from '../theme';
import { JobDetailType, workOrderStatus } from '../types/jobDetail.type';
import { formatCurrency } from '../utils/curencyUtil';

export const getStatusInfo = (
  t: any,
  status?: workOrderStatus,
): { text: string; color: string } => {
  switch (status) {
    case workOrderStatus.Pending:
      return { text: t('PENDING'), color: colors.alertColorWarning };
    case workOrderStatus.InProgress:
      return { text: t('IN_PROGRESS'), color: colors.mainColorPrimary };
    case workOrderStatus.Completed:
      return { text: t('COMPLETED'), color: colors.alertColorSuccess };
    default:
      return { text: '', color: colors.grayScale70 };
  }
};

export const formatAmount = (
  t: any,
  amount?: number,
  bonusAmount?: number,
): string => {
  if (!amount) return '';
  const formattedAmount = formatCurrency(amount);
  if (bonusAmount && bonusAmount > 0) {
    return `${formattedAmount} (${t('INCLUDE_BONUS')} ${formatCurrency(
      bonusAmount,
    )})`;
  }
  return formattedAmount;
};

export const mapJobDetail = (item: JobDetailType): Partial<JobDetailType> => {
  return {
    // =========================================================================
    // 1. IDENTIFICATION & STATUS
    // =========================================================================
    id: item.id,
    status: item.status,
    type: item.type,
    workOrderStatus: item.workOrderStatus,

    // =========================================================================
    // 2. JOB DETAILS
    // =========================================================================
    jobCategory: item.jobCategory,
    activityTypeName: item.activityTypeName,
    activityTypeEnum: item.activityTypeEnum,
    activityStatus: item.activityStatus,
    activityActualStart: item.activityActualStart,
    comments: item.comments,
    completedBy: item.completedBy,
    readyFormStatus: item.readyFormStatus,
    needVerifyMaterial: item.needVerifyMaterial,
    needVerifyVpo: item.needVerifyVpo,

    // =========================================================================
    // 3. FLAGS & INDICATORS
    // =========================================================================
    isReadyToStart: item.isReadyToStart,
    isShowMaterialLeft: item.isShowMaterialLeft,

    // =========================================================================
    // 4. ACTIVITY & WORK ORDER IDs
    // =========================================================================
    woActivityId: item.woActivityId,
    scheduleActivityId: item.scheduleActivityId,
    parentWorkOrderId: item.parentWorkOrderId,
    qIId: item.qIId,
    qIStatus: item.qIStatus,
    qIStatusName: item.qIStatusName,

    // =========================================================================
    // 5. SCHEDULE & DATES
    // =========================================================================
    estStartDate: item.estStartDate,
    estEndDate: item.estEndDate,
    actualStartDate: item.actualStartDate,
    actualEndDate: item.actualEndDate,
    wOEstStartDate: item.wOEstStartDate,
    wOEstEndDate: item.wOEstEndDate,
    duration: item.duration,

    // =========================================================================
    // 6. AMOUNT & PAYMENT
    // =========================================================================
    amount: item.amount,
    bonusAmount: item.bonusAmount,
    totalAmount: item.totalAmount,
    totalIncomplete: item.totalIncomplete,

    // =========================================================================
    // 7. PARTICIPANT & TOOLS
    // =========================================================================
    totalParticipant: item.totalParticipant,
    totalTools: item.totalTools,

    // =========================================================================
    // 8. DELIVERY
    // =========================================================================
    statusPODelivery: item.statusPODelivery,
    deliveryStatus: item.deliveryStatus,
    deliveryStatusName: item.deliveryStatusName,
    driverId: item.driverId,

    // =========================================================================
    // 9. WORKER INFO
    // =========================================================================
    workerId: item.workerId,
    workerInfo: item.workerInfo,

    // =========================================================================
    // 10. COMPANY INFO
    // =========================================================================
    companyId: item.companyId,
    companyName: item.companyName,
    companyRoleId: item.companyRoleId,

    // =========================================================================
    // 11. LOCATION - houseInfo
    // =========================================================================
    houseInfo: item.houseInfo,

    // =========================================================================
    // 12. COMPANY ROLE INFO
    // =========================================================================
    companyRoleInfo: item.companyRoleInfo,

    // =========================================================================
    // 13. ACTIVITY TYPE INFO
    // =========================================================================
    activityTypeInfo: item.activityTypeInfo,

    // =========================================================================
    // 14. CHECKLISTS
    // =========================================================================
    checklists: item.checklists,

    // =========================================================================
    // 15. ROLE TOOLS
    // =========================================================================
    roleTools: item.roleTools,

    // =========================================================================
    // 16. MATERIAL CONFIG
    // =========================================================================
    materialConfigResponse: item.materialConfigResponse,

    // =========================================================================
    // 17. CHECKLIST PERMISSION
    // =========================================================================
    canUpdateChecklist: item.canUpdateChecklist,

    taskInfomationGroup: {
      activityTypeName: item.activityTypeName,
      taskInfomationName: `${item.workerInfo?.fullName || ''} - ${
        item.companyRoleInfo?.name || item.companyName || ''
      }`,
      estStartDate: item.estStartDate,
      actualStartDate: item.actualStartDate || '',
      wOEstStartDate: item.wOEstStartDate,
      estCompletionDate: item.estEndDate,
      street: item.houseInfo?.street || '',
      city: item.houseInfo?.city || '',
      amount: item.amount,
      bonusAmount: item.bonusAmount,
      status: item.status,
      communityCode: item.houseInfo?.communityCode || '',
      lotNumber: item.houseInfo?.lotNumber || '',
      houseNumber: item.houseInfo?.houseNumber || '',
      duration: item.duration,
    },

    buttonActionGroup: {
      id: item.id,
      scheduleActivityId: item.scheduleActivityId,

      roleType: item.companyRoleInfo?.roleType,
      activityActualStart: item.activityActualStart || '',
      readyFormStatus: item.readyFormStatus,
      needVerifyMaterial: item.needVerifyMaterial,
      needVerifyVpo: item.needVerifyVpo,
      isShowMaterialLeft: item.isShowMaterialLeft,
    },

    optionDataGroup: {
      id: item.id,
      scheduleActivityId: item.scheduleActivityId,
      totalParticipant: item.totalParticipant,
      totalTools: item.totalTools,
    },

    checklistsGroup: {
      qIStatus: item.qIStatus,
      activityTypeEnum: item.activityTypeEnum,
      canUpdate: item.canUpdateChecklist.canUpdate,
      reason: item.canUpdateChecklist.reason,
    },
  };
};

// Helper to map array of items
export const mapToJobItems = (
  items: JobDetailType[],
): Partial<JobDetailType>[] => {
  return items.map(mapJobDetail);
};
