import React, { useCallback, useEffect, useState } from 'react';
import { View, ScrollView, TouchableOpacity } from 'react-native';
import { Text } from 'tradewize-component';
import { SvgXml } from 'react-native-svg';
import { useSelector } from 'react-redux';
import { colors } from '../../../theme';
import { styles } from '../styles';
import { IconChevronDown, IconChevronUp, IconInfo } from '../../../assets/svg';
import { useAppTranslation } from '../../../hooks/useAppTranslation';
import { getMyScore } from '../../../api/endpoint';

interface KPIScore {
  kpiTypeName: string;
  score: number;
}

interface KPIScoreItem {
  score: number;
  kpiType: number;
  kpiTypeName: string;
  scoreWeight: number;
  target: number;
  minTarget: number;
  totalItems: number;
  completedItems: number;
}

interface MyScoreProps {
  selectedMonth?: Date | null;
  selectedCompanyId?: string | null;
}

const MyScore: React.FC<MyScoreProps> = ({
  selectedMonth,
  selectedCompanyId,
}) => {
  const { t } = useAppTranslation();
  const user = useSelector((state: any) => state.auth.userProfile);
  const [totalScore, setTotalScore] = useState<number>(0);
  const [kpiScores, setKpiScores] = useState<KPIScore[]>([]);
  const [isLoading, setIsLoading] = useState(false);
  const [isExpanded, setIsExpanded] = useState(false);

  const fetchMyScore = useCallback(async () => {
    const monthToUse = selectedMonth || new Date();
    const year = monthToUse.getFullYear();
    const monthNumber = monthToUse.getMonth() + 1;
    const companyId = selectedCompanyId || user?.companyRoles?.[0]?.companyId;

    if (!companyId) {
      setTotalScore(0);
      setKpiScores([]);
      return;
    }

    setIsLoading(true);
    try {
      const client = await import('auth/api');
      const response = await client.getRequest(
        `${getMyScore}?Year=${year}&Month=${monthNumber}&CompanyId=${companyId}`,
      );

      let dataArray: KPIScoreItem[] = [];
      if (Array.isArray(response?.data)) {
        dataArray = response.data as KPIScoreItem[];
      } else if (response?.data?.data && Array.isArray(response.data.data)) {
        dataArray = response.data.data as KPIScoreItem[];
      }

      const total = dataArray.reduce((sum, item) => sum + (item.score || 0), 0);
      const kpiScoresData: KPIScore[] = dataArray.map((item: KPIScoreItem) => ({
        kpiTypeName: item?.kpiTypeName,
        score: item?.score,
      }));

      setTotalScore(total);
      setKpiScores(kpiScoresData);
    } catch (error) {
      console.error('Error fetching my score:', error);
      setTotalScore(0);
      setKpiScores([]);
    } finally {
      setIsLoading(false);
    }
  }, [selectedMonth, selectedCompanyId, user]);

  useEffect(() => {
    fetchMyScore();
  }, [fetchMyScore]);

  const formatScore = (score: number) => {
    return score >= 0 ? `${score.toFixed(2)}` : `${score.toFixed(2)}`;
  };

  return (
    <View style={styles.myScoreContainer}>
      <View style={styles.myScoreHeaderRow}>
        <Text style={styles.myScoreTitle}>{t('Your Score')}</Text>
        <View style={styles.myScoreBadgeContainer}>
          <View style={styles.myScoreTotalScoreBadge}>
            <Text style={styles.myScoreTotalScoreText}>
              {totalScore.toFixed(2)} pts
            </Text>
          </View>
          <TouchableOpacity
            onPress={() => setIsExpanded(!isExpanded)}
            activeOpacity={0.7}
            style={styles.myScoreExpandButton}
          >
            <SvgXml
              xml={
                isExpanded
                  ? IconChevronUp(colors.grayScale100)
                  : IconChevronDown(colors.grayScale100)
              }
              width={24}
              height={24}
            />
          </TouchableOpacity>
        </View>
      </View>

      {isExpanded && (
        <View style={styles.myScoreTableContainer}>
          <View style={styles.myScoreTableHeader}>
            <View style={styles.myScoreTableHeaderLeft}>
              <Text style={styles.myScoreTableHeaderText}>
                {t('KPI Type Name')}
              </Text>
              <SvgXml xml={IconInfo(colors.white)} width={18} height={18} />
            </View>
            <Text style={styles.myScoreTableHeaderText}>{t('Score')}</Text>
          </View>

          {isLoading ? (
            <View style={[styles.myScoreTableRow, { borderBottomWidth: 0 }]}>
              <Text>Loading...</Text>
            </View>
          ) : (
            <ScrollView
              style={styles.myScoreTableScrollView}
              nestedScrollEnabled={true}
              showsVerticalScrollIndicator={false}
            >
              {kpiScores.length > 0 ? (
                kpiScores.map((item, index) => (
                  <View
                    key={index}
                    style={[
                      styles.myScoreTableRow,
                      index === kpiScores.length - 1 && {
                        borderBottomWidth: 0,
                      },
                    ]}
                  >
                    <Text style={styles.myScoreKpiName}>
                      {item.kpiTypeName}
                    </Text>
                    <Text
                      style={[
                        styles.myScoreScoreValue,
                        item.score < 0 && styles.myScoreNegativeScore,
                      ]}
                    >
                      {formatScore(item.score)}
                    </Text>
                  </View>
                ))
              ) : (
                <View
                  style={[styles.myScoreTableRow, { borderBottomWidth: 0 }]}
                >
                  <Text style={styles.myScoreKpiName}>No data available</Text>
                </View>
              )}
            </ScrollView>
          )}
        </View>
      )}
    </View>
  );
};

export default MyScore;
