import React from 'react';
import { View, FlatList, Text, StyleSheet } from 'react-native';
import { BaseContainer, HeaderBase } from 'tradewize-component';
import { useSelector } from 'react-redux';
import { RootState } from '../../../../Auth/src/redux/store/types';
import { colors } from '../../theme';
import { useAppTranslation } from '../../hooks/useAppTranslation';
import { MaterialItem } from '../../types/jobDetail.type';
import { NavigationService } from '../../services/NavigationService';

const MaterialListScreen = () => {
  const { t } = useAppTranslation();

  const isLoading = useSelector(
    (state: RootState) => state.app.loadingOverlay.isVisible,
  );

  const { materialList } = useSelector((state: RootState) => state.jobDetail);

  const renderItem = ({ item }: { item: MaterialItem }) => (
    <View style={styles.itemContainer}>
      <View style={styles.leftColumn}>
        <Text style={styles.itemName} numberOfLines={2} ellipsizeMode="tail">
          {item.name}
        </Text>
        <Text style={styles.itemInfo}>
          {t('SKU')}: {item.code}
        </Text>
        <Text style={styles.itemInfo}>
          {t('SIZE')}: {item.size}
        </Text>
        <Text style={styles.itemInfo}>
          {t('COLOR')}: {item.colorName}
        </Text>
        <Text style={styles.itemInfo}>
          {t('U/M')}: {item.unitTypeName}
        </Text>
      </View>
      <View style={styles.rightColumn}>
        <Text style={styles.itemQuantity}>
          {item.unit}
          {'\n'}
          {item.unitTypeCode}
        </Text>
      </View>
    </View>
  );

  const renderEmptyComponent = () => (
    <View style={styles.emptyContainer}>
      <Text style={styles.emptyText}>{t('NO_DATA')}</Text>
    </View>
  );

  return (
    <BaseContainer>
      <HeaderBase
        title={t('MATERIAL_LIST')}
        onPressBack={() => {
          NavigationService.goBack();
        }}
        backgroundColor={colors.white}
      />
      {!isLoading && (
        <View style={styles.content}>
          <View style={styles.listHeader}>
            <Text style={styles.headerName}>{t('NAME')}</Text>
            <Text style={styles.headerQuantity}>{t('QUANTITY')}</Text>
          </View>
          <View style={styles.listContentWrapper}>
            <FlatList
              data={materialList || []}
              renderItem={renderItem}
              keyExtractor={item => `material-${item.materialId}`}
              ListEmptyComponent={renderEmptyComponent}
              contentContainerStyle={styles.listContentItems}
              showsVerticalScrollIndicator={false}
              ItemSeparatorComponent={() => null}
              removeClippedSubviews={false}
            />
          </View>
        </View>
      )}
    </BaseContainer>
  );
};

const styles = StyleSheet.create({
  container: {
    flex: 1,
    backgroundColor: colors.white,
  },
  content: {
    flex: 1,
    marginHorizontal: 8,
    borderWidth: 1,
    borderRadius: 8,
    borderColor: colors.grayScale30,
  },
  listHeader: {
    flexDirection: 'row',
    justifyContent: 'space-between',
    paddingVertical: 12,
    paddingHorizontal: 16,
    borderBottomWidth: 1,
    borderBottomColor: colors.grayScale30,
    marginBottom: 8,
    backgroundColor: colors.darkBlue,
    borderTopLeftRadius: 8,
    borderTopRightRadius: 8,
  },
  headerName: {
    fontSize: 14,
    fontWeight: '600',
    color: colors.white,
  },
  headerQuantity: {
    fontSize: 14,
    fontWeight: '600',
    color: colors.white,
  },
  listContentWrapper: {
    flex: 1,
  },
  listContentItems: {
    paddingBottom: 16,
  },
  itemContainer: {
    flexDirection: 'row',

    paddingVertical: 12,
    borderBottomWidth: 1,
    borderBottomColor: colors.grayScale30,
    flex: 1,
  },
  leftColumn: {
    flex: 4,
    paddingHorizontal: 8,
  },
  itemName: {
    fontSize: 16,
    fontWeight: '600',
    color: colors.black,
    marginBottom: 4,
  },
  itemInfo: {
    fontSize: 12,
    color: colors.grayScale70,
    marginBottom: 2,
  },
  rightColumn: {
    width: '25%',
    justifyContent: 'center',
    alignItems: 'center',
    borderLeftWidth: 1,
    borderLeftColor: colors.grayScale30,
  },
  itemQuantity: {
    fontSize: 18,
    fontWeight: '700',
    color: colors.mainColorPrimary,
    textAlign: 'center',
  },
  emptyContainer: {
    flex: 1,
    justifyContent: 'center',
    alignItems: 'center',
    paddingVertical: 40,
  },
  emptyText: {
    fontSize: 14,
    color: colors.grayScale70,
  },
});

export default MaterialListScreen;
