import React, { useMemo } from 'react';
import { View } from 'react-native';
import { Button, Text } from 'tradewize-component';
import { useAppTranslation } from '../../../../hooks/useAppTranslation.ts';

import FastImage, { Source } from 'react-native-fast-image';
import { styles } from './styles.ts';
import { useSelector } from 'react-redux';
import { UserProfile } from '../../../../types/profile.ts';
import colors from '../../../../theme/colors.ts';

type CardItemProps = {
  item: {
    icon: number | Source | undefined;
    title?: string;
    backgroundColor?: string;
    onPress?: () => void;
    isShow?: boolean;
    key?: string;
  };
};

const exception_keys = ['CONTRACTS', 'SYSTEM_QUALITY_GUIDE'];

const CardItem = (props: CardItemProps) => {
  const { t } = useAppTranslation();
  const { item } = props;
  const userProfile: UserProfile = useSelector(
    (state: any) => state.auth.userProfile,
  );

  const isDisabled = useMemo(() => {
    if (exception_keys.includes(item?.key || '')) {
      if (item?.key === 'CONTRACTS') {
        const hasContractsReady =
          userProfile?.hasContractsToSign && userProfile?.isFinishedTraining;
        const needsTraining =
          userProfile?.needWatchVideoNow || userProfile?.isDueTraining;

        return hasContractsReady || needsTraining;
      }
      return false;
    }

    if (
      userProfile?.hasContractsToSign ||
      !userProfile?.isFinishedTraining ||
      userProfile?.needWatchVideoNow
    ) {
      return true;
    }
    return false;
  }, [
    item?.key,
    userProfile?.hasContractsToSign,
    userProfile?.isFinishedTraining,
    userProfile?.needWatchVideoNow,
    userProfile?.isDueTraining,
  ]);

  return (
    <Button
      // disabled={isDisabled}
      variant="ghost"
      onPress={item?.onPress}
      style={[styles.container, !item?.isShow && styles.disabled]}
    >
      <View style={[styles.itemContent, isDisabled && styles.disabledContent]}>
        {item?.icon && (
          <FastImage
            tintColor={isDisabled ? colors.gray : colors.mainColorPrimary}
            source={item.icon}
            style={styles.icon}
          />
        )}
      </View>
      <Text
        numberOfLines={2}
        style={[styles.title, isDisabled && styles.disabledTitle]}
      >
        {t(item?.title || '')}
      </Text>
    </Button>
  );
};

export default CardItem;
