import { Image, View } from 'react-native';
import { JobItem } from '../ListJobs/types';
import { styles } from './styles';
import {
  Button,
  CheckboxButton,
  SCREEN_WIDTH,
  Text,
} from 'tradewize-component';
import { useAppTranslation } from '../../../../hooks/useAppTranslation';
import { formatCurrency, formatMiles, logger } from '../../../../utils';
import {
  defaultJob,
  iconFav,
  iconUnFav,
  rating,
} from '../../../../assets/images';
import { IconClock, IconLocation, IconUserRef } from '../../../../assets/svg';
import { SvgXml } from 'react-native-svg';
import dayjs from 'dayjs';
import { FORMAT_TIME_CLIENT_JOB_START } from '../../../../types/global';
import { colors } from '../../../../theme';
import { useDispatch } from 'react-redux';

interface JobItemCardProps {
  item: JobItem;
  isFirst?: boolean;
}

const JobItemCard = ({ item, isFirst }: JobItemCardProps) => {
  const { t } = useAppTranslation();

  const dispatch = useDispatch();

  const isShowBonus = item?.bonusAmount != null && item?.bonusAmount > 0;

  const handleToggleCheckbox = async () => {
    try {
      const redux = await import('auth/redux');

      if (item?.isOverlap) {
        return dispatch(
          redux.actionContainer.showAlertAsync({
            title: t('WARNING'),
            message: t('THIS_JOB_OVERLAPS_WITH_ANOTHER_JOB_IN_YOUR_SCHEDULE'),
            titleStyle: {
              color: colors.black,
            },
            buttonStyle: {
              backgroundColor: colors.mainColorPrimary,
            },
            type: 'warning',
            isBackdropPress: false,
            buttonText: t('CONFIRM'),
          }),
        );
      }

      if (item?.isDiffDays) {
        return dispatch(
          redux.actionContainer.showAlertAsync({
            title: t('WARNING'),
            buttonText: t('CONFIRM'),
            isBackdropPress: false,
            message: t(
              'CANNOT_REGISTER_FOR_JOBS_STARTING_MORE_THAN_14_DAYS_FROM_NOW',
            ),
            titleStyle: {
              color: colors.black,
            },
            buttonStyle: {
              backgroundColor: colors.mainColorPrimary,
            },
          }),
        );
      }

      if (item?.isDisabled) {
        return dispatch(
          redux.actionContainer.showAlertAsync({
            title: t('WARNING'),
            message: t('CANNOT_REGISTER_MORE_THAN_ONE_JOB_IN_THE_SAME_HOUSE'),
            titleStyle: {
              color: colors.black,
            },
            buttonText: t('CONFIRM'),
            isBackdropPress: false,
            buttonStyle: {
              backgroundColor: colors.mainColorPrimary,
            },
          }),
        );
      }

      dispatch(redux.actionContainer.toggleJobBoardCheckbox(item?.jobId));
    } catch (error) {
      logger('error handleToggleCheckbox', error);
    }
  };

  const handleToggleFav = async () => {
    try {
      const redux = await import('auth/redux');
      dispatch(redux.actionContainer.toggleJobBoardFavAsync(item?.jobId));
    } catch (error) {
      logger('error handleToggleFav', error);
    }
  };

  const getBackgroundColor = () => {
    if (item?.isChecked) {
      return colors.mainColorPrimary;
    }
    if (item?.isOverlap || item?.isDiffDays) {
      return colors.colorE0E0E0;
    }
    return colors.twColors.background.lot;
  };

  const getMarginTop = () => {
    if (isFirst && !item?.isOverlap) {
      return 0;
    }
    return 16;
  };

  return (
    <Button
      variant="ghost"
      onPress={handleToggleCheckbox}
      style={[styles.container, { marginTop: getMarginTop() }]}
      activeOpacity={1}
    >
      {isShowBonus && (
        <View
          style={[
            styles.ratingContainer,
            { backgroundColor: getBackgroundColor() },
          ]}
        >
          <Image source={rating} style={styles.ratingImage} />
          <Text
            style={[
              styles.ratingText,
              item?.isChecked && { color: colors.white },
            ]}
          >
            {t('EMERGENCY')} {t('JOBS')}:
          </Text>

          <Text style={styles.textBonusAmount}>
            {formatCurrency(item?.amount ?? 0)}
            {' + '}
            {formatCurrency(item?.bonusAmount ?? 0)} {t('BONUS')}
          </Text>
        </View>
      )}

      <View
        style={[
          styles.headerContainer,
          !isShowBonus && styles.borderHeaderTopRadius,
          { backgroundColor: getBackgroundColor() },
        ]}
      >
        <CheckboxButton
          isChecked={item?.isChecked}
          onChange={handleToggleCheckbox}
          checkboxStyle={{
            backgroundColor: colors.white,
            borderColor: !item?.isChecked
              ? colors.grayScale70
              : colors.mainColorPrimary,
          }}
          styleCheckmark={{ color: colors.mainColorPrimary }}
          style={styles.checkboxButton}
        />
        <Text
          style={[
            styles.lotNumberText,
            item?.isChecked && { color: colors.white },
          ]}
        >
          {t('LOT_NUMBER')}:{' '}
          <Text
            style={[
              styles.lotNumberValueText,
              item?.isChecked && { color: colors.white },
            ]}
          >
            {item?.lotNumber ?? ''}
          </Text>
        </Text>

        <Text style={styles.totalAmountText}>
          {t('TOTAL')}:{' '}
          <Text style={styles.totalAmountValueText}>
            {formatCurrency(item?.totalAmount ?? 0)}
          </Text>
        </Text>
        {item?.isOverlap && (
          <Text style={styles.overlapScheduleText}>
            {t('OVERLAPPING_SCHEDULE')}
          </Text>
        )}
      </View>

      <View style={styles.titleJobsContainer}>
        <Text style={styles.titleJobsText}>
          {item?.activityTypeName?.trim() || ''} -{' '}
          {item?.companyRoleName?.trim() || ''}
        </Text>

        <Button
          onPress={handleToggleFav}
          style={styles.iconFavContainer}
          variant="ghost"
        >
          <Image
            source={item?.isFavorite ? iconFav : iconUnFav}
            style={styles.iconFav}
          />
        </Button>
      </View>

      <View style={styles.estimatedDateContainer}>
        <SvgXml
          xml={IconClock()}
          width={SCREEN_WIDTH * 0.04}
          height={SCREEN_WIDTH * 0.04}
        />
        <Text style={styles.estimatedDateText}>
          {t('EST_START')}:{' '}
          <Text style={styles.estimatedDateValueText}>
            {dayjs(item?.estStartDate).isValid()
              ? dayjs(item?.estStartDate).format(FORMAT_TIME_CLIENT_JOB_START)
              : ''}
          </Text>
        </Text>

        {item?.wODuration !== 0 && (
          <>
            <SvgXml
              xml={IconClock()}
              width={SCREEN_WIDTH * 0.04}
              height={SCREEN_WIDTH * 0.04}
            />

            <Text style={styles.estimatedDateText}>
              {t('DURATION')}{' '}
              <Text style={styles.estimatedDateValueText}>
                {item?.wODuration ?? 0} {t('DAYS')}
              </Text>
            </Text>
          </>
        )}
      </View>
      <View style={styles.infoJobContainer}>
        <Image source={defaultJob} style={styles.infoJobImage} />
        <View style={styles.inforJobColumnContainer}>
          <View style={styles.infoJobTextContainer}>
            <SvgXml
              xml={IconLocation()}
              width={SCREEN_WIDTH * 0.03}
              height={SCREEN_WIDTH * 0.045}
            />
            <Text numberOfLines={2} style={styles.infoJobText}>
              {item?.houseDistance != null
                ? `${formatMiles(item.houseDistance)} ${t('MILES')} - `
                : ''}
              {item?.street ?? ''}, {item?.city ?? ''}, {item?.state ?? ''}
            </Text>
          </View>
          <View style={styles.infoJobMemberContainer}>
            <SvgXml
              xml={IconUserRef(colors.black)}
              width={SCREEN_WIDTH * 0.03}
              height={SCREEN_WIDTH * 0.045}
            />
            <Button variant="ghost" style={styles.infoJobMemberButton}>
              <Text numberOfLines={1} style={styles.infoJobMemberText}>
                {item?.currentMember ?? 0}/{item?.totalMember ?? 0}{' '}
                {t('MEMBERS')}
              </Text>
            </Button>

            <SvgXml
              xml={IconClock(colors.black)}
              width={SCREEN_WIDTH * 0.03}
              height={SCREEN_WIDTH * 0.045}
              style={styles.infoJobMemberClockIcon}
            />
            <Text
              numberOfLines={1}
              style={[styles.estimatedDateText, styles.infoEstCompletionText]}
            >
              {t('EST_COMPLETION')}:{' '}
              <Text style={styles.estimatedDateValueText}>
                {dayjs(item?.estEndDate).isValid()
                  ? dayjs(item?.estEndDate).format(FORMAT_TIME_CLIENT_JOB_START)
                  : ''}
              </Text>
            </Text>
          </View>
        </View>
      </View>
    </Button>
  );
};

export default JobItemCard;
