import React from 'react';
import { BaseContainer, HeaderBase } from 'tradewize-component';
import { useAppTranslation } from '../../hooks/useAppTranslation';
import { NavigationService } from '../../services/NavigationService';
import Pdf from 'react-native-pdf';
import { StyleSheet } from 'react-native';

const LegalPolicyScreen = () => {
  const { t } = useAppTranslation();
  const pdfPath: string =
    'https://trade-wize-public.s3.us-east-1.amazonaws.com/Trade+Wize+Privacy.pdf?index=0';

  return (
    <BaseContainer>
      <HeaderBase
        title={t('PRIVACY_SECURITY_POLICY')}
        onPressBack={() => NavigationService.goBack()}
      />
      <Pdf trustAllCerts={false} source={{ uri: pdfPath }} style={styles.pdf} />
    </BaseContainer>
  );
};

const styles = StyleSheet.create({
  pdf: {
    flex: 1,
    width: '100%',
    height: '100%',
  },
});

export default LegalPolicyScreen;
