import React from 'react';
import { View, StyleSheet, Dimensions } from 'react-native';
import FastImage from 'react-native-fast-image';
import ReactNativeModal from 'react-native-modal';
import { colors } from '../../theme';
import { IconClose } from '../../assets/svg';
import { SvgXml } from 'react-native-svg';
import { logoExtra } from '../../assets/images';
import { Button } from 'tradewize-component';

const { width: SCREEN_WIDTH, height: SCREEN_HEIGHT } = Dimensions.get('window');

interface ImageModalProps {
  visible: boolean;
  imageUrl: string | null;
  onClose: () => void;
}

export const ImageModal = ({ visible, imageUrl, onClose }: ImageModalProps) => {
  if (!imageUrl) return null;

  return (
    <ReactNativeModal
      isVisible={visible}
      onBackdropPress={onClose}
      onBackButtonPress={onClose}
      style={styles.modal}
      animationIn="fadeIn"
      animationOut="fadeOut"
      useNativeDriver
    >
      <View style={styles.container}>
        <Button
          style={styles.closeButton}
          onPress={onClose}
          activeOpacity={0.7}
        >
          <SvgXml xml={IconClose(colors.white)} width={24} height={24} />
        </Button>
        <FastImage
          source={imageUrl ? { uri: imageUrl } : logoExtra}
          style={styles.image}
          resizeMode="contain"
        />
      </View>
    </ReactNativeModal>
  );
};

const styles = StyleSheet.create({
  modal: {
    margin: 0,
    justifyContent: 'center',
    alignItems: 'center',
  },
  container: {
    width: SCREEN_WIDTH,
    height: SCREEN_HEIGHT,
    backgroundColor: 'rgba(0, 0, 0, 0.9)',
    justifyContent: 'center',
    alignItems: 'center',
  },
  closeButton: {
    position: 'absolute',
    top: 50,
    right: 20,
    zIndex: 10,
    width: 40,
    height: 40,
    borderRadius: 20,
    backgroundColor: 'rgba(0, 0, 0, 0.5)',
    justifyContent: 'center',
    alignItems: 'center',
  },
  image: {
    width: SCREEN_WIDTH,
    height: SCREEN_HEIGHT,
  },
});
