import { IMessage, normalizeFileUri } from 'tradewize-component';
import { logger, videoExtensions } from '../utils';
import i18n from '../i18n';
import mime from 'mime';
import { imageExtensions } from '../types/global';
import {
  getImageMetaData,
  getVideoMetaData,
  Image as ImageCompressor,
  uuidv4,
  Video,
} from 'react-native-compressor';
import RNFS from 'react-native-fs';
import { uploadFilesGeneral } from '../api/endpoint';
import { SocketChatMessage } from '../services/ChatSocketService';
import dayjs from 'dayjs';
import { AttachmentProps } from '../components/ChatContainer/types';

export const handleSendMessage = async (
  messages: IMessage[],
  chatId: string,
  setMessages: React.Dispatch<React.SetStateAction<IMessage[]>>,
  messagesRef: React.MutableRefObject<IMessage[]>,
  pushSocketEvent?: (payload: any) => void,
) => {
  try {
    const redux = await import('auth/redux');
    const { userProfile } = redux.store.getState().auth;
    const clientId = messages[0]._id || uuidv4();

    let payload: any = {
      chatId,
      content: messages[0].text,
      clientId, // gn clientId  map v sau
      sender: {
        id: messages[0].user._id,
        fullName: userProfile?.fullName,
      },
      file: null,
      reactions: [],
      quotedMessage: null,
    };

    if (messages[0]?.file && messages[0]?.file?.length > 0) {
      setMessages((prevMessages: IMessage[]) => {
        const deletedIsLast = prevMessages.map(msg => ({
          ...msg,
          isLast: false,
        }));
        const messageSave: any = {
          ...messages[0],
          _id: clientId,
          clientId,
          attachment: messages[0]?.file?.map((item: any) => ({
            ...item,
            url: item?.uri,
            isLoading: true,
            progress: 1,
          })),
          isSending: true,
        };
        const updated = [messageSave, ...deletedIsLast].sort(
          (a, b) =>
            new Date(b.createdAt).getTime() - new Date(a.createdAt).getTime(),
        );

        const processedMessages = preprocessMessages(
          updated,
          userProfile?.accountId || '',
        );

        messagesRef.current = processedMessages;
        return processedMessages;
      });

      const rawFiles = messages[0].file || [];
      const compressedFiles = await Promise.all(
        rawFiles.map(async (f: any) => {
          try {
            const _mime = (f?.mine || '').toLowerCase();
            const ext = (f?.fileExtension || '').toLowerCase();
            const typeFile =
              f?.typeFile ||
              (_mime.startsWith('image')
                ? 'image'
                : _mime.startsWith('video')
                ? 'video'
                : undefined);

            const uriStr = String(f?.uri || '');
            const alreadyCompressed =
              uriStr.includes('react-native-compressor') ||
              uriStr.includes('/Compress/');

            if (!alreadyCompressed) {
              if (
                typeFile === 'image' ||
                _mime.startsWith('image') ||
                ['jpg', 'jpeg', 'png', 'webp', 'gif'].includes(ext)
              ) {
                const out = await compressImage(uriStr);
                return { ...f, uri: out.uri, size: out.size ?? f.size };
              }
              if (
                typeFile === 'video' ||
                _mime.startsWith('video') ||
                ['mp4', 'mov', 'avi'].includes(ext)
              ) {
                const out = await compressVideo(uriStr);
                return { ...f, uri: out.uri, size: out.size ?? f.size };
              }
            }
          } catch (_) {}
          return f;
        }),
      );

      const formData = new FormData();
      formData.append('IsPublic', true);

      compressedFiles?.map((mediaFile: any, index: number) => {
        const fileObject = {
          uri: mediaFile?.uri,
          name:
            mediaFile?.name ||
            mediaFile?.uri?.split('/').pop() ||
            `file_${index}${mediaFile?.fileExtension}`,
          type:
            mime.getType(normalizeFileUri(mediaFile?.uri)) || mediaFile?.mine,
        };
        formData.append('Files', fileObject as any);
      });

      const responseUploadFileS3 = await uploadFilesMediaGeneral(
        formData,
        (progressEvent: any) => {
          setMessages((prevMessages: IMessage[]) => {
            const itemCanUpdate = prevMessages.find(
              (msg: any) => msg.clientId === clientId,
            );
            if (!itemCanUpdate) return prevMessages;
            const messageProgress = {
              ...itemCanUpdate,
              file: itemCanUpdate?.file?.map((item: any) => ({
                ...item,
                url: item?.uri,
                isLoading: true,
                progress: progressEvent?.progress * 100,
              })),
              attachment: itemCanUpdate?.file?.map((item: any) => ({
                ...item,
                url: item?.uri,
                isLoading: true,
                progress: progressEvent?.progress * 100,
              })),
            };

            const newArr = prevMessages?.map((msg: any) => {
              if (msg.clientId === clientId) {
                return messageProgress;
              }
              return msg;
            });
            const updated = newArr?.sort(
              (a, b) =>
                new Date(b.createdAt).getTime() -
                new Date(a.createdAt).getTime(),
            );

            const processedMessages = preprocessMessages(
              updated,
              userProfile?.accountId || '',
            );

            messagesRef.current = processedMessages;
            return processedMessages;
          });
        },
      );

      if (
        responseUploadFileS3?.statusCode === 200 &&
        responseUploadFileS3?.data?.length > 0 &&
        responseUploadFileS3?.data?.some((item: any) => item && item?.url)
      ) {
        const fileUploaded = responseUploadFileS3?.data?.map((file: any) => {
          return {
            id: file.id,
            fileName: file.fileName,
            url: file.url,
            key: file.key,
            fileSize: file.fileLength || 0,
            fileExtension: file.fileExtension,
            bucketName: file.bucketName,
            folderName: file.folderName,
            isPublic: file.isPublic,
            type: file.mine,
            originalName: file.originalName,
            size: file.fileLength || 0,
          };
        });

        payload.attachment = fileUploaded;
        payload.file = fileUploaded;
      }
    } else {
      payload.attachment = null;
      delete payload.file;
      setMessages((prevMessages: IMessage[]) => {
        const deletedIsLast = prevMessages.map(msg => ({
          ...msg,
          isLast: false,
        }));
        const messageSave: any = {
          ...messages[0],
          _id: clientId,
          clientId,
          isSending: true,
        };
        delete messageSave.file;
        const updated = [messageSave, ...deletedIsLast].sort(
          (a, b) =>
            new Date(b.createdAt).getTime() - new Date(a.createdAt).getTime(),
        );

        const processedMessages = preprocessMessages(
          updated,
          userProfile?.accountId || '',
        );

        messagesRef.current = processedMessages;
        return processedMessages;
      });
    }

    if (messages[0]?.messageReply) {
      let fileQuotedMessage: any[] = [];
      if (
        messages[0]?.messageReply?.file &&
        messages[0]?.messageReply?.file?.length > 0
      ) {
        fileQuotedMessage = messages[0]?.messageReply?.file?.map(
          (attachment: any) => ({
            ...attachment,
            uri: attachment?.url,
            typeFile: setTypeFile(attachment?.fileExtension),
            progress: 100,
          }),
        );
      }
      const id = normalizeId(messages[0]?.messageReply?._id?.toString() || '');

      payload.quotedMessage = {
        id,
        content: messages[0]?.messageReply?.text,
        sender: {
          id: messages[0]?.messageReply?.user?._id,
          fullName: messages[0]?.messageReply?.user?.name,
        },
        attachment: fileQuotedMessage?.length > 0 ? fileQuotedMessage : null,
      };
    }

    pushSocketEvent?.(payload);
  } catch (error) {
    const redux = await import('auth/redux');
    const { userProfile } = redux.store.getState().auth;
    logger('[ChatScreen]  Failed to send message:', error);
    setMessages((prevMessages: IMessage[]) => {
      const failedMessage = prevMessages.find(
        msg => msg._id === messages[0]._id,
      );
      if (failedMessage) {
        const updatedMessages = prevMessages.map(msg =>
          msg._id === messages[0]._id
            ? {
                ...msg,
                isLoading: false,
                errorMessage: i18n?.t('__MESSAGE_SCREEN.MESSAGE_STATUS.FAILED'),
                isSending: false,
              }
            : msg,
        );

        const processedMessages = preprocessMessages(
          updatedMessages,
          userProfile?.accountId || '',
        );

        messagesRef.current = processedMessages;
        return processedMessages;
      }
      return prevMessages;
    });
  }
};

export function preprocessMessages(messages: any[], accountId: string) {
  return messages.map((msg, index) => {
    // Nu l message ca mnh => b qua
    if (msg.user._id === accountId) {
      return { ...msg, isShowName: false };
    }

    const isLast = index === messages.length - 1;
    const next = !isLast ? messages[index + 1] : null;

    return {
      ...msg,
      isShowName: isLast || (next && msg.user._id !== next.user._id),
    };
  });
}

export function normalizeId(id: string) {
  // Regex match UUID v4 chun: 8-4-4-4-12 hex digits
  const uuidRegex =
    /^[0-9a-f]{8}-[0-9a-f]{4}-[0-9a-f]{4}-[0-9a-f]{4}-[0-9a-f]{12}$/i;

  // Nu id  chun UUID th gi nguyn
  if (uuidRegex.test(id)) {
    return id;
  }

  // Nu khng, ct phn sau du "-" cui cng
  return id.substring(0, id.lastIndexOf('-'));
}

export const setTypeFile = (fileExtension: string) => {
  const lowerExt = fileExtension.toLowerCase();
  if (imageExtensions.includes(lowerExt)) return 'image';
  if (videoExtensions.includes(lowerExt)) return 'video';
  return 'unknown';
};

export const uploadFilesMediaGeneral = async (
  payload: any,
  onUploadProgress?: (progressEvent: any) => void,
): Promise<any> => {
  try {
    const client = await import('auth/api');
    const response: any = await client.postRequest(
      uploadFilesGeneral,
      payload,
      {
        headers: {
          'Content-Type': 'multipart/form-data',
        },
        onUploadProgress: (progressEvent: any) => {
          onUploadProgress?.(progressEvent);
        },
      },
    );

    return response?.data;
  } catch (error: any) {
    console.error('[ChatService]  Failed to upload files:', error);
  }
};

export const compressImage = async (uri: string) => {
  try {
    const compressedUri = await ImageCompressor.compress(uri, {
      compressionMethod: 'auto',
      maxWidth: 1280, // resize v ~HD
      quality: 0.6, // nn kh mnh nhng vn r trn mobile
    });

    const meta = await getImageMetaData(compressedUri);

    return {
      uri: compressedUri,
      size: meta?.size ?? 0,
    };
  } catch (error) {
    return {
      uri,
      size: undefined,
    };
  }
};

export const compressVideo = async (uri: string) => {
  try {
    const compressedUri = await Video.compress(uri, {
      compressionMethod: 'auto',
      maxSize: 720,
      minimumFileSizeForCompress: 3,
      bitrate: 2_000_000,
      progressDivider: 1,
    });

    const meta: any = await getVideoMetaData(compressedUri);
    // Handle both object and array shapes
    let size: number | undefined;
    if (meta) {
      if (Array.isArray(meta)) {
        size = meta[0]?.size;
      } else {
        size = meta.size ?? meta.fileSize;
      }
    }

    // Fallback: stat the file if size is missing/0
    if (!size || size <= 0) {
      try {
        const stat = await RNFS.stat(compressedUri.replace('file://', ''));
        size = Number(stat.size);
      } catch (_) {
        // leave size undefined if stat fails
      }
    }

    return {
      uri: compressedUri,
      size,
    };
  } catch (error) {
    return {
      uri,
      size: undefined,
    };
  }
};

export const onMessage = async (
  data: any,
  setMessages: React.Dispatch<React.SetStateAction<IMessage[]>>,
  messagesRef: React.MutableRefObject<IMessage[]>,
  setIsTyping: React.Dispatch<React.SetStateAction<boolean>>,
) => {
  try {
    const redux = await import('auth/redux');
    if (!data?.type) {
      return;
    }

    if (data?.type === SocketChatMessage.USER_TYPING) {
      setIsTyping(data?.typingStatus?.isTyping);
    }

    if (
      data?.status === SocketChatMessage.ERROR &&
      data?.type === SocketChatMessage.MESSAGE_DELIVERED
    ) {
      setMessages((prevMessages: IMessage[]) => {
        const updatedMessages = prevMessages.map((msg: any) => {
          if (msg?.isSending) {
            return {
              ...msg,
              errorMessage: data?.error,
              isSending: false,
            };
          }
          return msg;
        });
        return updatedMessages;
      });
    }

    const message = data?.messages || data?.message;
    if (!message) {
      return;
    }
    if (
      data?.type === SocketChatMessage.NEW_MESSAGE ||
      data?.type === SocketChatMessage.MESSAGE_DELIVERED
    ) {
      const newMessage: IMessage & { clientId: string } = {
        isLast: true,
        _id: message?.id,
        clientId: message?.clientId, // map vi local
        text: message?.content,
        createdAt: new Date(message?.sentAt ?? dayjs().toISOString()),
        user: {
          _id: message.sender?.id,
          name: message?.sender?.fullName,
        },
      };

      if (message?.attachment && message?.attachment?.length > 0) {
        const file = message?.attachment?.map(
          (attachment: AttachmentProps) => ({
            ...attachment,
            uri: attachment.url,
            typeFile: setTypeFile(attachment.fileExtension),
          }),
        );
        newMessage.file = file;
      }

      if (message?.quotedMessage) {
        let file: any[] = [];
        if (message?.quotedMessage?.attachment?.length > 0) {
          file = message.quotedMessage.attachment.map(
            (attachment: AttachmentProps) => ({
              ...attachment,
              uri: attachment?.url,
              typeFile: setTypeFile(attachment?.fileExtension),
              progress: 100,
            }),
          );
        }

        const messageReply: IMessage = {
          _id: message.quotedMessage.id,
          text: message.quotedMessage.content,
          createdAt: new Date(message.quotedMessage.sentAt),
          user: {
            _id: message.quotedMessage.senderId,
            name: `${message.quotedMessage.firstName} ${message.quotedMessage.lastName}`,
          },
          ...(file?.length > 0 && { file }),
        };
        newMessage.messageReply = messageReply;
      }

      setMessages((prevMessages: IMessage[]) => {
        // tm message local theo clientId
        const messageSending = prevMessages?.find(
          (msg: any) => msg?.isSending && msg.clientId === newMessage.clientId,
        );

        const isDuplicate = prevMessages.some(
          msg => msg._id === newMessage._id,
        );
        if (isDuplicate) {
          return prevMessages;
        }

        let updatedMessages: IMessage[];

        if (messageSending) {
          updatedMessages = prevMessages.map((msg: any) => {
            if (msg?.clientId === newMessage.clientId) {
              return {
                ...msg,
                _id: newMessage?._id,
                text: newMessage?.text,
                isLast: true,
                isSending: false,
                createdAt: new Date(newMessage.createdAt),
                ...(newMessage.file && { file: newMessage.file }),
              };
            }
            return { ...msg, isLast: false };
          });
        } else {
          const deletedIsLast = prevMessages.map(msg => ({
            ...msg,
            isLast: false,
          }));
          updatedMessages = [newMessage, ...deletedIsLast];
        }

        const sorted = updatedMessages.sort(
          (a, b) =>
            new Date(b.createdAt).getTime() - new Date(a.createdAt).getTime(),
        );

        const processedMessages = preprocessMessages(
          sorted,
          redux.store.getState().auth?.user?.accountId || '',
        );

        messagesRef.current = processedMessages;
        return processedMessages;
      });
    }

    if (data?.type === SocketChatMessage.REACTION_ADDED) {
      setMessages((prevMessages: IMessage[]) => {
        const newArr = prevMessages?.map(msg => {
          if (normalizeId(msg._id?.toString()) === data?.messageId) {
            return {
              ...msg,
              reactionEmoji: data?.message?.reactions || data?.reaction,
            };
          } else {
            return msg;
          }
        });

        const processedMessages = preprocessMessages(
          newArr,
          redux.store.getState().auth?.user?.accountId || '',
        );
        messagesRef.current = processedMessages;
        return processedMessages;
      });
    }
  } catch (error) {
    logger('[onMessage]  Failed to on message:', error);
  }
};
