import React from 'react';
import { View, StyleSheet } from 'react-native';
import { Button, Text } from 'tradewize-component';
import { SvgXml } from 'react-native-svg';
import { colors } from '../../../../theme';
import { IconPdfRed } from '../../../../assets/svg';
import { useAppTranslation } from '../../../../hooks/useAppTranslation';
import { OptionDataGroup } from '../../../../types/jobDetail.type';

interface OptionCardProps {
  title: string;
  count?: number;
  icon?: string;
  backgroundColor?: string;
  onPress?: () => void;
}

const OptionCard: React.FC<OptionCardProps> = ({
  title,
  count,
  icon,
  backgroundColor = colors.grayScale20,
  onPress,
}) => {
  return (
    <Button
      style={[styles.card, { backgroundColor: backgroundColor + '20' }]}
      onPress={onPress}
      activeOpacity={0.7}
    >
      {icon && <SvgXml xml={icon} width={24} height={24} style={styles.icon} />}
      <Text style={styles.cardText} numberOfLines={2}>
        {title}
        <Text style={[styles.cardTextCount, { color: backgroundColor }]}>
          {count !== undefined && ` (${count})`}
        </Text>
      </Text>
    </Button>
  );
};

interface OptionDataProps {
  data: OptionDataGroup | undefined;
}

const OptionData: React.FC<OptionDataProps> = ({ data }) => {
  const { t } = useAppTranslation();

  return (
    <View style={styles.container}>
      <OptionCard
        title={t('OTHER_CREW_MEMBERS')}
        count={data?.totalParticipant || 0}
        backgroundColor={colors.alertColorWarning}
        onPress={() => {}}
      />
      <OptionCard
        title={t('TOOLS_NEEDED')}
        count={data?.totalTools || 0}
        backgroundColor={colors.alertColorSuccess}
        onPress={() => {}}
      />
      <OptionCard
        title={t('HOUSE_PLAN')}
        icon={IconPdfRed()}
        backgroundColor={colors.mainColorPrimary}
        onPress={() => {}}
      />
    </View>
  );
};

const styles = StyleSheet.create({
  container: {
    flexDirection: 'row',
    gap: 12,
    marginTop: 16,
  },
  card: {
    flex: 1,
    borderRadius: 8,
    alignItems: 'center',
    justifyContent: 'center',
    paddingHorizontal: 4,
    paddingVertical: 4,
  },
  icon: {
    marginBottom: 8,
  },
  cardText: {
    fontSize: 12,
    color: colors.black,
    textAlign: 'center',
    fontWeight: '500',
    lineHeight: 16,
  },
  cardTextCount: {
    fontSize: 12,
    color: colors.grayScale70,
    textAlign: 'center',
    fontWeight: '500',
  },
});

export default OptionData;
