import React from 'react';
import { View } from 'react-native';
import { Text } from 'tradewize-component';
import FastImage from 'react-native-fast-image';
import { SvgXml } from 'react-native-svg';
import { colors } from '../../../theme';
import { styles } from '../styles';

// Crown icon for rank 1
const IconCrown = (color: string = colors.mainColorSecondary) => `
<svg width="24" height="24" viewBox="0 0 24 24" fill="none" xmlns="http://www.w3.org/2000/svg">
<path d="M5 16L3 10L8 12L12 4L16 12L21 10L19 16H5Z" fill="${color}"/>
<path d="M5 18H19V20H5V18Z" fill="${color}"/>
</svg>`;

interface Top3User {
  rank: number;
  name: string;
  role: string;
  points: number;
  avatar?: string;
}

interface Top3RankingProps {
  users: Top3User[];
}

const Top3Ranking: React.FC<Top3RankingProps> = ({ users }) => {
  const getInitials = (name: string) => {
    const parts = name.trim().split(' ');
    if (parts.length >= 2) {
      return `${parts[0][0]}${parts[parts.length - 1][0]}`.toUpperCase();
    }
    return name.substring(0, 2).toUpperCase();
  };

  const renderTopUser = (user: Top3User, index: number) => {
    const rank = user.rank;
    const isRank1 = rank === 1;
    const isRank2 = rank === 2;
    const isRank3 = rank === 3;

    return (
      <View 
        key={rank} 
        style={[
          styles.top3RankingTopUserContainer,
          isRank1 && styles.top3RankingRank1Container,
          isRank2 && styles.top3RankingRank2Container,
          isRank3 && styles.top3RankingRank3Container
        ]}
      >
        {isRank1 && (
          <View style={styles.top3RankingCrownContainer}>
            <SvgXml xml={IconCrown()} width={32} height={32} />
          </View>
        )}
        <View style={styles.top3RankingAvatarWrapper}>
          <View style={styles.top3RankingAvatarContainer}>
            {user.avatar ? (
              <FastImage
                source={{ uri: user.avatar }}
                style={styles.top3RankingAvatar}
                resizeMode={FastImage.resizeMode.cover}
              />
            ) : (
              <View style={styles.top3RankingAvatarPlaceholder}>
                <Text style={styles.top3RankingAvatarInitials}>
                  {getInitials(user.name)}
                </Text>
              </View>
            )}
          </View>
          <View style={styles.top3RankingRankBadge}>
            <Text style={styles.top3RankingRankBadgeText}>{rank}</Text>
          </View>
        </View>
        <View style={styles.top3RankingInfoContainer}>
          {!isRank1 && (
            <Text style={styles.top3RankingName} numberOfLines={1}>
              {user.name}
            </Text>
          )}
          <View style={{ flexDirection: 'row', alignItems: 'baseline', justifyContent: 'center' }}>
            <Text style={[
              styles.top3RankingPoints,
              isRank1 && styles.top3RankingRank1Points
            ]}>
              {isRank1 
                ? Math.floor(user.points).toLocaleString()
                : user.points.toFixed(1).replace(/\B(?=(\d{3})+(?!\d))/g, ',')
              }
            </Text>
            <Text style={[
              styles.top3RankingPoints,
              isRank1 && styles.top3RankingRank1Points
            ]}>
              {isRank1 ? '.00 pts' : '0 pts'}
            </Text>
          </View>
          <Text style={styles.top3RankingRole}>{user.role}</Text>
        </View>
      </View>
    );
  };

  // Reorder users: rank 2 (left), rank 1 (center), rank 3 (right)
  const orderedUsers = [
    users.find(u => u.rank === 2),
    users.find(u => u.rank === 1),
    users.find(u => u.rank === 3),
  ].filter(Boolean) as Top3User[];

  return (
    <View style={styles.top3RankingContainer}>
      <View style={styles.top3RankingTop3Container}>
        {orderedUsers.map((user, index) => renderTopUser(user, index))}
      </View>
    </View>
  );
};

export default Top3Ranking;

