import React, { useEffect, useState } from 'react';
import { View, FlatList, TouchableOpacity, ScrollView, TextInput } from 'react-native';
import { BaseContainer, HeaderBase, Button, Text } from 'tradewize-component';
import { SvgXml } from 'react-native-svg';
import Modal from 'react-native-modal';
import { useDispatch } from 'react-redux';
import { navigationRef } from '../../services/NavigationService';
import { colors } from '../../theme';
import { styles } from './styles';
import { IconPen, IconTrash, IconPlay, IconChevronDown } from '../../assets/svg';
import { getBudget, addBudget, updateBudget, deleteBudget } from '../../api/endpoint';
import { useAppTranslation } from '../../hooks/useAppTranslation';
import { formatCurrency, logger } from '../../utils';

interface BudgetItem {
  id: string;
  name: string;
  amount: number;
  cadance: number;
  cadanceEnumName: string;
  monthly: number;
  createdAt: string;
}

const CADANCE_OPTIONS = [
  { label: 'Daily', value: 0 },
  { label: 'Weekly', value: 1 },
  { label: 'Bi-Weekly', value: 2 },
  { label: 'Monthly', value: 3 },
  { label: 'Yearly', value: 4 },
];

const MyBudget = () => {
  const dispatch = useDispatch();
  const { t } = useAppTranslation();
  const [budgetItems, setBudgetItems] = useState<BudgetItem[]>([]);
  const [totalMonthlyBudget, setTotalMonthlyBudget] = useState<number>(0);
  const [isLoading, setIsLoading] = useState(false);
  
  const [isAddModalVisible, setIsAddModalVisible] = useState(false);
  const [isEditModalVisible, setIsEditModalVisible] = useState(false);
  const [isDeleteModalVisible, setIsDeleteModalVisible] = useState(false);
  const [selectedItem, setSelectedItem] = useState<BudgetItem | null>(null);
  const [isCadancePickerVisible, setIsCadancePickerVisible] = useState(false);
  
  const [formName, setFormName] = useState('');
  const [formAmount, setFormAmount] = useState('');
  const [formCadance, setFormCadance] = useState<number>(0);

  useEffect(() => {
    fetchBudget();
  }, []);

  const fetchBudget = async () => {
    setIsLoading(true);
    try {
      const client = await import('auth/api');
      const response = await client.getRequest(getBudget);
      if (response.status === 200 && response.data) {
        setBudgetItems(response.data.budgets || []);
        setTotalMonthlyBudget(response.data.totalMonthlyBudget || 0);
      }
    } catch (error) {
      console.error('Error fetching budget:', error);
    } finally {
      setIsLoading(false);
    }
  };



  const resetForm = () => {
    setFormName('');
    setFormAmount('');
    setFormCadance(0);
    setSelectedItem(null);
  };

  const handleAddNewBudget = () => {
    resetForm();
    setIsAddModalVisible(true);
  };

  const handleEditBudget = (item: BudgetItem) => {
    setSelectedItem(item);
    setFormName(item.name);
    setFormAmount(item.amount.toString());
    setFormCadance(item.cadance);
    setIsEditModalVisible(true);
  };

  const handleDeleteBudget = (item: BudgetItem) => {
    setSelectedItem(item);
    setIsDeleteModalVisible(true);
  };

  const handleSaveBudget = async () => {
    if (!formName.trim() || !formAmount.trim()) {
      const redux = await import('auth/redux');
      dispatch(
        redux.actionContainer.showAlertAsync({
          title: t('WARNING') || 'Warning',
          message: t('PLEASE_FILL_IN_ALL_FIELDS') || 'Please fill in all fields',
          type: 'warning',
          buttonText: t('OK') || 'OK',
          titleStyle: {
            color: colors.black,
          },
          buttonStyle: {
            backgroundColor: colors.mainColorPrimary,
          },
        }),
      );
      return;
    }

    const amount = parseFloat(formAmount);
    if (isNaN(amount) || amount <= 0) {
      const redux = await import('auth/redux');
      dispatch(
        redux.actionContainer.showAlertAsync({
          title: t('WARNING') || 'Warning',
          message: t('PLEASE_ENTER_VALID_AMOUNT') || 'Please enter a valid amount',
          type: 'warning',
          buttonText: t('OK') || 'OK',
          titleStyle: {
            color: colors.black,
          },
          buttonStyle: {
            backgroundColor: colors.mainColorPrimary,
          },
        }),
      );
      return;
    }

    try {
      const client = await import('auth/api');
      const redux = await import('auth/redux');
      const data = {
        name: formName.trim(),
        amount: amount,
        cadance: formCadance,
        ...(selectedItem && { id: selectedItem.id }),
      };

      let response;
      if (selectedItem) {
        response = await client.putRequest(updateBudget, data);
      } else {
        response = await client.postRequest(addBudget, data);
      }

      if (response.status === 200) {
        dispatch(
          redux.actionContainer.showAlertAsync({
            title: selectedItem
              ? (t('UPDATE_BUDGET_SUCCESS'))
              : (t('ADD_BUDGET_SUCCESS')),
            type: 'success',
            buttonText: t('OK') || 'OK',
            titleStyle: {
              color: colors.black,
            },
            buttonStyle: {
              backgroundColor: colors.mainColorPrimary,
            },
            onClose: () => {
              dispatch(redux.actionContainer.hideAlertAsync());
            },
          }),
        );
        setIsAddModalVisible(false);
        setIsEditModalVisible(false);
        resetForm();
        fetchBudget();
      } else {
        const errorMessage = response?.message || t('AN_ERROR_OCCURRED_PLEASE_TRY_AGAIN');
        dispatch(
          redux.actionContainer.showAlertAsync({
            title: selectedItem
              ? (t('UPDATE_BUDGET_FAILED'))
              : (t('ADD_BUDGET_FAILED')),
            message: errorMessage,
            type: 'error',
            buttonText: t('OK') || 'OK',
            titleStyle: {
              color: colors.black,
            },
            buttonStyle: {
              backgroundColor: colors.mainColorPrimary,
            },
          }),
        );
      }
    } catch (error: any) {
      logger('Error saving budget:', error);
      const redux = await import('auth/redux');
      const errorMessage =
        error?.response?.data?.message ||
        error?.message ||
        t('AN_ERROR_OCCURRED_PLEASE_TRY_AGAIN');
      dispatch(
        redux.actionContainer.showAlertAsync({
          title: selectedItem
            ? (t('UPDATE_BUDGET_FAILED'))
            : (t('ADD_BUDGET_FAILED')),
          message: errorMessage,
          type: 'error',
          buttonText: t('OK') || 'OK',
          titleStyle: {
            color: colors.black,
          },
          buttonStyle: {
            backgroundColor: colors.mainColorPrimary,
          },
        }),
      );
    }
  };

  const handleConfirmDelete = async () => {
    if (!selectedItem) return;

    try {
      const client = await import('auth/api');
      const redux = await import('auth/redux');
      const response = await client.deleteRequest(`${deleteBudget}/${selectedItem.id}`);

      if (response.status === 200) {
        dispatch(
          redux.actionContainer.showAlertAsync({
            title: t('DELETE_BUDGET_SUCCESS'),
            type: 'success',
            buttonText: t('OK') || 'OK',
            titleStyle: {
              color: colors.black,
            },
            buttonStyle: {
              backgroundColor: colors.mainColorPrimary,
            },
            onClose: () => {
              dispatch(redux.actionContainer.hideAlertAsync());
            },
          }),
        );
        setIsDeleteModalVisible(false);
        setSelectedItem(null);
        fetchBudget();
      } else {
        const errorMessage = response?.message || t('AN_ERROR_OCCURRED_PLEASE_TRY_AGAIN');
        dispatch(
          redux.actionContainer.showAlertAsync({
            title: t('DELETE_BUDGET_FAILED'),
            message: errorMessage,
            type: 'error',
            buttonText: t('OK') || 'OK',
            titleStyle: {
              color: colors.black,
            },
            buttonStyle: {
              backgroundColor: colors.mainColorPrimary,
            },
          }),
        );
      }
    } catch (error: any) {
      logger('Error deleting budget:', error);
      const redux = await import('auth/redux');
      const errorMessage =
        error?.response?.data?.message ||
        error?.message ||
        t('AN_ERROR_OCCURRED_PLEASE_TRY_AGAIN');
      dispatch(
        redux.actionContainer.showAlertAsync({
          title: t('DELETE_BUDGET_FAILED'),
          message: errorMessage,
          type: 'error',
          buttonText: t('OK') || 'OK',
          titleStyle: {
            color: colors.black,
          },
          buttonStyle: {
            backgroundColor: colors.mainColorPrimary,
          },
        }),
      );
    }
  };


  const renderBudgetItem = ({ item }: { item: BudgetItem }) => (
    <View style={styles.budgetItemCard}>
      <View style={styles.budgetItemHeader}>
        <Text style={styles.budgetItemName}>{item.name}</Text>
        <View style={styles.budgetItemActions}>
          <TouchableOpacity
            onPress={() => handleEditBudget(item)}
            style={styles.budgetItemActionButton}
            activeOpacity={0.7}
          >
            <SvgXml
              xml={IconPen(colors.grayScale70)}
              width={20}
              height={20}
            />
          </TouchableOpacity>
          <TouchableOpacity
            onPress={() => handleDeleteBudget(item)}
            style={styles.budgetItemActionButton}
            activeOpacity={0.7}
          >
            <SvgXml
              xml={IconTrash(colors.grayScale70)}
              width={20}
              height={20}
            />
          </TouchableOpacity>
        </View>
      </View>

      <View style={styles.budgetItemDetails}>
        <View style={styles.budgetItemDetailRow}>
          <Text style={styles.budgetItemDetailLabel}>Amount:</Text>
          <Text style={styles.budgetItemDetailValue}>
            {formatCurrency(item.amount)}
          </Text>
        </View>
        <View style={styles.budgetItemDetailRow}>
          <Text style={styles.budgetItemDetailLabel}>Cadance:</Text>
          <Text style={styles.budgetItemDetailValueText}>{item.cadanceEnumName}</Text>
        </View>
        <View style={styles.budgetItemDetailRow}>
          <Text style={styles.budgetItemDetailLabel}>Total Monthly:</Text>
          <Text style={styles.budgetItemDetailValue}>
            {formatCurrency(item.monthly)}
          </Text>
        </View>
      </View>
    </View>
  );

  return (
    <BaseContainer>
      <HeaderBase
        title={t('MY_BUDGET')}
        onPressBack={() => {
          navigationRef.goBack();
        }}
      />

      <ScrollView
        style={styles.scrollView}
        contentContainerStyle={styles.scrollContent}
        showsVerticalScrollIndicator={false}
      >
        <View style={styles.totalBudgetCard}>
          <Text style={styles.totalBudgetLabel}>{t('TOTAL_MONTHLY_BUDGET')}:</Text>
          <Text style={styles.totalBudgetValue}>
            {formatCurrency(totalMonthlyBudget)}
          </Text>
        </View>

        <Button
          style={styles.addBudgetButton}
          onPress={handleAddNewBudget}
        >
          <Text style={styles.addBudgetButtonText}>Add New Budget</Text>
        </Button>

        <FlatList
          data={budgetItems}
          renderItem={renderBudgetItem}
          keyExtractor={item => item.id}
          scrollEnabled={false}
          contentContainerStyle={styles.budgetListContainer}
        />
      </ScrollView>
      <Modal
        isVisible={isAddModalVisible || isEditModalVisible}
        onBackdropPress={() => {
          setIsAddModalVisible(false);
          setIsEditModalVisible(false);
          resetForm();
        }}
        style={styles.modal}
      >
        <View style={styles.modalContent}>
          <Text style={styles.modalTitle}>
            {selectedItem ? (t('EDIT_BUDGET')) : (t('ADD_NEW_BUDGET'))}
          </Text>

          <View style={styles.modalField}>
            <Text style={styles.modalLabel}>{t('NAME')}</Text>
            <TextInput
              style={styles.modalInput}
              placeholder={t('PLEASE_ENTER_NAME')}
              placeholderTextColor={colors.grayScale70}
              value={formName}
              onChangeText={setFormName}
            />
          </View>

          <View style={styles.modalField}>
            <Text style={styles.modalLabel}>{t('AMOUNT')}</Text>
            <TextInput
              style={styles.modalInput}
              placeholder={t('PLEASE_ENTER_AMOUNT')}
              placeholderTextColor={colors.grayScale70}
              value={formAmount}
              onChangeText={setFormAmount}
              keyboardType="decimal-pad"
            />
          </View>

          <View style={styles.modalField}>
            <Text style={styles.modalLabel}>{t('CADANCE')}</Text>
            <TouchableOpacity
              style={styles.modalSelect}
              onPress={() => setIsCadancePickerVisible(true)}
            >
              <Text
                style={[
                  styles.modalSelectText,
                  !formCadance && formCadance !== 0 && styles.modalSelectPlaceholder,
                ]}
              >
                {CADANCE_OPTIONS.find(opt => opt.value === formCadance)?.label ||
                  t('SELECT_CADANCE')}
              </Text>
              <SvgXml
                xml={IconChevronDown(colors.grayScale70)}
                width={20}
                height={20}
              />
            </TouchableOpacity>
          </View>

          <View style={styles.modalButtons}>
            <Button
              style={[styles.modalButton, styles.modalButtonCancel]}
              onPress={() => {
                setIsAddModalVisible(false);
                setIsEditModalVisible(false);
                resetForm();
              }}
            >
              <Text style={styles.modalButtonCancelText}>{t('CANCEL')}</Text>
            </Button>
            <Button
              style={[styles.modalButton, styles.modalButtonSave]}
              onPress={handleSaveBudget}
            >
              <Text style={styles.modalButtonSaveText}>
                {selectedItem ? (t('SAVE')) : (t('ADD'))}
              </Text>
            </Button>
          </View>
        </View>
      </Modal>

      {/* Cadance Picker Modal */}
      <Modal
        isVisible={isCadancePickerVisible}
        onBackdropPress={() => setIsCadancePickerVisible(false)}
        style={styles.pickerModal}
      >
        <View style={styles.pickerContent}>
          {CADANCE_OPTIONS.map(option => (
            <TouchableOpacity
              key={option.value}
              style={styles.pickerOption}
              onPress={() => {
                setFormCadance(option.value);
                setIsCadancePickerVisible(false);
              }}
            >
              <Text style={styles.pickerOptionText}>{option.label}</Text>
              {formCadance === option.value && (
                <Text style={styles.pickerOptionCheck}></Text>
              )}
            </TouchableOpacity>
          ))}
        </View>
      </Modal>
      <Modal
        isVisible={isDeleteModalVisible}
        onBackdropPress={() => {
          setIsDeleteModalVisible(false);
          setSelectedItem(null);
        }}
        style={styles.modal}
      >
        <View style={styles.deleteModalContent}>
          <Text style={styles.deleteModalTitle}>{t('DELETE_BUDGET')}</Text>
          <Text style={styles.deleteModalMessage}>
            {t('ARE_YOU_SURE_YOU_WANT_TO_DELETE_THIS_BUDGET')}
          </Text>
          <View style={styles.modalButtons}>
            <Button
              style={[styles.modalButton, styles.modalButtonCancel]}
              onPress={() => {
                setIsDeleteModalVisible(false);
                setSelectedItem(null);
              }}
            >
              <Text style={styles.modalButtonCancelText}>{t('CANCEL')}</Text>
            </Button>
            <Button
              style={[styles.modalButton, styles.modalButtonDelete]}
              onPress={handleConfirmDelete}
            >
              <Text style={styles.modalButtonDeleteText}>{t('DELETE')}</Text>
            </Button>
          </View>
        </View>
      </Modal>
    </BaseContainer>
  );
};

export default MyBudget;
