import { Platform } from 'react-native';
import {
  check,
  request,
  PERMISSIONS,
  RESULTS,
  openSettings,
  PermissionStatus,
} from 'react-native-permissions';
import { logger } from '../utils';
import i18n from '../i18n';
import Geolocation from 'react-native-geolocation-service';
import { Image } from 'react-native-compressor';
import dayjs from 'dayjs';
import { colors } from '../theme';

export const dispatch = async (action: any) => {
  const redux = await import('auth/redux');
  return redux.store.dispatch(action);
};

export const showOpenSettingAlert = async (
  title: string = '',
  message: string = '',
  isHideModalOnClose: boolean = true,
  onPress: () => void = () => openSettings(),
) => {
  try {
    const redux = await import('auth/redux');
    dispatch(
      redux.actionContainer.showAlertAsync({
        type: 'warning',
        title: title,
        message: message,
        isBackdropPress: false,
        buttonText: i18n?.t('OPEN_SETTING'),
        isHideModalOnClose: isHideModalOnClose,
        onClose: onPress,
      }),
    );
  } catch (error) {
    logger('error showOpenSettingAlert', error);
  }
};

export const requestPermissionLocationWhenInUse = async (): Promise<{
  granted: boolean;
  status: boolean;
}> => {
  const permission =
    Platform.OS === 'ios'
      ? PERMISSIONS.IOS.LOCATION_WHEN_IN_USE
      : PERMISSIONS.ANDROID.ACCESS_FINE_LOCATION;

  const status = await check(permission);

  switch (status) {
    case RESULTS.GRANTED:
      return { granted: true, status: true };

    case RESULTS.DENIED: {
      // Ngi dng cha cho php trc  xin quyn
      const result = await request(permission);
      if (result === RESULTS.GRANTED) return { granted: true, status: true };

      return { granted: false, status: true };
    }

    case RESULTS.BLOCKED: {
      // User  chn "Don't ask again" (Android) hoc Block (iOS)
      showOpenSettingAlert(
        i18n?.t('WARNING'),
        i18n?.t('LOCATION_PERMISSION_BLOCKED'),
      );
      return { granted: false, status: true };
    }

    case RESULTS.LIMITED: {
      // iOS: quyn hn ch  vn dng c c bn
      return { granted: true, status: true };
    }

    default:
      return { granted: false, status: true };
  }
};

export const getCurrentLocation = async (): Promise<any> => {
  try {
    const permission =
      Platform.OS === 'ios'
        ? PERMISSIONS.IOS.LOCATION_WHEN_IN_USE
        : PERMISSIONS.ANDROID.ACCESS_FINE_LOCATION;

    const status = await check(permission);

    if (status !== RESULTS.GRANTED) return null;

    return await new Promise(resolve => {
      Geolocation.getCurrentPosition(
        position => {
          resolve({
            latitude: position.coords.latitude,
            longitude: position.coords.longitude,
            timestamp: position.timestamp,
          });
        },
        error => {
          logger('error getCurrentLocation', error);
          resolve(null);
        },
        { enableHighAccuracy: true, timeout: 15000, maximumAge: 10000 },
      );
    });
  } catch (error) {
    logger('error getCurrentLocation', error);
    return null;
  }
};

export const requestPermissionBiometric = async () => {
  try {
    const redux = await import('auth/redux');

    if (Platform.OS === 'ios') {
      const permission = PERMISSIONS.IOS.FACE_ID;

      // Kim tra trc
      const status: PermissionStatus = await check(permission);

      if (status === RESULTS.UNAVAILABLE) {
        dispatch(
          redux.actionContainer.showAlert({
            type: 'warning',
            title: i18n?.t('WARNING'),
            message: i18n?.t('YOUR_DEVICE_DOES_NOT_SUPPORT_THIS_FEATURE'),
            buttonText: i18n?.t('CONFIRM'),
            titleStyle: {
              color: colors.black,
            },
            buttonStyle: {
              backgroundColor: colors.mainColorPrimary,
            },
            onClose: () => redux.actionContainer.hideAlertAsync(),
          }),
        );
        return { granted: false };
      }

      if (status === RESULTS.BLOCKED) {
        dispatch(
          redux.actionContainer.showAlertAsync({
            type: 'warning',
            title: i18n?.t('WARNING'),
            message: i18n?.t('BIOMETRIC_PERMISSION_BLOCKED'),
            buttonText: i18n?.t('OPEN_SETTING'),
            onClose: () => {
              redux.actionContainer.hideAlertAsync();
              openSettings();
            },
          }),
        );
        return { granted: false };
      }

      if (status === RESULTS.GRANTED) {
        return { granted: true };
      }

      // Gi xin quyn
      const result = await request(permission);

      return {
        granted: result === RESULTS.GRANTED,
        status: result,
      };
    }

    // Android khng c permission ny
    return {
      granted: true,
      note: 'Android does not support biometric runtime permission. Only iOS FACE_ID is requestable.',
    };
  } catch (error) {
    logger('error requestBiometricAuthentication', error);
  }
};

export const updateProfile = async (payload: { [key: string]: any }) => {
  try {
    const redux = await import('auth/redux');
    const state = redux.store.getState();
    const userProfile = state.auth.userProfile;
    if (!userProfile) return;

    dispatch(redux.actionContainer.updateUserProfile(payload));
  } catch (error) {
    logger('error updateProfile', error);
  }
};

// format date with time like: Sep 2, 2025 10:00 AM
export const formatDateWithTime = (dateString: string) => {
  const date = new Date(dateString);
  return date.toLocaleDateString('en-US', {
    year: 'numeric',
    month: 'short',
    day: 'numeric',
    hour: 'numeric',
    minute: '2-digit',
    hour12: true,
  });
};

export const compressImage = async (uri: string) => {
  try {
    const result = await Image.compress(uri, {
      progressDivider: 10,
    });
    return result;
  } catch (error) {
    logger('error compressImage', error);
    return null;
  }
};

export const generateId = () => {
  return `${dayjs().valueOf()}${Math.random()
    .toString(36)
    .slice(2, 11)}`.toLowerCase();
};
