import { FlatList, StyleSheet, View } from 'react-native';
import { Button, SCREEN_WIDTH, Text } from 'tradewize-component';
import { colors } from '../../../theme';
import { useAppTranslation } from '../../../hooks/useAppTranslation';
import { useCallback, useMemo } from 'react';
import { logger } from '../../../utils';
import { useDispatch } from 'react-redux';
import ImagePicker, {
  Image as ImagePickerImage,
} from 'react-native-image-crop-picker';
import { generateId } from '../../../helper';
import FastImage from 'react-native-fast-image';

export interface QuestionItemProps {
  id: string;
  isActive: boolean;
  answer: any;
  questionType: number;
  question: string;
  translations: any;
  score: number;
  isRequired: boolean;
  requiredImage: boolean;
  optionsCount: number;
  options: Option[];
  trainingRoles: any;
  training: Training;
  questionAnswerImages: QuestionAnswerImage[];
  isError?: boolean;
}

export interface QuestionAnswerImage {
  id?: string;
  uri: string;
  filename: string;
  type: string;
  extension: string;
}

export interface Option {
  id: string;
  option: string;
  translations: any;
  isCorrect: boolean;
  isChecked?: boolean;
}

export interface Training {
  id: string;
  courseName: string;
  videoIntroLink: string;
  bannerPng: string;
  sortingOrder: number;
  content: string;
  minPassScore: number;
  percentage: any;
}

interface ItemProps {
  data: QuestionItemProps;
  index: number;
}

const Item = (props: ItemProps) => {
  const { data, index } = props;
  const { t } = useAppTranslation();
  const dispatch = useDispatch();

  const handleOptionChange = useCallback(
    async (optionId: string) => {
      try {
        if (!optionId) return;
        const redux = await import('auth/redux');
        dispatch(
          redux.actionContainer.updateTrainingTestAsync({
            questionId: data?.id,
            answerIds: optionId,
            type: 'isChecked',
          }),
        );
      } catch (error) {
        logger('error handleOptionChange', error);
      }
    },
    [dispatch, data?.id],
  );

  const handleUploadImage = useCallback(async () => {
    try {
      const redux = await import('auth/redux');
      const arrImage: ImagePickerImage[] = await ImagePicker.openPicker({
        multiple: true,
        maxFiles: 5 - (data?.questionAnswerImages?.length || 0),
        mediaType: 'photo',
        forceJpg: true,
        includeBase64: false,
        includeExif: true,
      });
      const imageData = arrImage?.map(img => ({
        uri: img?.sourceURL,
        filename: img?.filename,
        type: img?.mime,
        extension: `.${img?.filename?.split('.').pop()}`,
        id: generateId(),
      }));

      dispatch(
        redux.actionContainer.updateTrainingTestAsync({
          questionId: data?.id,
          type: 'isUploaded',
          questionAnswerImages: imageData,
        }),
      );
    } catch (error) {
      logger('error handleUploadImage', error);
    }
  }, [dispatch, data?.id, data?.questionAnswerImages?.length]);

  const handleRemoveImage = useCallback(
    async (imageId: string) => {
      try {
        const redux = await import('auth/redux');
        dispatch(
          redux.actionContainer.updateTrainingTestAsync({
            questionId: data?.id,
            type: 'removeImage',
            imageId: imageId,
          }),
        );
      } catch (error) {
        logger('error handleRemoveImage', error);
      }
    },
    [dispatch, data?.id],
  );

  const handleTakePhoto = useCallback(async () => {
    try {
      const redux = await import('auth/redux');
      const image = await ImagePicker.openCamera({
        cropping: false,
        compressImageQuality: 0.8,
        compressImageMaxWidth: 1280,
        compressImageMaxHeight: 1280,
        includeBase64: false,
        mediaType: 'photo',
        forceJpg: true,
      });
      const imageData = [image]?.map(img => ({
        uri: img?.path,
        filename: img?.filename || `image_${Date.now()}.jpg`,
        type: img?.mime || 'image/jpeg',
        extension: `.${img?.path?.split('.').pop()}`,
        id: generateId(),
      }));
      dispatch(
        redux.actionContainer.updateTrainingTestAsync({
          questionId: data?.id,
          type: 'isUploaded',
          questionAnswerImages: imageData,
        }),
      );
    } catch (error) {
      logger('error handleTakePhoto', error);
    }
  }, [data?.id, dispatch]);

  // Safe access to options array
  const options = useMemo(() => {
    return data?.options && Array.isArray(data.options) ? data.options : [];
  }, [data?.options]);

  if (!data) return null;

  return (
    <View style={styles.container}>
      <View style={styles.questionContainer}>
        <Text style={[styles.question, data?.isError && styles.questionError]}>
          {t('QUESTION')} {index + 1}: {data.question || ''}
        </Text>
      </View>

      <FlatList
        scrollEnabled={false}
        data={options}
        keyExtractor={item => item?.id || `option-${item?.option}`}
        renderItem={({ item }) => {
          if (!item?.id) return null;

          return (
            <Button
              onPress={() => handleOptionChange(item?.id)}
              variant="ghost"
              style={styles.optionContainer}
            >
              <View style={styles.checkboxContainer}>
                {item?.isChecked && <View style={styles.checkboxChecked} />}
              </View>
              <Text style={styles.optionText}>{item.option}</Text>
            </Button>
          );
        }}
      />

      {data?.requiredImage && (
        <View style={styles.footerContainer}>
          <Button
            disabled={data?.questionAnswerImages?.length >= 5}
            onPress={() => handleUploadImage()}
            style={[
              styles.footerButton,
              data?.questionAnswerImages?.length >= 5 && styles.disabledButton,
            ]}
            variant="ghost"
            title={t('UPLOAD_IMAGE')}
            textStyle={styles.footerButtonText}
          />
          <Button
            onPress={() => handleTakePhoto()}
            disabled={data?.questionAnswerImages?.length >= 5}
            style={[
              styles.footerButton,
              data?.questionAnswerImages?.length >= 5 && styles.disabledButton,
            ]}
            variant="ghost"
            title={t('TAKE_PHOTO')}
            textStyle={styles.footerButtonText}
          />
        </View>
      )}

      {data?.questionAnswerImages && data?.questionAnswerImages?.length > 0 && (
        <View style={styles.previewImagesContainer}>
          {data?.questionAnswerImages?.map(image => (
            <View key={image?.id}>
              <FastImage
                source={{ uri: image?.uri }}
                style={styles.previewImage}
                resizeMode="contain"
              />
              <Button
                variant="ghost"
                style={styles.previewImageRemoveButton}
                onPress={() => handleRemoveImage(image?.id || '')}
                textStyle={styles.previewImageRemoveButtonText}
                title=""
                size="small"
              />
            </View>
          ))}
        </View>
      )}
    </View>
  );
};

const styles = StyleSheet.create({
  container: {
    backgroundColor: colors.white,
    paddingVertical: 12,
    borderRadius: 8,
    borderWidth: 0.5,
    borderColor: colors.line,
  },
  questionContainer: {
    paddingHorizontal: 8,
  },
  question: {
    fontSize: 16,
    fontWeight: 'bold',
    color: colors.mainColorPrimary,
    lineHeight: 24,
    letterSpacing: -0.2,
    marginBottom: 8,
    paddingHorizontal: 8,
  },
  optionContainer: {
    marginBottom: 4,
    paddingVertical: 2,
    paddingHorizontal: 16,
    borderRadius: 0,
    minHeight: 0,
    gap: 8,
  },
  checkboxContainer: {
    borderRadius: SCREEN_WIDTH,
    width: SCREEN_WIDTH * 0.05,
    height: SCREEN_WIDTH * 0.05,
    backgroundColor: colors.white,
    borderWidth: 2,
    borderColor: colors.mainColorPrimary,
    alignItems: 'center',
    justifyContent: 'center',
  },
  checkboxChecked: {
    width: SCREEN_WIDTH * 0.035,
    height: SCREEN_WIDTH * 0.035,
    backgroundColor: colors.mainColorPrimary,
    borderRadius: SCREEN_WIDTH,
  },
  optionText: {
    fontSize: 16,
    fontWeight: '400',
    color: colors.black,
    lineHeight: 24,
    letterSpacing: -0.2,
    flex: 1,
  },
  emptyText: {
    fontSize: 14,
    color: colors.black || '#666',
    fontStyle: 'italic',
    textAlign: 'center',
    paddingVertical: 16,
  },
  footerContainer: {
    flexDirection: 'row',
    justifyContent: 'space-between',
    alignItems: 'center',
    gap: 8,
    marginTop: 12,
    paddingHorizontal: 16,
  },
  footerButton: {
    flex: 1,
    backgroundColor: colors.mainColorPrimary,
  },
  disabledButton: {
    backgroundColor: colors.disableGray2,
    borderColor: colors.disableGray2,
  },
  footerButtonText: {
    color: colors.white,
  },
  previewImagesContainer: {
    flexDirection: 'row',
    flexWrap: 'wrap',
    gap: 12,
    marginTop: 16,
    alignItems: 'center',
    paddingHorizontal: 16,
  },
  previewImage: {
    width: (SCREEN_WIDTH - 118) / 5,
    height: (SCREEN_WIDTH - 100) / 5,
    borderRadius: 4,
    borderWidth: 0.5,
    borderColor: colors.line,
    overflow: 'hidden',
  },
  previewImageRemoveButton: {
    position: 'absolute',
    top: -SCREEN_WIDTH * 0.02,
    right: -SCREEN_WIDTH * 0.02,
    backgroundColor: colors.red,
    borderRadius: SCREEN_WIDTH,
    paddingVertical: 2,
    paddingHorizontal: 6,
    justifyContent: 'center',
    alignItems: 'center',
    minHeight: 0,
  },
  previewImageRemoveButtonText: {
    color: colors.white,
    fontWeight: 'bold',
    lineHeight: 0,
  },
  questionError: {
    backgroundColor: colors.alertBgError,
    borderRadius: 4,
    paddingVertical: 4,
  },
});

export default Item;
