import React from 'react';
import { View, FlatList } from 'react-native';
import { useAppTranslation } from '../../hooks/useAppTranslation';
import { useSafeAreaInsets } from 'react-native-safe-area-context';
import { HeaderBase } from 'tradewize-component';
import { navigationRef } from '../../services/NavigationService';
import CompanyCard from '../../components/CompanyCard';
import { useCompany } from '../../hooks/useCompany';
import { BaseContainer, Text, Button } from 'tradewize-component';
import { styles } from './styles';

export default function RegisterCompanyListScreen() {
  const { t } = useAppTranslation();

  const {
    companyAndRoleData,
    selectedCompanyIds,
    handleToggleSelect,
    showModalConfirmRegister,
  } = useCompany();

  const insets = useSafeAreaInsets();

  const renderFooter = () => {
    if (selectedCompanyIds.length === 0) {
      return null;
    }

    return (
      <View
        style={[
          styles.footerContainer,
          { paddingBottom: Math.max(insets.bottom, 16) },
        ]}
      >
        <Button
          style={styles.registerButton}
          onPress={showModalConfirmRegister}
        >
          <Text style={styles.registerButtonText}>
            {t('REGISTER')} ({selectedCompanyIds.length})
          </Text>
        </Button>
      </View>
    );
  };

  return (
    <BaseContainer style={styles.container}>
      <HeaderBase
        containerStyle={styles.header}
        title={t('REGISTER_COMPANY')}
        onPressBack={() => {
          navigationRef.goBack();
        }}
      />

      <FlatList
        data={companyAndRoleData}
        keyExtractor={item => item.companyId}
        renderItem={({ item }) => (
          <CompanyCard
            company={item}
            isSelected={selectedCompanyIds.includes(item.companyId)}
            onToggleSelect={handleToggleSelect}
          />
        )}
        style={styles.content}
        contentContainerStyle={[
          styles.scrollContent,
          styles.scrollContentWithFooter,
        ]}
        showsVerticalScrollIndicator={false}
        extraData={[companyAndRoleData, selectedCompanyIds]}
      />

      <View style={styles.footerContainer}>{renderFooter()}</View>
    </BaseContainer>
  );
}
