import { FileType } from '../types';
import { FileInfo } from '../types';
import { MediaItem } from 'src/types/model/Work';

const isValidUrl = (url: string): boolean => {
  try {
    new URL(url);
    return true;
  } catch {
    return false;
  }
};

export const convertMediaItemToFileInfo = (media: MediaItem): FileInfo => {
  const url = media?.url || media?.uri || '';

  if (!url) {
    return {
      uri: '',
      type: 'unknown',
      title: 'unknown',
    };
  }

  const isYoutube = url.includes('youtube.com') || url.includes('youtu.be');
  const fileType = getFileType(url, media?.fileExtension);

  const type = isYoutube ? 'youtube' : fileType || 'unknown';

  return {
    uri: url,
    type,
    title: media?.fileName || 'unknown',
    isYoutube,
  };
};

