export const getFileInfo = (file: any): FileInfo => {
  if (!file) {
    return {
      uri: '',
      type: 'unknown',
      title: 'unknown',
    };
  }

  if (
    typeof file === 'string' ||
    ('url' in file && typeof file.url === 'string')
  ) {
    const url = typeof file === 'string' ? file : file.url;
    if (!isValidUrl(url)) {
      return {
        uri: '',
        type: 'unknown',
        title: 'unknown',
      };
    }
  }

  return convertMediaItemToFileInfo(file as MediaItem);
};

export const validateFile = (fileInfo: FileInfo): boolean => {
  if (!fileInfo.uri) return false;

  if (fileInfo.type !== 'unknown' && !fileInfo.uri.startsWith('http')) {
    return true;
  }

  return isValidUrl(fileInfo.uri);
};

export const getFileType = (uri?: string, fileExtension?: string): FileType => {
  if (!uri) return 'unknown';

  // Check for YouTube links first
  const lowerCaseUri = uri.toLowerCase();
  if (
    lowerCaseUri.includes('youtube.com') ||
    lowerCaseUri.includes('youtu.be')
  ) {
    return 'youtube';
  }

  // If fileExtension is provided, use it for type detection
  if (fileExtension) {
    const lowerCaseExt = fileExtension.toLowerCase();

    if (
      lowerCaseExt.endsWith('.jpg') ||
      lowerCaseExt.endsWith('.jpeg') ||
      lowerCaseExt.endsWith('.png') ||
      lowerCaseExt.endsWith('.gif') ||
      lowerCaseExt.endsWith('.webp') ||
      lowerCaseExt.endsWith('.heic') ||
      lowerCaseExt.endsWith('.heif')
    ) {
      return 'photo';
    }

    if (lowerCaseExt.endsWith('.mp4') || lowerCaseExt.endsWith('.mov')) {
      return 'video';
    }

    if (
      lowerCaseExt.endsWith('.pdf') ||
      lowerCaseExt.endsWith('.doc') ||
      lowerCaseExt.endsWith('.docx') ||
      lowerCaseExt.endsWith('.xls') ||
      lowerCaseExt.endsWith('.xlsx') ||
      lowerCaseExt.endsWith('.csv')
    ) {
      return 'document';
    }
  }

  // If no fileExtension or couldn't determine type from it, check URI
  // First check for exact file extensions in the URI
  if (lowerCaseUri.endsWith('.mp4') || lowerCaseUri.endsWith('.mov')) {
    return 'video';
  }

  if (
    lowerCaseUri.endsWith('.jpg') ||
    lowerCaseUri.endsWith('.jpeg') ||
    lowerCaseUri.endsWith('.png') ||
    lowerCaseUri.endsWith('.gif') ||
    lowerCaseUri.endsWith('.webp')
  ) {
    return 'photo';
  }

  if (
    lowerCaseUri.endsWith('.pdf') ||
    lowerCaseUri.endsWith('.doc') ||
    lowerCaseUri.endsWith('.docx') ||
    lowerCaseUri.endsWith('.xls') ||
    lowerCaseUri.endsWith('.xlsx') ||
    lowerCaseUri.endsWith('.csv')
  ) {
    return 'document';
  }

  // Check for AWS-style URLs with query parameters
  if (
    lowerCaseUri.includes('.jpg?') ||
    lowerCaseUri.includes('.jpeg?') ||
    lowerCaseUri.includes('.png?') ||
    lowerCaseUri.includes('.gif?') ||
    lowerCaseUri.includes('.webp?')
  ) {
    return 'photo';
  }

  if (lowerCaseUri.includes('.mp4?') || lowerCaseUri.includes('.mov?')) {
    return 'video';
  }

  if (
    lowerCaseUri.includes('.pdf?') ||
    lowerCaseUri.includes('.doc?') ||
    lowerCaseUri.includes('.docx?') ||
    lowerCaseUri.includes('.xls?') ||
    lowerCaseUri.includes('.xlsx?') ||
    lowerCaseUri.includes('.csv?')
  ) {
    return 'document';
  }

  return 'document';
};
