import React from 'react';
import { View, FlatList } from 'react-native';
import { Text } from 'tradewize-component';
import { useDispatch } from 'react-redux';
import { useAppTranslation } from '../../hooks/useAppTranslation';
import { styles } from './styles';
import { logger } from '../../utils';
import { getCategoryName } from '../../helper/RoleTypeScreen.helper';
import CategoryDropdown from './components/categoryDropdown';
import { CategoryGroup, RoleTypeItemProps } from '../../types/roleTypes';
import { Button } from 'tradewize-component';
import FastImage from 'react-native-fast-image';
import { thumbnail } from '../../assets/images';

export const RoleTypeItem = ({ item }: RoleTypeItemProps) => {
  const { i18n, t } = useAppTranslation();
  const dispatch = useDispatch();

  const handleVideoPress = async () => {
    try {
      // Get video URL based on current language, fallback to videoLink
      const currentLang = i18n?.language || 'en';
      const videoUrl = item?.videoUrl?.[currentLang] || item?.videoLink;

      if (!videoUrl) return;

      const redux = await import('auth/redux');
      dispatch(
        redux.actionContainer.showVideoModal({
          initialSubtitle: i18n?.language || 'en',
          source: videoUrl,
          subtitle: item?.subTitle?.[currentLang] || {},
          onClose: () => {
            dispatch(redux.actionContainer.hideVideoModal());
          },
        }),
      );
    } catch (error) {
      logger('error handleVideoPress', error);
    }
  };

  // Group checklists by category
  const groupedByCategory = React.useMemo(() => {
    const groups: { [key: number]: CategoryGroup } = {};

    (item?.checklists ?? []).forEach(checklist => {
      // Ensure category is a valid number
      const category = Number(checklist?.category);

      const categoryName = getCategoryName(category);

      // Initialize group if it doesn't exist
      if (!groups[category]) {
        groups[category] = {
          category,
          categoryName,
          checklists: [],
        };
      }

      // Add checklist to the correct category group
      groups[category]?.checklists?.push(checklist);
    });

    // Sort by category number to ensure consistent order
    const sortedGroups = Object.values(groups).sort(
      (a, b) => a.category - b.category,
    );

    return sortedGroups;
  }, [item.checklists]);

  const hasChecklists = item?.checklists && item?.checklists?.length > 0;

  return (
    <View style={styles.roleContainer}>
      <View style={styles.roleHeader}>
        <View style={styles.roleHeaderContent}>
          <Text style={styles.roleTitle}>
            {item?.companyName} - {item?.name}
          </Text>
          {item?.scope && (
            <View style={styles.scopeContainer}>
              <Text style={styles.scopeLabel}>{t('SCOPE')}:</Text>
              <Text style={styles.scopeValue}>{item?.scope}</Text>
            </View>
          )}
        </View>
      </View>

      {hasChecklists && (
        <View style={styles.roleContent}>
          <FlatList
            data={groupedByCategory}
            renderItem={({ item: categoryGroup }) => (
              <CategoryDropdown categoryGroup={categoryGroup} />
            )}
            keyExtractor={categoryGroup =>
              `category-${categoryGroup?.category}`
            }
            scrollEnabled={false}
            showsVerticalScrollIndicator={false}
          />
        </View>
      )}

      {/* Video Player at bottom */}
      {item?.videoLink && (
        <Button
          style={styles.videoContainer}
          onPress={handleVideoPress}
          activeOpacity={0.8}
        >
          <FastImage
            source={
              item?.videoThumbnai ? { uri: item?.videoThumbnai } : thumbnail
            }
            style={styles.videoThumbnailImage}
            resizeMode="cover"
          />
        </Button>
      )}
    </View>
  );
};
