  // const [activeTab, setActiveTab] = useState(TABS[0].id);

  const handleClose = async () => {
    try {
      const redux = await import('auth/redux');
      dispatch(redux.actionContainer?.hideCustomModal());
      dispatch(redux.actionContainer?.setIsShowDueVideo(true));
    } catch (error) {
      logger('error handleClose', error);
    }
  };

  const handleGoNow = async () => {
    try {
      await handleClose();
      setTimeout(() => {
        NavigationService.navigate(SCREEN_ENUM.SystemQualityGuideScreen);
      }, 350);
    } catch (error) {
      logger('error handleGoNow', error);
    }
  };

  // const handleScrollToTop = (tabId: number) => {
  //   if (tabId === activeTab) {
  //     return;
  //   }

  //   if (tabId === 1) {
  //     setTimeout(() => {
