  //         itemIndex: firstItemPositions?.jobType0?.itemIndex || 0,
  //       });
  //     }, 100);
  //   } else {
  //     setTimeout(() => {
  //       sectionRef.current?.scrollToLocation({
  //         sectionIndex: firstItemPositions?.jobType1?.sectionIndex || 0,
  //         itemIndex: firstItemPositions?.jobType1?.itemIndex || 0,
  //       });
  //     }, 100);
  //   }
  // };

  const renderEmptyComponent = () => {
    return (
      <View style={styles.emptyContainer}>
        {isLoading ? (
          <ActivityIndicator size="large" color={colors.mainColorPrimary} />
        ) : (
          <Text style={styles.emptyText}>
            {errorApi ? errorApi : t('NO_DUE_VIDEOS')}
          </Text>
        )}
      </View>
    );
  };

  // Filter out empty sections
  const validSections = (dataTutorialWarning || []).filter(
    section => section?.data?.length > 0,
  );

  // const isDisabledButton = (tabIndex: number) => {
  //   if (
  //     (tabIndex && validSections[tabIndex]?.data?.length === 0) ||
  //     !validSections[tabIndex]
  //   ) {
  //     return true;
