import React, { useCallback } from 'react';
import { ScrollView } from 'react-native';
import { BaseContainer, Button, HeaderBase } from 'tradewize-component';
import { useAppTranslation } from '../../hooks/useAppTranslation';
import { navigationRef } from '../../services/NavigationService';
import { styles } from './styles';
import { useRoute, useFocusEffect } from '@react-navigation/native';
import Question from './components/Question';
import useRegisterCompanyTestScreen from '../../hooks/useRegisterCompanyTestScreen';
import NecessaryTools from './components/NecessaryTools';

interface RouteParams {
  selectedCompanies?: Array<{ companyId: string; companyName: string }>;
  cleanData?: () => void;
}

export default function RegisterCompanyTestScreen() {
  const { t } = useAppTranslation();
  const route = useRoute();

  const { selectedCompanies: routeCompanies, cleanData } = (route.params ||
    {}) as RouteParams;

  const {
    scrollViewRef,
    itemRefs,
    companyAndRoleData,
    errorQuestion,
    selectedCompanies,
    selectedCompanyIds,
    companyAnswers,
    images,
    setImages,
    handleAnswerChange,
    handleSubmit,
    cleanupOnUnmount,
    dispatch,
  } = useRegisterCompanyTestScreen({
    selectedCompanies: routeCompanies,
    cleanData,
  });

  // Cleanup state when navigating away
  useFocusEffect(
    useCallback(() => {
      return () => {
        cleanupOnUnmount();
      };
    }, [cleanupOnUnmount]),
  );

  const handleSubmitPress = useCallback(async () => {
    handleSubmit();
  }, [handleSubmit]);

  return (
    <BaseContainer style={styles.container}>
      <HeaderBase
        containerStyle={styles.header}
        title={t('REGISTER_COMPANY_TEST')}
        onPressBack={() => {
          navigationRef.goBack();
        }}
      />

      <ScrollView
        ref={scrollViewRef}
        nestedScrollEnabled
        style={styles.scrollView}
        contentContainerStyle={styles.scrollContent}
      >
        <NecessaryTools
          selectedCompanies={selectedCompanies}
          companyAnswers={companyAnswers}
          images={images}
          setImages={setImages}
          handleAnswerChange={handleAnswerChange}
          errorQuestion={errorQuestion}
          itemRefs={itemRefs}
          t={t}
          dispatch={dispatch}
        />
        <Question
          itemRefs={itemRefs}
          selectedCompanyIds={selectedCompanyIds}
          companyAndRoleData={companyAndRoleData}
        />
      </ScrollView>

      <Button
        variant="primary"
        size="large"
        style={styles.btnSubmit}
        title={t('SUBMIT')}
        onPress={handleSubmitPress}
      />
    </BaseContainer>
  );
}
