import { Linking } from 'react-native';
import { dispatch } from '.';
import { ChecklistCategory, FileItem, VideoItem } from '../types/roleTypes';
import { logger } from '../utils';

export const getCategoryName = (category: ChecklistCategory): string => {
  switch (category) {
    case ChecklistCategory.JobStart:
      return 'JOB_START';
    case ChecklistCategory.General:
      return 'GENERAL';
    case ChecklistCategory.EndOfDay:
      return 'END_OF_DAY';
    case ChecklistCategory.Material:
      return 'MATERIAL';
    case ChecklistCategory.MaterialDelivery:
      return 'MATERIAL_DELIVERY';
    default:
      return 'UNKNOWN';
  }
};

export const normalizeFileExtension = (
  fileExtension: string | undefined,
): string => {
  if (!fileExtension) return '';
  let ext = fileExtension?.trim()?.toLowerCase();
  if (ext && !ext.startsWith('.')) {
    ext = '.' + ext?.toString();
  }
  return ext;
};

export const isImageFile = (fileExtension: string | undefined): boolean => {
  const normalizedExt = normalizeFileExtension(fileExtension);
  if (!normalizedExt) return false;

  const imageExts = ['.jpg', '.jpeg', '.png', '.gif'];
  return imageExts.some(imgExt => normalizedExt === imgExt);
};

export const handleFilePress = async (file: FileItem) => {
  try {
    if (!file?.url) return;

    await Linking.openURL(file?.url);
  } catch (error) {
    logger('error handleFilePress', error);
  }
};

export const handleVideoPress = async (video: VideoItem, i18n: any) => {
  try {
    if (!video?.url) return;

    const redux = await import('auth/redux');
    dispatch(
      redux.actionContainer.showVideoModal({
        initialSubtitle: i18n?.language || 'en',
        source: video.url,
        subtitle: {},
        onClose: () => {
          dispatch(redux.actionContainer.hideVideoModal());
        },
      }),
    );
  } catch (error) {
    logger('error handleVideoPress', error);
  }
};
