import { AppState, AppStateStatus } from 'react-native';
import NetInfo from '@react-native-community/netinfo';
import { logger } from '../utils';

export enum SocketStatus {
  IDLE = 'idle',
  CONNECTING = 'connecting',
  CONNECTED = 'connected',
  DISCONNECTED = 'disconnected',
  RECONNECTING = 'reconnecting',
  ERROR = 'error',
}

export enum SocketChatMessage {
  NEW_MESSAGE = 'newMessage',
  SEND_MESSAGE = 'sendMessage',
  MESSAGE_DELIVERED = 'messageDelivered',
  ADD_REACTION = 'addReaction',
  REACTION_ADDED = 'reactionAdded',
  USER_TYPING = 'userTyping',
  ERROR = 'error',
}

export class ChatSocketService {
  private static instance: ChatSocketService;
  private socket: WebSocket | null = null;
  private reconnectTimeout: any | null = null;
  private messageListener: ((data: any) => void) | null = null;
  private connectCallback: (() => void) | null = null;
  private onError: ((error: any) => void) | null = null;
  private onConnecting: (() => void) | null = null;
  private appState: AppStateStatus = 'active';
  private chatId: string = '';
  private isManualClose = false;
  private isConnectedNetwork = true;

  // retry state
  private isConnecting = false;

  // trng thi socket
  private status: SocketStatus = SocketStatus.IDLE;
  private statusListeners: ((status: SocketStatus) => void)[] = [];

  private constructor() {
    AppState.addEventListener('change', this.handleAppStateChange);
    NetInfo.addEventListener(this.handleNetworkChange);
  }

  public static getInstance(chatId: string): ChatSocketService {
    if (!ChatSocketService.instance) {
      ChatSocketService.instance = new ChatSocketService();
    }
    ChatSocketService.instance.chatId = chatId;
    return ChatSocketService.instance;
  }

  public connect(
    onMessage?: (data: any) => void,
    onConnected?: () => void,
    onError?: (error: any) => void,
    onConnecting?: () => void,
  ) {
    if (this.socket) {
      this.close();
    }

    this.isManualClose = false;
    this.messageListener = onMessage || null;
    this.connectCallback = onConnected || null;
    this.onError = onError || null;
    this.onConnecting = onConnecting || null;

    try {
      this.isConnecting = true;
      this.updateStatus(SocketStatus.CONNECTING);
      this.onConnecting?.();
      this.socket = new WebSocket(
        'wss://ja2uhgmekf.execute-api.us-east-1.amazonaws.com/production',
      );
    } catch (err) {
      this.isConnecting = false;
      this.updateStatus(SocketStatus.ERROR);
      console.error('[WS]  Failed to create socket:', err);
      return;
    }

    this.socket.onopen = () => {
      this.isConnecting = false;
      logger('[WS]  Connected');
      // this.startPing();
      this.updateStatus(SocketStatus.CONNECTED);
      this.connectCallback?.();
      this.sendInitConnection();
    };

    this.socket.onmessage = event => {
      try {
        const data = JSON.parse(event.data);
        if (!data?.type) return;
        if (data.action === 'pong') return;
        logger('[WS]  Message:', data);
        this.messageListener?.(data);
      } catch {
        if (
          typeof event.data === 'string' &&
          event.data.includes('"action":"ping"')
        )
          return;
        console.warn('[WS]  Failed to parse message:', event.data);
      }
    };

    this.socket.onerror = (error: any) => {
      this.isConnecting = false;
      this.updateStatus(SocketStatus.ERROR);
      if (this.isManualClose) return;
      if (error?.message) {
        console.warn('[WS]  Socket error:', error.message);
      } else {
        logger('[WS]  Socket error event (no details)');
      }
      this.onError?.(error);
    };

    this.socket.onclose = event => {
      this.isConnecting = false;
      // this.stopPing();
      this.sendDisconnect();
      if (this.isManualClose) {
        logger('[WS]  Socket closed manually');
        this.updateStatus(SocketStatus.DISCONNECTED);
      } else {
        console.warn('[WS]  Disconnected:', event.reason || 'no reason');
        this.updateStatus(SocketStatus.DISCONNECTED);
        this.scheduleReconnect();
      }
    };
  }

  public send(data: any) {
    if (this.socket?.readyState === WebSocket.OPEN) {
      try {
        logger('[WS]  Sending data:', data);
        this.socket.send(JSON.stringify(data));
      } catch (err) {
        console.error('[WS]  Failed to send data:', err);
      }
    } else {
      console.warn('[WS]  Socket not ready. Message dropped:', data);
    }
  }

  public isConnectingState() {
    return this.isConnecting;
  }

  public getStatus() {
    return this.status;
  }

  public subscribeStatus(listener: (status: SocketStatus) => void) {
    this.statusListeners.push(listener);
    return () => {
      this.statusListeners = this.statusListeners.filter(l => l !== listener);
    };
  }

  private updateStatus(newStatus: SocketStatus) {
    if (this.status !== newStatus) {
      this.status = newStatus;
      this.statusListeners.forEach(cb => cb(newStatus));
    }
  }

  private async sendDisconnect() {
    try {
      const redux = await import('auth/redux');
      if (this.isConnected()) {
        const msg = { action: '$disconnect' };
        this.socket?.send(JSON.stringify(msg));
        this.socket?.send(
          JSON.stringify({
            action: SocketChatMessage.USER_TYPING,
            isTyping: false,
            userId: redux.store.getState().auth.userProfile?.accountId,
            token: redux.store.getState().auth.token,
          }),
        );
        logger('[WS]  Sent disconnect:', msg);
      }
    } catch (err) {
      console.error('[WS]  Failed to send disconnect:', err);
    }
  }

  public close() {
    // this.stopPing();
    this.clearReconnect();
    this.isManualClose = true;

    this.sendDisconnect();

    if (this.socket) {
      this.socket.onopen = null;
      this.socket.onclose = null;
      this.socket.onmessage = null;
      this.socket.onerror = null;
      try {
        this.socket.close();
      } catch {}
    }

    this.socket = null;
    this.messageListener = null;
    this.connectCallback = null;
    this.onError = null;
    this.chatId = '';
    this.updateStatus(SocketStatus.DISCONNECTED);
    logger('[WS]  Socket closed manually');
  }

  public isConnected(): boolean {
    return this.socket?.readyState === WebSocket.OPEN;
  }

  // private startPing() {
  //   this.stopPing();
  //   this.pingInterval = setInterval(() => {
  //     try {
  //       if (this.isConnected()) {
  //         this.socket?.send(JSON.stringify({ action: 'ping' }));
  //         logger('[WS]  Ping sent');
  //       }
  //     } catch (err) {
  //       console.error('[WS]  Ping error:', err);
  //     }
  //   }, 25000);
  // }

  // private stopPing() {
  //   if (this.pingInterval) {
  //     clearInterval(this.pingInterval);
  //     this.pingInterval = null;
  //   }
  // }

  private scheduleReconnect() {
    if (this.reconnectTimeout || this.messageListener === null) return;
    if (!this.isConnectedNetwork) {
      logger('[WS]  No internet, skip reconnect');
      return;
    }

    this.updateStatus(SocketStatus.RECONNECTING);

    this.reconnectTimeout = setTimeout(() => {
      this.reconnectTimeout = null;
      this.connect(
        this.messageListener!,
        this.connectCallback!,
        this.onError || undefined,
      );
    }, 3000);
  }

  private clearReconnect() {
    if (this.reconnectTimeout) {
      clearTimeout(this.reconnectTimeout);
      this.reconnectTimeout = null;
    }
  }

  private async sendInitConnection() {
    try {
      const redux = await import('auth/redux');
      const token = redux.store.getState().auth.token;
      const userId = redux.store.getState().auth.userProfile?.accountId;
      if (!token || !userId) {
        console.warn('[WS]  Missing auth data, cannot send initConnection');
        return;
      }

      const initMessage = {
        action: 'initConnection',
        token,
        userId,
        chatId: this.chatId,
      };

      this.send(initMessage);
      logger('[WS]  Sent initConnection:', initMessage);
    } catch (error) {
      console.error('[WS]  Failed to send initConnection:', error);
    }
  }

  private handleAppStateChange = (nextAppState: AppStateStatus) => {
    if (!this.chatId) return;

    if (
      this.appState === 'active' &&
      nextAppState.match(/inactive|background/)
    ) {
      logger('[WS]  App in background  stop ping');
      this.sendDisconnect();
      // this.stopPing();
    }

    if (
      this.appState.match(/inactive|background/) &&
      nextAppState === 'active'
    ) {
      logger('[WS]  App in foreground  resume ping');
      if (this.isConnected()) {
        // this.startPing();
      } else if (this.messageListener) {
        this.connect(
          this.messageListener,
          this.connectCallback || undefined,
          this.onError || undefined,
        );
      }
    }

    this.appState = nextAppState;
  };

  private handleNetworkChange = (state: any) => {
    const isOnline = !!state.isConnected && !!state.isInternetReachable;
    if (isOnline && !this.isConnectedNetwork) {
      logger('[WS]  Network restored  reconnecting socket');
      if (!this.isConnected() && this.messageListener) {
        this.connect(
          this.messageListener,
          this.connectCallback || undefined,
          this.onError || undefined,
        );
      }
    }
    this.isConnectedNetwork = isOnline;
  };
}
