import { ActivityIndicator, FlatList, Image, View } from 'react-native';
import { BaseContainer, Button, HeaderBase } from 'tradewize-component';
import { styles } from './styles';
import { colors } from '../../theme';
import { NavigationService } from '../../services/NavigationService';
import { useAppTranslation } from '../../hooks/useAppTranslation';
import { iconChecklist, playInstructionalVideos } from '../../assets/images';
import { useDispatch, useSelector } from 'react-redux';
import { logger } from '../../utils';
import { useRoute } from '@react-navigation/native';
import { useTheTest } from '../../hooks/useTheTest';
import Item, { Option, QuestionItemProps } from './components/Item';
import { validateQuizItem } from '../../helper/TakeTheTest';
import { useRef } from 'react';

const TakeTheTest = () => {
  const flatListRef = useRef<FlatList>(null);
  const dispatch = useDispatch();
  const { t, i18n } = useAppTranslation();
  const route = useRoute<any>();
  const { trainingId, companyId } = route?.params || {};
  useTheTest({ trainingId });

  const dataMedia = useSelector((state: any) => state.app.dataMedia);
  const allQuestionsTrainingTest = useSelector(
    (state: any) => state.training.allTrainingTest,
  );
  const saveTrainingTest = useSelector(
    (state: any) => state.training.saveTrainingTest,
  );

  const handlePlayInstructionalVideos = async () => {
    try {
      const redux = await import('auth/redux');
      dispatch(
        redux.actionContainer.showVideoModal({
          initialSubtitle: i18n?.language || 'en',
          source:
            dataMedia?.['TRAINING_SESSION_SCREEN' as keyof typeof dataMedia]
              ?.url || '',
          subtitle:
            dataMedia?.['TRAINING_SESSION_SCREEN' as keyof typeof dataMedia]
              ?.subtitle || {},
          onClose: () => {
            dispatch(redux.actionContainer.hideVideoModal());
          },
        }),
      );
    } catch (error) {
      logger('error handlePlayInstructionalVideos', error);
    }
  };

  const handleSubmit = async () => {
    try {
      const redux = await import('auth/redux');

      const dataValid = validateQuizItem(allQuestionsTrainingTest?.data || []);
      if (dataValid.length > 0) {
        return dispatch(
          redux.actionContainer.showAlertAsync({
            title: t('WARNING'),
            message: t('PLEASE_ANSWER_ALL_QUESTIONS_CHECK_BOX_OR_FILE_UPLOAD'),
            type: 'warning',
            buttonText: t('CONFIRM'),
            titleStyle: {
              color: colors.black,
            },
            buttonStyle: {
              backgroundColor: colors.mainColorPrimary,
            },
            onClose: () => {
              dispatch(redux.actionContainer.hideAlertAsync());
            },
          }),
        );
      }

      const arrQuestionFailed = allQuestionsTrainingTest?.data?.filter(
        (item: QuestionItemProps) =>
          item.options?.some(
            (option: Option) => option.isChecked && !option.isCorrect,
          ),
      );

      const arrQuestionSuccess = allQuestionsTrainingTest?.data?.filter(
        (item: QuestionItemProps) =>
          item?.options?.some(
            (option: Option) => option.isChecked && option.isCorrect,
          ),
      );

      const newAllQuestionsTrainingTest = allQuestionsTrainingTest?.data?.map(
        (item: QuestionItemProps) => {
          if (
            arrQuestionFailed?.some(
              (question: QuestionItemProps) => question.id === item.id,
            )
          ) {
            return {
              ...item,
              isError: true,
            };
          }
          return {
            ...item,
            isError: false,
          };
        },
      );

      dispatch(
        redux.actionContainer.updateAllTrainingTest(
          newAllQuestionsTrainingTest,
        ),
      );

      if (arrQuestionFailed?.length > 0) {
        const firstFailedIndex = allQuestionsTrainingTest?.data?.findIndex(
          (item: QuestionItemProps) =>
            item.options?.some(
              (option: Option) => option.isChecked && !option.isCorrect,
            ),
        );

        setTimeout(() => {
          flatListRef.current?.scrollToIndex({
            index: firstFailedIndex,
            animated: true,
          });
        }, 350);

        return dispatch(
          redux.actionContainer.showAlertAsync({
            title: i18n.t('TEST_FAILED'),
            arrayMessage: [
              `${i18n.t('RESULT')}: ${Math.floor(
                (arrQuestionSuccess?.length /
                  allQuestionsTrainingTest?.data?.length) *
                  100,
              )}%`,
              `${i18n.t('TOTAL_QUESTIONS_NUMBER', {
                number: allQuestionsTrainingTest?.data?.length,
              })}`,
              `${i18n.t('WRONG_ANSWERS_NUMBER', {
                number: arrQuestionFailed?.length,
              })}`,
              `${i18n.t('RIGHT_ANSWERS_NUMBER', {
                number: arrQuestionSuccess?.length,
              })}`,
            ],
            titleStyle: {
              width: '100%',
              backgroundColor: colors.alertColorError,
              borderRadius: 8,
              paddingVertical: 8,
              fontSize: 16,
              fontWeight: '600',
              lineHeight: 24,
              textAlign: 'center',
              color: colors.white,
            },
            buttonStyle: {
              backgroundColor: colors.mainColorPrimary,
            },
            buttonText: i18n.t('RETAKE_THE_TEST'),
            isShowIcon: false,
            type: 'success',
          }),
        );
      }

      const formData = new FormData();
      formData.append('TrainingId', trainingId);
      allQuestionsTrainingTest?.data?.forEach(
        (item: QuestionItemProps, index: number) => {
          formData.append(`Answers[${index}].questionId`, item.id);
          const optionsChecked = item.options.filter(
            option => option.isChecked,
          );
          if (optionsChecked.length > 0) {
            formData.append(
              `Answers[${index}].selectedOptionIds`,
              optionsChecked[0].id,
            );
          }

          if (
            item?.questionAnswerImages &&
            item?.questionAnswerImages?.length > 0
          ) {
            item.questionAnswerImages.forEach(image => {
              formData.append(`Answers[${index}].Files`, {
                uri: image.uri,
                name: image.filename,
                type: image.type,
              });
            });
          }
        },
      );

      dispatch(
        redux.actionContainer.saveTrainingTestAsync({
          formData,
          navigationService: NavigationService,
          companyId,
          trainingId,
        }),
      );
    } catch (error) {
      logger('error handleSubmit', error);
    }
  };

  return (
    <View style={styles.container}>
      <BaseContainer
        backgroundColor={colors.mainColorPrimary}
        statusBarStyle="light-content"
        backgroundBottomSafeAreaColor={colors.white}
        style={styles.container}
      >
        <HeaderBase
          title={t('TAKE_THE_TEST')}
          onPressBack={() => NavigationService.goBack()}
          containerStyle={styles.headerContainer}
          titleStyle={styles.headerTitle}
          colorIconLeft={colors.white}
          rightActions={[
            {
              icon: (
                <Image
                  source={playInstructionalVideos}
                  style={styles.playInstructionalVideos}
                />
              ),
              onPress: handlePlayInstructionalVideos,
            },
          ]}
        />

        <FlatList
          ref={flatListRef}
          data={allQuestionsTrainingTest?.data}
          style={styles.flatList}
          contentContainerStyle={styles.contentContainer}
          renderItem={({ item, index }) => <Item data={item} index={index} />}
          keyExtractor={(item, index) => item?.id || `question-${index}`}
        />

        <Button
          variant="primary"
          style={styles.submitButton}
          rightIcon={
            <View style={styles.submitButtonIconContainer}>
              <View style={styles.submitButtonIconSeparator} />
              <Image source={iconChecklist} style={styles.submitButtonIcon} />
            </View>
          }
          title={t('SUBMIT')}
          onPress={handleSubmit}
        />
      </BaseContainer>
      {saveTrainingTest?.loading && (
        <View style={styles.loadingContainer}>
          <ActivityIndicator size="large" color={colors.mainColorPrimary} />
        </View>
      )}
    </View>
  );
};

export default TakeTheTest;
