import React, {
  useEffect,
  useMemo,
  useRef,
  useState,
  useCallback,
} from 'react';
import { ActivityIndicator, Text, TextInput, View } from 'react-native';
import { SvgXml } from 'react-native-svg';
import { IconPen } from '../../../../assets/svg';
import colors from '../../../../theme/colors';
import { useAppTranslation } from '../../../../hooks/useAppTranslation';
import { useDispatch, useSelector } from 'react-redux';
import { formatInput } from '../../../../helper/formatNumber.helper';
import { Button } from 'tradewize-component';
import { validateSsnValue } from '../../../../helper/PaymentInfoW9.helper';
import { styles } from './styles';

export function SsnField() {
  const { t } = useAppTranslation();
  const ssn = useSelector((state: any) => state.auth.userProfile.ssnOrITIN);
  const dispatch = useDispatch();
  const [value, setValue] = useState(formatInput(ssn || '', 'ssn'));
  const [isEditing, setIsEditing] = useState(!ssn);
  const [showSave, setShowSave] = useState(Boolean(ssn));
  const [saving, setSaving] = useState(false);
  const [error, setError] = useState<string>('');
  const inputRef = useRef<TextInput>(null);

  useEffect(() => {
    setValue(formatInput(ssn || '', 'ssn'));
    setIsEditing(!ssn);
  }, [ssn]);

  const displayValue = useMemo(
    () => (isEditing ? value : formatInput(value || ssn, 'ssn')),
    [isEditing, value, ssn],
  );

  const handleChange = useCallback((text: string) => {
    setError('');
    setValue(formatInput(text, 'ssn'));
  }, []);

  const handleEdit = useCallback(() => {
    setIsEditing(true);
    setShowSave(true);
    setTimeout(() => inputRef.current?.focus(), 10);
  }, []);

  const handleSave = useCallback(async () => {
    const validationError = validateSsnValue(value || '');
    if (validationError) {
      setError(t(validationError));
      return;
    }
    try {
      setSaving(true);
      const redux = await import('auth/redux');
      dispatch(
        redux.actionContainer.setUserProfileAsync({
          SSNOrITIN: value,
        }),
      );

      setIsEditing(false);
      setShowSave(false);
      setError('');
    } catch (e) {
      setError(t('SAVE_FAILED'));
    } finally {
      setSaving(false);
    }
  }, [t, value, dispatch]);

  return (
    <View style={styles.container}>
      <Text style={styles.label}>{t('SSN/ITIN')}</Text>

      <View
        style={[styles.inputWrapper, !isEditing && styles.inputWrapperDisabled]}
      >
        <TextInput
          ref={inputRef}
          keyboardType="number-pad"
          placeholder={`${t('ENTER_YOUR_SSN/ITIN')}`}
          value={displayValue}
          maxLength={11}
          editable={isEditing}
          selectTextOnFocus={isEditing}
          onChangeText={handleChange}
          onFocus={() => {
            setShowSave(true);
          }}
          style={[styles.input, !isEditing && styles.inputDisabled]}
          placeholderTextColor={colors.grayScale70}
        />

        <View style={styles.actions}>
          {!isEditing && (
            <Button
              style={styles.iconButton}
              onPress={handleEdit}
              hitSlop={{ top: 8, bottom: 8, left: 8, right: 8 }}
            >
              <SvgXml
                xml={IconPen(colors.mainColorPrimary)}
                width={18}
                height={18}
              />
            </Button>
          )}

          {showSave && isEditing && (
            <Button
              style={[styles.saveButton, saving && styles.saveButtonDisabled]}
              onPress={handleSave}
              disabled={saving}
              activeOpacity={0.8}
            >
              {saving ? (
                <ActivityIndicator size="small" color={colors.white} />
              ) : (
                <Text style={styles.saveText}>{t('SAVE')}</Text>
              )}
            </Button>
          )}
        </View>
      </View>

      {!!error && <Text style={styles.errorText}>{error}</Text>}
    </View>
  );
}

export default SsnField;
