import React from 'react';
import { View } from 'react-native';
import { Text } from 'tradewize-component';
import { styles } from '../styles';
import {
  getJobStatusDisplayInfo,
  calculateInstallationProgress,
} from '../../../../../helper/myJob.helpter';
import {
  ActivityTypeEnum,
  DeliveryStatus,
  JobStartTabStatus,
  UserRoleEnum,
  JobStatusData,
} from '../../../../../types/myJobs.types';
import { useAppTranslation } from '../../../../../hooks/useAppTranslation';

interface JobStatusProps {
  jobStatusData: JobStatusData;
  jumpTo?: JobStartTabStatus;
}

export default function JobStatus({ jobStatusData, jumpTo }: JobStatusProps) {
  const { t } = useAppTranslation();

  // Helper data from jobStatusData
  const jobReadyInfo = getJobStatusDisplayInfo({
    estStart: jobStatusData.jobReadyEstStart,
    actStart: jobStatusData.jobReadyActStart,
    actEnd: jobStatusData.jobReadyActEnd,
  });

  const deliveryInfo = getJobStatusDisplayInfo({
    estStart: jobStatusData.deliveryEstStart,
    actStart: jobStatusData.deliveryActStart,
    actEnd: jobStatusData.deliveryActEnd,
  });

  const installationInfo = getJobStatusDisplayInfo({
    estStart: jobStatusData.installationEstStart,
    actStart: jobStatusData.installationActStart,
    actEnd: jobStatusData.installationActEnd,
  });

  const qIInfo = getJobStatusDisplayInfo({
    estStart: jobStatusData.qiEstStart,
    actStart: jobStatusData.qiActStart,
    actEnd: jobStatusData.qiActEnd,
  });

  // Installation progress
  const installationProgress = calculateInstallationProgress(
    jobStatusData.currentSqftPercent ?? 0,
  );
  const progressValue = parseFloat(installationProgress.replace('%', ''));

  // Helper to check if roleType is LEADER
  const isLeader = jobStatusData.roleType === UserRoleEnum.LEADER;

  return (
    <View style={styles.jobStatusWrapper}>
      <View style={styles.jobStatusBox}>
        {/* Ready Checklist */}
        <View style={styles.jobStatusRow}>
          <Text
            style={[
              styles.jobStatusText,
              isLeader ? styles.underline : undefined,
            ]}
          >
            {t('READY_CHECKLIST')}
          </Text>
          <Text style={styles.myJobInfo}>{jobReadyInfo?.date || ''}</Text>
        </View>

        {/* Delivery */}
        <View style={styles.jobStatusRow}>
          <Text
            style={[
              styles.jobStatusText,
              isLeader ? styles.underline : undefined,
            ]}
          >
            {t('DELIVERY')}
          </Text>
          {jobStatusData.deliveryStatus === DeliveryStatus?.UNDELIVERED &&
          isLeader ? (
            <Text style={[styles.myJobInfo, styles.btnOnDelivery]}>
              {jobStatusData.deliveryStatusName || ''}
            </Text>
          ) : (
            <Text style={styles.myJobInfo}>{deliveryInfo?.date || ''}</Text>
          )}
        </View>

        {/* Installation */}
        <View style={styles.jobStatusRow}>
          <Text style={styles.jobStatusText}>{t('INSTALLATION')}</Text>
          <Text style={styles.myJobInfo}>{installationInfo?.date || ''}</Text>
        </View>

        {/* QI Checklist */}
        <View style={styles.jobStatusRow}>
          {jobStatusData.activityTypeEnum !== ActivityTypeEnum?.INSPECTION &&
            (jobStatusData.qiIncompleteItems === null ||
            jobStatusData.qiIncompleteItems === undefined ||
            jobStatusData.qiIncompleteItems === 0 ? (
              <>
                <Text
                  style={[
                    styles.jobStatusText,
                    isLeader ? styles.underline : undefined,
                  ]}
                >
                  {t('QI_CHECKLIST')}
                </Text>
                <Text style={styles.myJobInfo}>{qIInfo?.date || ''}</Text>
              </>
            ) : (
              <Text
                style={[
                  styles.jobStatusText,
                  isLeader ? styles.underline : undefined,
                ]}
              >
                {t('QUALITY_INSPECTION')}: {jobStatusData.qiIncompleteItems}{' '}
                {t('INCOMPLETE_ITEMS')}
              </Text>
            ))}
        </View>

        {/* Installation Progress */}
        {jumpTo === JobStartTabStatus.IN_PROGRESS && (
          <>
            <View style={styles.jobStatusRow}>
              <Text style={styles.jobStatusText}>
                {t('INSTALLATION_PROGRESS')}
              </Text>
              <Text style={[styles.myJobInfo, styles.underline]}>
                {installationProgress}
              </Text>
            </View>
            <View style={styles.installationProgressContainer}>
              <View style={styles.installationProgressBarContainer}>
                <View
                  style={[
                    styles.installationProgressBarFill,
                    { width: `${progressValue}%` },
                  ]}
                />
              </View>
            </View>
          </>
        )}
      </View>
      <View style={styles.jobStatusLabel}>
        <Text style={styles.headerLabel}>{t('JOB_STATUS')}</Text>
      </View>
    </View>
  );
}
