import React, { useState, useCallback } from 'react';
import { View, ScrollView, TouchableOpacity, TextInput } from 'react-native';
import Modal from 'react-native-modal';
import {
  Text,
  BottomSelector,
  type BottomSelectorOption,
} from 'tradewize-component';
import { useAppTranslation } from '../../../../hooks/useAppTranslation';
import { styles } from './styles';
import { IconClose, IconChevronDown } from '../../../../assets/svg';
import { SvgXml } from 'react-native-svg';
import { colors } from '../../../../theme';

interface CommunityItem {
  id: string;
  name: string;
}

interface FilterModalProps {
  isVisible: boolean;
  communities: CommunityItem[];
  initialFilter: {
    CommunityId?: string;
    LotNumber?: string;
    StartDate?: string;
  };
  onApply: (filter: {
    CommunityId?: string;
    LotNumber?: string;
    StartDate?: string;
  }) => void;
  onReset: () => void;
  onClose: () => void;
}

export default function FilterModal({
  isVisible,
  communities,
  initialFilter,
  onApply,
  onReset,
  onClose,
}: FilterModalProps) {
  const { t } = useAppTranslation();

  const [tempLotNumber, setTempLotNumber] = useState(
    initialFilter.LotNumber || '',
  );
  const [tempStartDate, setTempStartDate] = useState(
    initialFilter.StartDate || '',
  );
  const [selectedCommunity, setSelectedCommunity] =
    useState<BottomSelectorOption | null>(() => {
      if (initialFilter.CommunityId) {
        const community = communities.find(
          c => c.id === initialFilter.CommunityId,
        );
        if (community) {
          return { value: community.id, label: community.name };
        }
      }
      return null;
    });

  const [isCommunitySelectorVisible, setIsCommunitySelectorVisible] =
    useState(false);

  const communityOptions: BottomSelectorOption[] = communities.map(c => ({
    value: c.id,
    label: c.name,
  }));

  const handleApply = useCallback(() => {
    const newFilters: {
      CommunityId?: string;
      LotNumber?: string;
      StartDate?: string;
    } = {};

    if (selectedCommunity?.value) {
      newFilters.CommunityId = selectedCommunity.value as string;
    }
    if (tempLotNumber.trim()) {
      newFilters.LotNumber = tempLotNumber.trim();
    }
    if (tempStartDate.trim()) {
      newFilters.StartDate = tempStartDate.trim();
    }

    onApply(newFilters);
  }, [selectedCommunity, tempLotNumber, tempStartDate, onApply]);

  const handleReset = useCallback(() => {
    setTempLotNumber('');
    setTempStartDate('');
    setSelectedCommunity(null);
    onReset();
  }, [onReset]);

  const handleClose = useCallback(() => {
    onClose();
  }, [onClose]);

  return (
    <Modal
      isVisible={isVisible}
      onSwipeComplete={handleClose}
      swipeDirection={['down']}
      style={styles.modal}
      backdropOpacity={0.5}
      animationIn="slideInUp"
      animationOut="slideOutDown"
      useNativeDriver
      hideModalContentWhileAnimating
    >
      <View style={styles.modalContainer}>
        {/* Header */}
        <View style={styles.handleIndicatorContainer}>
          <TouchableOpacity style={styles.closeButton} onPress={handleClose}>
            <SvgXml
              xml={IconClose(colors.grayScale70)}
              width={24}
              height={24}
            />
          </TouchableOpacity>
        </View>

        <ScrollView
          style={styles.bottomSheetContent}
          contentContainerStyle={styles.scrollContent}
          showsVerticalScrollIndicator={false}
          bounces={false}
        >
          {/* Community Selector */}
          <View style={styles.selectorContainer}>
            <Text style={styles.selectorLabel}>{t('COMMUNITY')}</Text>
            <TouchableOpacity
              style={styles.selectorButton}
              onPress={() => setIsCommunitySelectorVisible(true)}
              activeOpacity={0.7}
            >
              <Text
                style={[
                  styles.selectorButtonText,
                  selectedCommunity && styles.selectorButtonTextSelected,
                ]}
              >
                {selectedCommunity?.label || 'Select Community'}
              </Text>
              <SvgXml
                xml={IconChevronDown(colors.grayScale100)}
                width={20}
                height={20}
              />
            </TouchableOpacity>
          </View>

          {/* Lot Number */}
          <Text style={styles.label}>{t('LOT_NUMBER')}</Text>
          <TextInput
            value={tempLotNumber}
            onChangeText={setTempLotNumber}
            placeholder="Enter Lot Number"
            keyboardType="number-pad"
            style={styles.textInput}
          />

          {/* Start Date */}
          <Text style={styles.label}>{t('START_DATE')}</Text>
          <TextInput
            value={tempStartDate}
            onChangeText={setTempStartDate}
            placeholder="YYYY-MM-DD"
            style={styles.textInput}
          />
        </ScrollView>

        {/* Action Buttons - Fixed at Bottom */}
        <View style={styles.filterFooter}>
          <TouchableOpacity style={styles.resetButton} onPress={handleReset}>
            <Text style={styles.resetButtonText}>{t('RESET')}</Text>
          </TouchableOpacity>
          <TouchableOpacity style={styles.applyButton} onPress={handleApply}>
            <Text style={styles.applyButtonText}>{t('APPLY')}</Text>
          </TouchableOpacity>
        </View>
      </View>

      {/* Community Bottom Selector */}
      <BottomSelector
        title="Select Community"
        isVisible={isCommunitySelectorVisible}
        onClose={() => setIsCommunitySelectorVisible(false)}
        selectedValue={selectedCommunity?.value}
        options={communityOptions}
        onSelect={(option: BottomSelectorOption) => {
          setSelectedCommunity(option);
          setIsCommunitySelectorVisible(false);
        }}
        containerStyle={styles.selectorModalContainer}
        cancelText={t('CLOSE')}
        emptyText={t('NO_DATA_AVAILABLE')}
      />
    </Modal>
  );
}
