import { dispatch, requestPermissionBiometric } from '.';
import i18n from '../i18n';
import { logger } from '../utils';
import ReactNativeBiometrics from 'react-native-biometrics';
import { deleteAccount } from '../api/endpoint';
import { NavigationService } from '../services/NavigationService';

const rnBiometrics = new ReactNativeBiometrics({
  allowDeviceCredentials: true,
});

export const handleBiometricAuthentication = async () => {
  try {
    const redux = await import('auth/redux');
    const isAvailable = await rnBiometrics.isSensorAvailable();

    if (!isAvailable?.available) {
      dispatch(
        redux.actionContainer.showAlert({
          type: 'warning',
          title: i18n?.t('WARNING'),
          message: i18n?.t(
            'YOUR_DEVICE_IS_NOT_SUPPORTED_OR_YOU_HAVE_NOT_REGISTERED_BIOMETRICS_ON_YOUR_DEVICE',
          ),
          buttonText: i18n?.t('CONFIRM'),
          onPress: () => redux.actionContainer.hideAlertAsync(),
        }),
      );
      return;
    }

    const biometricAuthentication =
      redux.store.getState().auth.biometricAuthentication;

    if (biometricAuthentication?.data) {
      dispatch(
        redux.actionContainer.showConfirmModal({
          isShowCloseButton: false,
          isBackdropPress: false,
          isShowIcon: true,
          title: i18n?.t('UNREGISTER_BIOMETRIC_AUTHENTICATION'),
          description: i18n?.t(
            'ARE_YOU_SURE_YOU_WANT_TO_UNREGISTER_BIOMETRIC_AUTHENTICATION',
          ),
          confirmText: i18n?.t('CONFIRM'),
          cancelText: i18n?.t('CANCEL'),
          onConfirm: () => {
            dispatch(redux.actionContainer.hideConfirmModal());
            setTimeout(() => {
              dispatch(
                redux.actionContainer.unregisterBiometricAuthenticationAsync(),
              );
            }, 350);
          },
          onCancel: () => {
            dispatch(redux.actionContainer.hideConfirmModal());
          },
        }),
      );
      return;
    }

    const permission = await requestPermissionBiometric();
    if (permission?.granted) {
      const result = await rnBiometrics.simplePrompt({
        promptMessage: 'Biometric authentication',
      });
      if (result?.success) {
        const key = await rnBiometrics.createKeys();

        dispatch(
          redux.actionContainer.registerBiometricAuthentication(key?.publicKey),
        );
      }
    }
  } catch (error) {
    logger('error handleResetBiometricAuthentication', error);
  }
};

export const handleDeleteAccount = async () => {
  try {
    const redux = await import('auth/redux');
    dispatch(
      redux.actionContainer.showConfirmModal({
        isShowCloseButton: false,
        title: i18n?.t('DELETE_ACCOUNT'),
        description: i18n?.t('ARE_YOU_SURE_YOU_WANT_TO_DELETE_YOUR_ACCOUNT'),
        confirmText: i18n?.t('CONFIRM'),
        cancelText: i18n?.t('CANCEL'),
        onConfirm: async () => {
          try {
            dispatch(redux.actionContainer.hideConfirmModal());
            const client = await import('auth/api');
            const user = redux.store.getState().auth.userProfile;
            const response = await client.deleteRequest(
              `${deleteAccount}/${user?.accountId}`,
            );
            if (response.status === 200) {
              dispatch(
                redux.actionContainer.showAlertAsync({
                  title: i18n?.t('ACCOUNT_DELETED'),
                  message: i18n?.t('YOUR_ACCOUNT_HAS_BEEN_DELETED'),
                  type: 'success',
                  buttonText: i18n?.t('OK'),
                  onClose: () => {
                    NavigationService.goBack();
                  },
                }),
              );
            }
          } catch (error) {
            logger('error deleteAccount API', error);
            dispatch(
              redux.actionContainer.showAlertAsync({
                title: i18n?.t('ERROR'),
                message: i18n?.t('SOMETHING_WENT_WRONG'),
                type: 'error',
                buttonText: i18n?.t('OK'),
              }),
            );
          }
        },
        onCancel: () => {
          dispatch(
            redux.actionContainer.hideConfirmModal({
              onClose: () => {
                NavigationService.goBack();
              },
            }),
          );
        },
      }),
    );
  } catch (error) {
    logger('error handleDeleteAccount', error);
  }
};
