import { useMemo } from 'react';
import { FileInfo } from '../types';
import { getFileInfo, validateFile } from '../utils/fileUtils';

export const useFileInfo = (file: any): { fileInfo: FileInfo; isValid: boolean } => {
  const fileInfo = useMemo(() => getFileInfo(file), [file]);
  const isValid = useMemo(() => validateFile(fileInfo), [fileInfo]);

  return { fileInfo, isValid };
}; 