import React, { useState } from 'react';
import { View } from 'react-native';
import { Text } from 'tradewize-component';
import { useAppTranslation } from '../../../hooks/useAppTranslation';
import { colors } from '../../../theme';
import { styles } from '../styles';
import { SvgXml } from 'react-native-svg';
import { CategoryDropdownProps } from '../../../types/roleTypes';
import { Button } from 'tradewize-component';
import { IconChevronDown, IconChevronUp } from '../../../assets/svg';
import { ChecklistRow } from './checklistRow';

const CategoryDropdown = ({ categoryGroup }: CategoryDropdownProps) => {
  const { t } = useAppTranslation();
  const [isExpanded, setIsExpanded] = useState(false);

  const translatedCategoryName = t(categoryGroup?.categoryName, {
    defaultValue: categoryGroup?.categoryName,
  });

  // Count number of checklists in this category
  const checklistCount = categoryGroup?.checklists?.length ?? 0;

  return (
    <View style={styles.checklistContainer}>
      <Button
        style={styles.checklistHeader}
        onPress={() => setIsExpanded(!isExpanded)}
        activeOpacity={0.7}
      >
        <Text style={styles.checklistName}>
          {translatedCategoryName} ({checklistCount})
        </Text>
        <SvgXml
          xml={
            isExpanded
              ? IconChevronUp(colors.black)
              : IconChevronDown(colors.black)
          }
          width={20}
          height={20}
        />
      </Button>

      {isExpanded && (
        <View style={styles.checklistContent}>
          {categoryGroup?.checklists?.map((checklist, index) => (
            <ChecklistRow
              key={checklist.id}
              checklist={checklist}
              index={index}
            />
          ))}
        </View>
      )}
    </View>
  );
};

export default CategoryDropdown;
