import React from 'react';
import { View } from 'react-native';
import { SvgXml } from 'react-native-svg';
import { IconSend } from '../../../../assets/svg';
import { GroupAvatar } from '../GroudAvatar';
import { styles } from './styles';
import { Button, Text } from 'tradewize-component';
import { MyRooms } from '../../types';
import { colors } from '../../../../theme';
interface ChatListItemProps {
  chat: MyRooms;
  onPress: () => void;
}

export const ChatListItem: React.FC<ChatListItemProps> = ({
  chat,
  onPress,
}) => {
  const displayName = chat.chatName;

  const isMessageUnRead = Number(chat?.unreadMessages) > 0;

  return (
    <Button variant="ghost" onPress={onPress} style={styles.container}>
      <GroupAvatar membersInfo={chat.memberInfo} size={48} />
      <View style={styles.content}>
        <Text
          style={[styles.name, isMessageUnRead && styles.textBold]}
          numberOfLines={1}
        >
          {displayName}
        </Text>
        <Text
          numberOfLines={1}
          style={[
            styles.role,
            isMessageUnRead && styles.textBold,
            { color: colors.grayScale70 },
          ]}
        >
          {chat?.lastMessage?.content ?? ''}
        </Text>
      </View>
      <SvgXml xml={IconSend(colors.line)} width={20} height={20} />
      {isMessageUnRead && <View style={styles.dot} />}
    </Button>
  );
};
