import { useFocusEffect } from '@react-navigation/native';
import React, { useCallback, useEffect, useState } from 'react';
import { useDispatch, useSelector } from 'react-redux';
import { logger } from '../utils';
import { UserProfile } from '../types/profile';
import { useAppTranslation } from './useAppTranslation';
import { navigationRef } from '../services/NavigationService';
import { SCREEN_ENUM } from '../types/global';
import { requestPermissionBiometric, updateProfile } from '../helper';
import DueVideo from '../screens/Home/components/DueVideo';
import ImportantThings from '../screens/Home/components/ImportantThings';
import { colors } from '../theme';
import { STORAGE_KEY, StorageService } from '../services/StorageService';
import ReactNativeBiometrics from 'react-native-biometrics';
import { StatusBar } from 'react-native';
import { appBackgroundService } from '../services/SetStateService';

const rnBiometrics = new ReactNativeBiometrics({
  allowDeviceCredentials: true,
});

interface HomeMedia {
  key: string;
  url: { [key: string]: string };
  subtitle: { [key: string]: string };
}

export const useHome = () => {
  const dispatch = useDispatch();
  const { t, i18n } = useAppTranslation();

  // THM STATE  TRACKING
  const [isInitialLoadComplete, setIsInitialLoadComplete] = useState(false);

  const userProfile: UserProfile = useSelector(
    (state: any) => state.auth.userProfile,
  );
  const isShowSuggestSettingLoginBiometricModal = useSelector(
    (state: any) => state.app.isShowSuggestSettingLoginBiometricModal,
  );

  const dataMedia: { [key: string]: HomeMedia } | null = useSelector(
    (state: any) => state.app.dataMedia,
  );
  const importantJobs = useSelector((state: any) => state.home.importantJobs);
  const myJobsToday = useSelector((state: any) => state.home.myJobsToday);
  const isShowDueVideo = useSelector((state: any) => state.home.isShowDueVideo);

  const fetchGetMyTodayJobs = useCallback(async () => {
    try {
      const redux = await import('auth/redux');
      dispatch(redux.actionContainer.getMyJobsTodayAsync());
    } catch (error) {
      logger('error fetchGetMyTodayJobs', error);
    }
  }, [dispatch]);

  const fetchGetImportantJobs = useCallback(async () => {
    try {
      const redux = await import('auth/redux');
      dispatch(redux.actionContainer.getImportantJobsAsync());
    } catch (error) {
      logger('error fetchGetImportantJobs', error);
    }
  }, [dispatch]);

  const handleRegisterBiometricAuthentication = useCallback(async () => {
    try {
      const redux = await import('auth/redux');

      const permission = await requestPermissionBiometric();
      if (permission?.granted) {
        const result = await rnBiometrics.simplePrompt({
          promptMessage: 'Biometric authentication',
        });
        if (result?.success) {
          const key = await rnBiometrics.createKeys();

          dispatch(
            redux.actionContainer.registerBiometricAuthentication(
              key?.publicKey,
            ),
          );
        }
      }
    } catch (error) {
      logger('error handleRegisterBiometricAuthentication', error);
    }
  }, [dispatch]);

  const handleClearBiometricAuthentication = useCallback(async () => {
    try {
      const redux = await import('auth/redux');
      dispatch(redux.actionContainer.clearBiometricAuthenticationAsync());
    } catch (error) {
      logger('error handleClearBiometricAuthentication', error);
    }
  }, [dispatch]);

  const fetchStatusAccount = useCallback(async () => {
    try {
      const redux = await import('auth/redux');
      const infoBiometricSaved = await StorageService.get(
        STORAGE_KEY.BIOMETRIC_KEY,
      );
      const isAvailable = await rnBiometrics.isSensorAvailable();
      //1. check if user is first reset password
      if (!userProfile?.isFirstResetPassword) {
        dispatch(
          redux.actionContainer.showAlert({
            isBackdropPress: false,
            title: t('CHANGE_PASSWORD'),
            message: t('FIRST_TIME_PASSWORD_CHANGE_MESSAGE'),
            type: 'warning',
            buttonText: t('CHANGE_NOW'),
            titleStyle: {
              color: colors.black,
            },
            buttonStyle: {
              backgroundColor: colors.mainColorPrimary,
            },
            onClose: () => {
              redux.actionContainer?.hideAlert();
              navigationRef.navigate(SCREEN_ENUM.ChangePasswordScreen);
            },
          }),
        );
        return;
      }

      //2. if open video training first time
      if (
        !userProfile?.isWatchedTrainingVideo &&
        userProfile?.isFirstResetPassword
      ) {
        dispatch(
          redux.actionContainer.showVideoModal({
            initialSubtitle: i18n?.language || 'en',
            source: dataMedia?.['3_ONBOARDING_VIDEO']?.url || {},
            subtitle: dataMedia?.['3_ONBOARDING_VIDEO']?.subtitle || {},
            onClose: () => {
              setTimeout(() => {
                updateProfile({ isWatchedTrainingVideo: true });
              }, 150);
              dispatch(redux.actionContainer?.hideVideoModal());
            },
          }),
        );
        return;
      }

      //3. if contract is not signed
      if (
        userProfile?.hasContractsToSign &&
        userProfile?.isFirstResetPassword &&
        userProfile?.isWatchedTrainingVideo
      ) {
        dispatch(
          redux.actionContainer.showConfirmModal({
            isShowCloseButton: false,
            isBackdropPress: false,
            title: t('SIGN_CONTRACT'),
            description: t('PLEASE_SIGN_CONTRACT_TO_START_WORKING'),
            confirmText: t('CONFIRM'),
            cancelText: t('CANCEL'),
            isShowIcon: true,
            onCancel: () => {
              dispatch(redux.actionContainer?.hideConfirmModal());
              setTimeout(() => {
                dispatch(redux.actionContainer.logout());
              }, 350);
            },
            onConfirm: () => {
              dispatch(redux.actionContainer?.hideConfirmModal());
              setTimeout(() => {
                navigationRef.navigate(SCREEN_ENUM.ContractScreen);
              }, 350);
            },
          }),
        );
        return;
      }

      //4. Check training session
      if (
        !userProfile?.isFinishedTraining &&
        userProfile?.isFirstResetPassword &&
        userProfile?.isWatchedTrainingVideo &&
        !userProfile?.hasContractsToSign
      ) {
        dispatch(
          redux.actionContainer.showConfirmModal({
            isShowCloseButton: false,
            isBackdropPress: false,
            isShowIcon: true,
            title: t('TRAINING_TEST'),
            description: t('PLEASE_PASS_TRAINING_TO_START_WORKING'),
            confirmText: t('COME_SEE'),
            cancelText: t('CANCEL'),
            onCancel: () => {
              dispatch(redux.actionContainer?.hideConfirmModal());
              setTimeout(() => {
                dispatch(redux.actionContainer.logout());
              }, 350);
            },
            onConfirm: () => {
              dispatch(redux.actionContainer?.hideConfirmModal());
              setTimeout(() => {
                navigationRef.navigate(SCREEN_ENUM.SystemQualityGuideScreen);
              }, 350);
            },
          }),
        );
        return;
      }

      // 5. Check due video
      if (
        (userProfile?.isDueTraining || userProfile?.needWatchVideoNow) &&
        userProfile?.isFirstResetPassword &&
        userProfile?.isWatchedTrainingVideo &&
        !userProfile?.hasContractsToSign &&
        userProfile?.isFinishedTraining &&
        !isShowDueVideo
      ) {
        dispatch(
          redux.actionContainer.showCustomModal({
            isBackdropPress: false,
            animationIn: 'zoomIn',
            animationOut: 'zoomOut',
            animationInTiming: 400,
            animationOutTiming: 400,
            backdropOpacity: 0.2,
            children: React.createElement(DueVideo),
          }),
        );
        return;
      }

      // 6. Check important jobs
      if (
        !userProfile?.isDueTraining &&
        !userProfile?.needWatchVideoNow &&
        userProfile?.isFirstResetPassword &&
        userProfile?.isWatchedTrainingVideo &&
        !userProfile?.hasContractsToSign &&
        userProfile?.isFinishedTraining &&
        importantJobs?.totalJobs > 0
      ) {
        dispatch(
          redux.actionContainer.showCustomModal({
            isBackdropPress: false,
            animationIn: 'zoomIn',
            animationOut: 'zoomOut',
            animationInTiming: 400,
            animationOutTiming: 400,
            backdropOpacity: 0.2,
            children: React.createElement(ImportantThings),
          }),
        );
      }

      // 7. suggest setting login biometric
      if (
        !userProfile?.isDueTraining &&
        !userProfile?.needWatchVideoNow &&
        userProfile?.isFirstResetPassword &&
        userProfile?.isWatchedTrainingVideo &&
        !userProfile?.hasContractsToSign &&
        userProfile?.isFinishedTraining &&
        !importantJobs?.totalJobs &&
        isShowSuggestSettingLoginBiometricModal &&
        isAvailable?.available &&
        (!infoBiometricSaved?.biometricKey ||
          userProfile?.email !== infoBiometricSaved?.email)
      ) {
        dispatch(
          redux.actionContainer.showConfirmModal({
            isShowCloseButton: false,
            isBackdropPress: false,
            isShowIcon: true,
            title: t('LOGIN_BIOMETRIC'),
            description: t('CONTENT_LOGIN_BIOMETRIC'),
            confirmText: t('CONFIRM'),
            cancelText: t('CANCEL'),
            onCancel: () => {
              dispatch(redux.actionContainer?.hideConfirmModal());
              dispatch(
                redux.actionContainer.isShowSuggestSettingLoginBiometricModal(
                  false,
                ),
              );
              setTimeout(() => {
                handleClearBiometricAuthentication();
              }, 350);
            },
            onConfirm: () => {
              dispatch(redux.actionContainer?.hideConfirmModal());
              dispatch(
                redux.actionContainer.isShowSuggestSettingLoginBiometricModal(
                  false,
                ),
              );
              setTimeout(() => {
                handleRegisterBiometricAuthentication();
              }, 350);
            },
          }),
        );
      }
    } catch (error) {
      logger('error fetchStatusAccount', error);
    }
  }, [
    userProfile?.isFirstResetPassword,
    userProfile?.isWatchedTrainingVideo,
    userProfile?.hasContractsToSign,
    userProfile?.isFinishedTraining,
    userProfile?.isDueTraining,
    userProfile?.needWatchVideoNow,
    userProfile?.email,
    isShowDueVideo,
    importantJobs?.totalJobs,
    isShowSuggestSettingLoginBiometricModal,
    dispatch,
    t,
    i18n?.language,
    dataMedia,
    handleRegisterBiometricAuthentication,
    handleClearBiometricAuthentication,
  ]);

  // CALL 2 API NG THI V TRACKING KHI XONG
  useEffect(() => {
    const fetchInitialData = async () => {
      try {
        await Promise.all([fetchGetMyTodayJobs(), fetchGetImportantJobs()]);

        // nh du  load xong
        setIsInitialLoadComplete(true);
      } catch (error) {
        logger('error fetchInitialData', error);
        setIsInitialLoadComplete(true); // Vn nh du xong  trnh block
      }
    };

    fetchInitialData();
  }, [fetchGetMyTodayJobs, fetchGetImportantJobs]);

  // CH CHY fetchStatusAccount KHI  LOAD XONG
  useFocusEffect(
    useCallback(() => {
      // Ch chy khi:
      // 1.  load xong ln u
      // 2. C userProfile
      // 3. Khng ang loading
      if (
        isInitialLoadComplete &&
        userProfile &&
        !importantJobs?.isLoading &&
        !myJobsToday?.isLoading
      ) {
        fetchStatusAccount();
      }
    }, [
      isInitialLoadComplete,
      fetchStatusAccount,
      userProfile,
      importantJobs?.isLoading,
      myJobsToday?.isLoading,
    ]),
  );

  useEffect(() => {
    const unsubscribe = appBackgroundService.subscribe(isBg => {
      if (!isBg) {
        StatusBar.setBackgroundColor(colors.mainColorPrimary);
        StatusBar.setBarStyle('light-content');
      }
    });

    return unsubscribe; //  () => void
  }, []);

  // Tnh ton loading state da trn iu kin
  const isLoadingStatusAccount =
    !isInitialLoadComplete ||
    !userProfile ||
    importantJobs?.isLoading ||
    myJobsToday?.isLoading;

  return {
    isLoadingStatusAccount,
  };
};
