// Date format constant
export const FORMAT_TIME_CLIENT = 'MM/DD/YYYY';

// Job status helper
export interface JobStatusInfo {
  date: string;
  color: string;
  isCompleted: boolean;
}

export const getJobStatusDisplayInfo = (
  sectionData: any,
): JobStatusInfo | null => {
  if (!sectionData) return null;

  const actStart = sectionData?.actStart;
  const actEnd = sectionData?.actEnd;
  const estStart = sectionData?.estStart;

  let date = '';
  let color = colors.text;

  if (estStart && dayjs(estStart).isValid()) {
    date = dayjs(estStart).format(FORMAT_TIME_CLIENT);
  }

  if (actStart && dayjs(actStart).isValid()) {
    date = dayjs(actStart).format(FORMAT_TIME_CLIENT);
    color = colors.alertColorWarning;
  }

  if (actEnd && dayjs(actEnd).isValid()) {
    date = dayjs(actEnd).format(FORMAT_TIME_CLIENT);
    color = colors.alertColorSuccess;
  }

  return {
    date,
    color,
    isCompleted: !!actEnd,
  };
};

// Calculate installation progress
export const calculateInstallationProgress = (
  currentSqftPercent: number,
): string => {
  if (!currentSqftPercent) return '0%';

  const progress = parseFloat(currentSqftPercent.toString());
  if (isNaN(progress)) return '0%';

  return progress === 100 ? '100%' : progress.toFixed(2) + '%';
};

// Format date helpers
export const getDayName = (dateString: string | null): string => {
  if (!dateString) return '';
  try {
    const date = new Date(dateString);
    if (isNaN(date.getTime())) return '';
    return date.toLocaleDateString('en-US', { weekday: 'short' }).toUpperCase();
  } catch {
    return '';
  }
};

export const getDayNumber = (dateString: string | null): string => {
  if (!dateString) return '';
  try {
    const date = new Date(dateString);
    if (isNaN(date.getTime())) return '';
    return date.getDate().toString().padStart(2, '0');
  } catch {
    return '';
  }
};

export const formatAmount = (totalAmount: number = 0) => {
  if (!totalAmount) return '$0';
  const formatted = formatCurrency(totalAmount);
  return formatted || `$${Number(totalAmount).toFixed(2)}`;
};

export const formatChecklist = (
  checklistChecked: number = 0,
  checklistTotal: number = 0,
) => {
  const checked = checklistChecked;
  const total = checklistTotal;
  const checkedStr = checked < 10 && checked > 0 ? `0${checked}` : checked;
  return `${checkedStr}/${total}`;
};

// to MMMM YYYY
export const formatDateHeader = (date: Date | string) => {
  return dayjs(date).format('MMMM YYYY');
};

// format date to MMMM D, YYYY
export const formatDate = (dateString?: string): string => {
  if (!dateString) return '';
  try {
    const date = new Date(dateString);
    return date.toLocaleDateString('en-US', {
      month: 'short',
      day: '2-digit',
      year: 'numeric',
    });
  } catch {
    return '';
  }
};

// Helper function to open Google Maps
export const openGoogleMap = (latitude?: number, longitude?: number) => {
  if (!latitude || !longitude) return;

  const url = Platform.select({
    ios: `maps://app?daddr=${latitude},${longitude}`,
    android: `google.navigation:q=${latitude},${longitude}`,
  });

  if (url) {
    Linking.openURL(url).catch(() => {
      // Fallback to web version
      const webUrl = `https://www.google.com/maps/search/?api=1&query=${latitude},${longitude}`;
      Linking.openURL(webUrl).catch(console.error);
    });
  }
};

export const getStatusColor = (statusName?: WarrantyStatus): string => {
  switch (statusName) {
    case WarrantyStatus.PENDING:
      return colors.alertColorPending;
    case WarrantyStatus.WAITING_ACCEPTANCE:
      return colors.alertColorInProcess;
    case WarrantyStatus.ACCEPTED:
      return colors.alertColorSuccess;
    case WarrantyStatus.REJECTED:
      return colors.alertColorError;
    case WarrantyStatus.PENDING_REVIEW:
      return colors.alertColorWarning;
    case WarrantyStatus.COMPLETED:
      return colors.alertColorSuccess;
    case WarrantyStatus.IN_COMPLETE:
      return colors.alertColorError;
    case WarrantyStatus.PENDING_REVIEW_BUILDER:
      return colors.alertColorPending;
    default:
      return colors.grayScale50;
  }
};

