import { useCallback, useState, useEffect } from 'react';
import { useDispatch, useSelector } from 'react-redux';
import { Company } from '../types/register-company';
import { logger } from '../utils';
import { NavigationService } from '../services/NavigationService';
import { SCREEN_ENUM } from '../types/global';
import { useAppTranslation } from './useAppTranslation';

export function useCompany() {
  const dispatch = useDispatch();
  const { t } = useAppTranslation();
  const [selectedCompanyIds, setSelectedCompanyIds] = useState<string[]>([]);

  const companyAndRoleData: Company[] = useSelector(
    (state: any) => state?.registerCompany?.data || [],
  );

  useEffect(() => {
    const fetchCompanyAndRoleData = async () => {
      const redux = await import('auth/redux');
      dispatch(redux.actionContainer.getCompanyAsync());
    };
    fetchCompanyAndRoleData();
  }, [dispatch]);

  const handleToggleSelect = useCallback((companyId: string) => {
    setSelectedCompanyIds(prev => {
      if (prev.includes(companyId)) {
        return prev.filter(id => id !== companyId);
      }
      return [...prev, companyId];
    });
  }, []);

  const cleanData = useCallback(() => {
    setSelectedCompanyIds([]);
  }, []);

  const handleRegisterPress = useCallback(async () => {
    try {
      if (selectedCompanyIds.length === 0) {
        return;
      }

      // Map selected company IDs to include company names
      const selectedCompanies = selectedCompanyIds.map(companyId => {
        const company = companyAndRoleData.find(
          item => item.companyId === companyId,
        );
        return {
          companyId,
          companyName: company?.companyName || '',
        };
      });

      NavigationService.navigate(SCREEN_ENUM.RegisterCompanyTestScreen, {
        selectedCompanies,
        cleanData,
      });
    } catch (error) {
      logger('error handleRegisterPress', error);
    }
  }, [selectedCompanyIds, companyAndRoleData, cleanData]);

  const showModalConfirmRegister = useCallback(async () => {
    const redux = await import('auth/redux');
    dispatch(
      redux.actionContainer.showConfirmModal({
        isShowCloseButton: false,
        isBackdropPress: false,
        isShowIcon: true,
        title: t('WARNING'),
        description: t('YOU_NEED_TO_START_TEST_TO_REGISTER_COMPANY'),
        confirmText: t('START_TEST'),
        cancelText: t('CANCEL'),
        onConfirm: () => {
          dispatch(redux.actionContainer?.hideConfirmModal());
          setTimeout(() => {
            handleRegisterPress();
          }, 350);
        },
        onCancel: () => {
          dispatch(redux.actionContainer?.hideConfirmModal());
        },
      }),
    );
  }, [dispatch, handleRegisterPress, t]);

  return {
    companyAndRoleData,
    selectedCompanyIds,
    handleToggleSelect,
    handleRegisterPress,
    showModalConfirmRegister,
  };
}
