import { View } from 'react-native';
import { useAppTranslation } from '../../../../hooks/useAppTranslation';
import { styles } from './styles';
import { ShadowedView } from 'react-native-fast-shadow';
import {
  CalendarPicker,
  Button,
  SCREEN_WIDTH,
  SCREEN_HEIGHT,
} from 'tradewize-component';
import { SvgXml } from 'react-native-svg';
import { shadowGlobal } from '../../../../types/global';
import { Dispatch, SetStateAction, useRef } from 'react';
import { colors } from '../../../../theme';
import { IconArrowRight, IconChevronLeft } from '../../../../assets/svg';
import {
  customDatesStylesFn,
  handleChangeDate,
} from '../../../../helper/JobsBoard';
import { FilterOption } from '../Filter/types';

interface CalenderPickerProps {
  rangeDate: {
    startDate: string;
    endDate: string;
  };
  setRangeDate: React.Dispatch<
    React.SetStateAction<{
      startDate: string;
      endDate: string;
    }>
  >;
  setIsFocusedCalendar: (isFocusedCalendar: boolean) => void;
  setFilter: (filter: FilterOption[]) => void;
}

const CalenderPicker = ({
  rangeDate,
  setRangeDate,
  setIsFocusedCalendar,
  setFilter,
}: CalenderPickerProps) => {
  const calendarPickerRef = useRef<CalendarPicker>(null);
  const { t } = useAppTranslation();
  return (
    <View style={styles.calendarContainer}>
      <ShadowedView style={[styles.calendarContainerContent, shadowGlobal]}>
        <CalendarPicker
          selectMonthTitle={`${t('SELECT_MONTH_IN')} `}
          ref={calendarPickerRef}
          width={SCREEN_WIDTH - 32}
          height={SCREEN_HEIGHT - 32}
          weekdays={[
            t('MO'),
            t('TU'),
            t('WE'),
            t('TH'),
            t('FR'),
            t('SA'),
            t('SU'),
          ]}
          months={[
            t('JANUARY'),
            t('FEBRUARY'),
            t('MARCH'),
            t('APRIL'),
            t('MAY'),
            t('JUNE'),
            t('JULY'),
            t('AUGUST'),
            t('SEPTEMBER'),
            t('OCTOBER'),
            t('NOVEMBER'),
            t('DECEMBER'),
          ]}
          scaleFactor={370}
          dayShape="square"
          startFromMonday={true}
          allowRangeSelection={true}
          todayBackgroundColor={colors.white}
          todayTextStyle={styles.calendarTextToday}
          selectedDayColor={colors.grayScale30}
          selectedMonthStyle={styles.selectedMonthStyle}
          selectedYearStyle={styles.selectedYearStyle}
          selectedMonthTextStyle={styles.selectedMonthTextStyle}
          selectedYearTextStyle={styles.selectedYearTextStyle}
          selectedRangeEndStyle={{
            backgroundColor: colors.mainColorPrimary,
          }}
          selectedRangeStartStyle={{
            backgroundColor: colors.mainColorPrimary,
          }}
          selectedRangeStartTextStyle={{
            color: colors.white,
          }}
          selectedRangeEndTextStyle={{
            color: colors.white,
          }}
          disabledDatesTextStyle={styles.calendarTextDisabled}
          nextComponent={
            <Button
              onPress={() => {
                calendarPickerRef?.current?.handleOnPressNext();
              }}
              style={styles.calendarButtonHeader}
            >
              <SvgXml
                xml={IconArrowRight(colors.black)}
                width={24}
                height={24}
              />
            </Button>
          }
          previousComponent={
            <Button
              onPress={() => {
                calendarPickerRef?.current?.handleOnPressPrevious();
              }}
              style={styles.calendarButtonHeader}
            >
              <SvgXml
                xml={IconChevronLeft(colors.black)}
                width={24}
                height={24}
              />
            </Button>
          }
          dayLabelsWrapper={{ borderTopWidth: 0, borderBottomWidth: 0 }}
          monthTitleStyle={styles.titleHeader}
          yearTitleStyle={styles.titleHeader}
          customDatesStyles={(date: Date) =>
            customDatesStylesFn(date, rangeDate)
          }
          selectedStartDate={rangeDate?.startDate || null}
          selectedEndDate={rangeDate?.endDate || null}
          onDateChange={(date: Date | null, type: string) =>
            setRangeDate(prev => ({
              ...prev,
              [type === 'START_DATE' ? 'startDate' : 'endDate']: date,
            }))
          }
        />

        <View style={styles.calendarFooter}>
          <Button
            onPress={() => {
              setIsFocusedCalendar(false);
            }}
            style={[
              styles.calendarFooterButton,
              styles.calendarFooterButtonCancel,
            ]}
            textStyle={styles.calendarFooterButtonText}
            title={t('CANCEL')}
          />
          <Button
            onPress={() => {
              handleChangeDate({
                rangeDate,
                setFilter: setFilter as Dispatch<
                  SetStateAction<FilterOption[]>
                >,
              });
              setIsFocusedCalendar(false);
            }}
            style={styles.calendarFooterButton}
            title={t('APPLY')}
          />
        </View>
      </ShadowedView>
    </View>
  );
};

export default CalenderPicker;
