import { useDispatch, useSelector } from 'react-redux';
import { logger } from '../utils';
import { useCallback, useEffect, useState } from 'react';
import { useFocusEffect } from '@react-navigation/native';
import { StatusBar } from 'react-native';
import { colors } from '../theme';
import { updateProfile } from '../helper';

export const useTraining = () => {
  const dispatch = useDispatch();

  const qualityGuideData = useSelector(
    (state: any) => state.training?.myTrainings?.data,
  );
  const tutorialsData = useSelector(
    (state: any) => state.training?.tutorials?.data?.tutorials,
  );
  const userData = useSelector((state: any) => state.auth.userProfile);

  const [loading, setLoading] = useState(false);

  const fetchMyTrainings = useCallback(async () => {
    try {
      const redux = await import('auth/redux');
      return dispatch(redux.actionContainer.getMyTrainingsAsync());
    } catch (error) {
      logger('error fetchMyTrainings', error);
    }
  }, [dispatch]);

  const fetchAllTutorials = useCallback(async () => {
    try {
      const redux = await import('auth/redux');
      return dispatch(redux.actionContainer.getAllTutorialsAsync());
    } catch (error) {
      logger('error fetchAllTutorials', error);
    }
  }, [dispatch]);

  const checkStatusData = useCallback(() => {
    try {
      if (!userData) {
        return;
      }

      const videoTraining =
        qualityGuideData?.trainings?.flatMap(
          (item: any) => item.trainings ?? [],
        ) ?? [];

      const videoTutorialsTraining =
        qualityGuideData?.tutorialsByCompany?.flatMap(
          (item: any) => item.tutorials ?? [],
        ) ?? [];

      const videosTrainingNotEligible = videoTraining.filter(
        (video: any) =>
          !video.isPassed && !video.isWatched && video.isHasQuestion,
      );

      const videosTutorialsTrainingNotEligible = videoTutorialsTraining.filter(
        (video: any) => video.required && !video.isWatched,
      );

      const arrayTutorialsNotWatchedRequired =
        tutorialsData?.filter(
          (tutorial: any) => tutorial.required && !tutorial.isWatched,
        ) ?? [];

      const shouldUpdateProfile =
        videosTrainingNotEligible.length === 0 &&
        videosTutorialsTrainingNotEligible.length === 0 &&
        arrayTutorialsNotWatchedRequired.length === 0;

      if (
        shouldUpdateProfile &&
        (!userData.isFinishedTraining ||
          userData.needWatchVideoNow ||
          userData.isDueTraining)
      ) {
        updateProfile({
          isFinishedTraining: true,
          needWatchVideoNow: false,
          isDueTraining: false,
        });
      }
    } catch (error) {
      logger('error checkStatusData', error);
    }
  }, [
    qualityGuideData?.trainings,
    qualityGuideData?.tutorialsByCompany,
    tutorialsData,
    userData,
  ]);

  useEffect(() => {
    const fetchData = async () => {
      setLoading(true);
      try {
        await Promise.all([fetchMyTrainings(), fetchAllTutorials()]);
      } finally {
        setLoading(false);
      }
    };

    fetchData();
  }, [fetchMyTrainings, fetchAllTutorials]);

  useEffect(() => {
    checkStatusData();
  }, [checkStatusData]);

  useFocusEffect(
    useCallback(() => {
      setTimeout(() => {
        StatusBar.setBackgroundColor(colors.mainColorPrimary);
        StatusBar.setBarStyle('light-content');
      }, 300);
    }, []),
  );

  return { loading };
};
