import { StyleSheet } from 'react-native';
import { FileThumbnailSize } from '../types';

export const getThumbnailSize = (size: FileThumbnailSize): number => {
  switch (size) {
    case 'small':
      return 32;
    case 'medium':
      return 48;
    case 'large':
      return 80;
    default:
      return 48;
  }
};

