import React, { useState, useEffect } from 'react';
import { View, TouchableOpacity, FlatList } from 'react-native';
import Modal from 'react-native-modal';
import { Button, Text, WheelMonthYearPicker } from 'tradewize-component';
import { SvgXml } from 'react-native-svg';
import { useSafeAreaInsets } from 'react-native-safe-area-context';
import { useSelector } from 'react-redux';
import { colors } from '../../../theme';
import { IconCalendar, IconChevronDown, IconCheck } from '../../../assets/svg';
import { useAppTranslation } from '../../../hooks/useAppTranslation';
import dayjs from 'dayjs';
import { styles } from '../styles';
import { IconClose } from '../../../assets/svg';

interface CompanyOption {
  companyId: string;
  companyName: string;
}

interface FilterBottomSheetProps {
  isVisible: boolean;
  onClose: () => void;
  onApply: (filters: { month: Date | null; companyId: string | null }) => void;
  companies: CompanyOption[];
  isLoadingCompanies: boolean;
  initialMonth?: Date | null;
  initialCompanyId?: string | null;
}

const FilterBottomSheet: React.FC<FilterBottomSheetProps> = ({
  isVisible,
  onClose,
  onApply,
  companies,
  isLoadingCompanies,
  initialMonth,
  initialCompanyId,
}) => {
  const { t } = useAppTranslation();
  const insets = useSafeAreaInsets();
  const userProfile = useSelector((state: any) => state.auth.userProfile);
  const [selectedMonth, setSelectedMonth] = useState<Date | null>(
    initialMonth || new Date(),
  );
  const [selectedCompanyId, setSelectedCompanyId] = useState<string | null>(
    initialCompanyId || null,
  );
  const [selectedCompanyName, setSelectedCompanyName] = useState<string | null>(
    null,
  );
  const [isMonthPickerVisible, setIsMonthPickerVisible] = useState(false);
  const [isCompanyModalVisible, setIsCompanyModalVisible] = useState(false);

  useEffect(() => {
    if (isVisible) {
      if (
        initialMonth &&
        (!selectedMonth ||
          dayjs(initialMonth).format('MM-YYYY') !==
            dayjs(selectedMonth).format('MM-YYYY'))
      ) {
        setSelectedMonth(initialMonth);
      } else if (!selectedMonth) {
        setSelectedMonth(new Date());
      }

      if (initialCompanyId && initialCompanyId !== selectedCompanyId) {
        setSelectedCompanyId(initialCompanyId);
        const company = companies.find(c => c.companyId === initialCompanyId);
        setSelectedCompanyName(company?.companyName || null);
      } else if (!selectedCompanyId) {
        const defaultCompanyId = userProfile?.companyRoles?.[0]?.companyId;
        const defaultCompanyName = userProfile?.companyRoles?.[0]?.companyName;
        if (defaultCompanyId) {
          setSelectedCompanyId(defaultCompanyId);
          setSelectedCompanyName(defaultCompanyName || null);
        }
      }
    }
    // eslint-disable-next-line react-hooks/exhaustive-deps
  }, [isVisible, initialMonth, initialCompanyId]);

  useEffect(() => {
    if (selectedCompanyId && companies.length > 0) {
      const company = companies.find(c => c.companyId === selectedCompanyId);
      if (company) {
        setSelectedCompanyName(company.companyName);
      }
    }
  }, [selectedCompanyId, companies]);

  const handleClearAll = () => {
    setSelectedMonth(new Date());
    setSelectedCompanyId(null);
    setSelectedCompanyName(null);
  };

  const handleSelectCompany = (company: CompanyOption) => {
    setSelectedCompanyId(company.companyId);
    setSelectedCompanyName(company.companyName);
    setIsCompanyModalVisible(false);
  };

  const handleApply = () => {
    onApply({
      month: selectedMonth,
      companyId: selectedCompanyId,
    });
    onClose();
  };

  const formatMonthYear = (date: Date | null) => {
    if (!date) return '';
    return dayjs(date).format('MM-YYYY');
  };

  return (
    <>
      <Modal
        isVisible={isVisible}
        style={styles.filterModal}
        backdropOpacity={0.3}
        backdropColor={colors.black}
        animationIn="slideInUp"
        animationOut="slideOutDown"
        onBackdropPress={onClose}
      >
        <View
          style={[styles.filterModalContent, { paddingBottom: insets.bottom }]}
        >
          <View style={styles.filterHeader}>
            <Text style={styles.filterTitle}>{t('Filter')}</Text>
          </View>

          <View style={styles.filterSection}>
            <Text style={styles.filterLabel}>{t('Select month and year')}</Text>
            <TouchableOpacity
              style={styles.filterInput}
              onPress={() => setIsMonthPickerVisible(true)}
              activeOpacity={0.7}
            >
              <Text
                style={[
                  styles.filterInputText,
                  selectedMonth
                    ? { color: colors.black }
                    : { color: colors.grayScale70 },
                ]}
              >
                {selectedMonth
                  ? formatMonthYear(selectedMonth)
                  : t('Select month and year')}
              </Text>
              <SvgXml
                xml={IconCalendar(colors.grayScale70)}
                width={24}
                height={24}
              />
            </TouchableOpacity>
          </View>

          <View style={styles.filterSection}>
            <Text style={styles.filterLabel}>{t('Select Company')}</Text>
            <TouchableOpacity
              style={styles.filterInput}
              onPress={() => setIsCompanyModalVisible(true)}
              activeOpacity={0.7}
            >
              <Text
                style={[
                  styles.filterInputText,
                  selectedCompanyName
                    ? { color: colors.black }
                    : { color: colors.grayScale70 },
                ]}
                numberOfLines={1}
              >
                {selectedCompanyName || t('Select Company')}
              </Text>
              <SvgXml
                xml={IconChevronDown(colors.grayScale70)}
                width={24}
                height={24}
              />
            </TouchableOpacity>
          </View>

          <View style={styles.filterActions}>
            <Button
              variant="secondary"
              style={styles.filterClearButton}
              onPress={handleClearAll}
            >
              <Text style={styles.filterClearButtonText}>{t('Clear All')}</Text>
            </Button>
            <Button style={styles.filterApplyButton} onPress={handleApply}>
              <Text style={styles.filterApplyButtonText}>{t('Apply')}</Text>
            </Button>
          </View>

          {isMonthPickerVisible && (
            <WheelMonthYearPicker
              key={`month-picker-${dayjs(selectedMonth || new Date()).format(
                'MM-YYYY',
              )}`}
              isVisible={isMonthPickerVisible}
              initialDate={dayjs(selectedMonth || new Date())
                .startOf('month')
                .toDate()}
              minDate={new Date(dayjs().year() - 5, 0)}
              maxDate={new Date()}
              handleSelectedDate={(date: any) => {
                const raw =
                  typeof date === 'string' ? date?.trim?.() || '' : '';
                const parsed = dayjs(
                  raw,
                  ['MM-YYYY', 'MM/YYYY', 'YYYY-MM', 'YYYY/MM'],
                  true,
                );
                const safe = parsed.isValid() ? parsed : dayjs();
                const parsedDate = safe.startOf('month').toDate();

                setSelectedMonth(parsedDate);
                setIsMonthPickerVisible(false);
              }}
              styleButtonCancel={{ backgroundColor: colors.mainColorPrimary }}
              styleButtonConfirm={{ backgroundColor: colors.mainColorPrimary }}
              styleTextCancel={{ color: colors.white }}
              styleTextConfirm={{ color: colors.white }}
              onCancel={() => setIsMonthPickerVisible(false)}
            />
          )}
        </View>
      </Modal>

      <Modal
        isVisible={isCompanyModalVisible}
        style={styles.filterModal}
        backdropOpacity={0.3}
        backdropColor={colors.black}
        animationIn="slideInUp"
        animationOut="slideOutDown"
        onBackdropPress={() => setIsCompanyModalVisible(false)}
      >
        <View
          style={[
            styles.filterModalContent,
            styles.filterModalContentCompany,
            { paddingBottom: insets.bottom },
          ]}
        >
          <View style={[styles.filterHeader, styles.filterHeaderCompany]}>
            <Text style={styles.filterTitle}>{t('Select Company')}</Text>
            <Button
              variant="ghost"
              onPress={() => setIsCompanyModalVisible(false)}
            >
              <Text style={styles.filterCloseText}>
                <SvgXml
                  xml={IconClose(colors.mainColorPrimary)}
                  width={24}
                  height={24}
                />
              </Text>
            </Button>
          </View>

          {isLoadingCompanies ? (
            <View style={styles.filterLoadingContainer}>
              <Text>{t('Loading')}...</Text>
            </View>
          ) : (
            <FlatList
              data={companies}
              keyExtractor={item => item.companyId}
              renderItem={({ item }) => (
                <TouchableOpacity
                  style={[
                    styles.filterCompanyItem,
                    selectedCompanyId === item.companyId &&
                      styles.filterCompanyItemSelected,
                  ]}
                  onPress={() => handleSelectCompany(item)}
                  activeOpacity={0.7}
                >
                  <Text
                    style={[
                      styles.filterCompanyItemText,
                      selectedCompanyId === item.companyId &&
                        styles.filterCompanyItemTextSelected,
                    ]}
                  >
                    {item.companyName}
                  </Text>
                  {selectedCompanyId === item.companyId && (
                    <SvgXml
                      xml={IconCheck(colors.mainColorPrimary)}
                      width={20}
                      height={20}
                    />
                  )}
                </TouchableOpacity>
              )}
              ListEmptyComponent={
                <View style={styles.filterLoadingContainer}>
                  <Text style={styles.filterEmptyText}>
                    {t('No companies found')}
                  </Text>
                </View>
              }
            />
          )}
        </View>
      </Modal>
    </>
  );
};

export default FilterBottomSheet;
