import { ActivityIndicator, Platform, View } from 'react-native';
import { Button, Text } from 'tradewize-component';
import { styles } from './styles';
import { useDispatch, useSelector } from 'react-redux';
import { colors } from '../../../../theme';
import { useAppTranslation } from '../../../../hooks/useAppTranslation';
import WebView from 'react-native-webview';
import dayjs from 'dayjs';
import EmptyComponent from '../EmptyComponent';
import { useState } from 'react';
import { SvgXml } from 'react-native-svg';
import { IconArrowDown2 } from '../../../../assets/svg';
import { getAvailableLanguages, logger } from '../../../../utils';

const Content = () => {
  const { t } = useAppTranslation();
  const pendingContract = useSelector(
    (state: any) => state?.contract?.pendingContract,
  );

  if (pendingContract?.data?.length > 0) {
    return (
      <View style={styles.cardCompany}>
        <Text style={styles.textCompany} numberOfLines={1}>
          {t('COMPANY')}: {pendingContract?.data?.[0]?.companyName}
        </Text>
        <Text style={styles.currentCompany} numberOfLines={1}>
          1/{pendingContract?.data?.length}
        </Text>
      </View>
    );
  }

  return null;
};

const ChangeLanguageContract = ({
  handleChangeLanguageContract,
  selectedLanguage,
}: {
  handleChangeLanguageContract: () => void;
  selectedLanguage: string;
}) => {
  const { t } = useAppTranslation();
  const pendingContract = useSelector(
    (state: any) => state?.contract?.pendingContract,
  );

  const languages = getAvailableLanguages()?.map((item: any) => ({
    id: item.key,
    label: item.value,
  }));

  const initialSelectedItem = languages.find(
    (item: any) => item.id === selectedLanguage,
  );

  if (pendingContract?.data?.length > 0) {
    return (
      <View style={styles.changeLanguageContractContainer}>
        <Text style={styles.changeLanguageContractText}>
          {t('CHANGE_TO_ANOTHER_LANGUAGE')}
          <Text style={styles.txtRequired}>(*)</Text>
        </Text>
        <Button
          onPress={handleChangeLanguageContract}
          title={initialSelectedItem?.label || 'English'}
          variant="primary"
          size="large"
          textStyle={styles.changeLanguageContractButtonText}
          style={styles.changeLanguageContractButton}
          rightIcon={
            <SvgXml
              xml={IconArrowDown2(colors.mainColorPrimary)}
              width={12}
              height={12}
            />
          }
        />
      </View>
    );
  }

  return null;
};

const WebViewComponent = ({
  setIsContractAccepted,
}: {
  setIsContractAccepted: (isContractAccepted: boolean) => void;
}) => {
  const { t } = useAppTranslation();
  const [loading, setLoading] = useState(true);

  const templateContractDetail = useSelector(
    (state: any) => state?.contract?.templateContractDetail,
  );

  const styledHtml = `
    <html>
      <head>
        <style>
          ol {
            list-style-type: disc !important;
            padding-left: 20px;
          }
        </style>
      </head>
      <body>
        ${templateContractDetail?.data?.contractHtml || ''}
      </body>
    </html>
  `;

  if (!templateContractDetail?.data?.contractHtml) {
    if (templateContractDetail?.error) {
      return (
        <EmptyComponent
          text={
            templateContractDetail?.error || t('NO_CONTRACTS_NEED_TO_BE_SIGNED')
          }
        />
      );
    }

    return null;
  }

  return (
    <View style={styles.webViewContainer}>
      <WebView
        originWhitelist={['*']}
        source={{ html: styledHtml }}
        style={styles.webView}
        onLoadStart={() => setLoading(true)}
        onLoadEnd={() => setLoading(false)}
        scalesPageToFit={true}
        zoomEnabled={true}
        javaScriptEnabled={true}
        domStorageEnabled={true}
        injectedJavaScript={`
                    document.body.style.fontSize = '2em';
                    ${
                      Platform.OS === 'ios'
                        ? 'document.getElementsByTagName("html")[0].style.zoom = 2;'
                        : 'document.body.style.transform = "scale(2)"; document.body.style.transformOrigin = "0 0";'
                    }
                    window.ReactNativeWebView.postMessage('Zoom applied');
                    true;
                  `}
      />
      <View style={styles.footerContainer}>
        <View style={styles.footerTimer}>
          <Text style={styles.footerTimerText}>
            {dayjs().format('MM/DD/YYYY')}
          </Text>
        </View>
        <Button
          onPress={() => setIsContractAccepted(true)}
          size="large"
          variant="primary"
          style={styles.footerButton}
          title={t('ACCEPT_CONTRACT')}
        />
      </View>

      {loading && (
        <View style={styles.loadingContainer}>
          <ActivityIndicator size={24} color={colors.mainColorPrimary} />
        </View>
      )}
    </View>
  );
};

const ContractsToSign = ({
  setIsContractAccepted,
}: {
  setIsContractAccepted: (isContractAccepted: boolean) => void;
}) => {
  const dispatch = useDispatch();
  const { t, i18n } = useAppTranslation();
  const [selectedLanguage, setSelectedLanguage] = useState<string>(
    i18n?.language || 'en',
  );

  const pendingContract = useSelector(
    (state: any) => state?.contract?.pendingContract,
  );
  const templateContractDetail = useSelector(
    (state: any) => state?.contract?.templateContractDetail,
  );

  const handleChangeLanguageContract = async () => {
    try {
      const redux = await import('auth/redux');
      const languages = getAvailableLanguages()?.map((item: any) => ({
        id: item.key,
        label: item.value,
      }));

      const initialSelectedItem = languages.find(
        (item: any) => item.id === selectedLanguage,
      );

      dispatch(
        redux.actionContainer.showSelectBottomModal({
          title: t('SELECT_AN_OPTION'),
          dataFilter: languages,
          initialSelectedItem,
          txtClose: t('CLOSE'),
          handleSelectItem: (item: any) => {
            setSelectedLanguage(item.id);
            dispatch(
              redux.actionContainer.getTemplateContractDetailAsync({
                companyId: pendingContract?.data?.[0]?.companyId,
                userType: pendingContract?.data?.[0]?.userType,
                language: item.id,
              }),
            );
          },
        }),
      );
    } catch (error) {
      logger('error handleChangeLanguageContract', error);
    }
  };

  return (
    <View style={styles.signedContractsContainer}>
      <Content />

      <ChangeLanguageContract
        handleChangeLanguageContract={handleChangeLanguageContract}
        selectedLanguage={selectedLanguage}
      />

      <WebViewComponent setIsContractAccepted={setIsContractAccepted} />

      {pendingContract?.data?.length === 0 &&
        !pendingContract?.isLoading &&
        !templateContractDetail?.isLoading &&
        !templateContractDetail?.data?.contractHtml &&
        !templateContractDetail?.error && (
          <EmptyComponent text={t('NO_CONTRACTS_NEED_TO_BE_SIGNED')} />
        )}

      {(pendingContract?.isLoading || templateContractDetail?.isLoading) && (
        <View style={styles.loadingContainer}>
          <ActivityIndicator size={24} color={colors.mainColorPrimary} />
        </View>
      )}
    </View>
  );
};

export default ContractsToSign;
