import React, { useState } from 'react';
import { View } from 'react-native';
import { Text, Button } from 'tradewize-component';
import FastImage from 'react-native-fast-image';
import { colors } from '../../../theme';
import { styles } from '../styles';
import { SvgXml } from 'react-native-svg';
import { FileItem, VideoItem, Checklist } from '../../../types/roleTypes';
import { IconPlay } from '../../../assets/svg';
import {
  handleFilePress,
  handleVideoPress,
  isImageFile,
} from '../../../helper/RoleTypeScreen.helper';
import { ImageModal } from '../../../components/ImageModal';
import { thumbnail } from '../../../assets/images';
import { useAppTranslation } from '../../../hooks/useAppTranslation';

interface ChecklistRowProps {
  checklist: Checklist;
  index: number;
}

export const ChecklistRow = ({ checklist, index }: ChecklistRowProps) => {
  const { i18n } = useAppTranslation();
  const [selectedImageUrl, setSelectedImageUrl] = useState<string | null>(null);
  const [isImageModalVisible, setIsImageModalVisible] = useState(false);

  // Get all media from this checklist
  const allMedia: Array<
    (FileItem & { type: 'file' }) | (VideoItem & { type: 'video' })
  > = [];

  // Add files from filesRaw
  (checklist?.filesRaw ?? []).forEach(file => {
    if (file?.url) {
      allMedia.push({ ...file, type: 'file' as const });
    }
  });
  // Add videos
  (checklist?.videos ?? []).forEach(video => {
    if (video?.url) {
      allMedia.push({ ...video, type: 'video' as const });
    }
  });

  // Separate images, videos, and files
  const images = allMedia.filter(
    item => item?.type === 'file' && isImageFile(item?.fileExtension),
  );
  const videos = allMedia.filter(item => item?.type === 'video');
  const files = allMedia.filter(
    item => item?.type === 'file' && !isImageFile(item?.fileExtension),
  );

  const hasMedia = allMedia?.length > 0;

  const handleImagePress = (imageUrl: string) => {
    setSelectedImageUrl(imageUrl);
    setIsImageModalVisible(true);
  };

  const handleCloseImageModal = () => {
    setIsImageModalVisible(false);
    setSelectedImageUrl(null);
  };

  return (
    <View style={styles.checklistRow}>
      <View style={styles.checklistRowTop}>
        <Text style={styles.checklistRowNumber}>{index + 1}.</Text>
        <Text style={styles.checklistRowName} numberOfLines={2}>
          {checklist?.name}
        </Text>
      </View>

      {/* Bottom: Media */}
      {hasMedia && (
        <View style={styles.checklistRowBottom}>
          {(videos?.length > 0 || images?.length > 0) && (
            <View style={styles.checklistRowMediaContainer}>
              {/* Display Videos with thumbnail */}
              {videos?.map((video: any, videoIndex: number) => {
                // Check if video has thumbnailUrl (if available in data)
                const thumbnailUrl = (video as any).thumbnailUrl || null;

                return (
                  <Button
                    key={video?.fileId || video?.url || `video-${videoIndex}`}
                    style={styles.checklistRowVideoThumbnail}
                    onPress={() => handleVideoPress(video, i18n)}
                    activeOpacity={0.7}
                  >
                    <FastImage
                      source={thumbnailUrl ? { uri: thumbnailUrl } : thumbnail}
                      style={styles.checklistRowVideoThumbnailImage}
                      resizeMode="cover"
                    />
                    <View style={styles.checklistRowVideoPlayButton}>
                      <SvgXml
                        xml={IconPlay(colors.white)}
                        width={30}
                        height={30}
                      />
                    </View>
                  </Button>
                );
              })}

              {/* Display Images */}
              {images.map((image: any, imageIndex: number) => (
                <Button
                  key={image?.fileId || image?.url || `image-${imageIndex}`}
                  style={styles.checklistRowImageWrapper}
                  onPress={() => handleImagePress(image?.url)}
                  activeOpacity={0.7}
                >
                  <FastImage
                    source={image?.url ? { uri: image?.url } : thumbnail}
                    style={styles.checklistRowImage}
                    resizeMode="cover"
                  />
                </Button>
              ))}
            </View>
          )}

          {/* Image Modal */}
          <ImageModal
            visible={isImageModalVisible}
            imageUrl={selectedImageUrl}
            onClose={handleCloseImageModal}
          />

          {/* Display Files */}
          {files?.length > 0 && (
            <View style={styles.checklistRowMediaContainer}>
              {files.map((file: any, fileIndex: number) => (
                <Button
                  key={file?.fileId || file?.url || `file-${fileIndex}`}
                  style={styles.checklistRowFileItem}
                  onPress={() => handleFilePress(file)}
                  activeOpacity={0.7}
                >
                  <Text style={styles.checklistRowFileName} numberOfLines={1}>
                    {file?.name || 'File'}
                  </Text>
                </Button>
              ))}
            </View>
          )}
        </View>
      )}
    </View>
  );
};
