import { TimePeriod } from '../types/global';
import languages from '@cospired/i18n-iso-languages';
import i18n from '../i18n';
import { Buffer } from 'buffer';
import { presignedUrl } from '../api/endpoint';

// m bo i18n  c khi to trc khi truy cp options
const getSupportedCodes = () => {
  try {
    if (i18n?.options?.resources) {
      return Object.keys(i18n.options.resources);
    }
  } catch (error) {
    console.warn('i18n not initialized yet:', error);
  }
  return [];
};

const supportedCodes = getSupportedCodes();

export const logger = (...args: any[]) => {
  if (__DEV__) {
    console.log(...args);
  }
};

// Hm import ng locale tng ng
supportedCodes.forEach(code => {
  try {
    const langData = require(`@cospired/i18n-iso-languages/langs/${code}.json`);
    languages.registerLocale(langData);
  } catch {
    // fallback nu locale khng tn ti trong th vin
    console.warn(`No locale found for: ${code}`);
  }
});

export const delay = (ms: number) =>
  new Promise<void>(resolve => setTimeout(resolve, ms));

export const formatCurrency = (amount: number): string => {
  return new Intl.NumberFormat('en-US', {
    style: 'currency',
    currency: 'USD',
  }).format(amount);
};

export const formatDate = (date: Date | string): string => {
  const d = typeof date === 'string' ? new Date(date) : date;
  return d.toLocaleDateString('en-US', {
    year: 'numeric',
    month: 'long',
    day: 'numeric',
  });
};

export function getTimePeriod(date: Date = new Date()): TimePeriod {
  const hours = date.getHours();
  const minutes = date.getMinutes();
  const totalMinutes = hours * 60 + minutes;

  const morningStart = 5 * 60; // 05:00
  const morningEnd = 11 * 60 + 59; // 11:59
  const afternoonStart = 12 * 60; // 12:00
  const afternoonEnd = 16 * 60 + 59; // 16:59
  const eveningStart = 17 * 60; // 17:00
  const eveningEnd = 20 * 60 + 59; // 20:59
  // const nightStart = 21 * 60; // 21:00
  // const nightEnd = 4 * 60 + 59; // 04:59 (hm sau)

  if (totalMinutes >= morningStart && totalMinutes <= morningEnd) {
    return 'morning';
  } else if (totalMinutes >= afternoonStart && totalMinutes <= afternoonEnd) {
    return 'afternoon';
  } else if (totalMinutes >= eveningStart && totalMinutes <= eveningEnd) {
    return 'evening';
  } else {
    return 'night';
  }
}

export const getPasswordStrength = (password: string = '') => {
  let score = 0;

  if (password) {
    if (password.length >= 8) {
      score++;
    }
    if (/[A-Z]/.test(password)) {
      score++;
    }
    if (/[@/#]/.test(password)) {
      score++;
    }
    if (/^\S*$/.test(password)) {
      score++;
    }
  }

  return score;
};

export const base64ToBinaryForFormData = (
  base64String: string | null,
  fileName = 'signature.png', // Mc nh tn file l 000_B.jpg
  mimeType = 'image/png', // Mc nh loi MIME l image/jpeg
) => {
  try {
    // Kim tra u vo
    if (!base64String || typeof base64String !== 'string') {
      throw new Error('Invalid Base64 string');
    }

    // Loi b prefix nu c (v d: "data:image/jpeg;base64,")
    const base64Data = base64String.replace(/^data:[a-z]+\/[a-z]+;base64,/, '');

    // Chuyn Base64 thnh binary bng Buffer
    const binaryData = Buffer.from(base64Data, 'base64');
    logger('binaryData (Buffer):', binaryData); // Log  kim tra binaryData

    // To i tng file cho FormData
    const fileObject = {
      uri: `data:${mimeType};${base64String}`, // URI cho React Native
      type: mimeType, // Loi file: image/jpeg
      name: fileName, // Tn file: 000_B.jpg
    };

    return fileObject;
  } catch (error) {
    logger('Error converting Base64 to binary:', error);
    throw error;
  }
};

export const getFileFromS3Key = async (key: string): Promise<string> => {
  try {
    const client = await import('auth/api');
    const encodedKey = encodeURIComponent(key);
    const response = await client.getRequest(`${presignedUrl}/${encodedKey}`);

    if (response && response.status === 200) {
      return response.data;
    }
    return '';
  } catch (error) {
    return '';
  }
};

export const formatRelativeTime = (dateString: string): string => {
  const date = new Date(dateString);
  const now = new Date();
  const diffInSeconds = Math.floor((now.getTime() - date.getTime()) / 1000);

  if (diffInSeconds < 60) {
    return 'Just now';
  }

  const diffInMinutes = Math.floor(diffInSeconds / 60);
  if (diffInMinutes < 60) {
    return `${diffInMinutes} ${diffInMinutes === 1 ? 'minute' : 'minutes'} ago`;
  }

  const diffInHours = Math.floor(diffInMinutes / 60);
  if (diffInHours < 24) {
    return `${diffInHours} ${diffInHours === 1 ? 'hour' : 'hours'} ago`;
  }

  const diffInDays = Math.floor(diffInHours / 24);
  if (diffInDays < 7) {
    return `${diffInDays} ${diffInDays === 1 ? 'day' : 'days'} ago`;
  }

  const diffInWeeks = Math.floor(diffInDays / 7);
  if (diffInWeeks < 4) {
    return `${diffInWeeks} ${diffInWeeks === 1 ? 'week' : 'weeks'} ago`;
  }

  const diffInMonths = Math.floor(diffInDays / 30);
  if (diffInMonths < 12) {
    return `${diffInMonths} ${diffInMonths === 1 ? 'month' : 'months'} ago`;
  }

  const diffInYears = Math.floor(diffInDays / 365);
  return `${diffInYears} ${diffInYears === 1 ? 'year' : 'years'} ago`;
};

export const getAvailableLanguages = () => {
  const current = i18n?.language || 'en';
  return supportedCodes.map(code => ({
    key: code,
    value:
      languages.getName(code, current) ||
      languages.getName(code, 'en') ||
      code.toUpperCase(),
  }));
};

export const formatMiles = (distance: number) => {
  const formatted = Number(distance).toFixed(1);
  return formatted.endsWith('.0') ? formatted.slice(0, -2) : formatted;
};

export const formatDateToMMDD = (dateString: string) => {
  if (!dateString) return '';
  const date = new Date(dateString);
  const day = date.getDate().toString().padStart(2, '0');
  const month = (date.getMonth() + 1).toString().padStart(2, '0');
  return `${month}/${day}`;
};

