import React, { useCallback, useEffect, useState } from 'react';
import { View } from 'react-native';
import { Text } from 'tradewize-component';
import FastImage from 'react-native-fast-image';
import { useSelector } from 'react-redux';
import { styles } from '../styles';
import { getMyScore } from '../../../api/endpoint';

interface LeaderboardUser {
  rank: number;
  name: string;
  role: string;
  points: number;
  avatar?: string;
  isCurrentUser?: boolean;
  userId?: string | number;
}

interface ListItemProps {
  user?: LeaderboardUser;
}

interface KPIScoreItem {
  score: number;
  kpiType: number;
  kpiTypeName: string;
  scoreWeight: number;
  target: number;
  minTarget: number;
  totalItems: number;
  completedItems: number;
}

const ListItem: React.FC<ListItemProps> = ({ user: userProp }) => {
  const userProfile = useSelector((state: any) => state.auth.userProfile);
  const [userData, setUserData] = useState<LeaderboardUser | null>(null);
  const [isLoading, setIsLoading] = useState(false);
  const currentAccountId = userProfile?.accountId || userProfile?.userId;

  const fetchMyScore = useCallback(async () => {
    setIsLoading(true);
    try {
      const selectedMonth = new Date();
      const year = selectedMonth.getFullYear();
      const monthNumber = selectedMonth.getMonth() + 1;
      const companyId = userProfile?.companyRoles?.[0]?.companyId;

      if (!companyId) {
        setIsLoading(false);
        return;
      }

      const client = await import('auth/api');
      const response = await client.getRequest(
        `${getMyScore}?Year=${year}&Month=${monthNumber}&CompanyId=${companyId}`,
      );

      let dataArray: KPIScoreItem[] = [];
      if (Array.isArray(response?.data)) {
        dataArray = response.data as KPIScoreItem[];
      } else if (response?.data?.data && Array.isArray(response.data.data)) {
        dataArray = response.data.data as KPIScoreItem[];
      }

      const totalScore = dataArray.reduce((sum, item) => sum + (item.score || 0), 0);
      const roleName = userProfile?.companyRoles?.[0]?.roles?.[0]?.roleName || 'N/A';
      const fullName = userProfile?.fullName || userProfile?.firstName || 'User';
      const avatar = userProfile?.photoUrl || userProfile?.bucketUrl;

      setUserData({
        rank: 0,
        name: fullName,
        role: roleName,
        points: totalScore,
        avatar: avatar,
        isCurrentUser: true,
      });
    } catch (error) {
      console.error('Error fetching my score:', error);
      setUserData(null);
    } finally {
      setIsLoading(false);
    }
  }, [userProfile]);

  useEffect(() => {
    if (!userProp) {
      fetchMyScore();
    } else {
      const workerId = userProp.userId;
      
      let isCurrentUser = false;
      if (currentAccountId && workerId) {
        isCurrentUser = String(currentAccountId) === String(workerId);
      }
      
      isCurrentUser = isCurrentUser || userProp.isCurrentUser || false;
      
      setUserData({
        ...userProp,
        isCurrentUser,
      });
    }
  }, [userProp, fetchMyScore, currentAccountId]);

  const getInitials = (name: string) => {
    const parts = name.trim().split(' ');
    if (parts.length >= 2) {
      return `${parts[0][0]}${parts[parts.length - 1][0]}`.toUpperCase();
    }
    return name.substring(0, 2).toUpperCase();
  };

  if (isLoading) {
    return (
      <View style={styles.listItemContainer}>
        <Text>Loading...</Text>
      </View>
    );
  }

  if (!userData) {
    return null;
  }

  const displayName = userData.isCurrentUser ? 'You' : userData.name;

  return (
    <View
      style={[
        styles.listItemContainer,
        userData.isCurrentUser && styles.listItemCurrentUserContainer,
      ]}
    >
      <Text style={styles.listItemRankNumber}>{userData.rank || '-'}</Text>

      <View style={styles.listItemAvatarContainer}>
        {userData.avatar ? (
          <FastImage
            source={{ uri: userData.avatar }}
            style={styles.listItemAvatar}
            resizeMode={FastImage.resizeMode.cover}
          />
        ) : (
          <View style={styles.listItemAvatarPlaceholder}>
            <Text style={styles.listItemAvatarInitials}>
              {getInitials(userData.name)}
            </Text>
          </View>
        )}
      </View>

      <View style={styles.listItemContentContainer}>
        <Text style={styles.listItemName} numberOfLines={1}>
          {displayName}
        </Text>
        <Text style={styles.listItemRole} numberOfLines={1}>
          {userData.role}
        </Text>
        <Text style={styles.listItemPoints}>
          {userData.points.toFixed(2)} pts
        </Text>
      </View>
    </View>
  );
};

export default ListItem;
