import React, { useMemo, useState, useEffect } from 'react';
import {
  View,
  StyleSheet,
  Platform,
  ActivityIndicator,
  Text,
  SafeAreaView,
} from 'react-native';
import { WebView } from 'react-native-webview';
import Pdf from 'react-native-pdf';
import { FileInfo } from '../../types';
import { SvgXml } from 'react-native-svg';
import { colors } from '../../../../theme';
import { IconClose } from '../../../../assets/svg';
import { Button, SCREEN_WIDTH } from 'tradewize-component';

interface DocumentViewerProps {
  fileInfo: FileInfo;
  onLoadEnd: () => void;
  onError: (error: string) => void;
  onClose: () => void;
}

const DocumentViewer: React.FC<DocumentViewerProps> = ({
  fileInfo,
  onLoadEnd,
  onError,
  onClose,
}) => {
  const [error, setError] = useState<string | null>(null);
  const [loading, setLoading] = useState(true);
  const [progress, setProgress] = useState(0);
  const [url, setUrl] = useState<string>('');

  // Reset states when fileInfo change
  useEffect(() => {
    // Clear any existing timer
    let timer: ReturnType<typeof setTimeout>;

    // Start a new timer
    timer = setTimeout(() => {
      const viewerUrl = `https://docs.google.com/viewer?url=${encodeURIComponent(
        fileInfo.uri || '',
      )}&embedded=true`;
      setUrl(viewerUrl);
    }, 1000);

    // Cleanup function to clear timer when fileInfo.uri changes or component unmounts
    return () => {
      if (timer) {
        clearTimeout(timer);
      }
    };
  }, [fileInfo.uri]); // Reset and restart timer when fileInfo.uri changes

  const handleError = (errorMessage: string) => {
    setError(errorMessage);
    onError(errorMessage);
    setLoading(false);
  };

  const handleLoadEnd = () => {
    setLoading(false);
    onLoadEnd();
  };

  const handleNavigationStateChange = (navState: any) => {
    setUrl(navState.url);
  };

  const handleProgress = (percent: number) => {
    setProgress(percent * 100);
    // Auto end loading on Android when progress reaches 90%
    if (Platform.OS === 'android' && percent >= 0.9) {
      handleLoadEnd();
    }
  };

  // Check if file is PDF
  const isPDF = useMemo(
    () => fileInfo.uri?.toLowerCase().endsWith('.pdf'),
    [fileInfo.uri],
  );

  const renderHeader = () => (
    <View style={styles.header}>
      <Text style={styles.urlText} numberOfLines={1}>
        {error || url || 'Loading document...'}
      </Text>
      <Button style={styles.closeButton} onPress={onClose}>
        <SvgXml xml={IconClose(colors.white)} width={24} height={24} />
      </Button>
    </View>
  );

  const renderError = () => (
    <SafeAreaView style={styles.container}>
      {renderHeader()}
      <View style={styles.errorContainer}>
        <Text style={styles.errorText}>{error}</Text>
      </View>
    </SafeAreaView>
  );

  if (error) {
    return renderError();
  }

  const renderWebView = (source: any) => (
    <SafeAreaView style={styles.container}>
      {renderHeader()}
      <View style={styles.webviewContainer}>
        <WebView
          source={source}
          style={styles.webview}
          onLoadEnd={handleLoadEnd}
          onError={syntheticEvent => {
            const { nativeEvent } = syntheticEvent;
            handleError(`Error loading document: ${nativeEvent.description}`);
          }}
          onLoadProgress={({ nativeEvent }) => {
            handleProgress(nativeEvent.progress);
          }}
          onNavigationStateChange={handleNavigationStateChange}
          javaScriptEnabled={true}
          domStorageEnabled={true}
          startInLoadingState={true}
          scalesPageToFit={true}
          mixedContentMode="always"
        />
        {loading && (
          <View style={styles.loadingContainer}>
            <ActivityIndicator size="large" color={colors.white} />
            <View style={styles.progressContainer}>
              <View style={[styles.progressBar, { width: `${progress}%` }]} />
            </View>
          </View>
        )}
      </View>
    </SafeAreaView>
  );

  // Handle PDF files using react-native-pdf
  if (isPDF) {
    return (
      <SafeAreaView style={styles.container}>
        {renderHeader()}
        <View style={styles.pdfContainer}>
          <Pdf
            source={{ uri: fileInfo.uri }}
            style={styles.pdf}
            onLoadComplete={() => {
              setUrl(fileInfo.uri || '');
              handleLoadEnd();
            }}
            onError={error => handleError(`PDF Error: ${error}`)}
            onLoadProgress={percent => {
              handleProgress(percent);
            }}
            enablePaging={true}
            horizontal={false}
            trustAllCerts={false}
          />
          {loading && (
            <View style={styles.loadingContainer}>
              <ActivityIndicator size="large" color={colors.white} />
              <View style={styles.progressContainer}>
                <View style={[styles.progressBar, { width: `${progress}%` }]} />
              </View>
            </View>
          )}
        </View>
      </SafeAreaView>
    );
  }

  return renderWebView({ uri: url });
};

const styles = StyleSheet.create({
  container: {
    flex: 1,
    backgroundColor: colors.black,
  },
  header: {
    height: 44,
    backgroundColor: 'rgba(0,0,0,0.8)',
    flexDirection: 'row',
    alignItems: 'center',
    justifyContent: 'space-between',
    paddingHorizontal: 16,
    borderBottomWidth: 1,
    borderBottomColor: 'rgba(255,255,255,0.1)',
  },
  urlText: {
    color: colors.white,
    fontSize: 14,
    flex: 1,
    marginRight: 16,
  },
  closeButton: {
    width: SCREEN_WIDTH * 0.15,
    height: SCREEN_WIDTH * 0.15,
    paddingHorizontal: 0,
    paddingVertical: 0,
    minHeight: 0,
    
    borderRadius: 16,
    backgroundColor: 'rgba(0,0,0,0.5)',
    justifyContent: 'center',
    alignItems: 'center',
  },
  webviewContainer: {
    flex: 1,
    backgroundColor: colors.black,
  },
  webview: {
    flex: 1,
    backgroundColor: colors.black,
  },
  pdfContainer: {
    flex: 1,
    backgroundColor: colors.black,
  },
  pdf: {
    flex: 1,
    backgroundColor: colors.black,
  },
  loadingContainer: {
    position: 'absolute',
    top: 0,
    left: 0,
    right: 0,
    bottom: 0,
    justifyContent: 'center',
    alignItems: 'center',
    backgroundColor: 'rgba(0,0,0,0.5)',
  },
  progressContainer: {
    width: '80%',
    height: 4,
    backgroundColor: 'rgba(255,255,255,0.2)',
    borderRadius: 2,
    marginTop: 40,
    overflow: 'hidden',
  },
  progressBar: {
    height: '100%',
    backgroundColor: colors.white,
    borderRadius: 2,
  },
  errorContainer: {
    flex: 1,
    justifyContent: 'center',
    alignItems: 'center',
    padding: 20,
    backgroundColor: colors.black,
  },
  errorText: {
    color: colors.white,
    fontSize: 16,
    textAlign: 'center',
  },
});

export default DocumentViewer;
