import React, { useState, useCallback, memo } from 'react';
import { View } from 'react-native';
import colors from '../../theme/colors';
import { Company } from '../../types/register-company';
import { SvgXml } from 'react-native-svg';
import { useAppTranslation } from '../../hooks/useAppTranslation';
import { getCompanyRegistrationStatus } from '../../helper/company';
import { IconArrowDown2, IconCheck } from '../../assets/svg/index';
import { Button, SCREEN_WIDTH, Text } from 'tradewize-component';
import { styles } from './style';

interface CompanyCardProps {
  company: Company;
  isSelected?: boolean;
  onToggleSelect?: (companyId: string) => void;
}

const CompanyCard: React.FC<CompanyCardProps> = memo(
  ({ company, isSelected = false, onToggleSelect }) => {
    const [isRoleExpanded, setIsRoleExpanded] = useState(false);
    const { t } = useAppTranslation();

    const handleToggleSelect = useCallback(() => {
      if (company?.canRegister !== true) {
        return;
      }

      onToggleSelect?.(company?.companyId);
    }, [company, onToggleSelect]);

    return (
      <View style={styles.companyCard}>
        {/* Header */}
        <View style={styles.cardHeader}>
          <View style={styles.titleContainer}>
            <Button
              variant={isSelected ? 'primary' : 'outline'}
              iconOnly
              leftIcon={
                isSelected ? (
                  <SvgXml
                    xml={IconCheck(colors.white)}
                    style={styles.checkboxIcon}
                  />
                ) : undefined
              }
              onPress={handleToggleSelect}
              isDisabled={!company.canRegister}
              style={[
                styles.checkbox,
                isSelected && styles.checkboxChecked,
                !company.canRegister && styles.checkboxDisabled,
              ]}
              size="small"
            />

            <Text style={styles.companyTitle}>
              {t('COMPANY')} - {company.companyName}
            </Text>
          </View>

          {getCompanyRegistrationStatus(company.companyRegistrationStatus)
            .statusName && (
            <View
              style={[
                styles.statusItem,
                {
                  borderColor: getCompanyRegistrationStatus(
                    company.companyRegistrationStatus,
                  ).color,
                },
              ]}
            >
              <Text
                style={[
                  styles.fieldValue,
                  {
                    color: getCompanyRegistrationStatus(
                      company.companyRegistrationStatus,
                    ).color,
                  },
                ]}
              >
                {t(
                  getCompanyRegistrationStatus(
                    company.companyRegistrationStatus,
                  ).statusName || '',
                )}
              </Text>
            </View>
          )}
        </View>

        {/* Content */}
        <View style={styles.cardBody}>
          {/* Business Name */}
          <View style={styles.companyItem}>
            <Text style={styles.fieldLabel}>{t('COMPANY_NAME')}:</Text>
            <Text style={styles.fieldValue}>{company?.companyName}</Text>
          </View>

          {/* Role Type Toggle */}
          <View style={styles.fieldRow}>
            <Button
              style={styles.roleTypeHeader}
              onPress={() => setIsRoleExpanded(!isRoleExpanded)}
              activeOpacity={0.7}
            >
              <Text style={styles.fieldLabel}>{t('ROLE_TYPE')}</Text>
              <SvgXml
                xml={IconArrowDown2(colors.black)}
                width={SCREEN_WIDTH * 0.03}
                height={SCREEN_WIDTH * 0.03}
                style={[
                  styles.dropdownIcon,
                  isRoleExpanded && styles.dropdownIconExpanded,
                ]}
              />
            </Button>

            {isRoleExpanded && (
              <View style={styles.roleListContainer}>
                {company?.roles?.map(role => (
                  <View key={role?.id}>
                    <View key={role?.id} style={styles.roleItem}>
                      <Text style={styles.roleText}>{role?.name}</Text>
                      {role?.isMyRole && (
                        <View style={styles.roleCheckmark}>
                          <SvgXml
                            xml={IconCheck(colors.mainColorPrimary)}
                            width={16}
                            height={16}
                          />
                        </View>
                      )}
                    </View>
                  </View>
                ))}
              </View>
            )}
          </View>
        </View>

        {getCompanyRegistrationStatus(company.companyRegistrationStatus)
          .message && (
          <View style={styles.statusContainer}>
            <Text style={styles.stepStatusText}>
              <Text style={styles.nonItalicText}>
                {t('REGISTRATION_STATUS')}
              </Text>
              : {''}
              {t(
                getCompanyRegistrationStatus(company.companyRegistrationStatus)
                  .message || '',
              )}
            </Text>
          </View>
        )}
      </View>
    );
  },
);

export default CompanyCard;
