import { StyleSheet } from 'react-native';
import { FileThumbnailSize } from '../types';
import { colors } from '../../../theme';

export const getThumbnailSize = (size: FileThumbnailSize) => {
  switch (size) {
    case 'small':
      return { width: 32, height: 32, borderRadius: 4, margin: 4 };
    case 'medium':
      return { width: 48, height: 48, borderRadius: 6, margin: 6 };
    case 'large':
      return { width: 80, height: 80, borderRadius: 8, margin: 8 };
    case 'xlarge':
      return { width: 120, height: 120, borderRadius: 12, margin: 12 };
    default:
      return { width: 48, height: 48, borderRadius: 6, margin: 6 };
  }
};

export const getIconSize = (size: FileThumbnailSize) => {
  switch (size) {
    case 'small':
      return 14;
    case 'medium':
      return 18;
    case 'large':
      return 24;
    case 'xlarge':
      return 32;
    default:
      return 18;
  }
};

export const getRemoveButtonSize = (size: FileThumbnailSize) => {
  switch (size) {
    case 'small':
      return 16;
    case 'medium':
      return 20;
    case 'large':
      return 24;
    case 'xlarge':
      return 28;
    default:
      return 16;
  }
};

export const commonStyles = StyleSheet.create({
  thumbnail: {
    overflow: 'hidden',
    backgroundColor: colors.lightGray,
  },
  thumbnailInstruction: {
    marginRight: 0,
  },
  mediaWrapper: {
    flex: 1,
  },
  mediaContent: {
    width: '100%',
    height: '100%',
    backgroundColor: colors.lightGray,
  },
  mediaTitle: {
    fontSize: 12,
    color: colors.darkGray,
    padding: 4,
  },
  removeButton: {
    position: 'absolute',
    top: 2,
    right: 2,
    backgroundColor: 'rgba(0,0,0,0.5)',
    alignItems: 'center',
    justifyContent: 'center',
  },
});

export * from './fileViewerStyles'; 