import React from 'react';
import { View } from 'react-native';
import { Button, Text } from 'tradewize-component';
import { colors } from '../../../../theme';
import { useAppTranslation } from '../../../../hooks/useAppTranslation';
import { formatDateHeader } from '../../../../helper/myJob.helpter';
import {
  formatAmount,
  getStatusInfo,
} from '../../../../helper/jobDetail.helpter';

interface TaskInformationProps {
  data: {
    activityTypeName?: string;
    taskInfomationName?: string;
    estStartDate?: string;
    wOEstStartDate?: string;
    actualStartDate?: string;
    estCompletionDate?: string;
    street?: string;
    city?: string;
    amount?: number;
    bonusAmount?: number;
    status?: number;
    communityCode?: string;
    lotNumber?: string;
    houseNumber?: string;
    duration?: number;
  };
  onPress?: () => void;
}

const TaskInformation: React.FC<TaskInformationProps> = ({ data }) => {
  const { t } = useAppTranslation();

  const address = [data.street].filter(Boolean).join(', ') || '-';

  const commLot = [data.communityCode, data.lotNumber]
    .filter(Boolean)
    .join('/');

  return (
    <View style={styles.content}>
      <View style={styles.row}>
        <Text style={styles.label}>{t('TASK')}:</Text>
        <Text style={styles.value}>{data.activityTypeName || '-'}</Text>
      </View>

      <View style={styles.row}>
        <Text style={styles.label}>{t('NAME')}:</Text>
        <Text style={styles.value}>{data.taskInfomationName || '-'}</Text>
      </View>

      <View style={styles.row}>
        <Text style={styles.label}>{t('EST_START')}:</Text>
        <Text style={styles.value}>
          {formatDateHeader(data.estStartDate || data.wOEstStartDate || '')}
        </Text>
      </View>

      <View style={styles.row}>
        <Text style={styles.label}>{t('EST_COMPLETION')}:</Text>
        <Text style={styles.value}>
          {formatDateHeader(data.estCompletionDate || '')}
        </Text>
      </View>

      <View style={styles.row}>
        <Text style={styles.label}>{t('ADDRESS')}:</Text>
        <Button style={styles.value} activeOpacity={0.7}>
          <Text style={[styles.linkText]}>{address}</Text>
        </Button>
      </View>

      <View style={styles.row}>
        <Text style={styles.label}>{t('AMOUNT')}:</Text>
        <Text style={[styles.value, styles.linkText]}>
          {formatAmount(t, data.amount, data.bonusAmount) || '-'}
        </Text>
      </View>

      <View style={styles.row}>
        <Text style={styles.label}>{t('STATUS')}:</Text>
        <View
          style={[
            styles.statusBadge,
            { backgroundColor: getStatusInfo(t, data.status).color + '20' },
          ]}
        >
          <Text
            style={[
              styles.statusText,
              { color: getStatusInfo(t, data.status).color },
            ]}
          >
            {getStatusInfo(t, data.status).text}
          </Text>
        </View>
      </View>

      <View style={styles.row}>
        <View style={styles.rowItem}>
          <View style={styles.rowItemLeft}>
            <Text style={styles.label}>{t('COMM_LOT')}:</Text>
            <Text style={[styles.linkText]}>{commLot || '-'}</Text>
          </View>
          <View style={styles.rowItemRight}>
            <Text style={styles.label}>{t('DURATION')}:</Text>
            <Text>{data.duration ? `${data.duration} ${t('DAYS')}` : '-'}</Text>
          </View>
        </View>
      </View>
    </View>
  );
};

const styles = {
  content: {
    paddingBottom: 0,
  },
  row: {
    flexDirection: 'row',
    justifyContent: 'space-between',
    gap: 16,
  },
  rowItem: {
    flex: 1,
    flexDirection: 'row',
    alignItems: 'center',
    gap: 4,
  },
  rowItem2: {
    flex: 1,
    flexDirection: 'row',
    justifyContent: 'space-between',
    gap: 4,
  },
  rowItemLeft: {
    flex: 1,
    flexDirection: 'row',
    alignItems: 'flex-end',
    justifyContent: 'flex-start',
    gap: 4,
  },
  rowItemRight: {
    flex: 1,
    flexDirection: 'row',
    alignItems: 'flex-end',
    justifyContent: 'flex-end',
    gap: 4,
  },
  label: {
    fontSize: 14,
    color: colors.black,
    fontWeight: '500',
  },
  value: {
    fontSize: 14,
    color: colors.black,
    flex: 1,
    textAlign: 'right',
    backgroundColor: colors.white,
    paddingHorizontal: 0,
    paddingVertical: 0,
    justifyContent: 'flex-end',
    minHeight: 0,
  },
  linkText: {
    color: colors.mainColorPrimary,
    textAlign: 'right',
  },
  separator: {
    width: 16,
  },
  statusBadge: {
    paddingHorizontal: 8,
    paddingVertical: 0,
    borderRadius: 4,
  },
  statusText: {
    fontSize: 12,
    fontWeight: '500',
  },
} as const;

export default TaskInformation;
