import notifee from '@notifee/react-native';
import { EventEmitter } from 'events';

class BadgeService {
  private static count = 0;
  private static emitter = new EventEmitter();

  // Gi khi app start  ly badge hin ti t OS
  static async init() {
    this.count = await notifee.getBadgeCount();
    this.emitChange();
  }

  static getCount() {
    return this.count;
  }

  static onChange(listener: (count: number) => void) {
    this.emitter.on('change', listener);
    return () => this.emitter.off('change', listener);
  }

  private static emitChange() {
    this.emitter.emit('change', this.count);
  }

  static async setCount(count: number) {
    this.count = count;
    await notifee.setBadgeCount(count);
    this.emitChange();
  }

  static async increase(amount = 1) {
    await this.setCount(this.count + amount);
  }

  static async decrease(amount = 1) {
    const newCount = Math.max(0, this.count - amount);
    await this.setCount(newCount);
  }

  static async clear() {
    await this.setCount(0);
  }
}

export default BadgeService;
