// ============================================================================
// ENUMS
// ============================================================================

// Job Type
export enum JobTypeEnum {
  OTHER = 0,
  WORK_ORDER = 1,
  WARRANTY = 2,
}

// Job Category
export enum JobCategoryEnum {
  LABOR_AND_MATERIAL = 0,
  LABOR_SKU_ONLY = 1,
}

// User Role
export enum UserRoleEnum {
  INSPECTOR = 1,
  LEADER = 2,
}

// Company Role Type
export enum CompanyRoleType {
  OTHER = 0,
  LEADER = 1,
  INSPECTOR = 2,
  SUPPLIER = 3,
  INSTALLER = 4,
}

// Work Order Status
export enum WorkOrderStatus {
  PENDING = 0,
  IN_PROGRESS = 1,
  COMPLETED = 2,
  PENDING_REVIEW = 3,
  IN_COMPLETE = 4,
}

// Activity Status
export enum ActivityStatus {
  PENDING = 0,
  IN_PROGRESS = 1,
  COMPLETED = 2,
}

// Activity Type
export enum ActivityTypeEnum {
  OTHER = 0,
  DELIVERY = 1,
  INSPECTION = 2,
  INSTALLATION = 3,
  JOB_READINESS = 4,
}

// Delivery Status
export enum DeliveryStatus {
  UNDELIVERED = 0,
  OUT_FOR_DELIVERY = 1,
  DELIVERED = 2,
  PENDING_REVIEW = 3,
  IN_COMPLETE = 4,
}

// QI Status
export enum QIStatus {
  PENDING = 0,
  INCOMPLETE_ITEMS = 1,
  IN_QI_REINSPECTION = 2,
  COMPLETED = 3,
}

// Purchase Order Type
export enum PurchaseOrderType {
  WORK_ORDER = 0,
  PURCHASE_ORDER = 1,
}

// Highlight Status
export enum HighlightStatus {
  NONE = 0,
  IN_QI_REVIEW = 1,
  ATTENTION_REQUIRED = 2,
  AWAITING_REINSPECTION = 3,
  NEED_QI_REVIEW = 4,
  AWAITING_CORRECTION = 5,
  NEEDS_REINSPECTION = 6,
}

// Warranty Status
export enum WarrantyStatus {
  PENDING = 0,
  WAITING_ACCEPTANCE = 1,
  ACCEPTED = 2,
  REJECTED = 3,
  PENDING_REVIEW = 4,
  COMPLETED = 5,
  IN_COMPLETE = 6,
  PENDING_REVIEW_BUILDER = 7,
}

// Delivery Job Type
export enum DeliveryJobType {
  OTHER = 0,
  PURCHASE_ORDER = 1,
  VPO = 2,
}

// Job Tab Status
export enum JobStartTabStatus {
  NOT_STARTED = 0,
  IN_PROGRESS = 1,
  COMPLETED = 2,
}

