// Component: Warranty - needs community, date, status
export interface WarrantyData {
  communityName?: string;
  estStartDate?: string;
  warrantyStatus?: WarrantyStatus;
  warrantyStatusName?: string;
}

// Component: JobStatus - needs all status-related fields
export interface JobStatusData {
  // Ready checklist
  jobReadyEstStart?: string | null;
  jobReadyActStart?: string | null;
  jobReadyActEnd?: string | null;

  // Delivery
  deliveryStatus?: DeliveryStatus;
  deliveryStatusName?: string;
  deliveryEstStart?: string | null;
  deliveryActStart?: string | null;
  deliveryActEnd?: string | null;

  // Installation
  installationEstStart?: string | null;
  installationActStart?: string | null;
  installationActEnd?: string | null;

  // QI
  qiStatus?: QIStatus;
  qiStatusName?: string;
  qiEstStart?: string | null;
  qiActStart?: string | null;
  qiActEnd?: string | null;
  qiIncompleteItems?: any;

  // Activity
  activityTypeEnum?: ActivityTypeEnum;

  // Role
  roleType?: UserRoleEnum;

  // Progress
  currentSqftPercent?: number | null;
}

// Component: JobInfo - needs amount, checklist, lotNumber
export interface JobInfoData {
  totalAmount?: number;
  bonusAmount?: number | null;
  checklistChecked?: number;
  checklistTotal?: number;
  lotNumber?: string;
}

