import React, { useEffect, useRef, useState } from 'react';
import {
  Modal,
  StyleSheet,
  TouchableOpacity,
  View,
  Platform,
  Text,
  SafeAreaView,
} from 'react-native';
import { SvgXml } from 'react-native-svg';
import Video from 'react-native-video';
import { FileViewerProps } from '../../types';
import { useFileInfo } from '../../hooks/useFileInfo';
import DocumentViewer from '../DocumentViewer';
import { SCREEN_HEIGHT, SCREEN_WIDTH } from '@gorhom/bottom-sheet';
import ImageView from 'react-native-image-viewing';
import { ActivityIndicator } from 'react-native-paper';
import { IconClose } from '../../../../assets/svg';
import { colors } from '../../../../theme';
import { Button } from 'tradewize-component';
type Stream = {
  resolution: string; // e.g. "1920x1080"
  uri: string; // e.g. "1080p.m3u8"
};

const getBaseUrl = (url: string) => {
  const parts = url.split('/');
  parts.pop();
  return parts.join('/');
};

const parseMasterPlaylist = (text: string): Stream[] => {
  const lines = text.split('\n');
  const streams: Stream[] = [];

  for (let i = 0; i < lines.length; i++) {
    if (lines[i].startsWith('#EXT-X-STREAM-INF')) {
      const resolutionMatch = lines[i].match(/RESOLUTION=(\d+x\d+)/);
      const resolution = resolutionMatch ? resolutionMatch[1] : 'Unknown';
      const uri = lines[i + 1]; // dng tip theo l uri con
      if (uri && !uri.startsWith('#')) {
        streams.push({ resolution, uri });
      }
    }
  }
  return streams;
};

const FileViewer: React.FC<FileViewerProps> = ({ visible, onClose, file }) => {
  const videoRef = useRef<any>(null);

  const [isLoading, setIsLoading] = useState(true);
  const [error, setError] = useState<string | null>(null);
  const { fileInfo, isValid } = useFileInfo(file);
  // const [streams, setStreams] = useState<Stream[]>([]);
  const [selectedUri, setSelectedUri] = useState<string | null>(null);
  const [currentTime, setCurrentTime] = useState(0);
  const [shouldSeek, setShouldSeek] = useState(false);

  useEffect(() => {
    const loadPlaylist = async () => {
      try {
        const linkVideo = fileInfo.uri || fileInfo.url || '';
        const isBunnyLink = linkVideo?.split('/').pop()?.includes('.m3u8');
        if (isBunnyLink) {
          const res = await fetch(linkVideo);
          const text = await res.text();
          const parsed = parseMasterPlaylist(text);

          // setStreams(parsed);
          if (parsed.length > 0) {
            const bestQuality = parsed.reduce((max, current) => {
              const [_, maxH] = max.resolution.split('x').map(Number);
              const [__, currH] = current.resolution.split('x').map(Number);
              return currH > maxH ? current : max;
            });
            setSelectedUri(bestQuality.uri);
          }
        }
      } catch (err) {
        console.error('Li khi load master playlist:', err);
      }
    };

    loadPlaylist();
  }, [fileInfo.uri, fileInfo.url]);

  if (!isValid) {
    return null;
  }

  const renderCloseButton = () => {
    return (
      <Button style={styles.closeButton} onPress={onClose}>
        <SvgXml xml={IconClose(colors.white)} width={24} height={24} />
      </Button>
    );
  };

  const renderContent = () => {
    if (fileInfo.type === 'video') {
      const linkVideo = fileInfo.uri || fileInfo.url || '';
      const isBunnyLink = linkVideo?.split('/').pop()?.includes('.m3u8');
      const baseUrl = getBaseUrl(linkVideo);
      const fullVideoUrl = selectedUri
        ? `${baseUrl}/${selectedUri}`
        : linkVideo;

      if (isBunnyLink && fullVideoUrl) {
        return (
          <SafeAreaView style={styles.baseVideoContainer}>
            {renderCloseButton()}

            {isLoading && (
              <View style={styles.loadingContainer}>
                <ActivityIndicator size="large" color={colors.white} />
              </View>
            )}
            <Video
              // ref={ref => (videoRef.current = ref)}
              source={{ uri: fullVideoUrl }}
              style={styles.videoBunny}
              resizeMode="contain"
              controls={true}
              onBuffer={({ isBuffering }) => setIsLoading(isBuffering)}
              onLoadStart={() => setIsLoading(true)}
              // eslint-disable-next-line @typescript-eslint/no-shadow
              onProgress={({ currentTime }) => setCurrentTime(currentTime)}
              onLoad={() => {
                if (shouldSeek && videoRef.current) {
                  videoRef.current.seek(currentTime);
                  setShouldSeek(false);
                }
              }}
            />
          </SafeAreaView>
        );
      }
      // if (fileInfo.isYoutube) {
      //   const youtubeId = getYouTubeVideoId(fileInfo.uri || '');
      //   return (
      //     <>
      //       {renderCloseButton()}
      //       <View style={styles.youtubeContainer}>
      //         {youtubeId ? (
      //           <YoutubePlayer
      //             height={300}
      //             width={SCREEN_WIDTH - 40}
      //             videoId={youtubeId || ''}
      //             onReady={() => setIsLoading(false)}
      //             onError={error => {
      //               setError(error);
      //               setIsLoading(false);
      //             }}
      //           />
      //         ) : null}
      //       </View>
      //     </>
      //   );
      // }
      return (
        <>
          {renderCloseButton()}
          <Video
            source={{ uri: fileInfo.uri }}
            style={styles.video}
            resizeMode="contain"
            controls={true}
            onLoad={() => setIsLoading(false)}
          />
        </>
      );
    }

    if (fileInfo.type === 'document') {
      const linkVideo = fileInfo.uri || fileInfo.url || '';
      const isBunnyLink = linkVideo?.split('/').pop()?.includes('.m3u8');
      const baseUrl = getBaseUrl(linkVideo);
      const fullVideoUrl = selectedUri
        ? `${baseUrl}/${selectedUri}`
        : linkVideo;

      if (isBunnyLink && fullVideoUrl) {
        return (
          <SafeAreaView style={styles.baseVideoContainer}>
            {renderCloseButton()}

            {isLoading && (
              <View style={styles.loadingContainer}>
                <ActivityIndicator size="large" color={colors.white} />
              </View>
            )}
            <Video
              // ref={ref => (videoRef.current = ref)}
              source={{ uri: fullVideoUrl }}
              style={styles.videoBunny}
              resizeMode="contain"
              controls={true}
              onBuffer={({ isBuffering }) => setIsLoading(isBuffering)}
              onLoadStart={() => setIsLoading(true)}
              // eslint-disable-next-line @typescript-eslint/no-shadow
              onProgress={({ currentTime }) => setCurrentTime(currentTime)}
              onLoad={() => {
                if (shouldSeek && videoRef.current) {
                  videoRef.current.seek(currentTime);
                  setShouldSeek(false);
                }
              }}
            />
          </SafeAreaView>
        );
      }

      return (
        <DocumentViewer
          fileInfo={fileInfo}
          onLoadEnd={() => setIsLoading(false)}
          onError={errorMessage => setError(errorMessage)}
          onClose={onClose}
        />
      );
    }

    return null;
  };

  return (
    <View style={styles.container}>
      {fileInfo.uri && fileInfo.type === 'photo' ? (
        <ImageView
          images={[
            {
              uri: fileInfo.uri,
            },
          ]}
          imageIndex={0}
          visible={visible}
          onRequestClose={onClose}
        />
      ) : (
        <Modal
          visible={visible}
          transparent={true}
          animationType="fade"
          onRequestClose={onClose}>
          <View style={styles.container}>
            {error ? (
              <View style={styles.errorContainer}>
                {renderCloseButton()}
                <Text style={styles.errorText}>{error}</Text>
              </View>
            ) : (
              renderContent()
            )}
          </View>
        </Modal>
      )}
    </View>
  );
};

const styles = StyleSheet.create({
  container: {
    flex: 1,
    backgroundColor: colors.black,
  },
  closeButton: {
    position: 'absolute',
    paddingHorizontal: 0,
    paddingVertical: 0,
    minHeight: 0,
    top: Platform.OS === 'ios' ? 50 : 20,
    right: 20,
    zIndex: 1,
    width: 40,
    height: 40,
    borderRadius: 20,
    backgroundColor: 'rgba(0,0,0,0.5)',
    justifyContent: 'center',
    alignItems: 'center',
  },
  video: {
    flex: 1,
    backgroundColor: colors.black,
  },
  webview: {
    flex: 1,
    backgroundColor: colors.black,
  },
  image: {
    flex: 1,
    backgroundColor: colors.black,
  },
  errorContainer: {
    flex: 1,
    justifyContent: 'center',
    alignItems: 'center',
    padding: 20,
  },
  errorText: {
    color: colors.white,
    textAlign: 'center',
  },
  youtubeContainer: {
    flex: 1,
    backgroundColor: colors.black,
    justifyContent: 'center',
    alignItems: 'center',
  },
  baseVideoContainer: {
    flex: 1,
    alignItems: 'center',
    justifyContent: 'center',
  },
  settingsButton: {
    position: 'absolute',
    right: 56,
  },
  videoBunny: {
    height: SCREEN_HEIGHT * 0.5,
    width: '100%',
  },
  loadingContainer: {
    flex: 1,
    alignItems: 'center',
    justifyContent: 'center',
  },
});

export default FileViewer;
