import React, { useMemo } from 'react';
import { View, StyleSheet } from 'react-native';
import { SvgXml } from 'react-native-svg';
import FastImage from 'react-native-fast-image';
import { FileInfo, FileThumbnailSize } from '../../types';
import { colors } from '../../../../theme';
import { IconPlay } from '../../../../assets/svg';
import { thumbnail } from '../../../../assets/images';

interface VideoThumbnailProps {
  file: FileInfo;
  size: FileThumbnailSize;
}

const VideoThumbnail: React.FC<VideoThumbnailProps> = ({ file, size }) => {
  const styles = getStyles();

  const thumbnailUrl = useMemo(() => {
    return file.thumbnailUrl || file.thumbnail || file.poster || file.url || file.uri;
  }, [file]);

  const isValidUrl = useMemo(() => {
    return thumbnailUrl && typeof thumbnailUrl === 'string' && thumbnailUrl.trim() !== '';
  }, [thumbnailUrl]);

  return (
    <View style={styles.mediaVideoContainer}>
      <FastImage
        source={isValidUrl ? { uri: thumbnailUrl } : thumbnail}
        style={styles.mediaVideo}
        resizeMode={FastImage.resizeMode.cover}
        defaultSource={thumbnail}
      />
      <View style={styles.videoOverlay}>
        <SvgXml
          xml={IconPlay(colors.white)}
          width={30}
          height={30}
        />
      </View>
    </View>
  );
};

const getStyles = () =>
  StyleSheet.create({
    mediaVideoContainer: {
      position: 'relative',
      width: '100%',
      height: '100%',
    },
    mediaVideo: {
      width: '100%',
      height: '100%',
    },
    videoOverlay: {
      ...StyleSheet.absoluteFillObject,
      justifyContent: 'center',
      alignItems: 'center',
      backgroundColor: 'rgba(0,0,0,0.2)',
    },
  });

export default VideoThumbnail;
